<template>
<div class="mess">
  <div class="header">
    <span class="back" @click="goback"><img src="../../assets/my/back.png" alt=""  ></span>
    <span>信息管理</span>
  </div>
  <div class="marinLogo clearfix">
    <p>店铺logo</p>
    <div class="img"><img :src="dataForm.logoImgUrl" alt="" class="logo"></div>
  </div>
  <mt-cell title="名称" :value="dataForm.name"></mt-cell>
  <mt-cell title="联系电话" :value="dataForm.shopPhoneNum"></mt-cell>
  <mt-cell title="联系地址">{{dataForm.address}}</mt-cell>
  <mt-cell title="标签" :value="dataForm.tags"></mt-cell>
  <mt-cell title="所属广场" v-if="isMarket">{{Market}}</mt-cell>
  <mt-cell title="展示状态">{{dataForm.showed ? '展示' : '不展示'}}</mt-cell>
  <mt-cell title="所属儿童分类" v-if="isChild">美食</mt-cell>
  <div class="maindesc">
    <p style="float: left">店铺详情</p>
    <p>&nbsp;</p>
    <p>&nbsp;</p>
    <div class="maindesc_info" v-html="dataForm.shopDesc">
    </div>
  </div>
  <div class="bottom_btn">
    <mt-button @click="godetail" size="large" type="primary">修改信息</mt-button>
  </div>
</div>
</template>
<script>
import { Base64 } from 'js-base64'
import { Toast } from 'mint-ui'
export default {
  data() {
    return {
      value: '',
      isMarket: false,
      isChild: false,
      dataForm: {
        logoImgUrl: '',
        name: '',
        shopPhoneNum: '',
        address: '',
        tags: '',
        showed: false,
        shopDesc: ''
      },
      Market: '',
      MarketId: 0,
      marketList: []
    }
  },
  methods: {
    getData() {
      this.$http({
        url: this.$http.adornUrl(this.apiList.shop.get),
        method: 'post',
        data: this.$http.adornData('', this.apiList.shop.get, true)
      }).then(({ data }) => {
        if (data.result) {
          this.dataForm = data.data
          this.dataForm.tags = data.data.tags.length === 2 ? data.data.tags[0].name + ' ' + data.data.tags[1].name : data.data.tags.length === 1 ? data.data.tags[0].name : data.data.tags.length === 0 ? '' : ''
          this.dataForm.shopDesc = Base64.decode(data.data.shopDesc)
          localStorage.setItem('logoUrl', data.data.logoImgUrl)
          localStorage.setItem('shopName', data.data.name)
          // 如果marketID不为空
          this.getMarketList(data.data.superModuleId)
          console.log('--------------------------')
          if (data.data.mallModuleId) {
            this.isMarket = true
            this.MarketId = Number(data.data.mallModuleId)
          }
        } else {
          if (data.msg === 'sign验证失败') {
            localStorage.clear()
            const instance = Toast({
              message: '您的账号登录超时',
              iconClass: 'icon icon-error'
            })
            setTimeout(() => {
              instance.close()
              this.$router.push({ name:  'login' })
            }, 2000)
          }
        }
      })
    },
    getMarketList(superModuleId) {
      this.$http({
        url: this.$http.adornUrl(this.apiList.shop.marketList),
        method: 'post',
        data: this.$http.adornData({ moduleId: superModuleId}, this.apiList.shop.marketList, true)
      }).then(({ data }) => {
        if (data.result) {
          this.marketList = data.data
          if (this.MarketId) {
            this.getMaketName()
          }
        }
      })
    },
    getMaketName() {
      this.marketList.map((v, index) => {
        if (Number(v.id) === this.MarketId) {
          this.Market = v.name
        }
        if (!this.Market){
          this.isMarket = false
        }
      })
    },
    goback() {
      this.$router.goback()
    },
    godetail() {
      this.$router.push({ name:'messDetail', params: {'shopInfo': this.dataForm, 'marketList': this.marketList }})
    }
  },
  created() {
    this.getData()
  }
}
</script>
<style lang="scss">
  .mess{
    .marinLogo{
      margin: 1.6rem auto;
      .img{
        float: right;
        display: block;
        width: 6rem;
        height: 6rem;
        line-height: 6rem;
        border: solid 0.1rem #D7D7D7;
        margin-right: 1.6rem;
        text-align: center;
        overflow: hidden;
        .logo{
          width: 100%;
          display: inline-block;
          vertical-align: middle;
        }
      }
      p{
        float: left;
        padding-left: 10px;
      }
    }
    .maindesc{
      box-sizing: border-box;
      padding: 10px;
      .maindesc_info{
        box-sizing: border-box;
        width: 100%;
        padding: 1.6rem;
        // text-indent: 2em;
        border: solid 0.1rem #d7d7d7;
        img{
          max-width: 100%;
        }
      }
    }
    .bottom_btn{
      width: 100%;
      box-sizing: border-box;
      padding: 0 10px;
      margin-top: 2.2rem;
      margin-bottom: 2.2rem;
    }
  }
</style>



