<template>
<div class="address_map">
  <div class="header">
    <span class="back" @click="goback"><img src="../../assets/my/back.png" alt="" ></span>
    <span>确认店铺地址</span>
    <!-- <span class="sure"><div @click="finish">确定</div></span> -->
  </div>
  <div :class="{'nav': true, 'isSearch': isSearch}" >
    <p><span>福州</span><span><i class="search"></i><input  v-model="address" @focus="isSearch = true; address = ''" type="text" placeholder="小区/写字楼/学校等" @keyup.enter="changeMap"></span><i class="close"></i></p>
    <div v-if="isSearch" class="cancel" @click="isSearch = false;address=''; dataList=[] ">取消</div>
  </div>
  <map-box v-show="!isSearch" ref="map" class="mapBox" :address='address' :lat="lat" :lng="lng" @changePoint="changePoint" @getDataList="getDataList"/>
  <div class="addressListNoSearch" v-show="!isSearch">
    <li v-for="item in dataList2" :key="item.id" @click="chooseAdd(item)">
      <p>{{ item.title }}</p>
      <p>{{ item.address }}</p>
    </li>
  </div>
  <div class="addressList" v-show="isSearch">
    <li v-for="item in dataList" :key="item.id" @click="chooseAdd(item)">
      <p>{{ item.title }}</p>
      <p>{{ item.address }}</p>
    </li>
  </div>
</div>
</template>
<script>
import mapBox from '../utils/map'
import { eventBus } from '../../utils/eventBus.js'
export default {
  data() {
    return {
      isSearch: false,
      address: '',
      lng: '',
      lat: '',
      dataList: [],
      dataList2: []
    }
  },
  components:{
    mapBox
  },
  methods: {
    goback() {
      this.$router.goback()
    },
    changePoint(data) {
      this.lat = data.lat
      this.lng = data.lng
      console.log(data.address.surroundingPois)
      this.dataList2 = data.address.surroundingPois
    },
    changeMap() {
      this.$refs.map.getAddList(this.address)
    },
    chooseAdd(data) {
      this.address = data.title
      this.lat = data.lat ? data.lat : data.point.lat
      this.lng = data.lng ? data.lng : data.point.lng
      // this.isSearch = false
      // this.$refs.map.changeAdd(this.lng, this.lat)
      this.finish()
    },
    getDataList(val) {
      console.log(val)
      this.dataList = []
      const data = val.map((v, i, a) => {
        let arr = v.split(',')
        const obj = {
          title: '',
          address: '',
          lat: '',
          lng: ''
        }
        obj.id = i
        obj.title = arr[0]
        obj.address = arr[1] ? arr[1] : ''
        obj.lat = arr[2] ? arr[2] : ''
        obj.lng = arr[3] ? arr[3] : ''
        this.dataList.push(obj)
      })
    },
    finish() {
      
      if(this.$route.params.activity == 'activity'){
        localStorage.setItem('address', this.address)
        localStorage.setItem('lat', this.lat)
        localStorage.setItem('lng', this.lng)
        this.goback()
        return
      }
      
      const vm = this
      const url = this.apiList.shop.edit
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData({address: this.address, lng: this.lng, lat: this.lat }, url , true)
      }).then(({ data }) => {
        if (data.result) {
          vm.goback()
        } else {
          if (data.msg === 'sign验证失败' || data.msg === '请检查userId或sign参数') {
            localStorage.clear()
            const instance = this.$toast({
              message: '您的账号登录超时',
              iconClass: 'icon icon-error'
            })
            setTimeout(() => {
              instance.close()
              this.$router.push({ name:  'login' })
            }, 1000)
          } else {
            this.$toast(res.msg)
          }
        }
      })
    }
  },
   mounted() {
    const address = this.$route.params.address
    console.log(address)
    if (address !== '未填写') {
      this.address = address
    } else {
      this.address = '福州市'
    }
    if(!address){
      let vm = this
      eventBus.$on('firstAddress', function(e) {
        vm.address = e.address
        eventBus.$off()
      }) 
    }
    
    this.lat = this.$route.params.lat
    this.lng = this.$route.params.lng
  }
}
</script>
<style lang="scss" scope>
.address_map{
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  ::-webkit-input-placeholder { /* WebKit browsers */
    color:    #999;
  }
  :-moz-placeholder { /* Mozilla Firefox 4 to 18 */
      color:    #999;
  }
  ::-moz-placeholder { /* Mozilla Firefox 19+ */
      color:    #999;
  }
  :-ms-input-placeholder { /* Internet Explorer 10+ */
      color:    #999;
  }
  .header{
    position: fixed;
    top: 0;
  }
  .sure{
    float: right; 
    margin-top: .1rem;
    color: #fff;
  }
  .nav{
    width: 36rem;
    margin: 1.5rem auto;
    height: 4.4rem;
    line-height: 4.4rem;
    position: fixed;
    top: 4rem;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1000;
    // background: #fff;
    transition: all .3s;
    p{
      background: #fff;
      border-radius: 2.2rem;
      border: solid 1px #eee;
      height: 4.4rem;
      box-sizing: border-box;
      padding: 0 1rem;
      span{
        display: inline-block;
        .search{
          display: inline-block;
          width: 1.5rem;
          height: 1.5rem;
          margin-left: 1rem;
          vertical-align: middle;
          background: url('../../assets/my/search.png');
          background-size: 100% 100%;
        }
      }
      span:nth-child(1){
        width: 5rem;
        text-align: center;
        border-right: solid .1rem #999;
        line-height: 1.65rem;
        height: 1.65rem;
        vertical-align: middle;
      }
      input{
        border: none;
        color: #666;
        background: #fff;
        vertical-align: middle;
        margin-left: 1rem;
        outline: none;
        width: 22rem;
        height: 3.6rem;
      }
    }
  }
  .isSearch{
    p{
      display: inline-block;
      width: 85%;
      input{
        width: 18rem;
      }
    }
    .cancel{
      display: inline-block;
      padding-left: 1.5rem;
      color: #3385ff;
    }
  }
  .mapBox{
    position: absolute;
    // bottom: 40%;
    height: 20rem;
    width: 100%;
    top: 12rem;
  }
  .addressList{
    position: absolute;
    top: 12rem;
    width: 100%;
    li{
      box-sizing: border-box;
      padding: 0 1.5rem;
      margin-bottom: 1.5rem;
      p{
        color: #666;
        font-size: 1.6rem; 
        width: 100%;
      }
      p:nth-child(2){
        color: #999;
        font-size: 1.2rem;
        padding-bottom: 1rem;
        border-bottom: solid 0.1rem #eee; 
      }
    }
  }
  .addressListNoSearch{
    position: absolute;
    top: 33rem;
    bottom: 0;
    width: 100%;
    li{
      box-sizing: border-box;
      padding: 0 1.5rem;
      margin-bottom: 1.5rem;
      p{
        color: #666;
        font-size: 1.4rem; 
        font-weight: 600;
        width: 100%;
      }
      p:nth-child(2){
        color: #999;
        font-size: 1.2rem;
        padding-bottom: 1rem;
        border-bottom: solid 0.1rem #eee; 
      }
    }
  }
}
</style>


