<template>
  <div class="aboutZxl">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../assets/my/back.png" alt=""  ></span>
      <span>关于众享乐</span>
    </div>
    <div class="main">
      <img src="../../assets/login/logo.png" alt="">
      <p class="edition">v{{ version }}</p>
      <p class="edition">更新时间{{ time }}</p>
      <p class="update" v-show="false" @click="getUpdata">检查更新</p>
    </div>
    <!-- <div class="copyright">
      <p>版权</p>
      <p>版权信息</p>
    </div> -->
  </div>
</template>
<script>
import { MessageBox } from 'mint-ui'
export default {
  data() {
    return{
      version: localStorage.getItem('app_version'),
      time: '2019-7-30'
    }
  },
  methods: {
    goBack(){
      this.$router.goback()
    },
    getUpdata() {
      const url = this.apiList.version
      this.$http({
        url: 'http://j.chnzxl.com/entryc' + url,
        method: 'post',
        data: this.$http.adornData({appType: localStorage.getItem('app_type'), platform: 2, versionCode: localStorage.getItem('appCurVersionNum')}, '' , false)
        // data: this.$http.adornData({appType: 1, platform: 2, versionCode: '1'}, '' , false)
      }).then(({ data }) => {
        if (data.result) {
          if (data.data.isUpdate) {
            const updatedes = Base64.decode(data.data.updatedes)
            MessageBox.confirm(updatedes, '有新版本，是否前往更新').then(() => {
              this.android.toUrl("http://f.chnzxl.com/download/index-b.html")
            })
          }
        }
      }).catch((err) => {
        this.$toast(err)
      })
    }
  }
}
</script>
<style lang="scss">
    .aboutZxl{
      min-height: 100%;
      background: #f5f5f5;
      .main{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        text-align: center;
        img{
          width: 4rem;
        }
        .update{
          height: 2.5rem;
          margin-top: 3rem;
          line-height: 2.5rem;
          border: 1px solid #e8e8e8;
          text-align: center;
       }
       .edition{
          padding-top: 1rem;
        }
        .copyright{
          position: absolute;
          bottom: 4rem;
          left: 50%;
          transform: translate(-50%)
        }
      }
    }
</style>


