      <span class="back" @click="goBack"><img src="../../assets/my/back.png" ></span>
      <p>BOSS资金(元)</p>
      <div class="top_moeny">{{sysTotal ? sysTotal : 0}}</div>
      <div class="top_classify">
        <div class="top_zfb top_solid">
          <div class="top_zfb_title">
            <img src="../../assets/capital/zfb.png" alt="">支付宝余额
          </div>
          <div class="top_zfb_moeny">
            {{moneyAli ? moneyAli : 0}}
          </div>
          <span>{{sysAliTotal ? sysAliTotal : 0}}</span>
        </div>
        <div class="top_zfb">
          <div class="top_zfb_title">
            <img src="../../assets/capital/wx.png" alt="">微信余额
          </div>
          <div class="top_zfb_moeny">
            {{moneyWx ? moneyWx : 0}}
          </div>
          <span>{{sysWxTotal ? sysWxTotal : 0}}</span>
        </div>
      </div>
