    </div>
    <div class="all_list" ref="all_list">
      <img src="../../assets/capital/noData.png" alt="" v-if="isShow">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="————没有更多了————"
        @load="onLoad"
      >
       <div class="capital_list" v-for="item in listMoney" :key="item.id">
        <div class="capital_list_top">
          <span>{{item.realName ?  item.realName : (item.outType == '101'?"支付宝":'微信')}}</span>
          <span class="capital_right">{{item.num/100}}</span>
        </div>
        <p>
          <span class="capital_list_time">{{item.createdAt}}</span>
          <span class="capital_list_channel" v-if="item.outType == '101'" >支付宝</span>
          <span class="capital_list_wx" v-if="item.outType == '1'">微信</span>
          <span class="capital_list_class" v-if="item.num<0">取</span>
          <span class="capital_list_class" v-else>存</span>
        </p>
      </div>
      <!--<div class="capital_list">
        <div class="capital_list_top">
          <span>888888888@qq.com</span>
          <span class="capital_right">788.63</span>
