        </p>
      </div>  -->
      </van-list>
    </div>
    <!-- -->
    <div class="capital_button">
      <div class="capital_deposit" @click="save">存入</div>
      <div class="capital_deposit capital_fetch" @click="fetch">取出</div>
    </div>
  </div>
</template>
<script>
export default {
  data(){
    return{
      isLoading: false,
      loading: false,
      finished: false,
      isShow: false,
      page: 0,
      moneyAli: '',
      moneyWx: '',
      sysAliTotal: '',
      sysTotal: '',
      sysWxTotal: '',
      listMoney:[]
    }
  },
  methods:{
    goBack(){
      this.$router.goback()
    },
    getMoneyNum(){
      const url = this.apiList.capital.getMoneyNum
      this.$http({
        url: this.$http.adornUrl(url),
        method: "post",
        data: this.$http.adornData( '', url, true)
      }).then(({data : res})=>{
         if (!res.result) {
          if(res.msg == 'sign验证失败'){
            this.$toast('您的账户登录超时')
            setTimeout(()=>{
              localStorage.clear();
              this.$router.push({name: 'login'})
            },1000)
          }
          return console.log("请求接收失败")
        }
        this.moneyAli = res.data.totalMoneyAli/100
        this.moneyWx = res.data.totalMoneyWx/100
        this.sysTotal = res.data.sysTotal/100
        this.sysAliTotal = res.data.sysAliTotal/100
        this.sysWxTotal = res.data.sysWxTotal/100
      })
    },
    onLoad(){
       return new Promise((resolve, reject) => {
        const url = this.apiList.capital.list
        this.page ++
        this.$http({
          url: this.$http.adornUrl(url),
          method: "post",
          data: this.$http.adornData({ page: this.page, size: 10 }, url, true)
        }).then(( {data : res} )=>{
          if (!res.result) {
            if(res.msg == 'sign验证失败'){
              this.$toast('您的账户登录超时')
              setTimeout(()=>{
                localStorage.clear();
                this.$router.push({name: 'login'})
              },1000)
            }
          return console.log("请求接收失败")
          } 
          this.listMoney = this.listMoney.concat(res.data.records)
          if(res.data.total == 0){
            this.isShow = true
          }
          if (res.data.pages <= this.page) {
            this.finished = true
          }
          this.$nextTick(() => {
            resolve(true)
            this.loading = false
          })
        })
       })
    },
    save(){
      this.$router.push( {name:'access',params : { id: 'save'}})
    },
    fetch(){
      this.$router.push( {name:'access',params : { id: 'fetch'}})
    }
  },
  created(){
    this.getMoneyNum()
  },
  mounted(){
    console.log(document.body.clientHeight)
    // console.log()
    var all_list = this.$refs.all_list
    console.log(this.$refs.all_list.offsetTop)
    console.log(this.$refs.all_list.style.height)
    all_list.style.height = document.body.clientHeight - all_list.offsetTop-55 +'px'
  }
}
</script>
<style lang="scss">
.capital{
  background-color: #F5F5F5;
  height: 100%;
  .back{
    position: absolute;
    left: 2rem;
    top: 2.8rem;
    width: 4rem;
    height: 4rem;
    text-align: left;
    img{
      width: 1rem;
    }
  }
  .top{
    width: 100%;
    height: 16.8rem;
    background-image: url('../../assets/capital/bgi.png');
    background-size: contain;
    text-align: center;
    // overflow: hidden;
    padding-top: 1px;
    >p{
      margin-top: 2.8rem;
      font-size: 1.2rem;
      color: #fff;
    }
    >.top_moeny{
      font-size: 4.4rem;
      color: #fff;
      margin-top: 1.85rem;
    }
    >.top_classify{
      width: 32rem;
      height: 6.3rem;
      background-color: #fff;
      margin: 0 auto;
      margin-top: 2rem;
      box-sizing: border-box;
      padding-top: 1.3rem;
      padding-bottom: 1.2rem;
      display: flex;
      >.top_zfb{
        width: 50%;
        // height: 100%;
        // height: 5rem;
        position: relative;
        span{
          font-size: 1.2rem;
          // margin-top: -.2rem;
          display: block;
          position: absolute;
          bottom: -1.5rem;
          width: 100%;
          text-align: center;
        }
        .top_zfb_title{
          font-size: 1rem;
          color: #666666;
          margin-top: -.5rem;
          img{
            width: 1.2rem;
            vertical-align: middle;
            margin-right: .2rem;
          }
        }
        .top_zfb_moeny{
          color: #171717;
          font-size: 1.7rem;
          margin-top: .8rem;
        }
      }
      .top_solid{
        border-right: 0.05rem solid #D7D7D7;
      }
    }
  }
  .capital_title{
    width: 100%;
    height: 3.9rem;
    background-color: #fff;
    margin-top: 4.5rem;
    box-sizing: border-box;
    padding-left: 2.2rem;
    padding-top: 1.5rem;
    font-size: 1.4rem;
    color: #171717;
    border-radius:1rem 1rem 0 0;
    img{
      width: 2.1rem;
      vertical-align: middle;
      margin-right: .6rem;
    }
  }
  .all_list{
    // height: auto;
    // height: 35.8rem;
    // height: 100%;
    // position: absolute;
    overflow:scroll;
    .van-list__finished-text{
      height: 1rem;
      line-height: 3rem;
    }
    >img{
      width: 13.4rem;
      display: block;
      margin: 3.9rem auto 2.9rem;
      
    }
  }
  .capital_list{
    width: 100%;
    background-color: #fff;
    // height: 5.8rem;
    box-sizing: border-box;
    padding-left: 2.2rem;
    overflow: hidden;
    position: relative;
    .capital_list_top{
      width: 100%;
      height: 1.5rem;
      margin-top: 1.3rem;
      font-size: 1.4rem;
      color: #000;
      .capital_right{
        position: absolute;
        right: 2.15rem;
        font-size: 1.8rem;
        font-weight: bold;
        color: #333333;
      }
    }
    >p{
      width: 100%;
      margin-top: .8rem;
      height: 1.2rem;
      padding-bottom: 1.5rem;
      border-bottom: .05rem solid #eee;
      .capital_list_time{
        color: #999999;
        font-size: 1.4rem
      }
      .capital_list_channel{
        width: 3.8rem;
        height: 1.5rem;
        line-height: 1.5rem;
        background-color: #D8EFFF;
        color: #176AA4;
        font-size: 1rem;
        display: inline-block;
        text-align: center;
        margin-left: 1.1rem;
        margin-right: 1.2rem;
      }
      .capital_list_wx{
         width: 3.8rem;
        height: 1.5rem;
        line-height: 1.5rem;
        background-color: #DCFEEC;
        color: #0C8847;
        font-size: 1rem;
        display: inline-block;
        text-align: center;
        margin-left: 1.1rem;
        margin-right: 1.2rem;
      }
      .capital_list_class{
        display: inline-block;
        width: 1.5rem;
        height: 1.5rem;
        line-height: 1.5rem;
        border: .1rem solid #D7D7D7;
        border-radius: 50%;
        font-size: 1rem;
        text-align: center;
      }
    }
  }
  .capital_button{
    width: 100%;
    height: 5.4rem;
    background-color: pink;
    display: flex;
    position: absolute;
    bottom: 0;
    .capital_deposit{
      width: 50%;
      height: 100%;
      font-size: 1.6rem;
      text-align: center;
      color: #fff;
      line-height: 5.4rem;
      background:linear-gradient(90deg,rgba(250,47,81,1),rgba(220,53,140,1));
    }
    .capital_fetch{
      background: rgba(253,85,82,1);
    }
  }
}
</style>




