<template>
	<div class="grouping_wrapper">
		<div class="header">
			<span class="back" @click="goback"><img src="../../assets/my/back.png" alt=""></span>
			<span>风采展示</span>
		</div>
		<div class="grouping">
			<!-- <div class="grouping_list" @click="$router.push({name :'photoshow', query: { id: 'photoShow' }})">默认图片</div> -->
			<div class="grouping_list" v-for="(item,index) in editableTabs" :key="index" @click="skip_photo(item.name)">
				<img src="../../assets/shopDetail/mrxc.png" alt="">
				<div>{{item.name}}</div>
				<i @click="clone(item.name)"></i>
			</div>
			<div class="grouping_list" @click="show_grouping">
				<img src="../../assets/shopDetail/adda.png" alt="">
				<div class="a">新增相册</div>
			</div>
		</div>
		<van-popup v-model="show">
			<div class="grouping_tck">
				<div class="grouping_tck_title">新增相册</div>
				<div class="grouping_tck_input">
					<label for="">相册名称</label>
					<input type="text" placeholder="请输入相册名称" v-model="grouping_name">
				</div>
				<div class="grouping_tck_footer">
					<button @click="show = false">取消</button>
					<button @click="add_grouping()">确认</button>
				</div>
			</div>
		</van-popup>
	</div>
</template>
<style>

</style>

<script>
	import {  MessageBox } from 'mint-ui'
	export default {
		data() {
			return {
				show: false,
				grouping_name: '',
				editableTabs: [],
				showAddUser: true
			}
		},
		created() {
			this.grouping()
		},
		methods: {
			grouping() {
				this.$http({
					url: this.$http.adornUrl(this.apiList.photoShow.list),
					method: 'post',
					data: this.$http.adornData('', this.apiList.photoShow.list, true)
				}).then(({
					data
				}) => {
					if (data.result) {
						this.dataList = data.data
						this.editableTabs.push({
							name: '全部',
							label: '全部',
							dataList: []
						})
						this.dataList.map(item => {
							const label = item.grouping
							const index = this.editableTabs.findIndex(item => item.name === label)
							if (index === -1) {
								const obj = {
									name: label,
									label: label,
									dataList: [{
										imgUrl: item.imgUrl,
										id: item.id
									}]
								}
								this.editableTabs.push(obj)
							} else if (label !== '全部') {
								this.editableTabs[index].dataList.push({
									imgUrl: item.imgUrl,
									id: item.id
								})
							}
							// 填入全部选项
							const _idx = this.editableTabs.findIndex(item => item.name === '全部')
							this.editableTabs[_idx].dataList.push({
								imgUrl: item.imgUrl,
								id: item.id
							})
						})
						let editableTabs = JSON.parse(localStorage.getItem('fz'))
						var set = this.editableTabs.map(item => item.label)
						console.log(set)
						var resArr = editableTabs.filter(item => !set.includes(item.label))
						console.log(resArr)
						for (var i in resArr) {
							this.editableTabs.push(resArr[i])
						}
					}
				})
			},
			show_grouping() {
				this.show = true;
			},
			add_grouping() {
				const _index = this.editableTabs.findIndex(item => item.name === this.grouping_name)
				if (this.grouping_name == '') {
					this.$toast('分组名不能为空')
					return
				}
				if (_index == -1) {
					this.editableTabs.push({
						name: this.grouping_name,
						label: this.grouping_name,
						dataList: []
					})
					localStorage.setItem('fz', JSON.stringify(this.editableTabs))
					this.$forceUpdate();
					this.show = false;
				} else {
					this.$toast('分组已存在')
					this.show = true;
				}

			},
			skip_photo(name) {
				let data
				for (let i in this.editableTabs) {
					if (name == this.editableTabs[i].name) {
						data = this.editableTabs[i]
					}
				}
				this.$router.push({
					name: 'photoshowa',
					query: {
						id: 'photoShow',
						data: data
					}
				})
			},
			goback() {
				this.$router.goback()
			},
			clone(name) {
				event.stopPropagation()
				const url = this.apiList.photoShow.del
				MessageBox.confirm('确定删除当前分组?').then(action => {
					for (var i in this.editableTabs) {
						if (name == this.editableTabs[i].name) {
							for (var j in this.editableTabs[i].dataList) {
								this.$http({
									url: this.$http.adornUrl(url),
									method: 'post',
									data: this.$http.adornData({
										id: this.editableTabs[i].dataList[j].id
									}, url, true)
								}).then(({
									data
								}) => {
									if (data.result) {
										toast({
											message: '操作成功',
											iconClass: 'fa fa-check fa-5x'
										})
										// this.dataList.splice(index, 1)
									} else {
										toast({
											message: data.msg,
											iconClass: 'icon icon-error'
										})
									}
								})
							}
						}
					}
					let editableTabs = JSON.parse(localStorage.getItem('fz'))
					if (editableTabs) {
						for (var i in editableTabs) {
							if (name == editableTabs[i].name) {
								editableTabs.splice(i, 1)
							}
						}
						localStorage.setItem('fz', JSON.stringify(editableTabs))
						this.editableTabs = editableTabs
					} else {
						let editableTabsa = this.editableTabs
						for (var i in editableTabsa) {
							if (name == editableTabsa[i].name) {
								editableTabsa.splice(i, 1)
							}
						}
						this.editableTabs = editableTabsa
					}
					// this.grouping()
				})
			}
		},
	}
</script>

<style lang="scss">
	.grouping_wrapper {
		min-height: 100%;
		background: #f5f5f5;
		.grouping {
		display: flex;
		flex-wrap: wrap;
		padding: .5rem .5rem;
	}

	.grouping_list {
		margin: .5rem;
		height: 16.6rem;
		width: 16.6rem;
		background: white;
		border-radius: 8px;
		border: .5px solid #D7D7D7;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		position: relative;
	}

	.grouping_list i {
		background: url('../../assets/index/close.png') no-repeat;
		width: 1.8rem;
		height: 1.8rem;
		position: absolute;
		right: .2rem;
		top: .2rem;
		z-index: 1;
		background-size: 100% 100%;
	}

	.grouping_list img {
		width: 4.5rem;
		height: 3.5rem;
	}

	.grouping_list div {
		color: #333333;
		font-size: 1.4rem;
		margin-top: 2rem;
		text-align: center;
	}

	.grouping_tck {
		width: 28.4rem;
		height: 20rem;
		background: white;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	.grouping_tck_input {
		display: flex;
		align-items: center;
		margin: 2rem 0;
		font-size: 1.4rem;
		color: #333333;
	}

	.van-popup--center {
		border-radius: 18px;
	}

	.grouping_tck input {
		height: 3.6rem;
		padding: 0 .5rem;
		width: 15rem;
		border: 0;
		outline: none;
		margin-left: .5rem;
	}

	.grouping_tck_title {
		font-size: 1.7rem;
	}

	.a {
		color: #999 ;
	}

	.grouping_tck_footer {
		width: 25.2rem;
		display: flex;
		justify-content: space-between;
	}

	.grouping_tck_footer button {
		/* margin-left: 2rem; */
		display: inline-block;
		width: 11.51rem;
		height: 3.6rem;
		background: #fff;
		color: #999;
		border: 0.1rem #999 solid;
		border-radius: 1.8rem;
		font-size: 1.4rem;
	}

	.grouping_tck_footer button:last-child {
		border: 0;
		background: linear-gradient(-82deg, rgba(239, 83, 80, 1), rgba(229, 57, 53, 1));
		color: #fff;
	}
	}

	
</style>



