
<template>
  <div class="activity_common_edit">
    <div class="header">
      <span class="back" @click="goBack">
        <img src="../../../assets/my/back.png" alt="">
      </span>
      <span>{{ title }}</span>
    </div>
    <div class="adp_main">
      <p>
        <span class="star">*</span>
        <span class="adp_left">名称</span>
        <input type="text" placeholder="请填写名称" v-model="name">
      </p>
      <p v-if=" activityType != 601">
        <span class="star" v-if="activityType != 301">*</span>
        <span :class="{adp_left: true, noFill: activityType == 301}">电话</span>
        <input 
          type="number" 
          placeholder="请填写电话" 
          v-model="phoneNum" 
        />
      </p>
      <p>
        <span class="star">*</span>
        <span class="adp_left">价格</span>
        <input
          type="number"
          placeholder="请填写价格"
          v-model="price"
          oninput="this.value = this.value.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3')"
        >
      </p>
      <p v-if="activityType != 101">
        <span class="star">*</span>
        <span class="adp_left">原价</span>
        <input
          type="number"
          placeholder="请填写原价"
          v-model="showPrice"
          oninput="this.value = this.value.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3')"
        >
      </p>
      <p v-if="activityType != 101">
        <span class="adp_left noFill" style="color: #ccc">商户结算价</span>
        <input
          type="number"
          placeholder="商户结算价"
          :value="moduleGetPrice"
          readonly
          style="color: #ccc"
        >
      </p>
      <p v-if="activityType == 301">
        <span class="adp_left noFill">使用商家码</span>
        <span class="adp_radio">
          <span class="yes" @click=" merchantCodeValue = true">
          <img :src="merchantCodeValue === true ? selected : noSelected" ><span class="text">是</span>
          </span>
          <span class="no" @click="merchantCodeValue = false">
            <img :src="merchantCodeValue === false ? selected : noSelected" ><span class="text">否</span>
          </span>
        </span>
      </p>
      <p class="textarea" v-if="merchantCodeValue">
        <span class="adp_left noFill merchantCodetext">商家码使用说明</span>
          <van-field class="merchantCodeInput"
            v-model="merchantCodeText"
            rows="1"
            autosize
            type="textarea"
            placeholder="请填写商家码使用说明"
          />
      </p>
      <p v-if="!merchantCodeValue && activityType != 101">
        <span class="star">*</span>
        <span class="adp_left">库存</span>
        <input
          type="number"
          placeholder="请填写库存数量"
          v-model="totalNum"
          oninput="this.value=this.value.replace(/[^0-9.]+/,'')"
        >
      </p>
      <p v-if="activityType == 301">
        <span class="star">*</span>
        <span class="adp_left">最大购买次数</span>
        <input
          type="number"
          placeholder="请填写购买次数"
          v-model="totalCanBuy"
          oninput="this.value=this.value.replace(/[^0-9.]+/,'')"
        >
      </p>
      <p v-if="activityType == 301">
        <span class="adp_left noFill">开启首单优惠</span>
        <span class="adp_radio">
          <span class="yes" @click=" firstOrderValue = true ">
          <img :src="firstOrderValue === true ? selected : noSelected" ><span class="text">是</span>
          </span>
          <span class="no" @click=" firstOrderValue = false ">
            <img :src="firstOrderValue === false ? selected : noSelected" ><span class="text">否</span>
          </span>
        </span>
      </p>
      <p v-if="firstOrderValue">
        <span class="star">*</span>
        <span class="adp_left">首单价格</span>
        <input
          type="number"
          placeholder="请填写首单价格"
          v-model="firstOrderPrice"
          oninput="this.value = this.value.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3')"
        >
      </p>
      <p v-if="!firstOrderValue && activityType == 301" >
        <span class="adp_left noFill">平台每单抽佣</span>
        <input
          type="number"
          placeholder="请填写金额"
          v-model="sysPrice"
          oninput="this.value = this.value.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3')"
        >
      </p>
      <p class="forward" @click="showChannel" v-if="activityType == 301">
        <span class="adp_left noFill" >销售渠道</span>
        <span>
          <input type="text" readonly :value=" type === 101 ? '仅App' : (type === 201 ? '仅小程序' : '全部') ">
        </span>
      </p>
      <van-popup
        v-model="showSale"
        position="bottom"
        :style="{ height: '30 %' }"
      >
        <div class="title">
          <img src="../../../assets/signUp/closeT.png" @click="showSale = false">
          <span>选择销售渠道</span>
        </div>
        <div class="popupMain">
          <div class="selectType" v-for="(item, index) in channel" :key="index" @click="typeChange = item.type">
            <span>{{ item.text }}</span><img v-if="typeChange == item.type" src="../../../assets/signUp/select.png" >
          </div>
        </div>
        <div class="popupBtn">
          <mt-button @click="submitChannelType">确认</mt-button>
        </div>
      </van-popup>
      <!-- <p v-if="activityType == 301">
        <span class="adp_left noFill">开启分销</span>
        <span class="adp_radio">
          <span class="yes" @click=" resellValue = true ">
          <img :src="resellValue === true ? selected : noSelected" ><span class="text">是</span>
          </span>
          <span class="no" @click=" resellValue = false ">
            <img :src="resellValue === false ? selected : noSelected" ><span class="text">否</span>
          </span>
        </span>
      </p> -->
     
      <p  class="forward" @click="showResellPopup" v-if="activityType == 301" >
        <span class="adp_left noFill" >分销信息</span>
        <span>
          <input type="text"  readonly :placeholder=" resellPrice  >= 1 ? '已编辑，查看详情':'点击编辑' ">
        </span>
      </p>
      <!-- 分销弹出层 -->
      <div class="resell-van-popup"  v-if="showResell">
        <div class="resell-van-popup-box">
        <h3>分销信息</h3>
        <p >
          <!-- <span class="star">*</span> -->
          <span class="adp_left">分销金额</span>
          <input
            type="number"
            placeholder="请填写金额（不低于一块）"
            v-model ="resellPrice"
            ref="resellPrice"
            oninput="this.value = this.value.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3')"
          >
        </p>
        <p>
          <!-- <span class="star startText">*</span> -->
          <span class="adp_left text" >分销文本</span>
            <van-field
              ref="textarea"
              v-model="resellerText"
              rows="1"
              autosize
              type="textarea"
              placeholder="请输入分销文本"
            />
        </p>
        <p class=" adp_img resellWrapper">
          <span class="adp_left noFill" >海报</span>
          <img class="img imgUrlResell" :src="imgUrlResell" ref="resellImg" alt="">
          <span class="addImg resell" ref="resell" @click="goMainImg('resell')"></span>
        </p>
        <div class="popupResellBtn">
          <mt-button @click="cancel">取消</mt-button><mt-button @click="confirmResell">确认</mt-button>
        </div>
        </div>
      </div>
      <!-- </van-popup> -->
      <p class="forward" v-if=" activityType != 601 ">
        <span class="star" v-if="activityType != 301">*</span>
        <span :class="{adp_left: true, noFill: activityType == 301}">地址</span>
        <input placeholder="请选择活动地址" v-model="address" @click="goAddress">
      </p>
      <p v-if="activityType != 601">
        <span class="adp_left noFill">详细地址</span>
        <input placeholder="请选择活动地址" v-model="doorAdd">
      </p>
      <p class="adp_img">
        <span class="adp_left noFill">主图</span>
        <img class="img" :src="imgUrl" ref="img" alt="">
        <span class="addImg" ref="mainImg" @click="goMainImg"></span>
      </p>
      <p class="squareWrapper">
        <span class="adp_left noFill">封面</span>
        <img class="img" ref="squareImg" :src="imgUrlSquare">
        <Upload
          :class="{square: true,  border: border}"
          :width="244"
          :height="252"
          @fileChange="fileChange"
        >
          <i class="avatar-uploader-icon" ref="square"></i>
        </Upload>
      </p>
      <p class="adp_img posterWrapper" v-if="activityType == 301">
        <span class="adp_left noFill">海报</span>
        <img class="img" :src="imgUrlPoster" ref="posterImg" alt="">
        <span class="addImg poster" ref="poster" @click="goMainImg('poster')"></span>
      </p>
      <p class="forward" v-if=" activityType != 601">
        <span class="star">*</span>
        <span class="adp_left" @click="openPicker(1)">开始时间</span>
        <input
          type="text"
          @click="openPicker(1)"
          readonly="readonly"
          placeholder="请选择"
          v-model="startTime"
        >
        <mt-datetime-picker ref="picker" type="dateTime" @confirm="confirm" :startDate="date"></mt-datetime-picker>
      </p>
      <p class="forward" v-if=" activityType != 601">
        <span class="star">*</span>
        <span class="adp_left">结束时间</span>
        <input
          type="text"
          placeholder="请选择"
          readonly="readonly"
          @click="openPicker(2)"
          v-model="endTime"
        >
      </p>
      <p class="forward" v-if=" activityType != 601">
        <span class="star">*</span>
        <span class="adp_left">凭证开始时间</span>
        <input
          type="text"
          @click="openPicker(3)"
          placeholder="请选择"
          readonly="readonly"
          v-model="startUseTime"
        >
      </p>
      <p class="forward" v-if=" activityType != 601">
        <span class="star">*</span>
        <span class="adp_left">凭证结束时间</span>
        <input
          type="text"
          @click="openPicker(4)"
          placeholder="请选择"
          readonly="readonly"
          v-model="endUseTime"
        >
      </p>
      <p class="forward" @click="godesc('text')">
        <span class="adp_left noFill" >详情</span>
        <span>
          <input type="text" readonly :placeholder=" text.length > 0 ? '已编辑，查看详情':'点击编辑' ">
        </span>
      </p>
      <p class="forward" @click="showPopup" v-if=" activityType != 601">
        <span class="adp_left noFill" >分组</span>
        <span>
          <input type="text" readonly placeholder="点击编辑" v-model="groupIdSeleceText">
        </span>
      </p>
      <!-- 弹出层 -->
      <van-popup
        v-model="show"
        position="bottom"
        :style="{ height: '43%' }"
      >
        <div class="title">
          <img src="../../../assets/signUp/closeT.png" @click="show = false">
          <span>选择分组类别</span>
        </div>
        <div class="popupMain">
          <div class="selectType" v-for="(item, index) in groupList" :key="index" @click="getGroupId(item.id, item.label)">
            <span>{{ item.label }}</span><img v-if="selectGroupId == item.id" src="../../../assets/signUp/select.png" >
          </div>
        </div>
        <div class="popupBtn">
          <mt-button @click="submitType">确认</mt-button>
        </div>
      </van-popup>
      <p class="forward" @click="godesc('prize')" v-if=" activityType == 101 || activityType == 501">
        <!-- <span class="star">*</span> -->
        <span class="adp_left noFill">奖品</span>
        <input type="text" readonly :placeholder="prizeFullText.length > 0 ? '已编辑，查看详情':'点击编辑' ">
      </p>
      <p class="forward" @click="goBuyingIns">
        <span class="adp_left noFill">购买须知</span>
        <span>
          <input type="text" readonly :placeholder=" attention.length > 0 ? '已编辑，查看详情':'点击编辑' ">
        </span>
      </p>
      <p class="forward" @click="goGroup" v-if="activityType == 101">
        <!-- <span class="star">*</span> -->
        <span class="adp_left noFill">报名小组</span>
        <input type="text" readonly :placeholder=" group.length > 0 ? '已编辑，查看详情':'点击编辑' ">
      </p>
      <p class="forward" @click="goSignUpInfo" v-if="activityType == 101">
        <!-- <span class="star">*</span> -->
        <span class="adp_left noFill">报名信息</span>
        <input type="text" readonly :placeholder=" enterMess.length > 0 ? '已编辑，查看详情':'点击编辑' ">
      </p>
      
      <p class="photos forward" @click="goPhotos">
        <span class="adp_left noFill">风采展示</span>
        <span >
          <input type="text" readonly :placeholder="photosImg.length > 0 ? '已编辑，查看详情':'点击编辑' ">
        </span>
      </p>
      <p class=" forward" @click="goSignUpInfo" v-if="activityType == 301">
        <span class="adp_left noFill">信息填写</span>
        <span >
          <input type="text" readonly :placeholder="enterMess.length > 0 ? '已编辑，查看详情':'点击编辑' ">
        </span>
      </p>
      <p v-if=" activityType != 301 ">
        <span class="adp_left noFill">是否上架</span>
        <span class="adp_radio">
          <span class="yes" @click="value = true">
          <img :src="value === true ? selected : noSelected" ><span class="text">是</span>
          </span>
          <span class="no" @click="value = false">
            <img :src="value === false ? selected : noSelected" ><span class="text">否</span>
          </span>
        </span>
      </p>
    </div>
    <div class="explain">说明：标“
      <span class="star">*</span>”为必填信息。
    </div>
    <mt-button class="btn-temp" @click="edit('temp')" v-if="!id">保存为模板</mt-button>
    <div class="pos" v-if="!id"></div>
    <mt-button class="btn" @click="edit('add')" v-if="!id">提交</mt-button>
    <mt-button class="btn-big" @click="edit('add')" v-if="id && title.includes('添加')" >提交</mt-button>
    <mt-button class="btn-big" @click="edit('edit')" v-if=" id && (title === '促销修改' || title === '编辑模板' || title === '报名修改' || title === '商品修改' || title === '活动修改') " >保存</mt-button>
  </div>
</template>
<script>
/** 
  促销 type: 301
  商品 type: 601
  报名 type: 101
  活动 type: 501 
**/
  import Upload from "../../utils/upload.vue";
  import { Base64 } from "js-base64";
  export default {
    data() {
      return {
        activityType: '',
        id: '',
        title: '',
        addScrollTop:'',
        imgUrl: "",
        imgUrlSquare: "",
        imgUrlResell: '',
        imgUrlPoster: '',
        imgUrlResellValue: '',
        name: "",
        phoneNum: "",
        firstOrderPrice: '',
        sysPrice: 0,
        img: "",
        timer: "",
        price: "",
        moduleGetPrice: '',
        moduleChou: '',
        totalCanBuy: '',
        resellerText: '',
        resellPrice: 0,
        address: "",
        doorAdd: "",
        totalNum: "",
        startTime: "",
        endTime: "",
        startUseTime: "",
        endUseTime: "",
        showPrice: "",
        text: "",
        attention: "",
        photosImg: '',
        enterMess: '',
        lng: '',
        lat: '',
        activityGroupIds: '',
        groupId: '',
        selectGroupId: '',
        groupIdText: '',
        groupIdSeleceText: '',
        textareaHeight: '',
        merchantCodeText: '',
        resellerTextValue:'',
        prizeFullText: '',
        groupList: [],
        channel: [
          {text: '全部', type: 1},
          {text: '仅app', type: 101},
          {text: '仅小程序', type: 201},
        ],
        type: 1,
        typeChange: 1,
        mintOpen: 0,
        selected: require('../../../assets/signUp/selected.png'),
        noSelected: require('../../../assets/signUp/noSelected.png'),
        group: '',
        showToC: true,
        value: false,
        date: new Date(),
        flag: false,
        border: true,
        resellBorder: true,
        posterBorder: true,
        show: false,
        showSale: false,
        showResell: false,
        resellValue: false,
        resellImg: false,
        firstOrderValue: false,
        merchantCodeValue: false,
        button: ''
      };
    },
    methods: {
      goBack() {
        this.$router.goback();
        this.clearStorage();
      },
      // 详情
      godesc(val) {
        switch(val){
          case 'text':
            this.$router.push({name:'activity_fullText', query: { fullText: val, type: 101} })
            break;
          case 'prize':
            this.$router.push({name:'activity_fullText', query: { fullText: val} })
            break;
        }
        this.setStorage();
      },
      // 购买须知
      goBuyingIns(){
        this.$router.push('templateBuyingIns')
        this.setStorage();
      },
      // 报名信息
      goSignUpInfo(){
        this.$router.push('key')
        this.setStorage()
      },
      //报名小组
      goGroup(){
        this.$router.push('signUp_item')
        this.setStorage()
      },
      // 地址
      goAddress() {
        this.$router.push({ name: "shopDetail_map", params: {activity :'activity', lng: this.lng, lat: this.lat, address: this.address } });
        this.setStorage();
      },
      // 获取主图方法
      goMainImg(title) {
        window.goMainClip = data => {
          this.$router.push({ 
            name: "main_img", 
            params: { imgdata: data, title: title } 
          });
        };
        if(this.imgUrlResell){
          localStorage.setItem('imgUrlResellValue', this.imgUrlResell)
        }
        this.setStorage();
        this.android.getImg("goMainClip");
      },
      // 风采展示
      goPhotos(){
        this.$router.push({name: 'activity_photos', query:{ type: 301}})
        this.setStorage();
      },
      // 打开时间选择器
      openPicker(val) {
        this.mintOpen = val;
        this.$refs.picker.open();
      },
      select(value){
        this.value = value
      },
      // 设置分组id
      setGroupId(){
        let groupIds = JSON.parse(localStorage.getItem('groupIds'))
        if(!groupIds) return
        groupIds.forEach(item =>{
          this.apiList.moduleList.forEach(items =>{
            if(items.id == item){
              if(items.id !== 8){
                items.children.forEach(i =>{
                  this.groupList.push(i)
                })
              }
            }
          })
        })
      },
      // 展示分组选择
      showPopup(){
        if(this.groupList.length === 0){
          this.$toast('请联系管理员添加分组')
        }
        this.show = true
        this.selectGroupId = this.groupId
      },
      // 选择类型
      submitType(){
        var market = ''
        this.groupIdSeleceText= this.groupIdText
        this.groupId = this.selectGroupId;
        this.apiList.moduleList.forEach( item => {
          if(item.id == 8){
            market = 8 + ','
          }
          item.children.forEach( items => {
            if(items.id == this.groupId){
              if(market){
                this.activityGroupIds = market + item.id + ',' + this.groupId
              }else{
                this.activityGroupIds = item.id + ',' + this.groupId
              }
            }
          })
        })
        this.show = false
        
      },
      // 取得分组类型
      getGroupId(id, text){
        this.selectGroupId = id
        this.groupIdText = text
      },
      // 展示渠道
      showChannel(){
        this.showSale = true
        this.typeChange= this.type
      },
      // 选中渠道
      submitChannelType(){
        this.showSale = false
        this.type = this.typeChange
      },
      // 展示分销
      showResellPopup(){
        this.showResell = true
        this.resellPriceValue = this.resellPrice;
        this.resellerTextValue = this.resellerText;
        if(!this.resellImg){
          this.imgUrlResell = ''
          if(this.$refs.resell){
          this.$refs.resellImg.style.display = 'none' 
          this.$refs.resell.style.opacity = 1;
          }
        }
        this.$nextTick(()=>{
          if(this.imgUrlResell){
            if(this.$refs.resell){
              this.$refs.resell.style.opacity = 0;
              this.$refs.resellImg.style.display = "inline-block";
              this.resellBorder = false;
            }
          }
        })
      },
      // 确定分销信息
      confirmResell(){
        if( Number(this.resellPrice) < 0){
          return this.$toast('分销金额不能小于0元')
        }
        this.imgUrlResellValue = this.imgUrlResell
        this.resellImg = true
        this.showResell = false
        this.$toast('已保存')
      },
      // 取消
      cancel(){
        this.resellerText = ''
        this.resellerText = this.resellerTextValue
        this.resellPrice = this.resellPriceValue;
        this.imgUrlResell = this.imgUrlResellValue
        this.showResell = false
      },
      // 时间选择器
      confirm(val) {
        switch (this.mintOpen) {
          case 1:
            this.startTime = this.setDate(val);
            break;
          case 2:
            this.endTime = this.setDate(val);
            break;
          case 3:
            this.startUseTime = this.setDate(val);
            break;
          case 4:
            this.endUseTime = this.setDate(val);
            break;
        }
      },
      // 发布提交修改
      edit(type) {
        if (this.flag) return;
        let startTime =
          new Date(this.startTime).getTime() ||
          new Date(this.startTime.replace(/\-/g, "/")).getTime();
        let endTime =
          new Date(this.endTime).getTime() ||
          new Date(this.endTime.replace(/\-/g, "/")).getTime();
        let startUseTime =
          new Date(this.startUseTime).getTime() ||
          new Date(this.startUseTime.replace(/\-/g, "/")).getTime();
        let endUseTime =
          new Date(this.endUseTime).getTime() ||
          new Date(this.endUseTime.replace(/\-/g, "/")).getTime();
        if (endTime < startTime) {
          this.endTime = "";
          this.startTime = "";
          return this.$toast("促销结束时间无法小于促销开始时间");
        }
        if (endUseTime < startUseTime) {
          this.startUseTime = "";
          this.endUseTime = "";
          return this.$toast("凭证结束时间无法小于凭证开始时间");
        }
        if(!this.resellImg){
          this.imgUrlResell = ''
        }
        if(Number(this.price) < 0){
          return this.$toast('价格不能小于0')
        } 
        if(!this.firstOrderValue){ // 首单判断
          this.firstOrderPrice = -1
        }else{
          if(!this.firstOrderPrice) return this.$toast('开启首单优惠后必须填写首单价格')
          if(Number(this.firstOrderPrice) < 0){
            return this.$toast('首单价必须大于等于0元')
          }
          if(Number(this.firstOrderPrice >= this.price)){
            return this.$toast('首单价不能小于等于真实价格')
          }
        }
        
        if (!this.name && !this.price && !this.showPrice ) return this.$toast('请填写必填项*')
        if(this.activityType != 601){
          if(!this.startTime && !this.endTime && !this.startUseTime && !this.endUseTime ) return this.$toast('请填写必填项*')
        }else{
          startTime = '';
          endTime = '';
          startUseTime = ''
          endUseTime = ''
        }
        if( this.activityType == 301){
          if( !this.totalCanBuy ) return this.$toast('请填写购买次数')
        }
        if(this.activityType == 101 || this.activityType == 501){
          if( !this.address ) return this.$toast('请填写地址')
          if( !this.phoneNum ) return this.$toast('请填写电话')
        }
        if(this.moduleGetPrice == -1){
          return this.$toast('请重新设置价格')
        }
        if(this.phoneNum){
          if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.phoneNum))){ 
          // 不是手机号
            if (!/^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$/.test(this.phoneNum)) {
            // 也不是座机号码
              return this.$toast('您输入的联系电话有误，请确认您要提交的内容')
            }
          } 
        }
        if(!this.merchantCodeValue){
          if(!this.totalNum && this.activityType != 101) return this.$toast("请填写库存")
        }
        // if(this.resellValue){
        //   if(!this.resellPrice || !this.resellerText) return this.$toast('您已开启分销，请填写分销信息')
        //   if( Number(this.resellPrice) >= Number(this.price) ) return this.$toast('分销金额不能大于等于真实价格')
        // }else{
        //   this.resellPrice = 0;
        //   this.resellerText = '';
        //   this.imgUrlResell = '';
        // }
        if( Number(this.price) > 0){
          if( Number(this.sysPrice) >= Number(this.price)) return this.$toast('佣金不能大于等于真实价格')
        }
        if(this.activityGroupIds === ''){
          let groupIds = JSON.parse(localStorage.getItem('groupIds'))
          if(!groupIds) return
          groupIds.forEach(item =>{
            this.apiList.moduleList.forEach(items=>{
              if(items.id == item){
                this.activityGroupIds =  this.activityGroupIds + items.id + ','
              }
            })
          })
        }
       
        let url;
        if( type === 'temp' ){
          url = this.apiList.template.add;
        }else if(type === 'edit'){
          if(this.title === '编辑模板'){
            url = this.apiList.template.update;
          }else{
            if(this.activityType == 101){
              url = this.apiList.enterName.edit;
            }else if(this.activityType == 301){
              url = this.apiList.pmSale.edit;
            }else if(this.activityType == 501){
              url = this.apiList.addActivity.edit;
            }else if(this.activityType == 601){
              url = this.apiList.commodity.edit;
            }
          }
          
        }else{
          if(this.activityType == 101){
            url = this.apiList.enterName.add;
          }else if(this.activityType == 301){
            url = this.apiList.pmSale.add;
          }else if(this.activityType == 501){
            url = this.apiList.addActivity.add;
          }else if(this.activityType == 601){
            url = this.apiList.commodity.add;
          }
        }
        if(this.activityType == 301){
          this.value = false
        }
          this.flag = true;
          this.upload()
          this.$http({
            url: this.$http.adornUrl(url),
            method: "post",
            data: this.$http.adornData(
              {
                id: this.id,
                name: this.name,
                phoneNum: this.phoneNum,
                mainImgUrl: this.activityType == 301 || this.activityType == 601    ? this.imgUrl : '',
                imgUrl: this.activityType != 301 || this.activityType != 601 ? this.imgUrl : '',
                poster: this.imgUrlPoster,
                imgUrlSquare: this.imgUrlSquare,
                totalNum: this.totalNum,
                price: Math.round(this.price * 10000) / 100,
                sysPrice: Math.round(this.sysPrice * 10000) / 100,
                firstOrderPrice: this.firstOrderPrice === -1 ? this.firstOrderPrice : Math.round(this.firstOrderPrice * 10000) / 100,
                totalCanBuy: this.totalCanBuy,
                sellType: this.type,
                resellPrice: Math.round(this.resellPrice * 10000) / 100,
                resellerText: this.resellerText,
                resellerPoster: this.imgUrlResell,
                startTime: startTime,
                endTime: endTime,
                startUseTime: startUseTime,
                endUseTime: endUseTime,
                showPrice: Math.round(this.showPrice * 10000) / 100,
                showToC: this.value,
                fullText: this.text,
                prizeFullText: this.prizeFullText,
                address: this.address,
                doorAdd: this.doorAdd,
                activityGroupIds: this.activityGroupIds,
                attention: JSON.stringify(this.attention),
                photoShow: JSON.stringify(this.photosImg),
                enterMess: JSON.stringify(this.enterMess),
                enterItemList: JSON.stringify(this.group), 
                lng: this.lng,
                lat: this.lat,
                useThirdCode: this.merchantCodeValue,
                thirdCodeExp: this.merchantCodeText,
                type: this.activityType,
                moduleGetPrice: this.activityType == 101 ? '' : Math.round(this.moduleGetPrice * 10000) / 100
              },
              url,
              true
            )
          }).then(({ data }) => {
            this.closeUpload()
            if (data.result) {
              if(type === 'add'){
                this.$toast({
                  message: '添加成功',
                  iconClass: 'fa fa-check fa-5x'
                })
              }else{
                this.$toast({
                  message: '保存成功',
                  iconClass: 'fa fa-check fa-5x'
                })
              }
              if( type === 'edit' && this.title !== '编辑模板'){
                let dataList = JSON.parse(localStorage.getItem('dataList'));
                dataList[this.index].name = this.name;
                dataList[this.index].imgUrl = this.imgUrl;
                dataList[this.index].imgUrlSquare = this.imgUrlSquare;
                dataList[this.index].totalNum = this.totalNum;
                dataList[this.index].price = this.price * 100;
                dataList[this.index].startTime = startTime;
                dataList[this.index].endTime = endTime;
                dataList[this.index].startUseTime = startUseTime;
                dataList[this.index].endUseTime = endUseTime;
                dataList[this.index].showPrice = this.showPrice * 100;
                dataList[this.index].showToC = this.value;
                // dataList[this.index].fullText = this.text;
                // dataList[this.index].address = this.address;
                // dataList[this.index].doorAdd = this.doorAdd;
                // dataList[this.index].lng = this.lng;
                // dataList[this.index].lat = this.lat;
                // dataList[this.index].activityGroupIds = this.activityGroupIds;
                // dataList[this.index].photoShow = JSON.stringify(this.photosImg);
                // dataList[this.index].attention = JSON.stringify(this.attention);
                dataList[this.index].useThirdCode = this.merchantCodeValue;
                localStorage.setItem('dataList', JSON.stringify(dataList) )
              }
              setTimeout(() => {
                this.goBack();
              }, 800);
            } else if (data.msg == "sign验证失败") {
              this.$toast("您的账户登录超时");
              setTimeout(() => {
                localStorage.clear();
                this.$router.push({ name: "login" });
              }, 1000);
            }else{
              this.flag = false
              this.$toast(data.msg)
            }
          });
      },
      fileChange(image) {
        this.imgUrlSquare = image;
        this.imgShow();
      },
      // 显示图片 
      imgShow() {
        if (this.imgUrlSquare) {
          this.$refs.square.style.opacity = 0;
          this.$refs.squareImg.style.display = "inline-block";
          this.border = false;
        }
        if (this.imgUrl) {
          this.$refs.mainImg.style.opacity = 0;
          this.$refs.img.style.display = "inline-block";
        }
        if(this.activityType == 301){
          if(this.imgUrlResell){
            if(this.$refs.resell){
              this.$refs.resell.style.opacity = 0;
              this.$refs.resellImg.style.display = "inline-block";
              this.resellBorder = false;
            }
          }
          if(this.imgUrlPoster){
            this.$refs.poster.style.opacity = 0;
            this.$refs.posterImg.style.display = "inline-block";
            this.posterBorder = false;
          }
        }
        
      },
      // 获取数据
      getData(){ 
        
        // localStorage.getItem("paramsId") || 
        // this.button = this.$route.params.templateAddId || this.$route.params.templateEditId;
        // console.log(!this.button);
        // 加载模板数据
        let url;
        var id = this.$route.params.templateAddId || this.$route.params.templateEditId || this.$route.params.modId
        if ( id ) {
          this.id = id 
          this.index = this.$route.params.index
          if(this.$route.params.type){
            this.activityType = this.$route.params.type
          }
          
          if(this.$route.params.templateAddId){
            if(this.activityType == 101){
              this.title = '报名添加'
            }else if(this.activityType == 301){
              this.title = '促销添加'
            }else if(this.activityType == 501){
              this.title = '活动添加'
            }else if(this.activityType == 601){
              this.title = '商品添加'
            }
            url = this.apiList.template.id;
          }else if(this.$route.params.templateEditId){
            this.title = '编辑模板'
            url = this.apiList.template.id;
          }else{
            if(this.activityType == 101){
              this.title = '报名修改'
              url = this.apiList.enterName.detail;
            }else if(this.activityType == 301){
              this.title = '促销修改'
              url = this.apiList.pmSale.detail;
            }else if(this.activityType == 501){
              this.title = '活动修改'
              url = this.apiList.addActivity.detail;
            }else if(this.activityType == 601){
              this.title = '商品修改'
              url = this.apiList.commodity.detail;
            }
            

          }
          this.loading()
          this.$http({
            url: this.$http.adornUrl(url),
            method: "post",
            data: this.$http.adornData({ id: id }, url, true)
          }).then(({ data }) => {
            this.closeUpload()
            if (data.result) {
              let imgUrl = []
              let attention = []
              let enterMess = []
              this.name = data.data.name;
              if( this.$route.params.templateAddId || this.$route.params.templateEditId ){
                this.imgUrl = data.data.mainImgUrl;
                if(!!data.data.attention){
                  this.attention =  JSON.parse(data.data.attention)
                  localStorage.setItem('attention', data.data.attention)
                }
                if(!!data.data.photoShow){
                  this.photosImg = JSON.parse(data.data.photoShow) 
                  localStorage.setItem('photosImg', data.data.photoShow)  
                }
                if(!!data.data.enterMess){
                  this.enterMess = JSON.parse(data.data.enterMess)
                  localStorage.setItem('enterMess', data.data.enterMess)
                }
                
              }else{
                this.id = data.data.activityId;
                this.imgUrl = data.data.imgUrl;
                if(!!data.data.attention){
                  data.data.attention.forEach(item => attention.push({noteKey: item.noteKey, noteValue: item.noteValue}))
                  localStorage.setItem('attention', JSON.stringify(attention))
                  this.attention = attention
                }
                if(!!data.data.photoShow){
                  data.data.photoShow.forEach(item=>imgUrl.push({url: item.imgUrl})); 
                  localStorage.setItem('photosImg', JSON.stringify(imgUrl))
                  this.photosImg = imgUrl
                }
                if(!!data.data.enterMess){
                  data.data.enterMess.forEach(item => enterMess.push({keyName: item.keyName, type: item.type, mustInput: item.mustInput}))
                  localStorage.setItem('enterMess', JSON.stringify(enterMess))
                  this.enterMess = enterMess
                }
              }
              this.imgUrlSquare = data.data.imgUrlSquare;
              this.price = (data.data.price / 100).toFixed(2);
              this.showPrice = (data.data.showPrice / 100).toFixed(2);
              this.totalNum = data.data.totalNum;
              this.startTime = this.setDate(data.data.startTime);
              this.endTime = this.setDate(data.data.endTime);
              this.startUseTime = this.setDate(data.data.startUseTime);
              this.endUseTime = this.setDate(data.data.endUseTime);
              this.text = data.data.fullText ? data.data.fullText : '';
              this.prizeFullText = data.data.prizeFullText ? data.data.prizeFullText : '';
              this.value = data.data.showToC;
              this.address = data.data.address;
              this.doorAdd = data.data.doorAdd;
              this.lat = data.data.lat;
              this.lng = data.data.lng;
              this.phoneNum = data.data.phoneNum === 'null' ?  '' : data.data.phoneNum;
              this.totalCanBuy = data.data.totalCanBuy;
              this.sysPrice = (data.data.sysPrice / 100).toFixed(2);
              this.type = data.data.sellType;
              this.resellPrice = (data.data.resellPrice / 100).toFixed(2);
              this.imgUrlResell = data.data.resellerPoster;
              this.imgUrlResellValue = data.data.resellerPoster;
              this.imgUrlPoster = data.data.poster;
              
              if( Number(this.resellPrice) >= 1 ){
                this.resellImg = true
              }
              this.resellerText = data.data.resellerText;
              if(data.data.activityGroupIds){
                data.data.activityGroupIds.split(',').forEach(item =>{
                  this.apiList.moduleList.forEach(items =>{
                    items.children.forEach(e =>{
                      if(item == e.id){
                        this.groupId = e.id
                        this.selectGroupId = e.id
                        
                        this.groupList.forEach(item =>{
                          if(e.id == item.id){
                            this.groupIdSeleceText = item.label
                          }
                        })
                      }
                    })
                  })
                })
                this.activityGroupIds = data.data.activityGroupIds
              }
              if(data.data.firstOrderPrice > -1){
                this.firstOrderPrice = (data.data.firstOrderPrice / 100).toFixed(2);
                this.firstOrderValue = true;
              }
              if(data.data.useThirdCode){
                this.merchantCodeValue = true
                this.merchantCodeText = data.data.thirdCodeExp
              }
              if(!!data.data.enterItemList){
                let enterItemList = []
                if(this.index || this.index == 0){
                  data.data.enterItemList.forEach(item => enterItemList.push({ id: item.id, name: item.name, price: item.price, totalNum: item.totalNum, beginTime: item.beginTime, endTime: item.endTime, moduleGetPrice: item.moduleGetPrice}))
                  this.group = enterItemList
                }else{
                  enterItemList = JSON.parse(data.data.enterItemList)
                  this.group = enterItemList
                }
                localStorage.setItem('group', JSON.stringify(enterItemList) )
              }
              this.index = this.$route.params.index;
              this.$nextTick(()=>{
                this.moduleGetPrice = data.data.moduleGetPrice === -1 ? data.data.moduleGetPrice : (data.data.moduleGetPrice / 100).toFixed(2);
                this.imgShow();
              })
            }
          });
        } else {
          this.getStorage();
          if(this.$route.params.type){
            this.activityType = this.$route.params.type
          }
          if(this.index){
            if(this.activityType == 101){
              this.title = '报名修改'
              url = this.apiList.enterName.detail;
            }else if(this.activityType == 301){
              this.title = '促销修改'
              url = this.apiList.pmSale.detail;
            }else if(this.activityType == 501){
              this.title = '活动修改'
              url = this.apiList.addActivity.detail;
            }else if(this.activityType == 601){
              this.title = '商品修改'
              url = this.apiList.commodity.detail;
            }
          }else{
            if(this.activityType == 101){
              this.title = '报名添加'
              }else if(this.activityType == 301){
                this.title = '促销添加'
              }else if(this.activityType == 501){
                this.title = '活动添加'
              }else if(this.activityType == 601){
                this.title = '商品添加'
            }
          }
          
          
          if(localStorage.getItem('group')){
            this.group = localStorage.getItem('group') ? localStorage.getItem('group') : ''
            if(this.group.length > 0){
              this.group = JSON.parse(this.group)
            }
          }
          this.imgUrlResellValue = localStorage.getItem('imgUrlResellValue') ? localStorage.getItem('imgUrlResellValue') : '';
          if(this.firstOrderPrice > -1 && this.firstOrderPrice){
            this.firstOrderValue = true;
          }
          if(this.attention.length > 0){
            this.attention = JSON.parse(this.attention)
          }
          if(localStorage.getItem('photosImg')){
            if(this.photosImg.length > 0){
              this.photosImg = JSON.parse(this.photosImg)
            }
          }
          if(localStorage.getItem('enterMess')){
            this.enterMess = localStorage.getItem('enterMess') ? localStorage.getItem('enterMess') : ''
            if(this.enterMess.length > 0){
              this.enterMess = JSON.parse(this.enterMess)
            }
          }
          this.$nextTick(()=>{
            this.imgShow();
          })
          
        }
        
      },
    },
    components: {
      Upload
    },
    watch:{
      price:function(){
        this.moduleGetPrice = (this.price - this.price * this.moduleChou / 10000).toFixed(2)
      }
    },
    mounted() {
      this.setAddScrollTop()
    },
    created() {
      this.moduleChou = localStorage.getItem('getModuleChou')
      this.setGroupId()
      this.getData()
    }
    
  };
</script>
