          display: inline-block;
          vertical-align: middle;
          margin-left: 2.5rem;
          text-align: center;
          width: 15rem;
          height: 7rem;
          border: 0.1rem dashed #d9d9d9;
          background: url("../../../assets/utils/add.png") no-repeat;
          background-size: 1.9rem;
          background-position: center;
          cursor: pointer;
        }
        .img {
          display: none;
          position: absolute;
          top: 20%;
          width: 15rem;
          height: 7rem;
          margin-left: 2.3rem;
        }
        .addImg:hover {
          border: 0.1rem dashed #00a1fc;
        }
      }
      .squareWrapper {
        position: relative;
        height: 9rem;
        margin-top: 2rem;
        line-height: 4rem;
        .border {
          border: 1px dashed #d9d9d9;
        }
        .border:hover {
          border: 0.1rem dashed #00a1fc;
        }
        .square {
          display: inline-block;
          width: 6.8rem;
          height: 6.8rem;
          margin-left: 2.3rem;
          vertical-align: middle;
          i {
            width: 6.8rem;
            height: 6.8rem;
          }
        }
        .img {
          display: none;
          position: absolute;
          margin-left: 2.3rem;
          top: 13%;
          width: 6.8rem;
          height: 6.8rem;
        }
      }
      .posterWrapper{
        height: 15rem;
        line-height: 10.9rem;
        .poster {
          margin-top: 0.5rem;
          width: 7.5rem;
          height: 13.34rem;
        }
        .img {
          top: 10%;
          width: 7.5rem;
          height: 13.34rem;
        }
      }
      .adp_radio {
        margin-left: 1.6rem;
        img{
          width: 1.6rem;
          height: 1.6rem;
          vertical-align: middle;
          margin-top: -0.4rem;
        }
        .no{
          margin-left: 2rem;
        }
        span {
          margin-left: 0.5rem;
          color: #333;
          font-size: 1.4rem;
        }
      }
    }
    .btn {
      width: 50%;
      margin-top: 4rem;
      background-color: #e63c39;
      color: #fff;
      font-size: 1.6rem;
      height: 4.75rem;
      border-radius: 0;
      float: right;
      position: relative;
    }
    .btn-big {
      width: 100%;
      margin-top: 4rem;
      background-color: #e63c39;
      color: #fff;
      font-size: 1.6rem;
      height: 4.75rem;
      border-radius: 0;
    }
    .pos {
      position: absolute;
      background-color: #e63c39;
      height: 4.75rem;
      width: 0.3rem;
      margin-top: 4rem;
      margin-left: 49.5%;
      z-index: 99;
    }
    .btn-temp {
      width: 50%;
      float: left;
      margin-top: 4rem;
      background: rgba(250, 95, 92, 1);
      height: 4.75rem;
      color: #fff;
      font-size: 1.6rem;
      border-radius: 0;
    }
  }
</style>



