<template>
  <div class="release">
    <div class="header">
      <span class="back" @click="$router.goback()"><img src="../../assets/my/back.png" alt="" ></span>
      <span>发布</span>
    </div>
    <div class="release_main">
      <ul>
        <router-link tag="li" class="forward" v-for="(item,index) in navList" :key="index" @click="goActivity(index)"  :to="item.to" v-show="item.hideModule">
          <img :src="item.scr" alt=""><span>{{item.name}}</span>
        </router-link>
      </ul>
    </div>
  </div>
</template>
<script>
  export default {
    data(){
      return{
        navList:[
          { name:'发布促销', scr: require('../../assets/release/promotion.png'), to: '/promotion', hideModule: this.isAuth('/pmSale/list') },
          { name:'发布拼团', scr: require('../../assets/release/collage.png'), to: '/promotion', hideModule: false },
          { name:'发布报名', scr: require('../../assets/release/signUp.png'), to: '/signUp', hideModule: this.isAuth('/enterName/list') },
          { name:'发布活动', scr: require('../../assets/release/activity.png'), to: '/addActivity', hideModule: this.isAuth('/activityPlan/list') },
          { name:'发布商品', scr: require('../../assets/release/commodity.png'), to: '/commodity', hideModule: this.isAuth('/product/list') },
          { name:'兑换商品', scr: require('../../assets/release/exchangeM.png'), to: '/commodityExchange', hideModule: this.isAuth('/outCoupon/list') },
        ]
      }
    },
    methods:{
      async getModuleChou(){
      const url = this.apiList.getModuleChou.getModuleChou
      let {data} = await this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData('', url, true)
        })
        if(data.result){
          localStorage.setItem('getModuleChou', data.data)
        }
      }
    },
    created(){
      this.getModuleChou()
    }
  }
</script>
<style lang="scss">
  .release{
    min-height: 100%;
    background-color: #f5f5f5;
    .release_main{
      margin-top: 1.4rem;
      background-color: #fff;
      padding: 0 1.6rem;
      ul{
        li:last-child{
          span{
            border: 0;
          }
        }
        li{
          display: flex;
          height: 7.2rem;
          line-height: 7.2rem;
          
          img{
            width: 4.4rem;
            height: 4.4rem;
            vertical-align: middle;
            margin-top: 1.2rem;
          }
          span{
            flex: 1;
            display: inline-block;
            margin-left: 1.6rem;
            border-bottom: 0.1rem solid #ededed;
          }
        }
        .forward{
          background: url('../../assets/my/next.png') no-repeat scroll right center ;
          background-size: 0.75rem 1.35rem;
          
        }
      }
    }
  }
</style>


