<template>
    <div class="shaoma">
      <div class="header" >
       <span @click="goBack">
       <img src="../../assets/my/back.png" alt=""  >
      </span>
        <span class="headerTitle">提现列表</span>
        <span style="width: 1.8rem"></span>
      </div>
      <div class="main">
        <div class="content">
          <li v-for="item in dataList" :key="item.id">
            <span class="nav1"><br/>{{ item.bankCardPersonName }}</span>
            <span class="nav2"><b>银行卡</b><br/> {{ item.bankCardNum }}</span>
            <!-- <span class="nav3">8月9日 18:29:52</span> -->
            <span class="nav3"><b>申请时间</b><br/> {{ item.createdAt }}</span>
            <span class="nav4"><b>金额</b><br/> ￥{{ item.num / 100 }}</span>
            <span class="nav4" v-if="item.state === 1"><b>状态</b><br/>待审核</span>
            <span class="nav4" v-if="item.state === 101 && item.moneyOutState === 101"><b>状态</b><br/>打款中</span>
            <span class="nav4" v-if="item.state === 101 && item.moneyOutState === 201"><b>状态</b><br/>已打款</span>
            <span class="nav4" v-if="item.state === 201"><b>状态</b><br/>已拒绝</span>
          </li>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  data() {
    return{
      moduleId: '',
      authCode: '',
      dataList: []
    }
  },
  methods:{
    goBack() {
      this.$router.goback()
    },
    getdataList () {
      const url = this.apiList.moneyApproval.list
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData({ page: 1, size: 10000}, url, true)
      }).then(({ data }) => {
        if (data.result) {
          this.dataList = data.data.records
        } else {
          this.$toast(data.msg)
        }
      })
    }
  },
  created() {
    this.moduleId = this.$route.params.moduleId
    this.authCode = this.$route.params.authCode
    console.log( this.authCode)
    console.log( this.moduleId)
    this.getdataList()
  }
}
</script>

<style lang="scss">
  .shaoma{
    background-color: #f5f5f5;
    position: absolute;
    top: 0;
    bottom: 0;
    .header {
      background-color: #fff;
      width: 100%;
      height: 4.4rem;
      line-height: 4.4rem;
      display: flex;
      justify-content: space-between;
      box-sizing: border-box;
      padding-left: 1.2rem;
      padding-right: 1.6rem;
      span img {
        vertical-align: middle;
        height: 1.8rem;
      }

      .headerTitle{
        font-size: 1.8rem;
        color: #1A1A1A;
        font-weight: 600;
      }
    }
    .main{
      background-color: #fff;
      border-radius: 0.8rem;
      margin-top: 2.2rem;
      padding: 0 1.6rem;
      .nav{
        height: 4.4rem;
        display: flex;
        align-items: center;
        justify-content: space-around;
        font-size: 1.4rem;
        color: #171717;
        .nav1{
          width: 26%;
          text-align: center;
        }
        .nav2{
          width: 26%;
          text-align: center;
          word-break:break-all;
          word-wrap: break-word;
        }
        .nav3{
          width: 30%;
          text-align: center;
          word-wrap: break-word;
          word-break: normal;
        }
        .nav4{
          width: 18%;
          text-align: center;
        }
      }
      .content{
        color: #666666;
        font-size: 1.2rem;
        li:first-child{
          border: none;
        }
        li{
          display: flex;
          align-items: center;
          justify-content: space-around;
          border-top:0.05rem solid #DDDDDD;
          padding: 1rem 0;
          text-align: center;
          word-wrap: break-word;
          word-break: normal;
          .nav1{
            width: 26%;
            text-align: center;
            display: flex;
            align-items: center;
            img{
              width: 2.0rem;
              height: 2.0rem;
              margin-right: 0.55rem;
            }
          }
          .nav2{
            width: 26%;
            text-align: center;
          }
          .nav3{
            width: 26%;
            text-align: center;
          }
          .nav4{
            width: 18%;
            text-align: center;
          }
        }
      }

    }
  }

</style>



