<template>
<div class="nameChange">
  <div class="header">
    <span class="back" @click="goback"><img src="../../assets/my/back.png" alt=""  ></span>
    <span>修改电话</span>
    <span class="sure"><div @click="finish">确定</div></span>
  </div>
  <div class="nameInput">
    <div class="layer"></div>
    <input type="text" v-model="name">
    <p>修改您店铺的联系电话</p>
  </div>
</div>
</template>
<script>
import { eventBus } from '../../utils/eventBus.js'
export default {
  data() {
    return{
      name: ''
    }
  },
  methods: {
    goback() {
      this.$router.goback()
    },
    finish() {
      if (!this.name) {
        return this.$toast('您输入的联系电话为空，请确认您要修改的内容')
      }
      if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.name))){ 
        // 不是手机号
        if (!/^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$/.test(this.name)) {
        // 也不是座机号码
          return this.$toast('您输入的联系电话有误，请确认您要修改的内容')
        }
      } 
      const vm = this
      this.upload()
      const url = this.apiList.shop.edit
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData({shopPhoneNum: this.name}, url , true)
      }).then(({ data }) => {
        this.closeUpload()
        if (data.result) {
          this.$toast({
            message: '修改成功',
            iconClass: 'fa fa-check fa-5x'
          })
          eventBus.$emit('phoneChangeSuccess', this.name)
        } else {
          this.$toast(data.msg)
        }
        setTimeout(function(){
          vm.goback()
        }, 600)
      })
    }
  },
  created() {
    this.name = this.$route.params.shopPhoneNum
  }
}
</script>
<style lang="scss">
.nameChange{
  width: 37.5rem;
  height: 100%;
  .sure{
    float: right; 
    margin-top: .1rem;
    color: #fff;
  }
  .nameInput{
    width: 90%;
    margin: 1rem auto;
    p{
      font-size: 1.2rem;
      color: #999;
      margin-top: 1rem;
    }
    .layer{
      width: 99%;
      position: absolute;
      height: 1rem;
      background-color: #fff;
      left: 1.5%;
    }
    input{
      width: 100%;
      height: 4rem;
      // line-height: 4rem;
      border: none;
      outline: none;
      font-size: 1.4rem;
      border-bottom: solid .1rem #FF3B30;
      border-top: solid .1rem pink;
      border-radius: .5rem;
      background-color: #fff;
      padding-left: .6rem ;
    }
    input:focus{color:#FF3B30}//光标颜色
    input{
      text-shadow: 0px 0px 0px #333;//文字颜色
      -webkit-text-fill-color: transparent;
    }
  }
}
</style>


