<template>
<div class="messDetail">
  <div class="header">
    <span class="back" @click="goback"><img src="../../assets/my/back.png" alt=""  ></span>
    <span>信息编辑</span>
  </div>
  <div class="logo clearfix">
    <p>店铺logo</p>
    <upload @fileChange="fileChange" class="avatar-uploader" :height="150" :width="150">
      <img alt="" v-show="dataForm.logoImgUrl !== ''" :src="dataForm.logoImgUrl" class="info" >
      <i v-show="dataForm.logoImgUrl === ''" class="avatar-uploader-icon"></i>
    </upload>
  </div>
  <div class="form">
    <mt-field label="名称" placeholder="请输入名称" v-model="dataForm.name"></mt-field>
    <mt-field label="联系电话" placeholder="请输入联系电话" v-model="dataForm.shopPhoneNum"></mt-field>
    <mt-cell title="店铺地址" @click.native="chooseAddressSwitch">{{dataForm.address}}</mt-cell>
    <mt-cell title="属于商场"><mt-switch v-model="isMarket" @change.native="isMarketChange"></mt-switch></mt-cell>
    <mt-cell v-if="isMarket" title="所属商场" @click.native="marketListVisible = true">{{chooseMarketLabel}}</mt-cell>
    <mt-cell title="属于儿童"><mt-switch v-model="isChild" @change.native="isChildChange"></mt-switch></mt-cell>
    <mt-cell v-if="isChild" title="分类所属" @click.native="childListVisible = true">{{chooseChildLabel}}</mt-cell>
    <mt-cell title="属于美妆"><mt-switch v-model="isMakeUp" @change.native="isMakeUpChange"></mt-switch></mt-cell>
    <mt-cell v-if="isMakeUp" title="分类所属" @click.native="makeupListVisible = true">{{chooseMakeupLabel}}</mt-cell>
    <mt-cell title="标签" @click.native="tagListVisible = true">{{chooseTagsLabel}}</mt-cell>
  </div>
  <!-- 富文本组件 -->
  <div class="desc">
    <p style="float: left">店铺详情</p>
    <p>&nbsp;</p>
    <p>&nbsp;</p>
    <vue-html5-editor ref="html5editor" :content="content" :z-index="1000" :auto-height="true" :show-module-name="false" @change="changeContent" @chooseImg="chooseImg"></vue-html5-editor>
  </div>
  <!-- 修改详情 -->
  <div class="bottom_btn">
    <mt-button size="large" type="primary" @click="submit">提交修改</mt-button>
    <mt-button size="large" style="margin: 1.6rem auto" @click="reset">重置信息</mt-button>
  </div>
  <!-- 商场列表 -->
  <mt-popup
    v-model="marketListVisible"
    :position="popupPosition">
    <div class="popupContainer">
       <mt-radio
        title="商场列表"
        v-model="market"
        :options="marketList"
        @change.native="changeMarket">
      </mt-radio>
    </div>
  </mt-popup>
  <div class="shadow" v-show="marketListVisible"></div>
  <!-- 儿童分类列表 -->
  <mt-popup
    v-model="childListVisible"
    :position="popupPosition">
    <div class="popupContainer">
       <mt-checklist
        title="儿童分类列表"
        v-model="child"
        :options="childList"
        @change.native="changeChildType">
      </mt-checklist>
    </div>
  </mt-popup>
  <div class="shadow" v-show="childListVisible"></div>
  <!-- 美妆分类列表 -->
  <mt-popup
    v-model="makeupListVisible"
    :position="popupPosition">
    <div class="popupContainer">
       <mt-checklist
        title="美妆分类列表"
        v-model="makeup"
        :options="makeupList"
        @change.native="changeMakeupType">
      </mt-checklist>
    </div>
  </mt-popup>
  <div class="shadow" v-show="makeupListVisible"></div>
  <!-- 标签列表 -->
  <mt-popup
    v-model="tagListVisible"
    :position="popupPosition">
    <div class="popupContainer">
      <mt-checklist
        title="所有标签列表"
        v-model="tags"
        :options="tagsList"
        @change.native="changeTagType">
      </mt-checklist>
    </div>
  </mt-popup>
  <div class="shadow" v-show="tagListVisible"></div>
  <!-- 地图 -->
  <mt-popup
    class="map"
    v-model="chooseAddress"
    popup-transition="popup-fade"
  > 
    <div class="full">
      <choose-address v-if="chooseAddressMap" :lng="dataForm.lng" :lat="dataForm.lat" :address="dataForm.address"  @chooseAddressPoint="chooseAddressPoint"> </choose-address>
    </div>
  </mt-popup>
</div>
</template>
<script>
import Upload from '../utils/upload.vue'
import { Toast } from 'mint-ui'
import { Base64 } from 'js-base64'
import ChooseAddress from '../utils/map.vue'
import { eventBus } from '../../utils/eventBus.js'
export default {
  data() {
    return {
      popupPosition: 'bottom',
      content: '',
      dataForm: {
        logoImgUrl: '',
        name: '',
        shopPhoneNum: '',
        address: '',
        tags: '',
        showed: false,
        shopDesc: '',
        lat: '',
        lng: ''
      },
      groupId: [],
      isMarket: false,
      isChild: false,
      isMakeUp: false,
      market: null,
      child: [],
      tags: [],
      makeup: [],
      marketList: [],
      childList: [],
      makeupList: [],
      tagsList: [],
      childListVisible: false,
      marketListVisible: false,
      makeupListVisible: false,
      tagListVisible: false,
      chooseAddress: false,
      chooseAddressMap: false,
      chooseChildLabel: '',
      chooseMakeupLabel: '',
      chooseTagsLabel: '',
      chooseMarketLabel: ''
    }
  },
  components: {
    Upload,
    ChooseAddress
  },
  methods: {
    changeContent(val) {
      this.dataForm.shopDesc = val
    },
    init() {
      // 初始化数据
      this.dataForm = this.$route.params.shopInfo
      this.content = this.$route.params.shopInfo.shopDesc
      this.marketList = this.$route.params.marketList
      this.marketList.map((v, i, a) => {
        v.label = v.name,
        v.disabled = false,
        v.value = String(v.id)
      })
      this.childList = this.apiList.groupId[1].children
      this.childList.map((v, i, a) => {
        v.value = v.id
        v.disabled = false
      })
      this.makeupList = this.apiList.groupId[2].children
      this.makeupList.map((v, i, a) => {
        v.value = v.id
        v.disabled = false
      })
      if (this.dataForm.tagIds) {
        this.tags = this.dataForm.tagIds.split(',')
        this.tags.pop()
        this.tags.shift()
      }
      // 初始化选择状态
      if (this.dataForm.groupIds) {
        this.groupIds = this.dataForm.groupIds.split(',')
        this.groupIds.pop()
        this.groupIds.shift()
        this.groupIds.forEach((v, i, a) => {
          // 初始化儿童分类的选择
          if(Number(v) === this.apiList.groupId[1].id) {
            this.isChild = true
          }
          // 初始化美妆分类的选择
          if (Number(v) === this.apiList.groupId[2].id) {
            this.isMakeUp = true
          }
          const childId = this.childList.filter(item => item.id === Number(v))
          const makeupId = this.makeupList.filter(item => item.id === Number(v))
          if (childId.length > 0) {
            this.child.push(childId[0].id)
          }
          if (makeupId.length > 0) {
            this.makeup.push(makeupId[0].id)
          }
        })
      }
      if (this.dataForm.mallModuleId) {
        this.isMarket = true
        this.market = String(this.dataForm.mallModuleId)
        this.changeMarket()
      }
      this.changeChildType()
      this.changeMakeupType()
      this.getTags(1, 20)
    },
    goback() {
      this.$router.goback()
    },
    fileChange(imgurl) {
      this.dataForm.logoImgUrl = imgurl
      console.log(imgurl)
    },
    isMarketChange() {
      if(!this.isMarket) {
        this.market = null
      } else {
        // Toast('当前城市没有符合广场')
        // this.isMarket = false
        if (this.marketList.length <= 0) {
          Toast('当前城市没有符合广场')
          this.isMarket = false
        } else {
          
        }
      }
    },
    isChildChange() {
      if(!this.isChild) {
        this.child = []
      }
    },
    isMakeUpChange() {
      if(!this.isMakeUp) {
        this.makeup = []
      }
    },
    // 初始化儿童分类选中状态
    changeChildType() {
      this.chooseChildLabel = ''
      this.child.forEach((v, i, a) => {
        const arr = this.childList.filter(item => item.id === Number(v))
        if (arr.length > 0) {
          this.chooseChildLabel += '  ' + arr[0].label
        }
      })
    },
    // 初始化美妆分类选择状态
    changeMakeupType() {
      this.chooseMakeupLabel = ''
      this.makeup.forEach((v, i, a) => {
        const arr = this.makeupList.filter(item => item.id === Number(v))
        if (arr.length > 0) {
          this.chooseMakeupLabel += '  ' + arr[0].label
        }
      })
    },
    changeMarket() {
      this.chooseMarketLabel = this.marketList.filter(item => item.id === Number(this.market))[0].name
    },
    getTags(page, size) {
      // 获得tag列表
      this.$http({
        url: this.$http.adornUrl(this.apiList.tag.list),
        method: 'post',
        data: this.$http.adornData({ page:page, size: size }, this.apiList.tag.list, true)
      }).then(({ data }) => {
        if(data.result) {
          let datalist = data.data.records
          datalist.map((v, i, a) => {
            v.label = v.name,
            v.disabled = false,
            v.value = v.id
          })
          this.tagsList = this.tagsList.concat(datalist)
          this.changeTagType()
        }
      })
    },
    changeTagType() {
      const _idx = this.tags.findIndex(item => item === '')
      if (_idx !== -1) {
        this.tags.splice(_idx, 1)
      }
      console.log(this.tags)
      if (this.tags.length > 2) {
        this.$toast('标签最多可选两个')
        this.tags = this.tags.splice(0, 2)
        this.tagListVisible = false
      }
      console.log(this.tags)
      this.chooseTagsLabel = ''
      this.tags.forEach((v, i, a) => {
        const arr = this.tagsList.filter(item => item.id === Number(v))
        if (arr.length > 0) {
          this.chooseTagsLabel += '  ' + arr[0].name
        }
      })
    },
    reset() {
      this.dataForm = {
        logoImgUrl: '',
        name: '',
        shopPhoneNum: '',
        address: '',
        tags: '',
        showed: false,
        shopDesc: ''
      }
      this.child = []
      this.tags = []
      this.groupId = []
    },
    submit() {
      // 点击提交
      let formData = {}
      console.log(this.dataForm)
      formData = JSON.parse(JSON.stringify(this.dataForm))
      // formData.groupIds = this.child.join(',')
      let groupIds = []
      // 属于儿童分类则添加对应ID
      if (this.isChild) {
        groupIds.push(this.apiList.groupId[1].id)
      } else {
        this.childList.forEach(v => {
          const _index = this.child.findIndex(item => Number(item) === Number(v.id))
          if (_index > -1) {
            this.child.splice(_index, 1)
          }
        })
      }
      if (this.isMakeUp) {
        groupIds.push(this.apiList.groupId[2].id)
      } else {
        this.makeupList.forEach(v => {
          const _index = this.makeup.findIndex(item => Number(item) === Number(v.id))
          if (_index > -1) {
            this.makeup.splice(_index, 1)
          }
        })
      }
      // 重置选择
      this.changeChildType()
      this.changeMakeupType()
      formData.id = localStorage.getItem('moduleId')
      // 如果不是属于广场删除groupids对应的id，讲对应的商场id设为0
      if(this.isMarket && this.market !== 0) {
        formData.mallModuleId = this.market ? this.market : 0
        groupIds.push(this.apiList.groupId[0].id)
      } else {
        formData.mallModuleId = 0
        const _index = groupIds.findIndex(item => Number(item) === Number(this.apiList.groupId[0].id))
        if (_index > -1) {
          groupIds.splice(_index, 1)
        }
      }
      groupIds = groupIds.concat(this.makeup, this.child)
      formData.groupIds = groupIds.join(',')
      formData.tagIds = this.tags.join(',')
      formData.shopDesc = Base64.encode(this.dataForm.shopDesc)
      formData.logoUrl = this.dataForm.logoImgUrl
      // console.log(this.content)
      this.$http({
        url: this.$http.adornUrl(this.apiList.shop.edit),
        method: 'post',
        data: this.$http.adornData(formData, this.apiList.shop.edit, true)
      }).then(({ data }) => {
        if(data.result) {
          // 提交成功
          const instance = Toast({
            message: '操作成功',
            iconClass: 'icon icon-success'
          })
          setTimeout(() => {
            instance.close()
            this.goback()
          }, 2000)
        } else {
          if (data.msg === 'sign验证失败') {
            localStorage.clear()
            const instance = Toast({
              message: '您的账号登录超时',
              iconClass: 'icon icon-error'
            })
            setTimeout(() => {
              instance.close()
              localStorage.clear()
              this.$router.push({ name:  'login' })
            }, 2000)
          }
        }
      })
    },
    chooseAddressSwitch() {
      this.chooseAddress = true
      // 滚动到顶部
      window.scrollTo('', 0)
      this.$nextTick(() => {
        this.chooseAddressMap = true
      })
    },
    chooseAddressPoint(point) {
      this.chooseAddress = false
      this.chooseAddressMap = false
      this.dataForm.lng = point.lng
      this.dataForm.lat = point.lat
      this.dataForm.address = point.address
    },
    chooseImg() {
      this.android.getImg()
    },
    dataURLtoFile(dataurl, filename = '0000img') {//将base64转换为文件
      var arr = dataurl.split(','), mime = arr[0].match(/:(.*?);/)[1],
          bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
      while(n--){
          u8arr[n] = bstr.charCodeAt(n);
      }
      return new File([u8arr], filename, {type:mime});
    },
  },
  created() {
    this.init()
  },
  mounted() {
    const vm = this
    eventBus.$on('getImgSuccess', (res) => {
      vm.$refs.html5editor.getImg(vm.dataURLtoFile('data:image/png;base64,' + res.image))
      // 隐藏基座loading
      vm.android.progressHUDend()
    })
  }
}
</script>
<style lang="scss">
.messDetail{
  .mint-field-core{
    text-align: right;
  }
  .shadow{
    position: fixed;
    top: 0;
    bottom: 0;
    width: 100%;
    z-index: 2000;
    background: #000;
    opacity: 0;
    filter: alpha(opacity=0)
  }
  .full{
    width: 37.5rem;
    height: 100%;
  }
  .map{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    transform: inherit;
  }
  .logo{
    box-sizing: border-box;
    padding: 1.6rem;
    p{
      float: left;
    }
    .avatar-uploader{
      line-height: 12rem;
      float: right;
      img{
        height: inherit;
        display: inline-block;
        vertical-align: middle;
      }
    }
  }
  .popupContainer{
    width: 37.5rem;
  }
  .desc{
    box-sizing: border-box;
    padding: 1.6rem;
  }
  .bottom_btn{
    width: 100%;
    box-sizing: border-box;
    padding: 0 10px;
    margin-top: 2.2rem;
    margin-bottom: 2.2rem;
  }
}
</style>


