<template>
  <div class="auto_index">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../assets/my/back.png" alt=""  ></span>
      <span>选择岗位</span>
      <span class="add_Pro">
        <img class="add" src="../../assets/auth/add.png" @click="goAdd" v-if="isAuth('/role/add') && isAuth('/roleApi/update') ">
      </span>
    </div>
    <div class="auto_mian">
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
        <van-list 
          v-model="loading" 
          :finished="finished" 
          finished-text="——没有更多了——"
          @load="onLoad"
          ref="listHeight"
        >
          <van-cell v-for="(item, index) in dataList" :key="index" @click="goList(item.roleName, item.id)">
            <span class="left">
              <span>{{item.roleName}}</span>
              <p>{{item.roleDesc}}</p>
            </span>
            <span class="right">
              <img src="../../assets/auth/edit.png" alt="" @click.stop="goEdit(item.id, item)" v-if="isAuth('/role/add') && isAuth('/roleApi/update')">
              <img src="../../assets/auth/del.png" alt="" @click.stop="del(item.id, index)" v-if="isAuth('/role/del')">
              <img src="../../assets/auth/next.png" alt="">
            </span>
          </van-cell>
          <div class="default" @click="goDefault_list">
            <span class="left">
              <span>默认岗位</span>
              <p>无权限</p>
            </span>
            <span class="right">
              <img src="../../assets/auth/next.png" alt="">
            </span>
          </div>
        </van-list >
      </van-pull-refresh>
    </div>
    <!-- <mt-button>确认</mt-button> -->
  </div>
</template>
<script>
import {MessageBox} from 'mint-ui';
  export default {
    data(){
      return{
        page: 0,
        dataList: [],
        loading: false,
        finished: false,
        isLoading: false,
        
      }
    },
    methods:{
      onLoad(){
        return new Promise((resolve, reject) =>{
          this.page++
          const url = this.apiList.role.list;
          this.$http({
            url: this.$http.adornUrl(url),
            method: 'post',
            data: this.$http.adornData({searchJson: '', page: this.page, size: 10}, url, true)
          }).then(({data})=>{
            if (data.result) {
              if (data.data.records.length >= 0) {
                this.dataList = this.dataList.concat(data.data.records)
              }
              if (data.data.pages <= this.page) {
                this.finished = true
              }
              this.$nextTick(() => {
                resolve(true)
                this.loading = false
              })
            }else if(data.msg == 'sign验证失败'){
              this.$toast('您的账户登录超时')
              setTimeout(()=>{
                localStorage.clear();
                this.$router.push({name: 'login'})
              },1000)
            }
          })
          
        })
      },
      onRefresh(){
        return new Promise((resolve, reject) =>{
          const url = this.apiList.role.list;
          this.$http({
            url: this.$http.adornUrl(url),
            method: 'post',
            data: this.$http.adornData({searchJson: '', page: 1, size: 10}, url, true)
          }).then(({data})=>{
            if (data.result) {
              if (data.data.records.length >= 0) {
                this.dataList = data.data.records
              }
              this.$nextTick(() => {
                this.$toast('刷新成功')
                this.isLoading = false
                this.page = 1 
                this.finished = false
                resolve(true)
              })
            }else if( data.msg == 'sign验证失败' ){
              this.$toast('您的账户登录超时')
              setTimeout(()=>{
                localStorage.clear();
                this.$router.push({name: 'login'})
              },1000)
            }
          })   
        })
      },
      goBack(){
        this.$router.goback()
      },
      goAdd(){
        this.$router.push({name: 'auth_add'})
      },
      goList(name, id){
        if(!this.isAuth('/moduleUser/listModuleUserByRoleId')) return this.$toast('权限不足，请联系管理员修改')
        this.$router.push({name: 'role_list', query:{name: name, id: id}})
      },
      goEdit(id, item){
        this.$router.push({name: 'auth_edit', query:{id: id, name: item.roleName, desc: item.roleDesc}})
      },
      goDefault_list(){
        if(!this.isAuth('/moduleUser/listModuleUserNoRole')) return this.$toast('权限不足，请联系管理员修改')
        this.$router.push('default_list')
      },
      del(id, index){
        MessageBox.confirm('确认删除该项？').then(()=>{
          this.delActivity()
          const url = this.apiList.role.del
          this.$http({
            url: this.$http.adornUrl(url),
            method: 'post',
            data: this.$http.adornData({id: id}, url, true)
          }).then(({data}) =>{
            this.closeUpload()
            if(data.result){
              this.dataList.splice(index, 1)
              this.$toast({
                message: '删除成功',
                iconClass: 'fa fa-check fa-5x'
              })
            }else{
              this.$toast(data.msg)
            }
          })
        })
        
      },
    },
    mounted() {
      this.$refs.listHeight.$el.style.minHeight = document.documentElement.clientHeight  - 12.2 * 10 + 'px'
    },
  }
</script>
<style lang="scss">
  .auto_index{
    background: #f5f5f5;
    min-height: 100%;
    .header{
      .add_Pro{
        float: right;
      }
    }
    .auto_mian{
      margin-top: 1.4rem;
      .van-list{
        .van-cell{
          padding: 0;
        }
        .van-cell__value, .default{
          display: flex;
          justify-content: space-between;
          align-items: center;
          background: #fff;
          height: 6rem;
          padding: 0 1.6rem;
          .left{
            font-family:"Microsoft YaHei",微软雅黑;
            span{
              color: #323233;
              font-size: 1.6rem;
            }
            p{
              margin-top: -0.5rem;
              color: #999999;
              font-size: 1.1rem;
            }
          }
          .right{
            display: flex;
            
            img{
              width: 1.4rem;
              height: 1.4rem;
              margin-right: 2.75rem;
            }
            img:last-child{
              margin: 0;
            }
          }
        }
        .default{
          .left{
            p{
              margin-top: 0.2rem;
            }
          }
        }
      }
    }
    
  }
</style>


