<template>
  <div class="manage_index">
    <div class="top">
      <div class="logo">
        <img src="../../assets/manage/logo.png" alt="">众享乐BOSS端信息管理系统
      </div>
      <div class="exit" @click="exit">
        退出
        <img src="../../assets/manage/exit.png" alt="">
      </div>
      <div class="title">账户余额(元)</div>
      <div class="moeny">{{ (moeny.totalMoneyWx+moeny.totalMoneyAli)/100 }}</div>
      <p>更新时间：{{time}}</p>
    </div>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh" >
    <div class="fund">
      <h3>资金管理</h3>
      <ul>
        <li>
          <div class="moeny">{{moeny.todayInMoney/100}}</div>
          <div class="title">平台今日收入(元)</div>
        </li>
        <li>
          <div class="moeny">{{moeny.todayTieMoney/100}}</div>
          <div class="title">今日贴补(元)</div>
          <!-- -->
        </li>
        <li>
          <div class="moeny">{{moeny.todayMoneyNum/100}}</div>
          <div class="title">今日交易额(元)</div>
        </li>
        <li>
          <div class="moeny">{{moeny.todayOrderNum}}</div>
          <div class="title">交易笔数(笔)</div> 
          <!--  -->
        </li>
        <li>
          <div class="moeny">{{moeny.todayUserNum}}</div>
          <div class="title">今日新增会员(人)</div>
          <!--  -->
        </li>
        <li>
          <div class="moeny">{{moeny.userNum}}</div>
          <div class="title">会员总人数(人)</div>
          <!-- <div class="moeny">{{moeny.monthOrderMoneyNum/100}}</div>
          <div class="title">本月总交易额(元)</div> -->
        </li>
      </ul>
      <div class="manage_details">
        <div>
          <p class="moeny">{{moeny.totalMoneyWx/100}}</p>
          <p class="title">微信资金</p>
        </div>|
        <div>
          <p class="moeny">{{moeny.totalMoneyAli/100}}</p>
          <p class="title">支付宝资金</p>
        </div>|
        <div @click="goAuth">
          <p class="moeny">{{moeny.sysTotal/100}}</p>
          <p class="title">BOSS资金</p>
        </div>|
        <div>
          <p class="moeny">{{moeny.todayRefundMoney/100}}</p>
          <p class="title">退款金额</p>
        </div>
      </div>
    </div>
    <div class="examine">
      <h3>审批管理</h3>
      <ul>
        <li @click="$router.push('manage_sales')">
          <img src="../../assets/manage/examine1.png" alt="">
          <p>分销</p>
          <span v-if="hint.resellerJoinWaitNum!=0">{{hint.resellerJoinWaitNum}}</span>
        </li>
        <li @click="$router.push('manage_outMoeny')">
          <img src="../../assets/manage/examine2.png" alt="">
          <p>提现</p>
          <span v-if="hint.moneyOutWaitNum!=0">{{hint.moneyOutWaitNum}}</span>
        </li>
        <li>
          <img src="../../assets/manage/examine3.png" alt="">
          <p>活动</p>
          <span v-if="hint.activityWaitNum!=0">{{hint.activityWaitNum}}</span>
        </li>
        <li @click="$router.push('sales_outMoeny')"> 
          <img src="../../assets/manage/examine4.png" alt="">
          <p>分销提现</p>
          <span v-if="hint.resellerMoneyOutWaitNum!=0">{{hint.resellerMoneyOutWaitNum}}</span>
        </li>
        <li @click="$router.push('manage_openAccount')">
          <img src="../../assets/manage/examine5.png" alt="">
          <p>开户管理</p>
        </li>
      </ul>
    </div>
    <div class="message">
      <h3>留言管理</h3>
      <ul>
        <li>
          <div class="num">0</div>
          <div class="title">留言数量</div>
        </li>
        <li>
          <div class="num">0</div>
          <div class="title">新增数量</div>
        </li>
        <li>
          <div class="num">0</div>
          <div class="title">商户申请</div>
        </li>
        <li>
          <div class="num">0</div>
          <div class="title">新增商户</div>
        </li>
      </ul>
    </div>
    <div class="data">
      <h3>数据管理</h3>
      <ul>
        <li>
          <div class="num">{{moeny.mallNum}}</div>
          <div class="name">商场</div>
        </li>|
        <li>
          <div class="num">{{moeny.shopNum}}</div>
          <div class="name">商户</div>
        </li>|
        <li>
          <div class="num">{{moeny.getallActiveNum}}</div>
          <div class="name">活动</div>
        </li>|
        <li>
          <div class="num">{{moeny.todayActivityNum}}</div>
          <div class="name">今日活动</div>
        </li>
      </ul>
    </div>
    </van-pull-refresh>
    
  </div>
</template>
<script>
import { MessageBox } from 'mint-ui'
export default {
  data(){
    return{
      isLoading: false,
      time: '',
      moeny:{
        totalMoneyWx: 0,
        totalMoneyAli: 0,
        todayOrderNum: 0,
        totalUserMoneyNum: 0,
        todayMoneyNum: 0,
        sysTotal: 0,
        userNum: 0,
        monthOrderMoneyNum: 0,
        todayUserNum: 0,
        mallNum: 0,
        shopNum: 0,
        getallActiveNum: 0,
        todayActivityNum: 0 ,
        todayRefundMoney: 0,
        todayInMoney: 0,
        todayTieMoney: 0
      },
      hint:{
        resellerJoinWaitNum: 0,
        moneyOutWaitNum: 0,
        activityWaitNum: 0,
        resellerMoneyOutWaitNum: 0
        
      }
    }
  },
  methods:{
    exit(){
      for(var key in localStorage){
        if(key != "loginUserName" && key != "password"){
          localStorage.removeItem(key)
        }
      }
      this.$router.push({ name: 'login' })
    },
    // 获取当前时间
    getTime(){
      var myDate = new Date()
      var year = myDate.getFullYear()
      var month = myDate.getMonth() + 1;
      var day = myDate.getDate()
      var h = myDate.getHours()
      var m = myDate.getMinutes() 
      var s = myDate.getSeconds()
      function time(value){
        if(value<10){
          return '0'+value.toString()
        }else{
          return value
        }
      }
      this.time = `${year}.${time(month)}.${time(day)} ${time(h)}:${time(m)}:${time(s)}`
    },
    // 微信 支付宝余额
    getBalance(){
      const url = this.apiList.admin.balance
      this.$http({
        url: this.$http.adornUrl(url),
        method: "post",
        data: this.$http.adornData({},url, true)
      }).then(({data:res})=>{
        if(res.result){
          this.moeny.totalMoneyWx = res.data.totalMoneyWx ? res.data.totalMoneyWx : 0
          this.moeny.totalMoneyAli = res.data.totalMoneyAli ? res.data.totalMoneyAli : 0
          this.moeny.sysTotal = res.data.sysTotal ? res.data.sysTotal  : 0
        }
      })
    },
    // 平台资金
    capital(){
      const url = this.apiList.admin.m
      this.$http({
        url: this.$http.adornUrl(url),
        method: "post",
        data: this.$http.adornData({},url, true)
      }).then(({data:res})=>{
        if(res.result){
          console.log(res)
          this.moeny.totalUserMoneyNum = res.data.totalUserMoneyNum ? res.data.totalUserMoneyNum : 0
          this.moeny.todayMoneyNum = res.data.todayMoneyNum ? res.data.todayMoneyNum : 0
          this.moeny.userNum = res.data.userNum ? res.data.userNum : 0
          this.moeny.monthOrderMoneyNum = res.data.monthOrderMoneyNum ? res.data.monthOrderMoneyNum :0
          this.moeny.todayUserNum = res.data.todayUserNum ? res.data.todayUserNum : 0
          this.moeny.mallNum = res.data.mallNum ? res.data.mallNum : 0
          this.moeny.shopNum = res.data.shopNum ? res.data.shopNum : 0
          this.moeny.getallActiveNum =  res.data.pmSaleNum + res.data.enterNameNum + res.data.activityPlanNum
          this.moeny.todayActivityNum = res.data.todayActivityNum ? res.data.todayActivityNum : 0
          this.moeny.todayOrderNum = res.data.todayOrderNum ? res.data.todayOrderNum :0
          this.moeny.todayRefundMoney = res.data.todayRefundMoney ? res.data.todayRefundMoney : 0
          this.moeny.todayInMoney = res.data.todayInMoney ? res.data.todayInMoney : 0
          this.moeny.todayTieMoney = res.data.todayTieMoney ? res.data.todayTieMoney : 0
        }
      })
    },
    // 修改密码
    hasPass() {
      if (localStorage.getItem('hasPasssConfim')) {
        return
      }
      const url = this.apiList.login.getPass
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData('', url , true)
      }).then(({ data }) => {
        if (data.result) {
          localStorage.setItem('hasPasssConfim', true)
          if (data.data) {
          } else {
            MessageBox.confirm('您尚未设置密码,请及时前往设置').then(() => {
              this.$router.push({
                name: 'pass'
              })
            })
          }
        }
      })
    },
    // boss入口
    goAuth(){
      // this.$router.push('capital')
    },
    // 刷新
    onRefresh(){
      this.getBalance()
      this.capital()
      setTimeout(() => {
        this.$toast('刷新成功');
        this.isLoading = false;
        this.getTime()
      }, 1000)
    },
    // 待审批提示
    waitApprovalNum(){
       const url = this.apiList.admin.waitApprovalNum
       this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData('', url , true)
      }).then(({data:res})=>{
        console.log(res)
        if(res.result){
          this.hint.resellerJoinWaitNum = res.data.resellerJoinWaitNum ? res.data.resellerJoinWaitNum : 0
          this.hint.moneyOutWaitNum = res.data.moneyOutWaitNum ? res.data.moneyOutWaitNum : 0
          this.hint.activityWaitNum = res.data.activityWaitNum ? res.data.activityWaitNum : 0
          this.hint.resellerMoneyOutWaitNum = res.data.resellerMoneyOutWaitNum ? res.data.resellerMoneyOutWaitNum : 0
        }else{
          this.$toast(res.msg)
        }
      })
    }
  },
  created(){
    this.getTime()
    this.getBalance()
    this.capital()
    this.hasPass()
    this.waitApprovalNum()
  }
}
</script>
<style lang="scss">
.mint-msgbox-wrapper .mint-msgbox {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 28.4rem ;
  // height: 20.3rem ;
  padding: 0 1.5rem;
  box-sizing: border-box;
  -webkit-transform: translate3d(-50%, -50%, 0);
  transform: translate3d(-50%, -50%, 0);
  background-color: #fff;
  border-radius: .8rem;
  font-size: 16px;
  -webkit-user-select: none;
  overflow: hidden;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-transition: .2s;
  transition: .2s;
  .mint-msgbox-content{
    border: none;
    margin-top: 2.6rem;
    .mint-msgbox-message{
      line-height: none;
    }
  }
  .mint-msgbox-btns{
    display: block;
    .mint-msgbox-cancel,.mint-msgbox-confirm{
      width:11.6rem;
      height:3.6rem;
      background:rgba(255,255,255,1);
      border:1px solid rgba(153, 153, 153, 1);
      border-radius:1.8rem;
      margin-top: 2.1rem;
      margin-bottom: 2.1rem;
      float: left;
      color: #999;
    }
    .mint-msgbox-confirm{
      float: right;
      background:linear-gradient(-82deg,rgba(239,83,80,1),rgba(229,57,53,1));
      color: #fff;
      border: none;
    }
  }
}
.manage_index{
  min-height: 100%;
  background-color: #f5f5f5;
  padding-bottom: 5rem;
  .top{
    overflow: hidden;
    height: 13rem;
    width: 100%;
    background-image: url('../../assets/manage/index-big.png');
    background-size: 100% 100%;
    position: relative;
    color: #fff;
    .logo {
      position: absolute;
      font-size: 1.2rem;
      top: 1.7rem;
      left: 2.4rem;
      img{
        width: 1.8rem;
        vertical-align:middle;
        margin-right: 0.65rem;
      }
    }
    .exit{
      top: 1.7rem;
      position: absolute;
      right: 2.1rem;
      font-size: 1.2rem;
      img{
        width: 1.7rem;
        vertical-align:middle;
      }
    }
    .title{
      text-align: center;
      margin-top: 5.8rem;
      font-size: 1.3rem;
    }
    .moeny{
      text-align: center;
      margin-top: 1.6rem;
      font-size: 2rem;
    }
    p{
      font-size: 1rem;
      text-align: right;
      padding-right: 1rem;
      opacity: .6;
    }
  }
  .fund{
    background-color: #fff;
    width: 100%;
    padding: 1rem 1.5rem;
    margin-top: 1.9rem;
    box-sizing: border-box;
    h3{
      color:rgba(51,51,51,1);
      font-size: 1.4rem;
      font-weight:bold;
    }
    ul{
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      li{
        width: 10.9rem;
        height: 6.7rem;
        background-color: pink;
        margin-top: 0.9rem;
        border-radius: .6rem;
        color:rgba(254,254,254,1);
        background-size:  100% 100%;
        .moeny{
          font-size: 1.6rem;
          font-weight:bold;
          margin-top: 2rem;
          text-align: center;
        }
        .title{
          font-size: 1rem;
          margin-top: .6rem;
          text-align: center;
        }
      }
      li:nth-of-type(1){
        background-image: url('../../assets/manage/bgi1.png');
      }
      li:nth-of-type(2){
        background-image: url('../../assets/manage/bgi2.png');
      }
      li:nth-of-type(3){
        background-image: url('../../assets/manage/bgi3.png');
      }
      li:nth-of-type(4){
        background-image: url('../../assets/manage/bgi4.png');
      }
      li:nth-of-type(5){
        background-image: url('../../assets/manage/bgi5.png');
      }
      li:nth-of-type(6){
        background-image: url('../../assets/manage/bgi6.png');
      }
    }
    .manage_details{
      width: 100%;
      display: flex;
      margin-top: 1rem;
      padding-top: .5rem;
      color: #DDD;
      div{
        margin-top: -.5rem;
        width: 25%;
        color: #4C4C4C;
        height: 3rem;
        // background-color: pink;
        position: relative;
        .moeny{
          font-size: 1.2rem;
          text-align: center;
        }
        .title{
          position: absolute;
          font-size: 1rem;
          text-align: center;
          bottom: 0;
          left: 0;
          right: 0;
        }
      }
    }
  }
  .examine{
    width: 100%;
    height: 11.4rem;
    background-color: #fff;
    margin-top: 1.5rem;
    padding: 1rem 1.5rem;
    box-sizing: border-box;
    h3{
      color:rgba(51,51,51,1);
      font-size: 1.4rem;
      font-weight:bold;
    }
    ul{
      margin-top: 2.7rem;
      display: flex;
      li{
        width: 20%;
        text-align: center;
        position: relative;
        span{
          height: 1.4rem;
          position: absolute;
          background-color: #F64542;
          font-size: 1rem;
          color: #fff;
          border-radius:.7rem;
          padding-left: .45rem;
          padding-right: .45rem;
          line-height: 1.6rem;
          top: -.9rem;
          right: 1rem;
        }
        img{
          width: 2.7rem;
        }
        p{
          font-size: 1rem;
          color: #333333;
        }
      }
      li:nth-of-type(4){
        span{
          right: 1.5rem;
        }
      }
    }
  }
  .message{
    width: 100%;
    height: 12.5rem;
    background-color: #fff;
    margin-top: 1.5rem;
    padding: 1rem 1.5rem;
    box-sizing: border-box;
    h3{
      color:rgba(51,51,51,1);
      font-size: 1.4rem;
      font-weight:bold;
    }
    ul{
      margin-top: 1.8rem;
      display: flex;
      justify-content: space-between;
      li{
        width: 7.2rem;
        // background-color: pink;
        height: 6.7rem;
        text-align: center;
        background-image: url('../../assets/manage/msg1.png');
        background-size: 100%;
        .num{
          font-size: 1.6rem;
          font-weight:bold;
          color:rgba(76,76,76,1);
          margin-top: 2rem;
        }
        .title{
          font-size: 1.2rem;
          color:rgba(153,153,153,1);
          margin-top: 1rem;
        }
      }
      li:nth-of-type(2){
        background-image: url('../../assets/manage/msg2.png');
      }
      li:nth-of-type(3){
        background-image: url('../../assets/manage/msg3.png');
      }
      li:nth-of-type(4){
        background-image: url('../../assets/manage/msg4.png');
      }
    }
  }
  .data{
    width: 100%;
    height: 8.7rem;
    background-color: #fff;
    margin-top: 1.5rem;
    padding: 1rem 1.5rem;
    box-sizing: border-box;
    margin-bottom: 2rem;
    h3{
      color:rgba(51,51,51,1);
      font-size: 1.4rem;
      font-weight:bold;
    }
    ul{
      display: flex;
      margin-top: 1rem;
      color:rgba(221,221,221,1);    
      li{
        width: 25%;
        text-align: center;
        .num{
          color:rgba(76,76,76,1);
          font-size: 1.2rem;
        }
        .name{
          color:rgba(76,76,76,1);
          font-size: 1rem;
          margin-top: .5rem;
        }
      }
    }
  }
}
</style>



