<template>
  <div class="activity_num">
    <div class="header ">
      <span class="back" @click="$router.goback()">
        <img src="../../assets/my/back.png" alt="" >
      </span>
      <span>我的活动</span>
    </div>
    <div class="activity_num_main ">
      <van-tabs :sticky="true" swipeable animated :offset-top='48.5' color="#fff">
        <van-tab title="促销" v-if="isAuth('/pmSale/list')">
          <activity-num-list :shows="0"></activity-num-list>
        </van-tab>
        <van-tab title="拼团" >
          <activity-num-list :shows="1"></activity-num-list>
        </van-tab>
        <van-tab title="报名" v-if="isAuth('/enterName/list')">
          <activity-num-list :shows="2"></activity-num-list>
        </van-tab>
        <van-tab title="活动" v-if="isAuth('/activityPlan/list')">
          <activity-num-list :shows="3"></activity-num-list>
        </van-tab>
        <van-tab title="商品" v-if="isAuth('/product/list')">
          <activity-num-list :shows="4"></activity-num-list>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>
<script>
import activityNumList from "./activityNumList";
// import isAuth from "../../utils/isAuth.js"
export default {
  data() {
    return {
      active: 0
    }
  },
  created(){
    this.getData()
  },
  methods: {
    getData() {
      this.$http({
        url: this.$http.adornUrl(this.apiList.shop.get),
        method: "post",
        data: this.$http.adornData("", this.apiList.shop.get, true)
      }).then(({ data }) => {
        if (data.result) {
          localStorage.setItem("shopName", data.data.name)
        } else {
          if (data.msg === "sign验证失败") {
            localStorage.clear()
            this.$toast({
              message: "您的账号登录超时",
              iconClass: "icon icon-error"
            })
            setTimeout(() => {
              this.$router.push({ name: "login" })
            }, 2000)
          }
        }
      })
    }
  },
  components: {
    activityNumList
  },
  mounted(){
    // this.andorid.navColorChange('#f64542')
    // this.android.navColorChange('#F64542')
    console.log(window.history);
  },
}
</script>
<style lang="scss" >
// #app{
//   background-color: #f5f5f5;
// }
.activity_num {
  min-height: 100%;
  background-color: #f5f5f5;
  .van-tabs__wrap{
    // position: fixed;
    // width: 100%;
    // top: 4.2rem;
    // z-index: 999;
    margin-top: -0.8rem;
  }
  .activity_num_main {
    // min-height: 58.6rem;
    // height: 100%;
    min-height: 100%;
    background-color: #f5f5f5;
    // margin-top: 3.7rem;
    .van-tab {
      flex-basis: 20% !important;
    }
    .activity-num-list{
      min-height: 100% !important;
    }
  }
}
</style>


