<template>
  <div class="commnet_index_child">
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <van-list
      v-model="loading" 
      :finished="finished" 
      finished-text="—— 没有更多了 ——"
      @load="onLoad"
      ref="listHeight"
      >
        <div class="bags" v-for="(item, index) in dataList" :key="index" @click="goDetail(item.id)">
          <div class="wrapper">
            <img :src="item.userEntity.avatar ? item.userEntity.avatar : require('../../../assets/my/userAvatar.png')" class="avatar">
            <div class="info">
              <span>{{ item.userEntity.name ? item.userEntity.name: '众享乐用户' }}</span>
              <span>{{ item.createdAt.substring(5,10).replace('-', '月') + '日'}}</span>
            </div>
            <img class="del" src="../../../assets/comment/dels.png" v-if="data.showDel" @click.stop="del(item.id, index)">
            <div class="evaluate">
              <van-rate
                allow-half
                v-model="item.starNum"
                readonly
                color="#34BFFF"
              />
              <span>消费后评价</span>
            </div>
            <div class="content">
              <span>{{ item.content }}</span>
            </div>
            <div class="img">
              <img :src="items" v-for="(items, i) in item.imgUrls" :key="i">
            </div>
            <div class="comment_box">
              <span class="browse">浏览{{ item.browseNum }}次</span>
              <div class="comment_info">
                <span class="comment_num">
                  <img src="../../../assets/comment/message.png">
                  <span>{{ item.replyNum }}</span>
                </span>
                <span class="zan">
                  <img src="../../../assets/comment/zan.png" alt="">
                  <span>{{ item.zanNum }}</span>
                </span>
              </div>
            </div>
          </div>
        </div>
      </van-list>
    </van-pull-refresh>
  </div>
</template>
<script>
import {MessageBox} from 'mint-ui';
  export default {
    props:['data'],
    data(){
      return{
        value: 0,
        page: 0,
        dataList: [],
        loading: false,
        finished: false,
        isLoading: false
      }
    },
    methods:{
      async onLoad(){
        this.page++
        var url;
        if(this.data.num === 0){
          url = this.apiList.comment.list;
        }
        else if(this.data.num === 1){
          url = this.apiList.comment.imgList;
          
        }else if(this.data.num === 2){
          url = this.apiList.comment.timeList;
        }
        let {data} = await this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({targetActivityId: this.$route.query.id, targetActivityType: this.$route.query.type, page: this.page, size: 10}, url, true)
        })
        if(data.result){
          if(data.data.records.length >= 0){
            if (data.data.pages <= this.page) {
              this.finished = true
              if(this.data.num === 0){
                let url = this.apiList.comment.imgList;
                let {data} = await this.$http({
                  url: this.$http.adornUrl(url),
                  method: 'post',
                  data: this.$http.adornData({targetActivityId: this.$route.query.id, targetActivityType: this.$route.query.type, page: this.page, size: 10}, url, true)
                })
                if(data.result){
                  this.data.imgTotal = data.data.total
                }
              }
            }
            if(this.data.num === 0){
              this.data.total = data.data.total

            }else if(this.data.num === 1){
              this.data.imgTotal = data.data.total
            }
            data.data.records.forEach(item=>{
              let arr = []
              JSON.parse( item.imgUrls).forEach(items => {
                arr.push(items)
              })
              item.starNum = item.starNum / 2
              item['imgUrls'] = arr;
              arr = []
            })
            this.dataList = this.dataList.concat(data.data.records)
            this.loading = false;
          }
         
        }else{
          this.$toast(data.msg)
        }
      },
      async onRefresh(){
        var url;
        if(this.data.num === 0){
          url = this.apiList.comment.list;
        }
        else if(this.data.num === 1){
          url = this.apiList.comment.imgList;
        }else if(this.data.num === 2){
          url = this.apiList.comment.timeList;
        }
        let {data} = await this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({
            targetActivityId: this.$route.query.id, 
            targetActivityType: this.$route.query.type, 
            page: 1, 
            size: 10
          }, url, true)
        })
        if(data.result){
          if(data.data.records.length >= 0){
            this.$toast({
              message: '刷新成功'
            })
            if(this.data.num === 0){
              this.data.total = data.data.total
            }else if(this.data.num === 1){
              this.data.imgTotal = data.data.total
            }
            this.page = 1;
            data.data.records.forEach(item=>{
              let arr = []
              JSON.parse( item.imgUrls).forEach(items => {
                arr.push(items)
              })
              item.starNum = item.starNum / 2
              item['imgUrls'] = arr;
              arr = []
            })
            this.dataList = data.data.records
            this.isLoading = false
            this.finished = false
          }
        }else{
          this.$toast(data.msg)
        }
      },
      goDetail(id){
        this.$router.push({name: 'comment_detail', query:{id: id}})
      },
      del(id, index){
        const url = this.apiList.comment.del;
        MessageBox.confirm('确认删除？').then(async ()=>{
          this.delActivity()
          let {data} = await this.$http({
            url: this.$http.adornUrl(url),
            method: 'post',
            data: this.$http.adornData({commentId: id }, url, true)
          })
          this.closeUpload()
          if(data.result){
            this.$toast({
              message: '删除成功',
              iconClass: 'fa fa-check fa-5x'
            })
            
            this.dataList = []
            this.page = 0;
            this.onLoad()
          }else{
            this.$toast(data.msg)
          }
        })
      },
       delAllData(){
        const url = this.apiList.comment.del;
        let flag = true
        MessageBox.confirm('确认删除？').then(()=>{
          this.dataList.forEach(async item =>{
            let {data} = await this.$http({
              url: this.$http.adornUrl(url),
              method: 'post',
              data: this.$http.adornData({commentId: item.id }, url, true)
            })
            if(data.result){
              if(flag){
                this.dataList = []
                this.page = this.page-1;
                this.onLoad()
                flag = false
                this.data.showDel = false
                this.$toast({
                  message: '删除成功',
                  iconClass: 'fa fa-check fa-5x'
                })
              }
              
            }else{
              this.$toast(data.msg)
            }
          })
        })
      }
    },
    mounted(){
      this.$refs.listHeight.$el.style.minHeight = document.documentElement.clientHeight   - 13.2 * 10 + 'px'
    },
  }
</script>
<style lang="scss">
  .van-rate__icon--half {
    position: absolute;
    top: 0;
    left: 0;
    width: .5em;
    overflow: hidden;
  }
  .commnet_index_child{
    margin-top: 1rem;
    .van-list{
      padding: 1.4rem 0;
    }
      .bags{
        font-family:Microsoft YaHei;
        position: relative;
        width: 33.45rem;
        margin: 0 auto 2rem;
        background: #fff;
        border-radius: 0.4rem;
        padding: 1rem 0;
        .wrapper{
          margin: 0 2.35rem;
          .avatar{
            position: absolute;
            left: -1.2rem;
            top: -1.2rem;
            width: 3.2rem;
            height: 3.2rem;
            border-radius: 50%;
          }
          .del{
            position: absolute;
            top: -0.95rem;
            right: -0.95rem;
            width: 1.9rem;
            height: 1.9rem;
          }
          .info, .evaluate{
            display: flex;
            justify-content: space-between;
            padding: 0.2rem 0;
          }
          .info{
            padding-top: 1rem;
            span{
              &:first-child{
                color: #666;
                font-size: 1.4rem;
              }
              &:last-child{
                color: #999;
                font-size: 1.1rem;
              }
            }
            
          }
          .evaluate{
            span{
              color: #999;
              font-size: 1.1rem;
            }
          }
          .content{
            margin-top: 1rem;
            span{
              color: #171717;
              font-size: 1.4rem;
            }
          }
          .img{
            margin-top: 1rem;
            img{
              width: 9.1rem;
              height: 9.1rem;
              margin-left: 0.5rem;
              &:first-child{
                margin: 0;
              }
              &:nth-child(4){
                margin: 0;
              }
            }
            
          }
          .comment_box{
            display: flex;
            justify-content: space-between;
            margin-top: 1rem;
            .browse{
              color: #929191;
              font-size: 1.1rem;
            }
            .comment_info{
              .comment_num, .zan{
                display: inline-block;
                height: 2rem;
                line-height: 2rem;
                border: 0.1rem solid #d7d7d7;
                border-radius: 1rem;
                padding: 0 1rem;
                span{
                  color: #333;
                  font-size: 1.1rem;
                }
              }
              .zan{
                img{
                  margin-top: -0.3rem;
                }
              }
              img{
                vertical-align: middle;
                width: 1.3rem;
                height: 1.3rem;
                // 
              }
            }
          }
        }
        
    }
    
  }
</style>


