<template>
<div class="authUsePayKey">
  <div class="header">
    <div class="back" @click="goBack">
        <img src="../../assets/login/back.png" alt="" style="width: 1rem; height: 1.8rem;vertical-align: middle;">
    </div>
    <span style="float: right;font-size: 1.4rem;">授权扫码</span>
  </div>
  <div class="main_banner">
    <img src="../../assets/login/authbanner.png" alt="">
  </div>
  <p class="text">对准取货码扫一扫</p>
  <get-scancode :sign="sign" :sign_detail="sign_detail" :moduleId="moduleId" style="" @success="success"/>
  <div class="bottom" id="authUsePayKeyActivityDetail">
    <div class="bottom_header">
      <span>授权商户：</span>
      <span class="shopName"><img :src="moduleLogo" alt="">{{ moduleName }}</span>
      <span class="shopTotle" @click="goList"><img src="../../assets/index/total.png" alt="">{{ alNum }}</span>
    </div>
    <div class="bottom_footer clearfix">
      <div class="left">
        <img :src="activityImgUrl" alt="">
      </div>
      <div class="right">
        <p>{{ activityName }}</p>
        <div class="main-progress"> 
          <div class="main-progress-number">已抢{{ alreadyNum }}件</div>
          <div class="main-progress-gress" :style="barStyle(parseInt(alreadyNum / totalNum * 100))"></div>
          <div class="main-progress-percent"><p>{{ parseInt(alreadyNum / totalNum * 100) }}%</p></div>
        </div>
        <p class="totle">共{{ totalNum }}件</p>
      </div>
    </div>
  </div>
</div>
</template> 
<script>
import GetScancode from '../utils/getScanCode.vue'
import sha256 from 'sha256'
export default {
  data() {
    return {
      sign: '',
      authCode: '',
      activityId: '',
      activityImgUrl: '',
      activityName: '',
      activityType: '',
      alreadyNum: '',
      moduleId: '',
      moduleLogo: '',
      moduleName: '',
      totalNum: '',
      alNum: '',
      sign_detail: ''
    }
  },
  components: {
    GetScancode
  },
   methods: {
    goBack(){
      this.$router.goback()
    },
    barStyle(percentage) {
      const style = {};
      style.width = percentage + '%';
      return style;
    },
    success(data) {
      const url = this.apiList.authorization.list
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData({ page: 1, size: 10000,moduleId: this.moduleId,authCode: sha256(this.authCode)}, url, true)
      }).then(({ data }) => {
        this.alNum  = data.data.total
      })
    },
    goList() {
      this.$router.push({name: 'authorizationList', params: {
        moduleId: this.moduleId,
        authCode: this.authCode 
      }})
    },
    getWindowHeight() {
      var windowHeight = 0;
      if (document.compatMode == "CSS1Compat") {
          windowHeight = document.documentElement.clientHeight;
      } else {
          windowHeight = document.body.clientHeight;
      }
      return windowHeight;
    },
  },
  mounted() {
    const dom = document.getElementById('authUsePayKeyActivityDetail')
    const height = this.getWindowHeight()
    this.$nextTick(() => {
      dom.style.top = height - dom.offsetHeight + 'px'
    })
    const url = this.apiList.authorization.qr
    const url_detail = this.apiList.active.detail
    let data = this.$route.params.activityData
    if (!data) {
      data = JSON.parse(localStorage.getItem('authUsePayKey_activityData'))
    } else {
      const JSONdata = JSON.stringify(this.$route.params.activityData)
      localStorage.setItem('authUsePayKey_authCode', this.$route.params.authCode)
      localStorage.setItem('authUsePayKey_activityData', JSONdata)
    }
    this.moduleId = data.moduleId
    this.activityImgUrl = data.activityImgUrl
    this.activityName = data.activityName
    this.activityType = data.activityType
    this.alreadyNum = data.alreadyNum
    this.moduleLogo = data.moduleLogo
    this.moduleName = data.moduleName
    this.totalNum = data.totalNum
    this.alNum = data.alNum
    this.authCode = this.$route.params.authCode ? this.$route.params.authCode : localStorage.getItem('authUsePayKey_authCode')
    this.sign = sha256(url + sha256(this.authCode))
    this.sign_detail = sha256(url_detail + sha256(this.authCode))
  },
  created() {
    this.android.navColorChange("#FA3F5C")
    // 
  },
  destroyed() {
    this.android.navColorChange("#ffffff")
  }
}
</script>
<style lang="scss">
.authUsePayKey{
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  // background: -webkit-linear-gradient(top, #f53b53 , #F4364C); /* Safari 5.1 - 6.0 */
  // background: -o-linear-gradient(top, #f53b53, #F4364C); /* Opera 11.1 - 12.0 */
  // background: -moz-linear-gradient(top, #f53b53, #F4364C); /* Firefox 3.6 - 15 */
  // background: linear-gradient(top, #f53b53 , #F4364C); /* 标准的语法 */
  background: #f53b53;
  .header{
    background: #f53b53;
    span{
      color: #fff;
    }
  }
  .text{
    color: #fff;
    font-size: 1.4rem;
    text-align: center;
    margin-top: 1.7rem;
  }
  .main_banner{
    width: 23.6rem;
    height: 18.2rem;
    img{
      width: 100%;
      height: 100%;
      margin-left: 4.3rem;
    }
  }
  .bottom{
    width: 100%;
    height: 13.6rem;
    background: url('../../assets/index/bottomBanner.png');
    background-size: 100% 100%;
    position: fixed;
    .bottom_header{
      font-size: 1.2rem;
      color: #fff;
      height: 4.5rem;
      line-height: 4.5rem;
      box-sizing: border-box;
      padding: 0 1.6rem;
      .shopName{
        img{
          width: 2.6rem;
          height: 2.6rem;
          overflow: hidden;
          border-radius: 100%;
          vertical-align: middle;
          margin-left: .5rem;
          margin-right: 1.5rem;
        }
      }
      .shopTotle{
        margin-left: 3rem;
        font-size: 2rem;
        vertical-align: middle;
        display: inline-block;
        color: #E0E0E0;
        float: right;
        img{
          width: 1.2rem;
          height: 1rem;
          margin-right: .5rem;
        }
      }
    }
    .bottom_footer{
      display: inline-block;
      width: 100%;
      box-sizing: border-box;
      height: 8.5rem;
      padding: 0 2.9rem;
      .left{
        display: inline-block;
        vertical-align: middle;
        padding-top: 1.5rem;
        padding-right: 1rem;
        img{
          width: 6.1rem;
          height: 6.4rem;
          border-radius: .8rem;
        }
      }
      .right{
        display: inline-block;
        vertical-align: middle;
        padding-top: 1.5rem;
        p{
          max-width: 22rem;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          color: #fff;
          font-size: 1.4rem;
          margin-bottom: 1rem;
        }
        .main-progress{
          width: 12rem;
          height: 1.3rem;
          line-height: 1.3rem;
          display: inline-block;
          background: #D7D7D7;
          font-size: 0.9rem;
          border-radius: 0.7rem;
          position: relative;
          overflow: hidden;
          // margin-top: 2rem;
          .main-progress-number{
            color: #FFF;
            position: absolute;
            left: 0.6rem;
            //top: 0.2rem;
            line-height: 1.3rem;
            z-index: 4;
            font-size: 0.95rem;
          }
          .main-progress-percent{
            color: #DB455E;
            position: absolute;
            right: 0.5rem;
            //top: 0.2rem;
            z-index: 4;
            p{
              font-size: 0.95rem;
            }
          }
          .main-progress-gress{
            background: #FC993C;
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            border-radius: 0.7rem;
            z-index: 3;
          }
        }
        .totle{
          float: right;
          padding-left: 1rem;
          width: 10rem;
        }
      }
    }
  }
}
</style>


