<template>
  <div class="shopdesc">
    <div class="header">
      <span class="back" @click="goback">
        <img src="../../assets/my/back.png" alt="">
      </span>
      <span>修改店铺详情</span>
      <span class="sure">
        <div @click="finish">确定</div>
      </span>
    </div>
    <div class="nameInput">
      <vue-html5-editor
        ref="html5editor"
        :content="content"
        :z-index="1000"
        :auto-height="true"
        :show-module-name="false"
        @change="changeContent"
        @chooseImg="chooseImg"
      ></vue-html5-editor>
    </div>
  </div>
</template>
<script>
import { eventBus } from "../../utils/eventBus.js";
import { Base64 } from "js-base64";
export default {
  data() {
    return {
      content: ""
    };
  },
  methods: {
    getData() {
      this.loading()
      this.$http({
        url: this.$http.adornUrl(this.apiList.shop.get),
        method: "post",
        data: this.$http.adornData("", this.apiList.shop.get, true)
      }).then(({ data }) => {
        this.closeUpload()
        if (data.result) {
          this.content = Base64.decode(data.data.shopDesc);
        } else {
          this.$toast(data.msg);
        }
      });
    },
    goback() {
      this.$router.goback();
    },
    chooseImg() {
      this.android.getImg();
    },
    changeContent(val) {
      // console.log(val)
      this.content = val;
    },
    finish() {
      // var conHtml = document.getElementsByClassName('content')
      // var pObj = document.createElement("p")
      // pObj.innerHTML = 'hhh'
      // conHtml[0].appendChild(pObj)
      console.log(this.content);
      var u = navigator.userAgent;
      var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
      // alert(isiOS)
      if (isiOS) {
        var conHtml = document.getElementsByClassName("content");
        // alert(conHtml[0].innerHTML)
        this.content = conHtml[0].innerHTML;
      }
      // alert(this.content )
      if (!this.content) {
        return this.$toast("您输入的内容为空，请确认您要修改的店铺名称");
      }
      // alert(Base64.encode(this.content))
      this.upload()
      const vm = this;
      const url = this.apiList.shop.edit;
      this.$http({
        url: this.$http.adornUrl(url),
        method: "post",
        data: this.$http.adornData(
          { shopDesc: Base64.encode(this.content) },
          url,
          true
        )
      }).then(({ data }) => {
        this.closeUpload()
        if (data.result) {
          this.$toast({
            message: '修改成功',
            iconClass: 'fa fa-check fa-5x'
          })
        } else {
          this.$toast(data.msg);
        }
        setTimeout(function() {
          vm.goback();
        }, 600);
      });
    },
    dataURLtoFile(dataurl, filename = "0000img") {
      //将base64转换为文件
      var arr = dataurl.split(","),
        mime = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]),
        n = bstr.length,
        u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new File([u8arr], filename, { type: mime });
    }
  },
  mounted() {
    const vm = this;
    this.getData();
    eventBus.$on("getImgSuccess", res => {
      vm.$refs.html5editor.getImg(
        vm.dataURLtoFile("data:image/png;base64," + res.image)
      );
      vm.android.progressHUDend();
    });
  }
};
</script>
<style lang="scss">
.shopdesc {
  sub,
  sup {
    font-size: 75%;

    line-height: 0;

    position: relative;

    vertical-align: baseline;
  }
  sup {
    top: -0.5em;
  }
  sub {
    bottom: -0.25em;
  }
  .sure {
    float: right;
    margin-top: 0.1rem;
    color: #fff;
  }
  .content {
    img {
      display: block;
      max-width: 100%;
    }
    p {
      img {
        display: block;
        max-width: 100%;
      }
    }
  }
}
</style>



