<template>
  <div class="details_search">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../../../assets/my/back.png" alt=""></span>
      <span class="search_box">
        <img src="../../../../assets/head/header_search.png" alt="">
        <input type="number" placeholder="请输入订单号进行搜索" ref="iphone" v-model="keyword">
        <img src="../../../../assets/auth/X.png" v-if="keyword.length > 0" @click="keyword = ''">
      </span>

      <span class="text" @click="searchData">搜索</span>
    </div>
    <div class="main">
      <div class="history" v-if="showHistory">
        <span class="searchHis">
          <span class="historyText">搜索历史</span>
          <img src="../../../../assets/search/del.png" @click="delHistory" alt="">
        </span>
        <span class="textContent">
          <span class="wrapper" v-for=" (item, index) in historyList" :key="index">
            <span class="text" @click="getText(index)" @touchstart="star(index)" @touchend="end">{{item}}</span>
          </span>
        </span>
      </div>
      <van-list
        v-model="loading" 
        :finished="finished" 
        finished-text="—— 没有更多了 ——"
        @load="onLoad"
        :immediate-check="false"
        ref="list"
      >
        <van-cell  v-for="(item, index) in searchList" :key="index">
          <div class="wrapper">
            <div class="top">
              <span class="order">
                <span class="orderText">订单号：</span><span class="orderNum" v-html="orderNum(item.outTradeNo)"></span>
              </span>
              <span class="state" style="color:#333">
                {{item.useState == 1 ? '待使用' : (item.useState == 101 ? '已提货' : '已退款')}}
              </span>
            </div>
            <div class="centent">
              <span class="left">
                 <span><img :src="item.userAvatar ? item.userAvatar : require('../../../../assets/my/userAvatar.png')" alt=""></span>
                <span class="username">
                  {{item.userNickName ? item.userNickName : '众享乐用户'}}
                </span>
              </span>
              
            </div>
            <div class="time">
              <span class="timeText">付款时间：</span>
              <span class="timeNum">{{item.createdAt}}</span>
            </div>
            <div class="price">
              <span class="priceText">金额：</span><span class="priceNum">￥{{(item.price / 100).toFixed(2)}}</span>
            </div>
            <div class="getGoodTime" v-if="item.useState == 101">
              <span>提货时间：</span><span>{{ item.updatedAt }}</span>
            </div>
          </div>
          <div class="btn" v-if="item.useState == 1">
            <mt-button @click="refund(item.orderId, index)">
              办理退款
            </mt-button>
          </div>
        </van-cell>
      </van-list>
    </div>
  </div>
</template>
<script>
  import { MessageBox } from 'mint-ui'
  export default {
    data(){
      return{
        page: 1,
        keyword:'',
        keywords:'',
        showHistory: true,
        loading: false,
        finished: false,
        loop:'',
        activeUrl: this.$route.query.list,
        dataList:[],
        searchList: [],
        historyList:[]
      }
    },
    methods:{
      goBack(){
        this.$router.goback()
      },
      onLoad(){
        return new Promise((resolve, reject)=>{
          const activeUrl = this.activeUrl
          const url = this.apiList[activeUrl].orderList
          this.page++
          this.$http({
            url:this.$http.adornUrl(url),
            method:'post',
            data: this.$http.adornData({activityId: this.$route.query.id, page: this.page, size: 10, nameLike: this.keyword }, url, true)
          }).then(({ data }) => {
            if( data.result ){
              this.searchList = this.searchList.concat(data.data.records);
            }
            if (data.data.pages <= this.page) {
                this.finished = true
            }
            this.$nextTick(() => {
              resolve(true)
              this.loading = false
            })
          })
        }) 
      },
      star(index){
        this.loop = setTimeout(()=>{
          MessageBox.confirm('确定删除此纪录？').then(()=>{
            this.orderHisList.splice(index,1)
            const his = this.activeUrl + '_' + 'orderHisList'
            localStorage.setItem(his, JSON.stringify(this.orderHisList) )
          })
        },500)
      },
      end(item){
        clearTimeout(this.loop)
      },
      orderNum(val){
        val = val + '';
        if(val.indexOf(this.keyword !== -1 && this.keyword !== '')){
          return val.replace(this.keyword, '<m style="color: red">'+ this.keyword+'</m>')
        }else{
          return val
        }
      },
      getText(index){
        this.historyList.forEach((item, i)=>{
          if(index == i){
            this.keyword = item
            this.searchData()
          }
        })
      },
      searchData(){
        return new Promise((resolve, reject)=>{
          const activeUrl = this.activeUrl
          const url = this.apiList[activeUrl].orderList
          this.$http({
            url:this.$http.adornUrl(url),
            method:'post',
            data: this.$http.adornData({activityId: this.$route.query.id, page: 1, size: 10, idLike: this.keyword }, url, true)
          }).then(({ data }) => {
            if( data.result ){
              this.dataList = data.data.records;
              this.$nextTick(()=>{
                resolve(true)
                this.searchList = []
                this.keywords = ''
                let key = this.keyword.trim()
                if(key == '') return this.$toast('关键词不能为空')
                this.dataList.filter( item=>{
                  if(item.outTradeNo.indexOf(this.keyword) != -1){
                    this.showHistory = false
                    return this.searchList.push(item)
                  }
                })
                if(this.searchList.length == 0){
                  this.showHistory = true
                  this.$toast('暂时没有数据')
                }
                //判断数据中有没有重复的值
                if(this.historyList.length !== 0){
                    this.historyList.forEach((item, index)=>{
                    if(this.historyList[index] === this.keyword){
                      this.keywords = item
                      this.keywordsIndex = index
                    }
                  })
                }
                if(this.historyList.length < 4){
                  if(this.keywords != ''){
                    //替换重复的值
                    this.historyList.splice(this.keywordsIndex, 1)
                    this.historyList.unshift(this.keywords)
                  }else{
                    this.historyList.unshift(this.keyword)
                  } 
                }else{
                  if(this.keywords != ''){
                    this.historyList.splice(this.keywordsIndex, 1)
                    this.historyList.unshift(this.keywords)
                  }else{
                    this.historyList.pop()
                    this.historyList.unshift(this.keyword)
                  }
                }
                const his = this.activeUrl + '_' + 'orderHisList'
                localStorage.setItem( his, JSON.stringify(this.historyList) )
              })
            }else if(data.msg == 'sign验证失败'){
              this.$toast('您的账户登录超时')
              setTimeout(()=>{
                localStorage.clear();
                this.$router.push({name: 'login'})
              },1000)
            }
          })
         
        })
        
      },
      delHistory(){
        MessageBox.confirm('确定删除全部记录？').then(()=>{
          const his = this.activeUrl + '_' + 'orderHisList'
          localStorage.removeItem(his)
            this.historyList = []
            this.showHistory = false
          })
      },
      refund( id, index){
        const  url = this.apiList.businessRefund.refund;
        MessageBox.confirm('确认退款？').then( ()=>{
          this.$http({
            url: this.$http.adornUrl(url),
            method: 'post',
            data: this.$http.adornData({ orderId: id }, url, true)
          }).then(({data})=>{
            if(data.result){
              this.$toast("退款成功")
              this.searchList[index].useState = 201
            }
          })
        })
      }
    },
    watch:{
      keyword: function(){
        if(this.keyword == ''){
          this.showHistory = true;
          this.searchList = []
          this.page = 1
          this.$refs.list.$el.style.display = 'none'
        }else{
          this.$refs.list.$el.style.display = 'block'
        }
      }
    },
    mounted(){
      var u = navigator.userAgent
      var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
      // 判断是否为苹果手机
        if(isiOS){
          this.$refs.iphone.style.marginTop = 0
        }
    },
    created(){
      // this.getData()
      const his = this.activeUrl + '_' + 'orderHisList'
      if(localStorage.getItem(his)){
        this.historyList = JSON.parse(localStorage.getItem(his)) 
      }
      
    }
  }
</script>
<style lang="scss">
.details_search{
  min-height: 100%;
  background: #F5F5F5;
  .header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    .back{
      line-height: 0;
      img{
        height: 1.8rem;
      }
    }

    .search_box{
      width: 20.9rem;
      padding: 0.5rem 0;
      margin-left: 3rem;
      background: #F5F5F5;
      border-radius: 1.45rem;
      display: flex;
      align-items: center;
      overflow: hidden;
      padding-right: 2rem;
      img{
        width: 1.8rem;
        height: 1.9rem;
        vertical-align: top;
        margin-left: 1rem;
      }
      input{
        outline: none;
        border: none;
        background: #F5F5F5;
        line-height: 2rem;
        margin-left: 0.5rem;
        font-size: 1.3rem;
        color: #333;
        margin-top: 0.5rem;
        width: 100%;
        
      }
       ::-webkit-input-placeholder {
         color: #666;
        line-height: 2rem;
        font-size: 1.3rem;
        
      }
      ::-webkit-input-placeholder  {
        color: #666;
        line-height: 2rem;
        font-size: 1.3rem;
        
      }
      :-moz-placeholder {
        color: #666;
        line-height: 2rem;
        font-size: 1.3rem;
      }
      ::-moz-placeholder {
        color: #666;
        line-height: 2rem;
        font-size: 1.3rem;
      }
      :-ms-input-placeholder {
        color: #666;
        line-height: 2rem;
        font-size: 1.3rem;
      }
    }
    .text{
        margin-left: 1rem;
        color: #fff;
        font-size: 1.4rem;
        height: 0;
        line-height: 0;
      }
  }
  .main{
    margin-top: 1.4rem;
    .history{
      margin: 0 1.6rem;
      .searchHis{
        display: flex;
        justify-content: space-between;
        .historyText{
          color: #333;
          font-size: 1.4rem;
        }
        img{
          width: 1.4rem;
          height: 1.5rem;
          vertical-align: middle;
          margin-top: 0.5rem;
        }
      }
      .textContent{
        .wrapper{
          display: inline-block;
          margin-top: 1.2rem;
          .text{
            display: inline-block;
            padding: 0 1rem;
            max-width: 12.8rem;
            height: 3.3rem;
            margin-right: 1rem;
            line-height: 3.3rem;
            text-align: center;
            border-radius: 1.65rem;
            font-size: 1.2rem;
            background: #EBEBEB;
            user-select: none;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            min-width: 5.9rem;
          }
        }
      }
    }
    .van-cell{
      width: 34.3rem;
      margin: 0 auto 1.2rem;
      border-radius: 0.8rem;
      background: #fff;
      padding: 0;
      .wrapper{
        padding-bottom: 1.2rem;
        .top{
          width: 100%;
          height: 5rem;
          display: flex;
          justify-content: space-between;
          padding: 0.8rem 1.3rem;
          background: url("../../../../assets/merchant/dingdan.png") no-repeat;
          background-size: 100% 100%;
          background-color: #eee;
          box-sizing: border-box;
          .orderText,.orderNum{
            color: #333;
            font-size: 1.3rem;
          }
          .state{
            color: #E73E3B;
            font-size: 1.1rem;
          }              
        }
        .centent{
          display: flex;
          justify-content: space-between;
          padding: 0 1.3rem 0.5rem;
          img{
            display: inline-block;
            width: 2.7rem;
            height: 2.7rem;
            border-radius: 50%;
            
          }
          .username{
            display: inline-block;
            vertical-align: top;
          }
          .username, .priceText, .priceNum{
            color: #333;
            font-size: 1.3rem;
          }
        }
        .time{
          margin-left: 2rem;
          margin-top: -0.5rem;
          padding-bottom: 0.5rem;
          .timeText,.timeNum{
            color: #333;
            font-size: 1.3rem;
          }
        }
        .price{
          color: #333;
          font-size: 1.3rem;
          margin-left: 2rem;
        }
        .getGoodTime{
          margin-left: 2rem;
          span{
            font-size: 1.3rem;
            color: #333;
          }
        }
      }
      .btn{
        float: right;
        padding-bottom: 1rem;
        button{
          width: 7.3rem;
          height: 2.7rem;
          margin-right: 1rem;
          border-radius: 1.35rem;
          background: #fff;
          color: #333;
          font-size: 1.2rem;
        }
      }
      
    }
  }
    
}
</style>


