<!-- 提现方式 -->
<template>
<div class="withdrawalType">
  <div class="header red">
    <span class="back" @click="goback"><img src="../../assets/vip/back.png" alt="" ></span>
    <span>提现</span>
    <span class="deposit" @click="addBank"><img src="../../assets/vip/deposit.png" alt=""></span>
  </div>
  <div class="main_info">
      <div class="form">
        <p class="monery_num">可提现金额：<span>{{money | figure}}</span> 元,    {{notnumber}}元不可提现</p>
        <!-- <div>请输入提现金额：</div> -->
        <div class="input"><input type="number" v-model="outnumber" :max="money" placeholder="请输入提现金额（最低一元）" oninput="this.value = this.value.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3')"></div>
        <div class="wrapper">
          <p class="cost">
            <span>提现费用：</span>
            <span>{{ ( outnumber * moduleChou / 10000 ).toFixed(2)}}元</span>
          </p>
          <p class="arrival_time">
            <span>预计到账时间：</span>
            <span>{{this.setDate(new Date().getTime() + 1000 * 60 * 60 * 24 * 3).slice(0, 10) }}到账</span>
          </p>
        </div>
      </div>
      
    </div>
  <div class="way">
    <div class="about_xzl">
      <p class="title">选择提现方式</p>
    </div>
    <!-- 微信 -->
    <div class="about_xzl" @click="goWithdrawal(101)">
      <p>
        <span>
          <img v-if="!wxChecked" src="../../assets/vip/check.png" alt="" class="img-left">
          <img v-else src="../../assets/vip/checked.png" alt="" class="img-left">
          <img class="List_icon" src="../../assets/vip/3.png">微信提现
          <span v-show="!wxRealName">未绑定</span>
        </span>
        <!-- 未绑定 -->
        <span v-if="!wxRealName" @click.stop="bindWx">
          <img src="../../assets/vip/binding.png" alt="" >
        </span>
        <span v-else  @click.stop="abindWx">
          <img src="../../assets/vip/bind.png" alt="">
        </span>
      </p>
    </div>
    <!-- 支付宝 -->
    <div class="about_xzl" @click="goWithdrawal(201)">
      <p>
        <span>
          <img v-if="!zfbChecked" src="../../assets/vip/check.png" alt="" class="img-left">
          <img v-else src="../../assets/vip/checked.png" alt="" class="img-left">
          <img class="List_icon" src="../../assets/vip/2.png">支付宝提现
          <span v-show="!aliRealName">未绑定</span>
        </span>
        <!-- 未绑定 -->
        <span v-if="!aliRealName" @click.stop="bindzfb">
          <img src="../../assets/vip/binding.png" alt="" >
        </span>
        <span v-else @click.stop="abindzfb">
          <img src="../../assets/vip/bind.png" alt="" >
        </span>
      </p>
    </div>
    <!-- 银行卡 -->
    <div class="about_xzl">
      <div v-for="item in dataList" :key="item.id"  @click="goWithdrawal(1,item.id)">
        <p v-if="item.bankCode == 'ICBC'">
          <span>
            <img v-if="bankChenke!=item.id" src="../../assets/vip/check.png" alt="" class="img-left">
            <img v-else src="../../assets/vip/checked.png" alt="" class="img-left">
            <img src="../../assets/my/zgyh.png" alt="" class="List_icon">
            中国工商银行(尾号{{ item.cardNum }})
          </span>
        </p>
        <p v-if="item.bankCode == 'CCB'">
          <span>
            <img v-if="bankChenke!=item.id" src="../../assets/vip/check.png" alt="" class="img-left">
            <img v-else src="../../assets/vip/checked.png" alt="" class="img-left">
            <img src="../../assets/my/jsyh.png" alt="" class="List_icon">
            中国建设银行(尾号{{ item.cardNum }})
          </span>
        </p>
        <p v-if="item.bankCode == 'PSBC'">
          <span>
            <img  v-if="bankChenke!=item.id" src="../../assets/vip/check.png" alt="" class="img-left">
            <img v-else src="../../assets/vip/checked.png" alt="" class="img-left">
            <img src="../../assets/my/zgyzyh.png" alt="" class="List_icon">
            中国邮政银行(尾号{{ item.cardNum }})
          </span>
        </p>
        <p v-if="item.bankCode == 'ABC'">
          <span>
            <img v-if="bankChenke!=item.id" src="../../assets/vip/check.png" alt="" class="img-left">
            <img v-else src="../../assets/vip/checked.png" alt="" class="img-left">
            <img src="../../assets/my/nyyh.png" alt="" class="List_icon">
            农业银行(尾号{{ item.cardNum }})
          </span>
        </p>   
      </div>
    </div>
  </div>
  <div class="hint">
    <img src="../../assets/vip/explain.png" alt="">
    <p>1、促销：用户支付后全额不可提，确认收货后全额可提。</p>
    <p>2、报名：用户支付后可提一半，确认收货后全额可提。</p>                    
  </div>
  <mt-button class="button" @click="deposit">
    提现
  </mt-button>
</div>
</template>
<script>
import { eventBus } from '../../utils/eventBus.js'
import { MessageBox } from 'mint-ui'
export default {
  data() {
    return{
      openid: '',
      type: '',
      accessToken: '',
      realName: '',
      aliRealName: '',
      aliAccount: '',
      wxRealName: '',
      show: false,
      // 可提现金额
      money:'',
      outnumber:'',
      notnumber:'',
      moduleChou: '',
      // 银行卡
      dataList:'',
      // 选中
      wxChecked: false,
      zfbChecked: false,
      bankChenke: false
    }
  },
  filters:{
    figure(val){
      // return val.toFixed(2)
      // console.log(val.toDecimal(2))
      if(val === ''){
        return val
      }
      var num = val.toFixed(2)
      let result = ''; //结果
        num = num.split('.') //以小数点进行切割
        let lastNum = num[1] //得出小数点后的数值
        num = num[0] //小数点前的数值
        if(num.length > 3){
          while (num.length > 3) {
            result = ',' + num.slice(-3) + result; //每次循环取num值的最后三位数 + 之前的结果
            num = num.slice(0, num.length - 3); //每次切割掉num最后三位
          } 
        }
      return num + result + '.' + lastNum
    }
  },
  methods: {
    goback() {
      this.$router.goback()
    },
    sliceTime(){
      return 
    },
    addBank() {
      if( !this.isAuth(this.apiList.bankCard.list) ){
        return this.$toast("权限不足，请联系管理员修改")
      }
      this.$router.push('/myBanklist')
    },
    // 获得当前可提现金额
    getData() {
      const url = this.apiList.money.get
      this.loading()
       this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData('', url, true)
      }).then(({ data }) => {
        this.closeUpload()
        if (data.result) {
          this.money = data.data.canOutMoney / 100
          var num  = (data.data.totalMoney/100 - this.money ).toFixed(2)
          let result = ''; //结果
          num = num.split('.') //以小数点进行切割
          let lastNum = num[1] //得出小数点后的数值
          num = num[0] //小数点前的数值
          if(num.length > 3){
            while (num.length > 3) {
              result = ',' + num.slice(-3) + result; //每次循环取num值的最后三位数 + 之前的结果
              num = num.slice(0, num.length - 3); //每次切割掉num最后三位
            } 
          }
          this.notnumber =  num + result + '.' + lastNum
          this.moduleChou = data.data.moduleChou
        } else {
          if (data.msg === 'sign验证失败') {
            localStorage.clear()
            this.$toast({
              message: '您的账号登录超时',
              iconClass: 'icon icon-error'
            })
            setTimeout(() => {
              instance.close()
              this.$router.push({ name:  'login' })
            }, 2000)
          }
        }
      })
    },
    // 最大可提现金额
    moneyMax() {
      if (this.outnumber > this.money) {
        this.$toast('提现金额超过可提现余额')
        this.outnumber = this.money
      }
    },
    // 选中功能
    goWithdrawal(type,id) {
      switch(type) {
        case 1:
          // this.type = 1
          // this.$router.push({
          //   name: 'withdrawal',
          //   query: {
          //     type: type
          //   }
          // })
          this.wxChecked = false
          this.zfbChecked = false
          if(this.bankChenke == id){
            return this.bankChenke = false
          }
          this.bankChenke = id
          console.log(id)
          break
        case 101:
          this.type = 101
          // if (this.openid) {
          //   this.$router.push({
          //     name: 'withdrawal',
          //     query: {
          //       type: this.type,
          //       openid: this.openid
          //     }
          //   })
          // } else {
          //   this.$toast('请先绑定微信号')
          // }
          if(!this.openid){
            this.$toast('请先绑定微信号')
          }else{
            this.zfbChecked = false
            this.bankChenke = false
            this.wxChecked = !this.wxChecked
          }
          break
        case 201:
          // this.type = 201
          // this.$router.push({
          //   name: 'withdrawal',
          //   query: {
          //     type: this.type
          //   }
          // })
          if(!this.aliRealName){
            this.$toast('请先绑定支付宝')
          }else{
            this.wxChecked = false
            this.bankChenke = false
            this.zfbChecked = !this.zfbChecked
          }
          break
      }
    },
    // 跳转绑定微信账号
    bindWx() {
      // MessageBox.prompt('请确保您输入的是真实有效的姓名，因虚假信息造成的错误概不负责', '请输入绑定人姓名').then(({ value }) => {
      //   console.log(value)
      //   this.realName = value
      //   if (value) {
      //     this.android.loginThird(0)
      //   } else {
      //     this.$toast('请确保您输入的是真实有效的提款人姓名')
      //   }
      // })
      if( !this.isAuth(this.apiList.money.bindWxInfo) ){
        return this.$toast("权限不足，请联系管理员修改")
      }
      this.$router.push({name:'bindWechat'})
    },
    // bindWxRequest() {
    //   const url = this.apiList.money.bindWxInfo
    //   this.$http({
    //     url: this.$http.adornUrl(url),
    //     method: 'post',
    //     data: this.$http.adornData({ openId: this.openid, accessToken: this.accessToken, realName: this.realName }, url, true)
    //   }).then(({ data }) => {
    //     if (data.result) {
    //       this.$toast('绑定提现微信号成功')
    //       this.getDataList()
    //     } else {
    //       this.$toast(data.msg)
    //     }
    //   })
    // },
    // 解绑微信号
    abindWx() {
      if( !this.isAuth(this.apiList.money.aBindWxInfo) ){
        return this.$toast('权限不足，请联系管理员修改')
      }
      let str = `您的微信将解绑，请确认`
      MessageBox.confirm(str).then(() =>{
        this.upload()
        const url = this.apiList.money.aBindWxInfo
        this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData('', url, true)
        }).then(({ data }) => {
          this.closeUpload()
          if (data.result) {
            this.$toast({
              message: '解绑成功',
              iconClass: 'fa fa-check fa-5x'
            })
            this.getDataInfo()
          } else {
            this.$toast(data.msg)
          }
        })
      })
    },
    // 跳转绑定支付宝账号
    bindzfb() {
      if( !this.isAuth(this.apiList.money.bindAliInfo) ){
        return this.$toast('权限不足，请联系管理员修改')
      }
      this.$router.push({name: 'bindzfb'})
    },
    // 解绑支付宝账号
    abindzfb() {
      if( !this.isAuth(this.apiList.money.aBindAliInfo) ){
        return this.$toast('权限不足，请联系管理员修改')
      }
      let str = `您的支付宝将解绑，请确认`
      
      MessageBox.confirm(str).then(() =>{
        this.show = true
        const url = this.apiList.money.aBindAliInfo
        this.upload()
        this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData('', url, true)
        }).then(({ data }) => {
          this.closeUpload()
          if (data.result) {
            this.$toast("解绑成功")
            this.getDataInfo()
          } else {
            this.$toast(data.msg)
          }
        })
      })
    },
    // 获得详细信息
    getDataInfo() {
      const url = this.apiList.money.getInfo
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData('', url, true)
      }).then(({ data }) => {
        console.log("这是详细信息：",data.data)
        if (data.result) {
          this.openid = data.data.wxOpenId
          if(this.openid){
            this.zfbChecked = false
            this.bankChenke = false
            this.wxChecked = true
          }else{
            this.wxChecked = false
          }
          this.accessToken = data.data.accessToken
          this.realName = data.data.realName
          this.aliRealName = data.data.aliRealName
          this.aliAccount = data.data.aliAccount
          this.wxRealName =data.data.wxRealName
        } else {
          this.$toast(data.msg)
        }
      })
    },
    // 获取银行卡
    getDataList() {
      const url = this.apiList.bankCard.list
      if( !this.isAuth(url) ){
        return 
      }
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData('', url, true)
      }).then(({ data }) => {
        console.log(data)
        if (data.result) {
          data.data.map((v, i, a) => {
            v.cardNum = v.cardNum.slice(v.cardNum.length - 4, v.cardNum.length)
          })
          this.dataList = data.data
        } else {
          this.$toast(data.msg)
        }
      })
    },
    // 提现
    deposit(){
      if( !this.apiList.moneyApproval.moneyApproval ){
        return this.$toast('权限不足，请联系管理员修改')
      }
      if (this.outnumber > this.money) {
        this.$toast('提现金额超过可提现余额')
        return this.outnumber = this.money
      }
      if(!this.outnumber){
        return this.$toast('请输入提现的金额')
      }
      if(this.outnumber < 1){
        return this.$toast('最低提现金额1元')
      }
      if(!this.wxChecked && !this.zfbChecked && !this.bankChenke){
        return this.$toast('请选择提现的方式')
      } 
      let str = `平台将收取${ this.moduleChou / 100}%的手续费，确认提现？`
      MessageBox.confirm(str).then(() =>{
        const url = this.apiList.moneyApproval.moneyApproval
        // 微信
        
        if(this.wxChecked){
          this.upload()
          this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({  num: Math.round(this.outnumber * 10000) / 100, moneyOutType: 101 }, url, true)
          }).then(({ data}) => {
            this.closeUpload()
            if (data.result) {
              this.$toast('提现申请提交成功')
              this.getData()
              setTimeout(() => {
                this.goback()
              }, 1500);
            } else {
              this.$toast(data.msg)
            }
          })
        }
        // 支付宝
        if(this.zfbChecked){
          this.upload()
          this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({  num: Math.round(this.outnumber * 10000)  / 100, moneyOutType: 201 }, url, true)
          }).then(({ data}) => {
            this.closeUpload()
            if (data.result) {
              this.$toast('提现申请提交成功')
              this.getData()
              setTimeout(() => {
                this.goback()
              }, 1500);
            } else {
              this.$toast(data.msg)
            }
          })
        }
        // 银行卡
        if(this.bankChenke){
          this.upload()
          this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({ bankCardId: this.bankChenke, num: Math.round(this.outnumber * 10000) / 100, moneyOutType: 1 }, url, true)
          }).then(({ data}) => {
            this.closeUpload()
            if (data.result) {
              this.$toast('提现申请提交成功')
              this.getData()
              setTimeout(() => {
                this.goback()
              }, 1500)
            } else {
              this.$toast(data.msg)
            }
          })
        }
      })  
    }
  },
  created() {
    
    this.getData()
    this.getDataList()
    this.getDataInfo()
    const vm = this
    eventBus.$on("android_loginThird", (data) => {
      vm.openid = data.openid
      vm.accessToken = data.accessToken
      vm.bindWxRequest()
    })
  }
}
</script>
<style lang="scss">
 .withdrawalType{
   min-height: 100%;
   background-color: #F5F5F5;
   padding-bottom: 4.9rem;
   box-sizing: border-box;
   .red{
     background-color: #F64542;
     span{
       color: #fff; 
     }
     .deposit{
       float: right
    }
   }
   .main_info{
    // height: 14.8rem;
    box-sizing: border-box;
    border-top: solid #EEEEEE .1rem;
    width: 100%;
    background: #fff;
    padding: 0.8rem 1.5rem 1.2rem;
    .form{
      // line-height: 4.7rem;
      // padding-bottom: 4rem;
      
      div{
        display: inline-block;
        font-size: 1.4rem;
        color: #666;
        box-sizing: border-box;
        
        input{
          width: 18rem;
          // margin-top: -0.5rem;
          -webkit-appearance: none; /*去除系统默认的样式*/
          vertical-align: top;
          margin-top: 1.2rem;
          border: none;
          padding-left: 0.5rem;
          font-size: 2.5rem;
          color: #333;
          font-weight: 600;
        }
         ::-webkit-input-placeholder { /* WebKit, Blink, Edge */
            color: #D7D7D7;
            font-size: 1.4rem;
          }
          :-moz-placeholder { /* Mozilla Firefox 4 to 18 */
            color: #D7D7D7;
            font-size: 1.4rem;
          }
          ::-moz-placeholder { /* Mozilla Firefox 19+ */
            color: #D7D7D7;
            font-size: 1.4rem;
          }
          :-ms-input-placeholder { /* Internet Explorer 10-11 */
            color: #D7D7D7;
            font-size: 1.4rem;
          }
      }
      .wrapper{
        width: 100%;
        .cost, .arrival_time {
          display: flex;
          justify-content: space-between;
        }
        .cost{
          span{
            font-size: 1.2rem;
            color: #333333;
          }
        }
        .arrival_time{
          margin-top: 0.5rem;
          span{
            font-size: 1.1rem;
            color: #999;
          }
        }
      }
      .input{
        position: relative;
        // border: solid .1rem #D7D7D7;
        &::before{
          content: '￥';
          font-size: 2rem;
          color: #333;
          display: inline-block;
          vertical-align: bottom;
          // position: absolute;
          left: 1rem;
          line-height: 5.5rem;
          font-weight:bold;
        }
      }
    }
    .monery_num{
      width: 34.5rem;
      margin: auto;
      height: 3.6rem;
      line-height: 3.6rem;
      color: #333;
      font-size: 1.4rem;
      font-weight:400;
      span{
        // color: #34AFF6;
      }
    }
  }
  .way{
    margin-top: 2rem;
    .title{
      font-size: 1.4rem;
      color: #333;
      font-weight: 400;
    }
    .about_xzl:last-of-type{
      border-bottom: none;
      height: auto;
      >div:last-child{
        p{
          border-bottom: none;
        }
      }
      p{
       height: auto;
       border-bottom: solid .1rem #EDEDED;
       &:last-child{
          // border-bottom: none;
       }
       span{
         font-size: 1.4rem;
         .List_icon{
            width: 2.8rem;
            height: 2.8rem;
            vertical-align: middle;
            margin-right: 1.1rem;
          }

       }
      }
    }
    .about_xzl{
       background-color: #fff;
       align-items: center;
       padding: 0 1.6rem;
       height: 5rem;
       border-bottom: solid .1rem #EDEDED;
       p{
         width: 100%;
         height: 100%;
         line-height: 5rem;
         display: flex;
         justify-content: space-between;
         box-sizing: border-box;
         span{
           font-size: 1.4rem;
           color: #666;
           >span{
             color: red;
             font-size: 1.2rem;
           }
           .img-left{
             width: 1.6rem;
             height: 1.6rem;
           }
           .List_icon{
             width: 2.4rem;
             height: 2.4rem;
             vertical-align: middle;
             margin-right: 1.1rem;
           }
           .btn{
             display: inline-block;
             vertical-align: middle;
             background: #fff;
             height: 2rem;
             line-height: 2rem;
             padding-left: 1rem;
             padding-right: 1rem;
             border: 1px solid #00a3fc;
             border-radius: 1.5rem;
             font-weight: 400;
             color: #00a3fc;
           }
         }
         span:nth-child(2){
           color: #999;
           line-height: 5.4rem;
         }
         img{
           width: 1.9rem;
           height: 1.9rem;
           vertical-align: middle;
           margin-left: .8rem;
           margin-top: -.2rem;
         }
       }
     }

  }
  .hint{
    margin-top: 2.15rem;
    height: 7.9rem;
    text-align: center;
    padding-left: 1.6rem;
    background-color: #fff;
    p{
      font-size: 1.2rem;
      text-align: start;
      color: #666;
    }
    img{
      margin-top: -0.6rem;
      width: 12.85rem;
      height: 3rem;
    }
  }
  .button{
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 4.9rem;
    font-size: 1.4rem;
    text-align: center;
    line-height: 4.9rem;
    color: #fff;
    background: #F64542;
    border-radius: 0;
    
  }
 } 
  .mint-msgbox-wrapper .mint-msgbox {
    position: fixed;
    top: 50%;
    left: 50%;
    width: 28.4rem ;
    // height: 20.3rem ;
    padding: 0 1.5rem;
    box-sizing: border-box;
    -webkit-transform: translate3d(-50%, -50%, 0);
    transform: translate3d(-50%, -50%, 0);
    background-color: #fff;
    border-radius: .8rem;
    font-size: 16px;
    -webkit-user-select: none;
    overflow: hidden;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    -webkit-transition: .2s;
    transition: .2s;
    .mint-msgbox-content{
      border: none;
      margin-top: 2.6rem;
      .mint-msgbox-message{
        line-height: none;
      }
    }
    .mint-msgbox-btns{
      display: block;
      .mint-msgbox-cancel,.mint-msgbox-confirm{
        width:11.6rem;
        height:3.6rem;
        background:rgba(255,255,255,1);
        border:1px solid rgba(153, 153, 153, 1);
        border-radius:1.8rem;
        margin-top: 2.1rem;
        margin-bottom: 2.1rem;
        float: left;
        color: #999;
      }
      .mint-msgbox-confirm{
        float: right;
        background:linear-gradient(-82deg,rgba(239,83,80,1),rgba(229,57,53,1));
        color: #fff;
        border: none;
      }
    }
  }
</style>



