<template>
  <div class="signUp_indexChild" >
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <van-list 
        v-model="loading" 
        :finished="finished" 
        finished-text="——没有更多了——"
        @load="onLoad"
        ref="listHeight"
      >
      <van-cell class="signUp_indexChild_wrapper" v-for="(item, index) in dataList" :key="index" v-show=" shows==0 ? true :(shows==1?item.showToC: !item.showToC) ">
        <div class="content">
          <div class="commodityInfo" >
            <div class="head">
              <span class="title"> {{ item.name }} </span>
              <span class="right">
                <span class="upper">{{ item.showToC === true  ?  ( item.totalNum === 0 ? '请设置报名人数' : item.alreadyNum === item.totalNum ? '已抢光' : (Date.now() >= item.endTime ? '已过期' : '已上架'))   : '已下架'  }}</span>
              </span>
            </div>
            <div class="wrapper" >
              <div class="main" >
                <div class="commodityImg">
                  <img :src="item.imgUrlSquare" alt="">
                </div>
                <div class="commodityInfo">
                  <div class="info">
                    <span class="price">
                      <span class="text">价格：</span><span class="text">￥{{ (item.price / 100).toFixed(2) }}</span>
                    </span>
                    <span class="penson_wrapper">
                      <span class="pensonText">人数：</span><span class="pensonNum">{{item.alreadyNum}}/{{item.totalNum}}</span>
                    </span>
                  </div>
                  <div class="time">
                    <span>报名开始时间：</span><span>{{ setDate(item.startTime) }}</span>
                  </div>
                  <div class="time">
                    <span>报名结束时间：</span><span>{{ setDate(item.endTime) }}</span>
                  </div>
                </div>
              </div>
              <div class="foot">
                <!-- <img  @click.stop="showBtn(index)" src="../../assets/signUp/buttonGroup.png" alt="" v-if="isAuth('/enterNameKV/list') || isAuth('/enterNameItem/list') || isAuth('/activityNote/listAll') || isAuth('/activityPhotoShow/listByTypeAndTargetId')"> -->
                <mt-button @click="goDetails(item.activityId)" v-if="isAuth('/enterName/listOrderData')">明细</mt-button>
                 <mt-button class="upperBtn" @click="upperClick(item.activityId, index, item.approvalId)" v-if="isAuth('/enterName/mod')">{{ item.showToC == true ? '下架' :  '上架'}}</mt-button>
                <mt-button class="modifyBtn" @click="goModify(item.activityId, index)" v-if="isAuth('/enterName/mod')">修改</mt-button>
                <mt-button @click="delData(index, item.activityId)" v-if="isAuth('/enterName/del')">删除</mt-button>
              </div>
              <!-- <div class="hideBtn" v-if="showBtnNum == index">
                <img src="../../assets/signUp/hideBtn.png" alt="">
                <mt-button @click="goKey(item.activityId)" v-if="isAuth('/enterNameKV/list')">报名信息</mt-button>
                <mt-button @click="goItem(item.activityId)" v-if="isAuth('/enterNameItem/list')">报名小组</mt-button>
                <mt-button @click="goBuyingIns(item.activityId)" v-if="isAuth('/activityNote/listAll')">注意事项</mt-button>
                <mt-button @click="goPhotos(item.activityId)" v-if="isAuth('/activityPhotoShow/listByTypeAndTargetId')">风采展示</mt-button>
              </div> -->
            </div>
          </div>
        </div>
      </van-cell>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import { MessageBox } from 'mint-ui'
  export default {
    props:['shows'],
    data(){
      return{
        dataList:[],
        page:0,
        showBtnNum: -1,
        showbtn: false,
        loading: false,
        finished: false,
        isLoading: false
      }
    },
    methods:{
      goBack(){
        this.$router.goback()
      },
      goDetails(id){
        // this.$router.push({name:'activity_details', query:{id: id, list: 'enterName'}})
        const url = this.apiList.enterName.orderList
        if(!this.isAuth(url)){
          return this.$toast("权限不足，请联系管理员修改")
        }
        this.setScrollTop()
        this.$router.push({name:'detail',params:{'activityId':id,url,'shows': 2}})
      },
      setScrollTop(){
        var scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
        let num = localStorage.getItem('activeNum')
        if(num == 0){
          localStorage.setItem('scrollTop0', scrollTop)
        }else if(num == 1){
          localStorage.setItem('scrollTop1', scrollTop)
        }else if(num == 2){
          localStorage.setItem('scrollTop2', scrollTop)
        }
        document.documentElement.scrollTop = 0;
        document.body.scrollTop = 0;
        
      },
      goModify(id, index){
        this.setScrollTop()
        // this.$router.push({name:'signUp_modify', params:{modId: id, index: index, type: 101}})
        this.$router.push({name:'activity_common_edit', params:{modId: id, index: index, type: 101}})
        // localStorage.removeItem('dataList')
      },
      
      onLoad(){
        let showToC;
        if(this.shows === 0){
          showToC = ''
        }else if(this.shows === 1){
          showToC = true
        }else{
          showToC = false
        }
        return new Promise((resolve, reject) =>{
          this.page++
          let url = this.apiList.enterName.list
          this.$http({
            url: this.$http.adornUrl(url),
            method: 'post',
            data: this.$http.adornData({ showToC: showToC, page: this.page, size: 10}, url, true)
          }).then(({data})=>{
            if (data.result) {
              if (data.data.records.length >= 0) {
                if(localStorage.getItem('dataList')){
                  let arr = data.data.records
                    this.dataList.forEach((item, index)=>{
                      arr.forEach(items=>{
                        if(item.activityId === items.activityId){
                          this.dataList.splice(index, 1, items)
                        }
                      })
                    })
                  let list = this.dataList.concat(arr)
                  this.dataList = Array.from(new Set([...list, ...list])) // 去重
                  localStorage.setItem('dataList', JSON.stringify(this.dataList))
                }else{
                  this.dataList = data.data.records
                  localStorage.setItem('dataList', JSON.stringify(this.dataList))
                }
                
              }
              if (data.data.pages <= this.page) {
                this.finished = true
              }
              if(this.shows === 0){
                localStorage.setItem('pageNum', this.page)
              }else if(this.shows === 1){
                localStorage.setItem('pageNum2', this.page)
              }else if(this.shows === 2){
                localStorage.setItem('pageNum3', this.page)
              }
              this.$nextTick(() => {
                resolve(true)
                this.loading = false
                if ('scrollRestoration' in history) {
                  history.scrollRestoration = 'manual'; //改为manual之后，就不会记录滚动位置
                }
              })
            }else if( data.msg == 'sign验证失败' ){
              this.$toast('您的账户登录超时')
              setTimeout(()=>{
                localStorage.clear();
                this.$router.push({name: 'login'})
              },1000)
            }
          })
          
        })
      },
      onRefresh(){
        let showToC;
        if(this.shows === 0){
          showToC = ''
        }else if(this.shows === 1){
          showToC = true
        }else{
          showToC = false
        }
        return new Promise((resolve, reject) =>{
          const url = this.apiList.enterName.list
          this.$http({
            url: this.$http.adornUrl(url),
            method: 'post',
            data: this.$http.adornData({ showToC: showToC, page: 1, size: 10}, url, true)
          }).then(({data})=>{
            if (data.result) {
              if (data.data.records.length >= 0) {
                let arr = data.data.records
                  this.dataList.forEach((item, index)=>{
                    arr.forEach(items=>{
                      if(item.activityId === items.activityId){
                        this.dataList.splice(index, 1, items)
                      }
                    })
                  })
                let list = this.dataList.concat(arr)
                this.dataList = Array.from(new Set([...list, ...list]))
                localStorage.setItem('dataList', JSON.stringify(this.dataList))
              }
              this.$nextTick(() => {
                this.$toast('刷新成功');
                this.isLoading = false;
                this.finished = false;
                this.page = 1;
                // if(this.$refs.listHeight.$el.offsetHeight < document.documentElement.offsetHeight){
                //   this.finished = false
                //   this.loading = false
                //   this.onLoad()
                // }  
                localStorage.setItem('pageNum', this.page)
                resolve(true)
              })
            }else if( data.msg == 'sign验证失败' ){
              this.$toast('您的账户登录超时')
              setTimeout(()=>{
                localStorage.clear();
                this.$router.push({name: 'login'})
              },1000)
            }
          })   
        })
      },
      getData(){
        let url = this.apiList.enterName.list
        this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData( {page: this.page, size: 10, }, url, true)
        }).then( ({ data }) => {
          if(data.result){
            this.dataList = data.data.records;
          }else if(data.msg == 'sign验证失败'){
            this.$toast('您的账户登录超时')
            setTimeout(()=>{
              localStorage.clear();
              this.$router.push({name:  'login' })
            },1000)
          }
        })
      },
      // upperClick(id, index, approvalId){
      //   let url;
      //   var upperText;
      //   if(this.dataList[index].showToC== true){
      //     upperText = '确认下架？'
      //   }else if(approvalId){
      //     upperText = '取消审批？'
      //   }else{
      //     upperText = '确认上架？'
      //   }
      //   var showToC;
      //   if(approvalId){ //取消审批
      //     showToC = false
      //     url = this.apiList.showToC.cancel
      //   }else{ // 上下架
      //     showToC = !this.dataList[index].showToC;
      //     url  = this.apiList.showToC.showToC
      //   }
        
      //   MessageBox.confirm(upperText).then( ()=>{
      //     this.upload()
      //     this.$http({
      //       url:this.$http.adornUrl(url),
      //       method:'post',
      //       data: this.$http.adornData( { approvalId: approvalId, activityId: id, activityType: 101, showToC: showToC}, url, true )
      //     }).then(({ data }) => {
      //       this.closeUpload()
      //       if(data.result){
              
      //         // this.$nextTick(()=>{
      //             this.page = 0;
      //             this.loading = false;
      //             this.finished = false;
      //             this.onLoad();
                
      //         // })
              
              
      //         if(this.dataList[index].showToC){
      //           this.dataList[index].showToC = showToC
      //         }
      //           this.$toast({
      //             message: '操作成功',
      //             iconClass: 'fa fa-check fa-5x'
      //           })
      //           if(this.$refs.listHeight.$el.offsetHeight < document.documentElement.offsetHeight){
      //             this.finished = false
      //             this.loading = false
      //             this.onLoad()
      //           } 
      //           localStorage.setItem('dataList', JSON.stringify(this.dataList) )
      //         }else if(data.msg == 'sign验证失败'){
      //           this.$toast('您的账户登录超时')
      //           setTimeout(()=>{
      //             localStorage.clear();
      //             this.$router.push({name: 'login'})
      //           },1000)
      //         }else{
      //           this.$toast(data.msg)
      //         }
      //       })
      //   })
      // },
      upperClick(id, index, item){
        let url = this.apiList.enterName.edit
        if(this.dataList[index].showToC== true){
          var upperText = '确认下架？'
        }else{
          var upperText = '确认上架？'
        }
        MessageBox.confirm(upperText).then( ()=>{
          this.upload()
          this.$http({
            url:this.$http.adornUrl(url),
            method:'post',
            data: this.$http.adornData( { id: id, showToC: !this.dataList[index].showToC}, url, true )
          }).then(({ data }) => {
            this.closeUpload()
            if(data.result){
              this.dataList[index].showToC = !this.dataList[index].showToC
              this.$toast({
                message: '操作成功',
                iconClass: 'fa fa-check fa-5x'
              })
              if(this.$refs.listHeight.$el.offsetHeight < document.documentElement.offsetHeight){
                this.finished = false
                this.loading = false
                this.onLoad()
              } 
              localStorage.setItem('dataList', JSON.stringify(this.dataList))
            }else if(data.msg == 'sign验证失败'){
              this.$toast('您的账户登录超时')
              setTimeout(()=>{
                localStorage.clear();
                this.$router.push({name: 'login'})
              },1000)
            }else{
              this.$toast(data.msg)
            }
          })
        })
      },
      // showBtn(index){
      //   this.dataList.forEach((item, i)=>{
      //     if(index == i){
      //       this.showBtnNum = this.showBtnNum == i ? -1 : i
      //     }
      //   })
        
      // },
      // hideBtn(){
      //   this.showBtnNum = -1
      // },
      delData(index, id){
        MessageBox.confirm('确定删除该报名？').then( ()=>{
          let url = this.apiList.enterName.del
          this.delActivity()
          this.$http({
            url: this.$http.adornUrl(url),
            method: 'post',
            data: this.$http.adornData( { id: id }, url, true)
          }).then( ({data}) =>{
            this.closeUpload()
            if(data.result){
              this.$toast({
                message: '删除成功',
                iconClass: 'fa fa-check fa-5x'
              })
              this.dataList.splice(index, 1)
              if(this.$refs.listHeight.$el.offsetHeight < document.documentElement.offsetHeight){
                this.finished = false
                this.loading = false
                this.onLoad()
              } 
              localStorage.setItem('dataList', JSON.stringify(this.dataList) )
            }else if(data.msg == 'sign验证失败'){
              this.$toast('您的账户登录超时')
              setTimeout(()=>{
                localStorage.clear();
                this.$router.push({name:  'login' })
              },1000)
            } else{;
              this.$toast(data.msg)
            }
          })
        })
        
      },
      getScrollTop(){
        if(this.shows == 0){
          this.$nextTick(()=>{
            document.documentElement.scrollTop = localStorage.getItem('scrollTop0')
            document.body.scrollTop = localStorage.getItem('scrollTop0')
          })
        }else if(this.shows == 1){
          this.$nextTick(()=>{
            document.documentElement.scrollTop = localStorage.getItem('scrollTop1')
            document.body.scrollTop = localStorage.getItem('scrollTop1')
          })
        }else{
          this.$nextTick(()=>{
            document.documentElement.scrollTop = localStorage.getItem('scrollTop2')
            document.body.scrollTop = localStorage.getItem('scrollTop2')
          })
        }
      },
      change(){
        this.dataList = JSON.parse(localStorage.getItem('dataList'));
        this.getScrollTop()
      },
    },
    mounted(){
      this.$refs.listHeight.$el.style.minHeight = document.documentElement.clientHeight   - 21.4 * 10 + 'px'
    },
    created(){
      if(window.history.length > 3){
        if(localStorage.getItem('dataList')){
          if(this.shows === 0){
            this.change()
            if(localStorage.getItem('pageNum')){
              this.page = Number(localStorage.getItem('pageNum'))
            }
          }else if( this.shows === 1){
            this.change()
            if(localStorage.getItem('pageNum2')){
              this.page = Number(localStorage.getItem('pageNum2'))
            }
          }else if(this.shows === 2){
            this.change()
            if(localStorage.getItem('pageNum3')){
              this.page = Number(localStorage.getItem('pageNum2'))
            }
          }
        }else{
          this.getScrollTop()
        }
      }
    }
  }
</script>
<style lang="scss">
  .signUp_indexChild{
    background-color: #f5f5f5;
    margin-top: 1.4rem;
    .signUp_indexChild_wrapper{
      margin: 0 1.6rem 1.2rem;
      border-radius: .8rem;
    }
    .van-list {
      padding-bottom: 9.4rem;
    }
    .van-cell{
      padding: 0;
      width: 34.3rem;
      overflow: visible;
      .van-cell__value{
        overflow: visible;
      }
    }
    .content{
      width: 100%;
      .commodityInfo{
        position: relative;
        background-color: #fff;
        border-radius: .8rem;
        .main{
          box-sizing: border-box;
          margin: 0 1.6rem;
          display: flex;
          .commodityImg{
            img{
              display: inline-block;
              width: 7.4rem;
              height: 7.4rem;
            }
          }
          .commodityInfo{
            flex:1;
            margin-left: 1.4rem;
            .info{
              display: flex;
              justify-content: space-between;
              .text{
                font-size: 1.3rem;
                color: #333;
              }
            }
            .time{
              margin-top: 0.5rem;
              span{
                color: #333;
                font-size: 1.3rem;
              }
            }
          }
        }
        .foot{
          height: 5.2rem;
          display: flex;
          justify-content:flex-end;
          align-items: center;
          img{
            width: 2.4rem;
            height: 2.4rem;
            margin-right: 9.2rem;
          }
          button{
            min-width: 6.3rem;
            height: 2.7rem;
            outline: none;
            background-color: none;
            border-radius: 1.35rem;
            color: #333;
            background-color: #fff;
            margin-right: .9rem;
            font-size: 1rem;
          }
        }
        // .hideBtn{
        //   position: absolute;
        //   top: 15.8rem;
        //   z-index: 999;
        //   img{
        //     display: block;
        //     width: 1.5rem;
        //     height: 0.65rem;
        //     margin-left: 1.5rem;
        //   }
        //   button{
        //     display: block;
        //     width: 9.3rem;
        //     background-color: #3E3E3E;
        //     font-size: 1.3rem;
        //     color: #fff;
        //     border-radius: 0;
        //     box-shadow: none;
        //     border-bottom: 1px solid rgba(255, 255, 255, 0.1);
        //   }
          
        //   button:nth-child(2){
        //     border-bottom: 1px solid rgba(255, 255, 255, 0.1);
        //     border-top-left-radius: 0.3rem;;
        //     border-top-right-radius: 0.3rem;;
        //   }
        //   button:nth-child(5){
        //     border-bottom-left-radius: 0.3rem;
        //     border-bottom-right-radius: 0.3rem;
        //   }
        // }
      }
      
    }
 
    
  }
</style>




