<template>
<div class="myInfo">
  <div class="mainInfoHeader">
    <div class="header clearfix">
      <div class="left"><img :src="logo ? logo : require('../assets/my/userAvatar.png')" alt=""></div>
      <div class="right">
        <p><span>{{name ? name : '众享乐用户'}}</span><span v-if="showtoc"><img v-if="tag === '官方'" src="../assets/my/vip_4.png" alt=""><img v-else :src="vipLogo" alt="">{{tag}}</span></p>
        <p>ID：000{{ id }}</p>
      </div>
      <div class="bill"  @click="goMoneryDetails" v-if="isAuth('/money/logModule')">  
        <mt-button>
          <img src="../assets/my/bill.png" />
          账 单
        </mt-button>
      </div>
    </div>
    <div class="money" v-if="isAuth(apiList.money.get)">
      <p>我的余额</p>
      <p>
        <span>{{ money }}</span>
        <mt-button class="withdrawalBtn"  @click="gowithDrawal" v-if="isAuth('/money/getMoney')">
          <img src="../assets/my/withdrawalBtn.png" alt="">
          <span>去提现</span>
        </mt-button>
      </p>
    </div>
  </div>
  <div class="vipBanner clearfix" @click="goVip" v-if="isAuth(this.apiList.vip.state)">
    <div class="left">
      <p><span><img src="../assets/my/tobevip.png" alt=""></span><span>众享乐会员</span></p>
      <p>让您享受更多特权，获得更多机会</p>
    </div>
    <div class="right">
      <div>立即升级</div>
    </div>
  </div>
  <div class="btnContainer">
    <div class="about_xzl" >
      <p @click="tovip">
        <span><img class="List_icon" src="../assets/my/count.png">关注人数</span>
        <span>
          {{ collectNum }}<img src="../assets/my/more.png">
        </span>
      </p>
    </div>
    <div class="about_xzl" @click="gotoshop" >
      <p>
        <span><img class="List_icon" src="../assets/my/preview.png">店铺预览</span>
        <span>
          <img src="../assets/my/more.png">
        </span>
      </p>
    </div>
    <div class="about_xzl" @click="gotoStaff">
      <p @click="gotoStaff">
        <span><img class="List_icon" src="../assets/my/staff.png">联系客服</span>
        <span>
          <img src="../assets/my/more.png">
        </span>
      </p>
    </div>
    
    <div class="about_xzl" >
      <p @click="goProblem">
        <span><img class="List_icon" src="../assets/my/moneryProblem.png">常见问题</span>
        <span>
          <img src="../assets/my/more.png">
        </span>
      </p>
    </div>
    <div class="about_xzl" @click="gotySet">
      <p>
        <span><img class="List_icon" src="../assets/my/set.png">设置</span>
        <span>
          <img src="../assets/my/more.png">
        </span>
      </p>
    </div>
    <!-- <div class="about_xzl" @click="goAuth" v-if='isBoss' >
      <p>
        <span><img class="List_icon" src="../assets/my/moeny.png" >系统资金</span>
        <span>
          <img src="../assets/my/more.png">
        </span>
      </p>
    </div> -->
  </div>
</div>
</template>
<script>
export default {
  data() {
    return{
      logo: '',
      name: '',
      tag: '认证',
      collectNum: '0',
      id: 0,
      showtoc: false,
      money: 0,
      vipLogo: '',
      isBoss: false
    }
  },
  methods: {
    tovip() {
      if( !this.isAuth(this.apiList.shop.collect) ){
        return this.$toast('权限不足，请联系管理员修改')
      }
      this.$router.push('/vipMember')
    },
    goProblem(){
      this.$router.push('/problem')
    },
    goAuth(){
      // this.$router.push('/nodata')
      this.$router.push('capital')
    },
    goMoneryDetails(){
      this.$router.push('moneryDetails')
    },
    getData() {
      const url = this.apiList.shop.get
      if( !this.isAuth(url) ){
        return 
      }
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData('', url, true)
      }).then(({ data }) => {
        if (data.result) {
          localStorage.setItem('logoUrl', data.data.logoImgUrl)
          localStorage.setItem('shopName', data.data.name)
          this.logo = data.data.logoImgUrl
          this.name = data.data.name
          this.id = data.data.id
          this.collectNum = data.data.collectNum
          this.showtoc = data.data.showed
          const tags = data.data.tags ? data.data.tags : []
          tags.forEach(element => {
            if (element.name === '官方' || element.name === '企业官方') {
              this.tag = '官方'
            } else {
              this.getVipState
            }
          })
        } else {
          if (data.msg === 'sign验证失败') {
            localStorage.clear()
            this.$toast({
              message: '您的账号登录超时',
              iconClass: 'icon icon-error'
            })
            setTimeout(() => {
              // instance.close()
              this.$router.push({ name:  'login' })
            }, 2000)
          }
        }
      })
    },
    // 获得vip状态
    getVipState() {
      
      const url = this.apiList.vip.state
      // if( !this.isAuth(url)){
      //   return 
      // }
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData('', url, true)
      }).then(({ data }) => {
        console.log(data);
        if (data.data.vipType === 101) {
          this.vipLogo = require('../assets/my/vip_3.png')
          this.tag = '年费会员'
        } else if (data.data.vipType === 1) {
          this.vipLogo = require('../assets/my/vip_2.png')
          this.tag = '月费会员'
        } else {
          this.vipLogo = require('../assets/my/vip_1.png')
          this.tag = '认证'
        }
      })
    },
    getMoney() {
      const url = this.apiList.money.get
      if( !this.isAuth(url)){
        return
      }
      this.loading()
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData('', url, true)
      }).then(({ data }) => {
        this.closeUpload()
        if (data.result) {
          let num  = (data.data.totalMoney / 100).toFixed(2)
          let result = ''; //结果
            num = num.split('.') //以小数点进行切割
            let lastNum = num[1] //得出小数点后的数值
            num = num[0] //小数点前的数值
            if(num.length > 3){
              while (num.length > 3) {
                result = ',' + num.slice(-3) + result; //每次循环取num值的最后三位数 + 之前的结果
                num = num.slice(0, num.length - 3); //每次切割掉num最后三位
              } 
            }
          this.money = num + result + '.' + lastNum
        } else {
          if (data.msg === 'sign验证失败') {
            localStorage.clear()
            this.$toast({
              message: '您的账号登录超时',
              iconClass: 'icon icon-error'
            })
            setTimeout(() => {
              instance.close()
              this.$router.push({ name:  'login' })
            }, 2000)
          }
        }
      })
    },
    gotoshop() {
      // window.location.href= "http://47.111.174.168/#/shop?shopId="+ localStorage.getItem('moduleId')
      this.$router.push('/iframe')
    },
    gotySet() {
      this.$router.push('/set')
    },
    gowithDrawal() {
      this.$router.push('/withdrawalType')
      // this.$toast('项目部署中')
    },
    gotoStaff() {
      this.$router.push('/service')
    },
    goVip() {
      this.$router.push('/vip')
      // this.$toast('项目部署中')
    }
  },
  created() {
    this.android.navColorChange('#f64542')
    this.getMoney()
    this.getData()
    this.getVipState()
    if(localStorage.getItem('moduleId')==='25' && localStorage.getItem('uid')==='25'){
      this.isBoss = true
    }
    if (localStorage.getItem('logoUrl')) {
      this.name = localStorage.getItem('shopName')
      this.logo = localStorage.getItem('logoUrl')
    } else{
    }
  },
  destroyed() {
    
  }
}
</script>
<style lang="scss">
.myInfo{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 5rem;
  color: #333;
  background: #f5f5f5;
  .mainInfoHeader{
    width: 100%;
    height: 18.5rem;
    background: url('../assets/my/bg.png');
    background-size: 100% 100%;
    text-align: center;
    box-sizing: border-box;
    .header{
      height: 6rem;
      line-height: 6rem;
      box-sizing: border-box;
      padding: 1rem 1.8rem;
      background: transparent;
      .left{
        float: left;
        width: 3.8rem;
        height: 3.8rem;
        img{
          width: 100%;
          height: 100%;
          border-radius: 100%;
        }
      }
      .right{
        float: left;
        margin-left: 1rem;
        p{
          color: #fff;
          font-size: 1.6rem;
          text-align: left;
          line-height: 1.6rem;
          span{
            color: #fff;
            font-size: 1.6rem;
            font-weight: 600;
          }
          
          span:nth-child(2){
            font-size: 1rem;
            height: 1.2rem;
            vertical-align: top;
            margin-left: .7rem;
            font-weight: 400;
            img{
              width: 1.4rem;
              height: 1.4rem;
              vertical-align: top;
            }
          }
        }
        p:nth-child(2){
          font-size: 1.1rem;
        }
      }
      .bill{
        position: absolute;
        right: 0;
        top: 4rem;
        button{
          width: 7rem;
          height: 2.5rem;
          line-height: 2.5rem;
          font-size: 1.2rem;
          color: #fff;
          background: linear-gradient( 90deg, rgba(198,61,255,1), rgba(106,75,242,1));
          border-radius: 1.25rem 0 0 1.2rem;
          img{
            width: 1.1rem;
            vertical-align: middle;
            margin-top: -0.2rem;
            margin-right: 0.3rem;

          }
        }
        
      }
    }
    .money{
      margin-top: 1.5rem;
      
      p{
        position: relative;
        color: #fff;
        text-align: left;
        font-size: 1rem;
        width: 100%;
        box-sizing: border-box;
        padding-left: 5rem;
        padding-right: 1.6rem;
        span{
          font-size: 4.4rem;
          font-weight: 400;
        }
        .mint-button--default{
          box-shadow: none;
        }
        .withdrawalBtn{
          display: flex;
          justify-content: space-around;
          align-items: center;
          position: absolute;
          right: 0;
          top: 0.8rem;
          height: 2.5rem;
          background:linear-gradient(90deg,rgba(242,154,75,1),rgba(255,116,61,1));
          border-radius: 1.25rem 0px 0px 1.25rem;
          span{
            font-size: 1.1rem;
            color: #fff;
          }
          img{
            width: 1.1rem;
            height: 1.1rem;
          }
        }
      }
    }
    
  }
  .vipBanner{
    width: 31.3rem;
    height: 5rem;
    border-radius: 5rem;
    background: #1E1E1E;
    padding: .7rem 0 .8rem 1.8rem;
    box-sizing: border-box;
    margin: auto;
    transform: translateY(-65%);
    p{
      color: #F5E0D0;
      font-size: 1.2rem;
    }
    p:nth-child(2) {
      margin-top: .4rem;
    }
    span{
      font-size: 1rem;
      img{
        width: 1rem;
        height: 1rem;
        margin-right: .3rem;
        vertical-align: baseline;
      }
    }
    .left{
      float: left;
      width: 18.7rem;
    }
    .right{
      width: 9rem;
      float: right;
      margin-top: 1.9rem;
      transform: translateY(-50%);
      div{
        width: 6rem;
        height: 2.5rem;
        line-height: 2.5rem;
        text-align: center;
        color: #0E0804;
        font-size: 1rem;
        border-radius: 2.5rem;
        background: -webkit-linear-gradient(left,#FCF8ED,#CB9E7F); /* Safari 5.1-6.0 */
        // background: -o-linear-gradient(left,#FCF8ED,#CB9E7F); /* Opera 11.1-12.0 */ 
        // background: -moz-linear-gradient(left,#FCF8ED,#CB9E7F); /* Firefox 3.6-15 */
        // background: linear-gradient(left,#FCF8ED,#CB9E7F); /* 标准语法 */
        margin: auto;
      }
    }
  }

  .btnContainer{
    // margin-top: 1.2rem;
    width: 100%;
    text-align: center;
    box-sizing: border-box;
    // background: #fff;
    .about_xzl{
      background-color: #fff;
      align-items: center;
      padding: 0 1.6rem;
      height: 5rem;
      p{
        width: 100%;
        height: 100%;
        line-height: 5rem;
        border-bottom: solid .1rem #EDEDED;
        display: flex;
        justify-content: space-between;
        span{
          font-size: 1.4rem;
          color: #666;
          .List_icon{
            width: 2.4rem;
            height: 2.4rem;
            vertical-align: middle;
            margin-right: 1.1rem;
          }
        }
        span:nth-child(2){
          color: #999;
          line-height: 5.4rem;
        }
        img{
          width: 0.8rem;
          height: 1.4rem;
          vertical-align: middle;
          margin-left: .8rem;
          margin-top: -.2rem;
        }
      }
    }
    .about_back{
      background-color: #fff;
      margin: 1.2rem 0;
      padding: 0 1.6rem;

      p{
        display: flex;
        justify-content: space-between;
        height: 4.7rem;
        align-items: center;
        span{
          font-size: 1.6rem;
          color: #333333;
        }
        i{
          font-size: 1.4rem;
          color: #999999;
        }
      }
    }
    // p{
    //   height: 4.4rem;
    //   width: 100%;
    //   margin: 1.6rem auto;
    //   border-radius: 2.2rem;
    //   background: linear-gradient(to right, #00C5FC, #00AFFC);
    //   color: #FFF;
    //   font-size: 1.7rem;
    //   line-height: 4.4rem;
    // }
  }
}
</style>


