<template>
  <div class="outCoupon">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../../assets/my/back.png" alt=""  ></span>
      <span>兑换商品管理</span>
      <span class="add" v-if="isAuth(apiList.outCoupon.add)">
        <img  src="../../../assets/head/add.png" @click="goAdd" >
      </span>
    </div>
    <div class="main">
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh" >
      <van-list 
        v-model="loading" 
        :finished="finished" 
        finished-text="——没有更多了——"
        @load="onLoad"
      >
      <van-cell class="content" v-for="(item, index) in dataList" :key="index">
        <div class="box" >
          <div class="head">
            <span class="title"> {{ item.name }} </span>
            <span class="right">
              <span class="upper">{{ item.showToC === true   ? '已上架'   : '已下架' }}</span>
            </span>
          </div>
          <div class="wrapper">
            <img :src="item.imgUrlSquare" alt="">
            <span class="time">
              创建时间：{{ item.updatedAt}}
            </span>
          </div>
          <div class="foot"> 
            <mt-button @click="goExchangeCode(item.id)" v-if="isAuth(apiList.outCouponCode.list)">兑换码</mt-button>
            <mt-button @click="showToc(item.id, index)" v-if="apiList.outCoupon.mod">{{ item.showToC === true ? '下架'   : '上架'}}</mt-button>
            <mt-button @click="goMod(item)" v-if="apiList.outCoupon.mod">修改</mt-button>
            <mt-button @click="del(item.id, index)"  v-if="apiList.outCoupon.del">删除</mt-button>
          </div>
        </div>
      </van-cell>
      
      </van-list>
    </van-pull-refresh>
    </div>
  </div>
</template>
<script>
import { Indicator, MessageBox } from 'mint-ui'

  export default {
    data(){
      return{
        page: 0,
        dataList: [],
        finished: false,
        loading: false,
        isLoading: false
      }
    },
    methods:{
      goBack(){
        this.$router.goback()
      },
      goAdd(){
        this.$router.push('commodityExchange_add')
      },
      goMod(item){
        this.$router.push({ name:'commodityExchange_add', params: {item: item}})
      },
      goExchangeCode(id){
        this.$router.push( { name: 'commodityExchange_EC', query: {id: id}})
      },
      async onLoad(){
        const url = this.apiList.outCoupon.list;
        this.page++;
        let {data} = await this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData( {page: this.page, size: 10}, url, true)
        })
        if(data.result){
          if(data.data.records.length > 0){
            this.dataList = this.dataList.concat(data.data.records) 
          }
          if(this.page >= data.data.pages){
            this.finished = true;
          }
          this.loading = false
        }
        
      },
      async onRefresh(){
        const url = this.apiList.outCoupon.list;
        let {data} = await this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData( {page: 1, size: 10}, url, true)
        })
        if(data.result){
          if(data.data.records.length > 0){
            this.dataList = data.data.records
          }
          this.$toast('刷新成功')
          this.isLoading = false
        }
      },
      del(id, index){
        const url = this.apiList.outCoupon.del;
        MessageBox.confirm("确认删除？").then(async ()=>{
          this.delActivity()
          let {data} = await this.$http({
            url: this.$http.adornUrl(url),
            method: 'post',
            data: this.$http.adornData( {id: id}, url, true)
          })
          this.closeUpload()
          if(data.result){
            this.$toast({
              message: '删除成功',
              iconClass: 'fa fa-check fa-5x'
            })
            this.dataList.splice(index, 1)
            if(this.dataList.length <= 9){
              this.dataList = []
              this.page = 0;
              this.onLoad()
            }
          }else{
            this.$toast(data.msg)
          }
        })
        
      },
      async showToc(id, index){
        const url = this.apiList.outCoupon.mod
        Indicator.open({
          text: '正在上传...',
          spinnerType: 'fading-circle'
        })
        let {data} = await this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData( {id: id, showToC: !this.dataList[index].showToC}, url, true)
        })
        this.closeUpload()
        if(data.result){
          this.dataList[index].showToC = !this.dataList[index].showToC;
          this.$toast({
            message: '操作成功',
            iconClass: 'fa fa-check fa-5x'
          })
        }else{
          this.$toast(data.msg)
        }
      }
    }
  }
</script>

<style lang="scss">
  .outCoupon{
    min-height: 100%;
    background: #f5f5f5;
    .header{
      .add{
        float: right;
        img{
          margin-top: -0.5rem;
        }
      }
    }
    .main{
      margin-top: 2rem;
      .van-pull-refresh{
        overflow: visible;
      }
      .van-cell{
        margin: 0 auto;
        margin-bottom: 1rem;
        width: 34.3rem;
        padding: 0;
        overflow: visible;
        background-color: #fff;
        border-radius: .8rem;
      }
      .box{
        .wrapper{
          display: flex;
          align-items: center;
          padding: 0 1.6rem;
          img{
            width: 7.4rem;
            height: 7.4rem;
          }
          .time{
            margin-left: 2rem;
            color: #333;
            font-size: 1.4rem;
          }
        }
        .foot{
          position: relative;
          height: 5.2rem;
          margin-right: 1rem;
          display: flex;
          justify-content:flex-end;
          align-items: center;
          img{
            position: absolute;
            left: 1rem;
            width: 2rem;
            height: 2rem;
            margin-right: 2.4rem;
          }
          button{
            min-width: 5.8rem;
            height: 2.7rem;
            outline: none;
            background-color: none;
            border-radius: 1.35rem;
            color: #333;
            background-color: #fff;
            margin-right: .9rem;
            font-size: 1rem;
          }
          button:last-child{
            margin: 0;
          }
        }
      }
    }
  }
</style>


