<template>
<div class="addbankList">
  <div class="header">
    <span class="back" @click="goback"><img src="../../assets/my/back.png" alt=""  ></span>
    <span>添加银行卡</span>
  </div>
  <div class="marin_body">
    <div class="item">
      <span>持卡人</span> <input type="text" v-model="personName" placeholder="请输入持卡人姓名" >
    </div>
    <div class="item">
      <span>银行卡号</span><input  v-model="cardNum" @change="bankTypeChange" placeholder="请输入银行卡号" class="suo">
    </div>
    <div class="item">
      <span>开户行</span><input  v-model="bankName" @click="bankTypeChange" placeholder="开户银行名称" class="suo" readonly="readonly">
    </div>   
  </div>
  <mt-button @click="addBank" class="addbtn">立即添加</mt-button>
</div>
</template>
<script>
import { bankCardAttribution } from '../../utils/bankList'
export default {
  data() {
    return{
      personName: '',
      cardNum: '',
      bankCode: '',
      bankName: '',
    }
  },
  watch:{
    // 银行卡输入格式限制
    cardNum(newValue ,oldValue){
      // 每输入四位加空格
      if (/\S{5}/.test(newValue)) {
         this.cardNum = newValue.replace(/\s/g, '').replace(/(.{4})/g, "$1 ");
      }
      // 长度限制
      var str = newValue.replace(/\s/g, '').replace(/(.{4})/g, "$1 ").replace(/\s*/g,"")
      if(str.length>18){
        this.cardNum = str.substr(0, 19).replace(/\s/g, '').replace(/(.{4})/g, "$1 ")
      }
      // 输入类型限制
      if(str.replace(/[^\D]/g,'')){
        // this.$toast('只能输入数字')
        console.log(str)
        if(!parseInt(str)){
          return this.cardNum = ""
        }
        return this.cardNum = ""
      }
    }
  },
  methods: {
    goback() {
      this.$router.goback()
    },
    addBank() {
      if (! this.bankCode) {
        return this.$toast('请输入正确的银行卡号')
      }
      if (! this.personName) {
        return this.$toast('请输入正确的持卡人姓名')
      }
      const url = this.apiList.bankCard.add
      this.upload()
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData({ personName: this.personName, cardNum: this.cardNum.replace(/(.{4})/g, "$1 ").replace(/\s*/g,""), bankCode: this.bankCode }, url, true)
      }).then(({ data }) => {
        this.closeUpload()
        if (data.result) {
          // 添加成功
          this.$toast({
            message: '添加成功',
            iconClass: 'fa fa-check fa-5x'
          })
          setTimeout(() => {
            this.goback()
          }, 1500);
        } else {
          this.$toast(data.msg)
        }
      })
    },
    bankTypeChange() {
      console.log("输入框内容",this.cardNum) 
      var str = this.cardNum.replace(/\s*/g,"")
      console.log("去除空格",str)
      const result = bankCardAttribution(str)
      if (result !== 'error') {
        // 银行卡号输入正确
        this.bankCode = result.bankCode
        this.bankName = result.bankName
      } else {
        this.bankCode = null
        this.$toast('请输入正确的银行卡号')
      }
    },
  },
}
</script>
<style lang="scss">
  .addbankList{
    .suo{
      margin-left: .4rem;
      width: 17.5rem;
    }
    position: fixed;
    top: 0;
    bottom: 0;
    background: #f5f5f5;
    .marin_body{
      margin-top: 1.5rem;
      background: #fff;
      width: 100%;
      .item{
        background: #fff;
        padding:1.4rem 0;
        margin: 0 2rem;
        border-bottom: solid #EEEEEE .1rem;
        box-sizing: border-box;
        span{
          display: inline-block;
          color: #171717;
          font-size: 1.4rem;
          text-align: left;
          width: 8rem;
        }
        input{
          border: none;
          color: #666666;
          font-size: 1.4rem;
          outline: none;
          // height: 95%;
          // height: 9rem;
          height: 2.7rem;
        }
         input::-webkit-input-placeholder {
          /* placeholder颜色  */
          color: #aab2bd;
        }
      }
      .item:last-child{
        border: none;
      }
    }
    .addbtn{
      display: block;
      width: 31.6rem;
      height: 4.4rem;
      line-height: 4.4rem;
      text-align: center;
      font-size: 1.4rem;
      color: #fff;
      background: #e51d27;
      margin: 9.7rem auto 0;
      border-radius: 0;
    }
    .van-cell {
      position: relative;
      display: -webkit-box;
      display: -webkit-flex;
      display: flex;
      box-sizing: border-box;
      width: 100%;
      padding: 1.4rem 2rem;
      overflow: hidden;
      color: #323233;
      font-size: 1.4rem;
      line-height: 29.8px;
      background-color: #fff;
      span{
        font-size: 1.4rem;
      }
      .van-field__body{
        input{
          border: none;
          color: #666666;
          font-size: 1.4rem;
          outline: none;
          // height: 95%;
          // height: 9rem;
          height: 2.7rem;
        }
        input::-webkit-input-placeholder {
          /* placeholder颜色  */
          color: #aab2bd;
        }
      }
    }
  }
</style>


