<template>
  <div class="activity_fullText">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../../assets/my/back.png" alt=""  ></span>
      <span>编辑详情</span>
      <span><div @click="finish">确定</div></span>
    </div>
    <vue-html5-editor ref="html5editor" :content="content"  :show-module-name="false" :z-index="1000" :auto-height="true" @change="textChange" @chooseImg="chooseImg" >
    </vue-html5-editor>
    <div class="explain1 ">说明：推荐您登录众享乐电脑端进行编辑</div>
  </div>
</template>

<script>
import { eventBus } from '../../../utils/eventBus.js'
import { Base64 } from 'js-base64'
  export default {
    data(){
      return{
        content:'',
        title:'',
        id:'',
        fullName:''
      }
    },
    methods:{
      goBack(){
        this.$router.goback()
      },
      chooseImg(){
        this.android.getImg()
      },
      
      finish(){
        var u = navigator.userAgent;
        var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
        // alert(isiOS)
        if (isiOS) {
          var conHtml = document.getElementsByClassName("content");
          // alert(conHtml[0].innerHTML)
          this.content = conHtml[0].innerHTML;
        }
        if(this.content){
          switch(this.$route.query.fullText){
            case 'text':
              localStorage.setItem('text', Base64.encode(this.content))
            break;
            case 'prize':
              localStorage.setItem('prize', Base64.encode(this.content))
            break;
            case 'mod_text':
              localStorage.setItem('text', Base64.encode(this.content))
            break;
            case 'mod_prize':
               localStorage.setItem('prize', Base64.encode(this.content))
            break;
          }
            this.$toast("已保存")
            setTimeout(()=>{
              this.$router.goback()
            },500)
        }else{
          this.$toast("内容不能为空")
        }
      },

      getFull(){        
        // if(this.$route.query.type == 101){
        //   this.title = '报名详情'
        // }else if(this.$route.query.type == 102){
        //   this.title = '修改报名详情'
        // }else if(this.$route.query.type == 501){
        //   this.title = '活动详情'
        // }else if(this.$route.query.type == 502){
        //   this.title = '修改活动详情'
        // }else if(this.$route.query.type == 301){
        //   this.title = '促销详情'
        // }else if(this.$route.query.type == 302){
        //   this.title = '修改促销详情'
        // }else if(this.$route.query.type == 601){
        //   this.title = '商品详情'
        // }else if(this.$route.query.type == 602){
        //   this.title = '修改商品详情'
        // }
        switch(this.$route.query.fullText){
          case 'text':
            this.content = localStorage.getItem('text') ? localStorage.getItem('text') : ''
          break;
          case 'prize':
            this.content = localStorage.getItem('prize') ? localStorage.getItem('prize') : ''
            this.title = '奖品'
          break;
           case 'mod_text':
            this.content = localStorage.getItem('text') ? localStorage.getItem('text') : ''
          break;
          case 'mod_prize':
            this.content = localStorage.getItem('prize') ? localStorage.getItem('prize') : ''
            this.title = '修改奖品'
          break;

        }
        // console.log(123);
        this.content = Base64.decode(this.content)

      },
      
      textChange(val){
        this.content = val
      },
      dataURLtoFile(dataurl, filename = '0000img') {//将base64转换为文件
        var arr = dataurl.split(','), mime = arr[0].match(/:(.*?);/)[1],
            bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
        while(n--){
            u8arr[n] = bstr.charCodeAt(n);
        }
        return new File([u8arr], filename, {type:mime});
      },
    },
    mounted(){
      const vm = this
      eventBus.$on('getImgSuccess', (res) => {
        vm.$refs.html5editor.getImg(vm.dataURLtoFile('data:image/png;base64,' + res.image))
        vm.android.progressHUDend()
      })
    },
    created(){
       this.getFull()
      // console.log(this.$route.query.type);
     
    }
  }
</script>
<style lang="scss">
.vue-html5-editor{
  sub{

    // font-size: 1.2rem;
  }
}
.activity_fullText{
  .header{
    span:last-child{
      margin-top: 0.2rem;
      font-size: 1.3rem;
      color: #fff;
      float: right;

    }
  }
  .content {
    img {
      display: block;
      max-width: 100%;
    }
    p {
      img {
        display: block;
        max-width: 100%;
      }
    }
  }
  .explain1{
    margin-top: 1rem;
    margin-left: .3rem;
    color: #999;
    font-size: 1.1rem;
  }
}
</style>


