<template >
  <div class="access">
    <div class="header">
      <span class="back" @click="$router.goback()"><img src="../../assets/my/back.png" alt=""  ></span>
      <span v-if="id == 'save'">存入</span>
      <span v-else>转出</span>
    </div>
    <div class="moeny_box">
       <div class="title" v-if="id == 'save'">
        转入金额(元)
      </div>
      <div class="title" v-else>
        转出金额(元)
      </div>
     
      <div class="moeny">
        <span>￥</span> 
        <input type="number" placeholder="请输入转入金额" v-model="moeny" oninput="this.value = this.value.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3')" v-if="id == 'save'">
        <input type="number" placeholder="请输入转出金额" v-model="moeny" oninput="this.value = this.value.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3')"  v-else>
      </div>
    </div>
    <div class="way">
      <p v-if="id == 'save'">请选择支付方式</p>
      <p v-else>请选择转出方式</p>
      <div @click="checkWx" v-if="id == 'save'">
        <img src="../../assets/capital/792.png" alt="">
        微信支付
        <img src="../../assets/capital/1020.png" alt="" class="yes" v-if="isWx">
      </div>
      <div @click="checkWx" v-else>
        <img src="../../assets/capital/792.png" alt="">
        微信转出
        <img src="../../assets/capital/1020.png" alt="" class="yes" v-if="isWx">
      </div>
      <div @click="checkAli" v-if="id == 'save'">
        <img src="../../assets/capital/791.png" alt="" >
        支付宝支付
        <img src="../../assets/capital/1020.png" alt="" class="yes" v-if="isAli" >
      </div>
      <div @click="checkAli" v-else>
        <img src="../../assets/capital/791.png" alt="" >
        支付宝转出
        <img src="../../assets/capital/1020.png" alt="" class="yes" v-if="isAli" >
      </div>
    </div>
    <div class="buttton" @click="save" v-if="id == 'save'">
      存入
    </div>
    <div class="buttton" @click="fetch" v-else>
      转出
    </div>
    <div class="shadow"  v-show="shareAppSelect" @click="shareAppSelect = false"> 
    </div>
    <div class="box" v-show="shareAppSelect">
      <img src="../../assets/capital/top.png" alt="">
      <div class="account" v-if="isAli">支付宝账号 <input type="text" placeholder="请输入支付宝账号" v-model="aliNumber"></div>
      <div class="account" v-else>微信实名 <input type="text" placeholder="请输入真实姓名" v-model="wxName"></div>
      <div class="btn">
        <div class="cancel"  @click="shareAppSelect = false">取消</div>
        <div class="affirm" @click="affirm">确认</div>
      </div>
    </div>
  </div>
</template>
<script>
import { eventBus } from '../../utils/eventBus.js'
export default {
  data(){
    return{
      id: '',
      isWx: true,
      isAli: false,
      moeny: '',
      shareAppSelect: false,
      wxName: '',
      aliNumber: '',
      isGo: true
    }
  },
  methods:{
    checkWx(){
      this.isAli = false
      this.isWx = !this.isWx
    },
    checkAli(){
      this.isWx = false
      this.isAli = !this.isAli
    },
    goback(){
      this.$router.goback() 
    },
    // 提现
    fetch(){
      if(!this.isGo){
        return
      }
      this.isGo = false
      if (!this.moeny){
        this.isGo = true
        return this.$toast("请输入提现金额")
      }
      if (this.moeny*100<100){
        this.isGo = true
        return this.$toast("要大于一块钱")
      }
      if (this.moeny*100==0){
        this.isGo = true
        return this.$toast("不能为零")
      }
      if (!this.isWx && !this.isAli){
        this.isGo = true
        return this.$toast("请选择提现方式")
      }
      // this.shareAppSelect = true
      let outType = ''
      if (this.isWx){
        outType = '1'
      }
      if (this.isAli){
        outType = '101'
      }
      const url = this.apiList.capital.moneyOut
      this.$http({
        url: this.$http.adornUrl(url),
        method: "post",
        data: this.$http.adornData( {outType  ,outNum: Math.ceil(this.moeny *100)}, url, true)
      }).then(( {data : res} )=>{
        if(res.result){
          if(res.msg == "OK"){
            this.$toast("提现成功")
            this.isGo = true
            this.moeny  = ''
            setTimeout(() => {
              this.goback()
            }, 600)
          }
        }else{
          this.$toast(res.msg)
          this.isGo = true
          this.moeny  = ''
        }
      })
    },
    // 充值
    save(){
      if(!this.isGo){
        return
      }
      this.isGo = false
      if (!this.moeny){
        this.isGo = true
        return this.$toast("请输入转入金额")
      }
      if (this.moeny*100<100){
        this.isGo = true
        return this.$toast("要大于一块钱")
      }
      if (!this.isWx && !this.isAli){
        this.isGo = true
        return this.$toast("请选择支付方式")
      }
      if (this.moeny*100==0){
        this.isGo = true
        return this.$toast("不能为零")
      }
      let outType = ''
      let url = this.apiList.capital.moneyIn
      if (this.isWx){
        this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData({outType: 1, inNum :Math.ceil(this.moeny *100)}, url, true)
        }).then(({ data : res}) => {
          this.isGo = true
          console.log(res)
          if (res.result) {
            this.android.pay(101, res.data)
          }else {
            this.isGo = true
            this.$toast(data.msg)
          }
        })
      }
      if (this.isAli){
        this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData({outType: 101, inNum :Math.ceil(this.moeny *100)}, url, true)
        }).then(({ data : res}) => {
          this.isGo = true
          console.log(res)
          if (res.result) {
            this.android.pay(201, res.data)
            // console.log(outType,res.data)
            console.log(outType)
            // this.android.pay(201, res.data)
          }else {
            this.isGo = true
            this.$toast(data.msg)
          }
        })
      }
    },
    affirm(){
      if(this.isWx){
        if(!this.wxName.trim()){
          this.$toast('请输入真实姓名')
          return
        }
      }
      if(this.isAli){
        if(!this.wxName.trim()){
          this.$toast('请输入支付宝账号')
          return
        }
      }
      const url = this.apiList.capital.moneyOut
      this.$http({
        url: this.$http.adornUrl(url),
        method: "post",
        data: this.$http.adornData( {outType: '101' ,outNum: this.moeny*100}, url, true)
      }).then(( {data : res} )=>{
        console.log(res)
      })
    }
  },
  created(){
    this.id = this.$route.params.id
    eventBus.$on('payScccess', ({ res }) => {
      if(Number(res.code) === 0 || Number(res.resultStatus) === 9000){
        this.$toast({
          message: '支付成功',
          iconClass: 'fa fa-check fa-5x'
        })
        setTimeout(() => {
          this.goback()
        }, 600)
      }else{
        this.$toast('支付失败')
      }
   })
  }
}
</script>
<style lang="scss">
.access{
  width: 100%;
  height: 100%;
  background-color: #FAFAFA;
  .mint-toast.is-placemiddle {
    z-index: 99999999;
  }
  .moeny_box{
    width: 34.3rem;
    height: 13.5rem;
    background-color: #fff;
    margin: 2.5rem  auto;
    box-shadow:0 .1rem .5rem 0 rgba(0, 0, 0, 0.09);
    overflow: hidden;
    .title{
      font-size: 1.4rem;
      color: #333333;
      margin-top: 2.5rem;
      margin-left: 1.5rem;
    }
    .moeny{
      width: 31.4rem;
      margin: 0 auto;
      margin-top: 4rem;
      border-bottom: .05rem solid rgba(238,238,238,1);
      span{
        font-size: 2.5rem;
      }
      input{
        border: none;
        width: 90%;
        height: 3rem;
        margin-bottom: .2rem;
      }
    }
  }
  .way{
    width: 100%;
    background-color: #fff;
    margin-top: 2.5rem;
    box-sizing: border-box;
    padding-left: 1.5rem;
    p{
      height: 4.5rem;
      line-height: 4.5rem;
      font-size: 1.4rem;
    }
    div{
      width: 100%;
      height: 5.4rem;
      border-top: .05rem solid rgba(238,238,238,1);
      line-height: 5.4rem;
      img{
        width: 2.4rem;
        vertical-align: middle;
        margin-right: 1rem;
      }
      .yes{
        margin-left: 20rem;
        width: 1.6rem;
      }
    }
    :last-of-type{
      .yes{
        margin-left: 18.5rem;
      }
    }
  }
  .buttton{
    width: 34.3rem;
    height: 4.4rem;
    background-color: #F64542;
    margin: 4.9rem auto;
    border-radius: 2.2rem;
    font-size: 1.6rem;
    text-align: center;
    line-height: 4.4rem;
    color: #fff;
  }
  .shadow{
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: #000;
    opacity: 0.6;
    z-index: 9999;
  }
  .box{
    position: absolute;
    top: 0;
    height: 28.5rem;
    width: 28.4rem;
    background-color: #fff;
    border-radius:.8rem;
    z-index: 99999;
    margin-top: 20rem;
    left: 4.5rem;
    img{
      width: 100%;
    }
    .account{
      margin-top: 5rem;
      font-size: 1.4rem;
      padding-left: 2.8rem;
      input{
        width: 14.7rem;
        height: 3.4rem;
        box-sizing: border-box;
        padding-left: .5rem;
        border: none;
        border-bottom: .05rem solid rgba(221,221,221,1);
      }
    }
    .btn{
      display: flex;
      justify-content: center;
      div{
        width: 11.6rem;
        height: 3.6rem;
        border-radius: 1.7rem;
        line-height: 3.6rem;
        text-align: center;
        margin-top: 5rem;
      }
      .cancel{
        border: .05rem solid rgba(153, 153, 153, 1);
        background:rgba(255,255,255,1);
      }
      .affirm{
        background:linear-gradient(-82deg,rgba(239,83,80,1),rgba(229,57,53,1));
        color: #fff;
        margin-left: 2rem;
      }
    }
  }
}
</style>



