<template>
  <div class="manage_reject">
    <div class="header">
      <span class="back" @click="$router.goback()"><img src="../../../assets/my/back.png" alt=""  ></span>
      <span>驳回申请</span>
    </div>
    <div class="manage_box">
      <p>驳回原因</p>
      <textarea v-model="text" >
      </textarea>
    </div>
    <div class="outMoeny_pass" @click="reject">驳回</div>
  </div>
</template>
<script>
export default {
  data(){
    return{
      text:'',
      info:''
    }
  },
  methods:{
    reject(){
      const url = this.apiList.admin.dealReseller
      const approvalId = this.$route.params.id
      if(!this.text){
        return this.$toast('请备注驳回原因')
      }
      this.$http({
        url: this.$http.adornUrl(url),
        method: "post",
        data: this.$http.adornData({ approvalId, agree: false ,refuseRemark: this.text}, url, true)
      }).then(({ data:res })=>{
        if(res.result){
          this.$toast({
            message: '驳回成功',
          })
          setTimeout(()=>{
            this.$router.goback()
          },1000)
        }else{
          this.$toast(res.msg)
        }
      })
    }
  },
  created(){
    console.log(this.$route.params)
  }
}
</script>
<style lang="scss">
.manage_reject{
  min-height: 100%;
  background: #f5f5f5;
  .manage_box{
    width: 34.3rem;
    margin: 0 auto;
    margin-top: 5rem;
  }
  textarea{
    width: 34.3rem;
    height: 14.2rem;
    // margin-left: 1.6rem;
    margin: 0 auto;
    margin-top: 1rem;
    border: none;
    resize: none;
    border-radius: 0.8rem;
    padding: 1rem .5rem 0;
    box-sizing: border-box; 
  }
  .outMoeny_pass{
    width: 33rem;
    height:4.3rem;
    background:rgba(246,69,66,1);
    border-radius:2.15rem;
    font-size: 1.6rem;
    text-align: center;
    line-height: 4.3rem;
    color: #FFFFFF;
    margin: 0 auto;
    margin-top: 7.25rem;
  }
}
</style>




