import request from './request'
import api from './api'
let role = [] // 用来保存权限数组
/**
 * 1. 判断是否拥有权限
 * @param {Array} key 
 */
export function isAuth (key) {
  return role.indexOf(key) !== -1 || false
}

/**
 * 2. 设置权限列表
 * @param {Array} arr 
 */
export function getRole () {
  // 新建一个异步请求
  const url = '/menu/getMenu' // 获得目录的接口
  return new Promise((resolve, reject) => {
    request({
      url: request.adornUrl(url),
      method: 'post',
      data: this.$http.adornData('', url, true)
    }).then(({ data }) => {
      console.log(data);
      if (data.result) {
        const roles = []
        const roles_chinese = []
        for (const i in data.data) {
          if (data.data[i].show === 1) {
            roles.push(data.data[i].url)
          }
          roles_chinese.push(data.data[i].name)
        }
        if (roles.length === 0) {
          roles.push('noData')
        }
        role = roles
        resolve(role)
        console.log(roles);
      } else {
        reject(data.msg)
      }

    })
    
  })
}
export function clear (){
   role = [] 
}


