<template>
  <div class="service">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../assets/my/back.png" alt=""  ></span>
      <span>我的客服</span>
    </div>
    <div class="concact">
        <p>客服电话：{{tel}}</p> 
        <mt-button @click="call">联系客服</mt-button>
    </div>
  </div>
</template>
<script>
  export default {
    data(){
      return{
        tel: '0591-83966369'
      }
    },
    methods:{
      goBack(){
        this.$router.goback()
      },
      call(){
        this.android.toUrl('tel:'+this.tel)
      }
    }
  }
</script>
<style lang="scss">
  .service{
    width: 100%;
    height: 100%;
    background-color: #F5F5F5;
    
    .concact{
      margin-top: 10rem;
      text-align: center;
      p{
          padding-bottom: 2rem;
          font-size: 1.4rem;
      }
      button{
        display: block;
        width: 92%;
        height: 4.4rem;
        margin: auto;
        margin-top: 5rem;
        background:linear-gradient(to right,rgba(249,82,94,1),rgba(244,75,89,1));
        border-radius:2.2rem;
        border: none;
        outline: 0;
        color: rgba(254,255,255,1);;
        font-size: 1.6rem;
      }
    }
  }
</style>



