<template>
  <div class="activity_common_details">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../../../assets/my/back.png" alt="" ></span>
      <span class="headText">{{ title }}</span>
      <span class="add_Pro" >
        <img class="searchBtn" @click="goSearch" src="../../../../assets/head/search.png">
      </span>
    </div>
    <div class="main">
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
        <van-list class=""
          v-model="loading" 
          :finished="finished" 
          finished-text="—— 没有更多了 ——"
          @load="onLoad"
          ref="list"
        >
          <van-cell class="pro_det_li" v-for="(item, index) in dataList" :key="index">
            <div class="wrapper">
              <div class="top">
                <span class="order">
                  <span class="orderText">订单号：</span><span class="orderNum">{{item.outTradeNo}}</span>
                </span>
                <span class="state">
                  {{item.useState == 1 ? '未提货' : (item.useState == 101 ? '已提货' : '已退款')}}
                </span>
              </div>
              <div class="centent">
                <span class="left">
                  <span><img :src="item.userAvatar ? item.userAvatar : require('../../../../assets/my/userAvatar.png')" alt=""></span>
                  <span class="username">
                    {{item.userNickName ? item.userNickName : '众享乐用户'}}
                  </span>
                </span>
                <span class="price">
                  <span class="priceText">金额：</span><span class="priceNum">￥{{(item.price / 100).toFixed(2)}}</span>
                </span>
              </div>
              <div class="time">
                <span class="timeText">购买时间：</span>
                <span class="timeNum">{{item.createdAt}}</span>
              </div>
            </div>
            <div class="btn" v-if="item.useState == 1">
              <mt-button @click="refund(item.orderId, index)" v-if="isAuth('/order/moduleRefundOrder')">
                退款
              </mt-button>
            </div>
          </van-cell>
        </van-list>
      </van-pull-refresh> 
    </div>
  </div>
</template>
<script>
import { MessageBox } from 'mint-ui'
  export default {
    data(){
      return{
        page: 0,
        dataList:[],
        title: '',
        activeUrl: this.$route.query.list, 
        loading: false,
        finished:false,
        isLoading: false
      }
    },
    methods:{
      goBack(){
        this.$router.goback()
      },
      onLoad(){
        return new Promise((resolve, reject) =>{
          this.page++
          const activeUrl = this.activeUrl
          const url = this.apiList[activeUrl].orderList
          this.$http({
            url: this.$http.adornUrl(url),
            method: 'post',
            data: this.$http.adornData({ activityId: this.$route.query.id, page: this.page, size: 10 }, url, true)
          }).then(({data})=>{
            if (data.result) {
              if (data.data.records.length >= 0) {
                this.dataList = this.dataList.concat(data.data.records)
              }
              if (data.data.pages <= this.page) {
                this.finished = true
              }
              this.$nextTick(() => {
                resolve(true)
                this.loading = false
              })
            }else if( data.msg == 'sign验证失败' ){
              this.$toast('您的账户登录超时')
              setTimeout(()=>{
                localStorage.clear();
                this.$router.push({name: 'login'})
              },1000)
            }
          })   
        })
      },
      onRefresh(){
        return new Promise((resolve, reject) =>{
          const activeUrl = this.activeUrl
          const url = this.apiList[activeUrl].orderList
          this.$http({
            url: this.$http.adornUrl(url),
            method: 'post',
            data: this.$http.adornData({activityId: this.$route.query.id, page: 1, size: 10}, url, true)
          }).then(({data})=>{
            if (data.result) {
              if (data.data.records.length >= 0) {
                this.dataList = data.data.records
              }
              this.$nextTick(() => {
                this.$toast('刷新成功');
                this.isLoading = false;
                this.finished = false;
                this.page = 1;
                resolve(true)
              })
            }else if( data.msg == 'sign验证失败' ){
              this.$toast('您的账户登录超时')
              setTimeout(()=>{
                localStorage.clear();
                this.$router.push({name: 'login'})
              },1000)
            }
          })   
        })
      },
      goSearch(){
        // this.$router.push({name:'details_search', query: {id: this.$route.query.id}})
        this.$router.push({name:'activity_search_details', query: { list: this.activeUrl, id: this.$route.query.id}})
      },
      refund(id, index){
        let url = this.apiList.businessRefund.refund;
        MessageBox.confirm('确认退款？').then( ()=>{
          this.$http({
            url: this.$http.adornUrl(url),
            method: 'post',
            data: this.$http.adornData({ orderId: id }, url, true)
          }).then(({data})=>{
            if(data.result){
              this.$toast("退款成功")
              this.dataList[index].useState = 201
            }else if(data.msg == 'sign验证失败'){
              this.$toast('您的账户登录超时')
              setTimeout(()=>{
                localStorage.clear();
                this.$router.push({name: 'login'})
              },1000)
            }
          })
        })
      }
    },
    mounted(){
      this.$refs.list.$el.style.minHeight = document.documentElement.clientHeight - 7.6 * 10 + 'px'
    },
    created(){
      alert(11111)
     this.title =  this.activeUrl == 'commodity' ? '商品明细' : (this.activeUrl == 'addActivity' ? '活动明细' : (this.activeUrl == 'pmSale' ? '促销明细' : '报名明细'))
    }
  }
</script>
<style lang="scss">
  .activity_common_details{
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    background: #F5F5F5;
    .header{
      .add_Pro{
        float: right;
        img{
          width: 1.8rem;
          height: 1.9rem;
        }
      }
    }
    .main{
      margin-top: 1.4rem;
      .pro_det_li{
        width: 34.3rem;
        margin:0 auto 1.2rem;
        border-radius: 0.8rem;
        background: #fff;
        padding: 0;
        .wrapper{
          margin: 0 1.3rem;
          padding-bottom: 1.6rem;
          .top{
            display: flex;
            justify-content: space-between;
            padding: 1.6rem 0;
            .orderText,.orderNum{
              color: #333;
              font-size: 1.3rem;
            }
            .state{
              color: #E73E3B;
              font-size: 1.1rem;
            }              
          }
          .centent{
            display: flex;
            justify-content: space-between;
            img{
              display: inline-block;
              width: 2.7rem;
              height: 2.7rem;
              border-radius: 50%;
            }
            .username{
              display: inline-block;
              vertical-align: top;
            }
            .username, .priceText, .priceNum{
              color: #333;
              font-size: 1.3rem;
            }
          }
          .time{
            margin-left: 3.15rem;
            margin-top: -0.5rem;
            .timeText,.timeNum{
              color: #333;
              font-size: 1.3rem;
            }
          }
          
        }
        .btn{
          border-top: 0.1rem solid #eee;
          padding: 1rem 0;
          button{
            display: block;
            width: 6.3rem;
            height: 2.7rem;
            margin:auto ;
            border: 0 solid #666;
            border-radius: 1.35rem;
            background: #fff;
            color: #333;
            font-size: 1.2rem;
          }
        }
      }
    }
  }
</style>


