<template>
  <div id="app">
    <transition>
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>
import { MessageBox } from 'mint-ui'
import { eventBus } from './utils/eventBus.js'
export default {
  name: 'App',
  data () {
    return {
      transitionName: 'slide-left'
    }
  },
  methods: {
    getVersion() {
      const url = this.apiList.version
      this.$http({
        url: 'http://j.chnzxl.com/entryc' + url,
        method: 'post',
        data: this.$http.adornData({appType: localStorage.getItem('app_type'), platform: 2, versionCode: localStorage.getItem('appCurVersionNum')}, '' , false)
      }).then(({ data }) => {
        if (data.result) {
          if (data.data.isUpdate) {
            const updatedes = Base64.decode(data.data.updatedes)
            if (data.data.updateflag) {
              MessageBox.alert(updatedes, '发现新版本').then(() => {
                this.android.toUrl("http://f.chnzxl.com/download/index-b.html")
              })
            } else {
              MessageBox.confirm(updatedes, '发现新版本').then(() => {
                this.android.toUrl("http://f.chnzxl.com/download/index-b.html")
              })
            }
          }
        }
      }).catch((err) => {
        this.$toast(err)
      })
    }
  },
  mounted () {
    
    var screenX = document.documentElement.offsetWidth || document.body.offsetWidth
    this.fontSize = 20 / 750 * screenX + 'px'
    document.documentElement.style.fontSize = this.fontSize
    // 获取参数
    window.onresize = () => {
      return (() => {
        var _screenX = document.documentElement.offsetWidth || document.body.offsetWidth
        this.fontSize = 20 / 750 * _screenX + 'px'
        document.documentElement.style.fontSize = this.fontSize
      })()
    }
      
  },
  created() {
    // this.android.navColorChange('#f64542/')
    const vm = this
    /**
     * 注册基座返回键
     * @param {}
     */
    let btnTime =new Date().getTime()
    window.andBackbtn = function() {
      if(vm.$router.history.current.name === 'login' || vm.$router.history.current.name === 'index'){
        let btnTime =new Date().getTime()
        vm.$toast({
          message: '再按一次退出众享乐',
          duration: 2000
        })
        if(btnTime - vm.btnTime < 2000) {
          vm.clear();
          vm.android.outToApp()
        }
        vm.btnTime = btnTime
      }else {
        if(vm.$router.history.current.name === 'addActivity' || vm.$router.history.current.name === 'commodity' || vm.$router.history.current.name === 'signUp' || vm.$router.history.current.name === 'promotion'){
          localStorage.removeItem('dataList')
          localStorage.removeItem('pageNum')
          localStorage.removeItem('scrollTop0')
          localStorage.removeItem('scrollTop1')
          localStorage.removeItem('scrollTop2')
          localStorage.removeItem('activeNum')
        }
        if(vm.$router.history.current.name === 'activity_common_edit'){
          localStorage.removeItem('addScrollTop')
          vm.clearStorage()
        }
        vm.$router.goback()
      }
    }
    localStorage.setItem('layout_index', 0)
    localStorage.removeItem('hasPasssConfim')
    eventBus.$on('app_version', function() {
      vm.getVersion()
      eventBus.$off('app_version')
    }) 
  }
}
</script>
<style lang="scss">
@import "./assets/font-awesome/css/font-awesome.min.css";
