  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  // position: absolute;
  // top: 0;
  // left: 0;
  // 
  // overflow: scroll;
  // -webkit-overflow-scrolling: touch; 
  // position: absolute;
