<template>
  <div class="withdrawal">
    <div class="header">
      <span class="back" @click="goback"><img src="../../assets/my/back.png" alt=""  ></span>
      <span>提现</span>
      <span style="position: absolute; right: 1.5rem;font-size: 1.4rem; color: #e51d27; cursor: pointer" @click="goList">提现记录</span>
    </div>
    <div class="main_info">
      <div class="form">
        <div>请输入提现金额：</div>
        <div class="input"><input type="number" v-model="outnumber" :max="money" @change="moneyMax"></div>
        <div>元</div>
      </div>
      <p>账户可用余额：<span>{{ money }}</span> 元</p>
    </div>
    <div class="main_contrl" v-if="type === 1">
      <p class="contrl_header">选择银行卡</p>
      <ul>
        <li v-for="item in dataList" :key="item.id" @click="bankchoose = item.id ">
          <input type="radio" name="bankcardList" :value="item.id" v-model="bankchoose">
          <!-- 工商银行 -->
          <img src="../../assets/my/zgyh.png" alt="" v-if="item.bankCode == 'ICBC'">
          <span v-if="item.bankCode == 'ICBC'">中国工商银行(尾号{{ item.cardNum }})</span>
          <span class="del" v-if="item.bankCode == 'ICBC'" @click.stop="delCard(item.id)"></span>
          <!-- 建设银行 -->
          <img src="../../assets/my/jsyh.png" alt="" v-if="item.bankCode == 'CCB'">
          <span v-if="item.bankCode == 'CCB'">中国建设银行(尾号{{ item.cardNum }})</span>
          <span class="del" v-if="item.bankCode == 'CCB'" @click.stop="delCard(item.id)"></span>
          <!-- PSBC中国邮政 -->
          <img src="../../assets/my/zgyzyh.png" alt="" v-if="item.bankCode == 'PSBC'">
          <span v-if="item.bankCode == 'PSBC'">中国邮政银行(尾号{{ item.cardNum }})</span>
          <span class="del" v-if="item.bankCode == 'PSBC'" @click.stop="delCard(item.id)"></span>
          <!-- 农业银行 -->
          <img src="../../assets/my/nyyh.png" alt="" v-if="item.bankCode == 'ABC'">
          <span v-if="item.bankCode == 'ABC'">农业银行(尾号{{ item.cardNum }})</span>
          <span class="del" v-if="item.bankCode == 'ABC'" @click.stop="delCard(item.id)"></span>
        </li>
      </ul>
      <div class="add_bank" @click="addBank">
        <img src="../../assets/vip/add.png" alt="">
        <span>添加新的银行卡</span>
      </div>
      <div class="buyNow" @click="submit">立即提现</div>
    </div>
    <div v-else>
      <div class="buyNow" @click="submit">立即提现</div>
    </div>
  </div>
</template>
<script>
import { MessageBox } from 'mint-ui'
export default {
  data() {
    return{
      money: 0,
      outnumber: 0,
      dataList: [],
      bankchoose: null,
      type: 101
    }
  },
  methods: {
    goback() {
      this.$router.goback()
    },
    addBank() {
      this.$router.push('/addBank')
    },
    goList() {
      this.$router.push('/withdrawalList')
    },
    // 获得当前可提现金额
    getData() {
      const url = this.apiList.money.get
       
       this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData('', url, true)
      }).then(({ data }) => {
        if (data.result) {
          this.money = data.data.canOutMoney / 100
        } else {
          if (data.msg === 'sign验证失败') {
            localStorage.clear()
            this.$toast({
              message: '您的账号登录超时',
              iconClass: 'icon icon-error'
            })
            setTimeout(() => {
              instance.close()
              this.$router.push({ name:  'login' })
            }, 2000)
          }
        }
      })
    },
    // 最大可提现金额
    moneyMax() {
      if (this.outnumber > this.money) {
        this.$toast('提现金额超过可提现余额')
        this.outnumber = this.money
      }
    },
    // 获取银行卡
    getDataList() {
      const url = this.apiList.bankCard.list
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData('', url, true)
      }).then(({ data }) => {
        if (data.result) {
          data.data.map((v, i, a) => {
            v.cardNum = v.cardNum.slice(v.cardNum.length - 4, v.cardNum.length)
          })
          this.dataList = data.data
          console.log(this.dataList)
        } else {
          this.$toast(data.msg)
        }
      })
    },
    // 删除当前银行卡
    delCard(id) {
      const url = this.apiList.bankCard.del
      // 弹出询问
      MessageBox.confirm('确认删除？').then(() =>{
        this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({ bankCardId: id }, url, true)
        }).then(({ data }) => {
          if (data.result) {
            this.$toast('删除成功')
            this.getDataList()
          } else {
            this.$toast(data.msg)
          }
        })
      }).catch(() => {
        console.log(1111)
      })
    },
    // 发起提现
    submit() {
      if (this.outnumber > this.money) {
        return this.$toast('请确认您选择提现的金额')
      }
      let str = `平台将收取1%的手续费，确认提现？`
      MessageBox.confirm(str).then(() =>{
        const url = this.apiList.moneyApproval.moneyApproval
        this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({ bankCardId: this.bankchoose, num: this.outnumber*100, moneyOutType: this.type }, url, true)
        }).then(({ data}) => {
          if (data.result) {
            this.$toast('提现申请提交成功')
            this.getData()
            setTimeout(() => {
              this.goback()
            }, 1500);
          } else {
            this.$toast(data.msg)
          }
        })
      })
    }
  },
  created() {
    this.getData()
    this.getDataList()
    this.type = this.$route.query.type
  }
}
</script>
<style lang="scss">
.withdrawal{
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  background: #f5f5f5;
  input{
    outline: none;
  }
  .main_info{
    height: 14.8rem;
    box-sizing: border-box;
    border-top: solid #EEEEEE .1rem;
    width: 100%;
    background: #fff;
    padding: 3.5rem 1.5rem 1.2rem;
    .form{
      height: 4.7rem;
      line-height: 4.7rem;
      padding-bottom: 3rem;
      div{
        display: inline-block;
        font-size: 1.4rem;
        color: #666;
        input{
          width: 18rem;
          height: 100%;
          line-height: 4.7rem;
          box-sizing: border-box;
          border: solid .1rem #D7D7D7;
          padding: 1rem;
          padding-left: 2.2rem;
          font-size: 2.5rem;
          color: #333;
          font-weight: 600;
        }
      }
      .input{
        position: relative;
        &::before{
          content: '￥';
          font-size: 1.2rem;
          display: inline-block;
          vertical-align: bottom;
          position: absolute;
          left: 1rem;
          line-height: 5.5rem;
        }
      }
    }
    p{
      width: 34.5rem;
      margin: auto;
      height: 3.6rem;
      line-height: 3.6rem;
      border-top: solid .1rem #eee;
      color: #999;
      span{
        color: #34AFF6;
      }
    }
  }
  .main_contrl{
    background: #fff;
    position: absolute;
    top: 21.2rem;
    bottom: 0;
    width: 100%;
    .contrl_header{
      width: 100%;
      height: 4.5rem;
      line-height: 4.5rem;
      font-size: 1.4rem;
      text-align: left;
      box-sizing: border-box;
      padding-left: 1.5rem;
      border-bottom: solid .1rem #eee;
    }
    ul{
      li{
        // margin: 2.4rem 1.5rem;
        box-sizing: border-box;
        margin: 0 2.4rem;
        border-bottom: solid .1rem #EEEEEE;
        // border-radius: .2rem;
        height: 6rem;
        box-sizing: border-box;
        line-height: 6rem;
        padding: 0 1.5rem;
        input{
          vertical-align: middle;
        }
        img{
          width: 3.4rem;
          height: 3.4rem;
          vertical-align: middle;
          margin-left: 1.2rem;
          margin-right: .8rem;
        }
        &:last-child{
          margin-bottom: 0;
        }
        .del{
          color: #34AFF6;
          float: right;
          display: inline-block;
          width: 1.3rem;
          height: 1.3rem;
          background: url('../../assets/vip/del.png');
          background-size: 100% 100%;
          margin-top: 3rem;
          transform: translateY(-50%);
        }
      }
    }
    .add_bank{
      width: 100%;
      box-sizing: border-box;
      padding: 2rem;
      color: #999;
      text-align: center;
      font-size: 1.2rem;
      img{
        width: .9rem;
        height: .9rem;
      }
    }
  }
  .buyNow{
    // width: 31.6rem;
    width: 100%;
    height: 4.9rem;
    text-align: center;
    line-height: 4.9rem;
    color: #fff;
    background: #e51d27;
    border-radius: .4rem;
    position: fixed;
    bottom: 0;
  }
}
</style>


