<template>
  <div class="notice">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../assets/my/back.png" alt=""  ></span>
      <span>发布公告</span>
    </div>
    <div class="notice_main">
      <!-- <div class="notice_text">
        <span class="left">
          <img src="" alt="">
        </span>
        <span class="right">
          <p>福州麦蛤欢乐宠物</p>
          <textarea placeholder="点击输入公告" maxlength='60' v-model="text"></textarea>
          <span>{{text.length}}/30</span>
        </span>
      </div> -->
      <textarea placeholder="点击输入公告" maxlength='60' v-model="text"></textarea>
      <span>{{text.length}}/60</span>
      <span class="clear" @click="claer" v-if="text" ><img src="../../assets/login/close.png" alt=""></span>
    </div>
    <div class="btn">
      <mt-button @click="publish">发布</mt-button>
      <mt-button plain type="danger" @click="goMessageNotice" v-if="false">消息通知</mt-button>
      <mt-button plain type="danger" @click="goShortMessage" v-if="false">短信通知</mt-button>
    </div>
  </div>
</template>
<script>
export default {
  data(){
    return{
      text: '',
      flag: false
    }
  },
  methods:{
    goBack(){
      this.$router.goback()
    },
    goShortMessage(){
      this.$toast('暂未开放')
      // this.$router.push('shortMessage')
    },
    goMessageNotice(){
      this.$toast('暂未开放')
      // this.$router.push('messageNotice')
    },
    publish(){
      if(this.text.length>60){
        return this.$toast('内容不能大于60个字符')
      }
      if(this.text.trim()=='' && this.text!=''){
        return this.$toast('不能输入空内容')
      }
      if(this.flag) return
      this.upload()
      const url = this.apiList.shop.edit
      this.flag = true
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData({ shopNotice: this.text }, url , true)
      }).then(({data})=>{
        this.closeUpload()
        if(data.result){
          this.$toast({
            message: '发布成功',
            iconClass: 'fa fa-check fa-5x'
          })
          setTimeout(()=>{
            this.$router.goback()
          },200)
        } else{
          this.flag = false
          this.$toast(data.msg)
        }
      })
    },
    getText(){
      const url = this.apiList.shop.get
      this.loading()
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData({}, url , true)
      }).then(({data})=>{
        this.closeUpload()
        if(data.result){
          if(data.data.shopNotice){
            this.text = data.data.shopNotice
          }
          
        }else if(data.msg == 'sign验证失败'){
          this.$toast('您的账户登录超时')
          setTimeout(()=>{
            localStorage.clear()
            this.$router.push({name:  'login' })
          },1000)
        } else{
          this.$toast(data.msg)
        }
      })
    },
    claer(){
      this.text = ''
    }
  },
  created(){
    this.getText()
  }
}
</script>
<style lang="scss">
  .notice{
    background: #f5f5f5;
    min-height: 100%;
    .notice_main{
      margin: 0 1.6rem;
      height: 10rem;
      position: relative;
      textarea{
        width: 100%;
        height: 100%;
        margin-top: 1rem;
        border: none;
        resize: none;
        border-radius: 0.8rem;
        padding: 1rem .5rem 0;
        box-sizing: border-box; 
      }
      span{
        position: absolute;
        bottom: -3rem;
        right: 0.2rem;
        img{
          width: 1.5rem;
        }
      }
      .clear{
        bottom: -1rem;
        right: 1rem;
      }
      // .notice_text{
      //   display: flex;
      //   justify-content: space-between;
      //   align-items: center;
      //   width: 34.3rem;
      //   height: 13rem;
      //   margin: 0 auto;
      //   padding: 0 1rem;
      //   box-sizing: border-box;
      //   border-radius: 0.8rem;
      //   .left{
      //     width: 8.1rem;
      //     img{
      //       display: inline-block;
      //       width: 8.1rem;
      //       height: 8.1rem;
      //       background: green;
      //       border-radius: 50%;
      //     }
      //   }
      //   .right{
      //     flex: 1;
      //     margin-left: 2rem;
      //     height: 10rem;

      //     textarea{
      //       width: 100%;
      //       height: 6rem;
      //       margin-top: 1rem;
      //       border: none;
      //     }
      //     span{
      //       float: right;
      //       color: #999;
      //     }
      //     ::-webkit-input-placeholder { /* WebKit, Blink, Edge */
      //       color: #00A3FF;
      //     }
      //     :-moz-placeholder { /* Mozilla Firefox 4 to 18 */
      //       color: #00A3FF;
      //     }
      //     ::-moz-placeholder { /* Mozilla Firefox 19+ */
      //       color: #00A3FF;
      //     }
      //     :-ms-input-placeholder { /* Internet Explorer 10-11 */
      //       color: #00A3FF;
      //     }
      //   }
      // }
    }
    .btn{
      margin-top: 4rem;
      button{
        display: block;
        width: 31.6rem;
        height: 4.4rem;
        margin: 0 auto;
        color: #F64542;
        font-size: 1.6rem;
        margin-top: 2rem;
        border-radius: 0;
        &:nth-child(1){
          background: #F64542;
          color: #fff;
        }
      }
     
    }
  }
</style>


