<template>
<div class="vipMember">
  <div class="header">
    <span class="back" @click="goback"><img src="../../assets/my/back.png" alt=""  ></span>
    <span>会员列表</span>
  </div>
  <div class="listContainer">
    <ul>
      <li v-for="item in dataList" :key="item.id" @click="emGoToChatList(item)">
        <img :src="item.userAvatar  ? item.userAvatar : require('../../assets/my/userAvatar.png')"/>
        <div>
          <p>{{ item.userNickName ? item.userNickName : '众享乐用户' }}</p>
          <!-- <p>15633336612</p> -->
        </div>
        <div class="time">
          <p>{{ item.updatedAt.split(' ')[0] }}关注</p>
        </div>
      </li>
    </ul>
  </div>
</div>
</template>
<script>
export default {
  data() {
    return{
      page: 1,
      size: 1000,
      dataList: []
    }
  },
  methods: {
    goback() {
      this.$router.goback()
    },
    // 点击聊天
    emGoToChatList(item) {
      const fromData = {
        userId: "3-" + item.userId,
        url_title: item.userNickName ? item.userNickName : '众享乐用户',
        navBackgroudColor: '#f54340',
        avatar: item.userAvatar ? item.userAvatar : '',
        nickName: item.userNickName ? item.userNickName: '众享乐用户'
      }
      this.android.emGoToChat(fromData)
    },
    getDataList() {
      const url = this.apiList.shop.collect
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData({ page: this.page, size: this.size }, url , true)
      }).then(({ data }) => {
        if (data.result) {
          this.dataList = data.data.records
        } else if (data.msg == 'sign验证失败') {
          this.$toast('您的账号登陆超时')
          setTimeout(() => {
            localStorage.clear()
            this.$router.push({ name:  'login' })
          }, 1000)
        }
      })
    }
  },
  created() {
    this.getDataList()
  }
}
</script>
<style lang="scss">
  .vipMember{
    position: absolute;
    min-height: 100%;
    width: 100%;
    background: #f5f5f5;
    .listContainer{
      margin-top: 1.2rem;
      ul{
        width: 100%;
        box-sizing: border-box;
        padding: 0 1.5rem;
        background: #fff;
        li{
          width: 100%;
          height: 6.5rem;
          box-sizing: border-box;
          border-bottom: solid .1rem #F5F5F5;
          line-height: 6.5rem;
          img{
            width: 4.6rem;
            height: 4.6rem;
            border-radius: 100%;
            margin-right: 1rem;
            vertical-align: middle;
          }
          div{
            display: inline-block;
            max-width: 20rem;
            vertical-align: middle;
            p{
              color: #333333;
              line-height: 1.8rem;
            }
            p:nth-child(2) {
              color: #999999
            }
          }
          .time{
            float: right;
            margin-top: 4rem;
            p{
              color: #999999;
              font-size: 1rem;
            }
          }
        }
      }
    }
  }
</style>


