<template>
  <div class="problem">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../assets/my/back.png" alt=""  ></span>
      <span>常见问题</span>
    </div>
    <div class="ProblemMain">
      <ul>
        <li v-for="(item,index) in dataList" :key="item.index">
          <div class="ProblemWrapper">
            <div class="ProblemImg">
              <img src="../../assets/my/problem.png" :class="{change: index == show }" />
            </div>
            <p
              class="ProblemTitle"
              @click="showText(index)"
              :class="{titleChange: index == show }"
            >{{item.question}}</p>
            <transition>
              <p class="text" v-show="index == show">{{item.answer}}</p>
            </transition>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      show: -1,
      dataList: [
        // {
        //   title: "怎么报名参加活动?",
        //   text:
        //     "点击【我要报名】按钮，选择组别，填写报名信息，支付完成即报名成功。支付完成即报名成功。支付完成即报名成功。支付完成即报名成功。支付完成即报名成功。"
        // },
        // {
        //   title: "怎么参加拼团想知道吗我告诉你?",
        //   text:
        //     "点击【我要拼团】按钮，选择组别，填写报名信息，支付完成即报名成功。支付完成即报名成功，支付完成即报名成功。支付完成即报名成功。支付完成即报名成功。"
        // },
        // {
        //   title: "怎么参加拼团想知道吗告诉你?",
        //   text:
        //     "点击【我要拼团】按钮，选择组别，填写报名信息，支付完成即报名成功。支付完成即报名成功。支付完成即报名成功。支付完成即报名成功。支付完成即报名成功。"
        // },
        // {
        //   title: "怎么参加拼团想知道吗我告诉你?",
        //   text:
        //     "点击【我要拼团】按钮，选择组别，填写报名信息，支付完成即报名成功。支付完成即报名成功。支付完成即报名成功。支付完成即报名成功。支付完成即报名成功。"
        // },
        // {
        //   title: "怎么参加拼团想知道吗?",
        //   text:
        //     "点击【我要拼团】按钮，选择组别，填写报名信息，支付完成即报名成功。支付完成即报名成功。支付完成即报名成功。支付完成即报名成功。支付完成即报名成功。"
        // }
      ]
    };
  },
  methods: {
    goBack() {
      this.$router.goback();
    },
    showText(index) {
      this.dataList.forEach((child, i) => {
        if (i == index) {
          this.show = this.show == i ? -1 : i;
        }
      });
    },
    getData(){
      const url = this.apiList.problem.list
        this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({page: 1, size: 1000}, url, true)
        }).then(({data})=>{
          console.log(data);
          if(data.result){
            // console.log(data);
            this.dataList = data.data.records
          }
      })
    }
  },
  created(){
    this.getData()
  }
};
</script>

<style lang="scss">
.problem {
  height: 100%;
  background-color: #fafafa;
  .ProblemMain {
    margin-top: 1.4rem;
  }
  ul {
    li {
      display: block;
      background-color: #fff;
      .ProblemWrapper {
        margin: 1.6rem;
        .ProblemImg {
          display: inline-block;
          img {
            width: 2rem;
            height: 2rem;
            vertical-align: middle;
            margin-top: -0.5rem;
            transition: transform 0.3s ease;
            -webkit-transition: -webkit-transform 0.3s ease;
          }
        }
        .ProblemTitle {
          display: inline-block;
          height: 4.4rem;
          width: 85%;
          margin-left: 1.4rem;
          font-size: 1.6rem;
          line-height: 4.4rem;
        }
        .titleChange {
          border-bottom: 0.1rem solid #e8e8e8;
        }
        .text {
          width: 29rem;
          padding: 1.6rem;
          margin-left: 1.8rem;
          color: #999;
          font-size: 1.4rem;
          font-weight: 400;
          line-height: 1.8rem;
        }
        .v-enter,
        .v-leave-to {
          opacity: 0;
          transform: translateY(-4rem);
        }
        .v-enter-active,
        .v-leave-active {
          transition: all 0.5s ease;
        }
        .change {
          transform: rotate(180deg);
          -webkit-transform: rotate(180deg);
        }
      }
    }
  }
}
</style>



