<template>
<div class="messageItem">
  <div class="header">
    <span class="back" @click="goback"><img src="../../assets/my/back.png" alt=""  ></span>
    <span>系统消息</span>
  </div>
  <img class="nodata" v-if="nodata" src="../../assets/mess/nodata.png">
  <p class="nodataText"  v-if="nodata">暂时没有任何消息~</p>
  <ul class="messContainer">
    <li v-for="item in dataList" :key="item.id" class="messItem">
      <p class="time">{{ item.createdAt }}</p>
      <div class="main_desc">
        <h2>{{ item.title }}</h2>
        <P>{{ item.content }}</P>
      </div>
    </li>
  </ul>
</div>
</template>
<script>
export default {
  data() {
    return {
      nodata: true,
      dataList: [],
      page: 1,
      length: 10,
      messType: 0,
    }
  },
  methods: {
    goback() {
      this.$router.goback()
    },
    getData() {
      if (this.messType === 0) {
        this.nodata = false
        const url =  this.apiList.message.getSys
        this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({page: this.page, size: this.length}, url , true)
        }).then(({ data }) => {
          if (data.result) {
            this.dataList = data.data.records
            if (data.data.records.length === 0) {
              this.nodata = true
            }
          } else {
            this.$toast(data.msg)
          }
        })
      }
    }
  },
  mounted() {
    this.messType = 0
    this.getData() 
  }
}
</script>
<style lang="scss">
  .messageItem{
    position: relative;
    width: 100%;
    height: 100%;
    min-height: 30rem;
    .nodata{
      width: 10rem;
      height: 10rem;
      position: absolute;
      top: 8.4rem;
      left: 50%;
      transform: translateX(-50%);
    }
    .nodataText{
      font-size: 1.4rem;
      color: #666;
      width: 100%;
      position: absolute;
      top: 20.5rem;
      text-align: center;
    }
    .messItem{
      width: 34.3rem;
      margin: 2rem auto;
      text-align: center;
      .time{
        font-size: 1.2rem;
        color: #999;
        text-align: center;
        margin-bottom: 1.2rem;
      }
      .main_desc{
        width: 100%;
        background: #fff;
        border: solid 0.1rem #D7D7D7;
        border-radius: 0.3rem;
        box-sizing: border-box;
        padding: 1.2rem;
        h2{
          margin-top: -1.2rem;
          box-sizing: border-box;
          color: #333;
          font-size: 1.6rem;
          height: 4.5rem;
          text-align: left;
          line-height: 4.5rem;
          border-bottom: solid 0.1rem #d7d7d7;
          font-weight: 400;
        }
        p{
          color: #999;
          font-size: 1.3rem;
          text-align: left;
          margin-top: 1.2rem;
        }
      }
    }
  }
</style>


