<template>
  <div class="sales_list">
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh" >
      <van-list v-model="loading" :finished="finished" finished-text="——没有更多了——" @load="onLoad" ref="li" >
        <div class="list_info" v-for="(item , index) in list" :key="index">
          <p>
            <!-- <img src='../../../assets/my/userAvatar.png' alt="">  -->{{item.name}}
            <span v-if="item.state == 1" >未审批</span>
            <span v-if="item.state == 201" style="color : red">驳回</span>
            <span v-if="item.state == 101" >已同意</span>
          </p>
          <ul>
            <li>上级：{{item.fatherRealName ? item.fatherRealName : item.fatherNickName}}</li>
            <li>手机：{{item.phoneNum}}</li>
            <li>时间：{{item.createdAt}}</li>
            <li v-if="item.refuseRemark">备注：{{item.refuseRemark}}</li>
          </ul>
          <div class="btn_reject" v-if="item.state == 1" @click="$router.push('sales_reject/'+item.id)">驳回</div>
          <div class="btn_pass btn_reject" v-if="item.state == 1" @click="pass(item.id,index)">审批通过</div>
        </div>
      </van-list>
    </van-pull-refresh>
  </div>
</template>
<script>
export default {
  data(){
    return{
      isLoading: false,
      loading: false,
      finished: false,
      page: 0,
      list: [],
    }
  },
  props:['shows'],
  methods:{
    onRefresh(){
      this.list = []
      this.page = 0
      this.onLoad()
      setTimeout(()=>{
        this.$toast('刷新成功')
        this.isLoading = false
        this.finished = false
      },2000)
    },
    onLoad(){
      this.page++
      const url = this.apiList.admin.listReseller
      // 未审核
      if(this.shows == 2){
        var state = 1
      }
      this.$http({
        url: this.$http.adornUrl(url),
        method: "post",
        data: this.$http.adornData({ page: this.page, size: 10 , state}, url, true)
      }).then(({ data : res })=>{
        console.log(res)
        if (!res.result) {
          console.log(res.msg)
        }
        if (res.data.records.length >= 0) {
          // 已审核  筛选数据
          if( this.shows == 1){
            const data = res.data.records
            for (var i = 0; i < data.length; i++){
              if(data[i].state == 1){
                data.splice(i,1)
              }
            }
            this.list = this.list.concat(data)
          }else{
            // 全部
            this.list = this.list.concat(res.data.records)
          }
        }
        if (res.data.pages <= this.page) {
          this.finished = true
        }
        this.$nextTick(() => {
          this.loading = false
        })
      })
    },
    // 审核通过
    pass(approvalId,index){
      console.log(approvalId,index)
      const url = this.apiList.admin.dealReseller
      this.$http({
        url: this.$http.adornUrl(url),
        method: "post",
        data: this.$http.adornData({ approvalId, agree: true }, url, true)
      }).then(({ data : res })=>{
        if(res.result){
          this.$toast('审批通过成功')
          // 改变当前项的状态
          this.list[index].state = 101
        }else{
          this.$toast(res.msg)
        }
      })
    }
  },
  mounted(){
    this.$refs.li.$el.style.minHeight = document.documentElement.clientHeight -  12.2 * 10 + 'px'
    console.log(this.shows)
  },
}
</script>
<style lang="scss">
.sales_list{
  min-height: 100%;
  .list_info{
    width: 31.6rem;
    // height: 11.5rem;
    background-color: #fff;
    margin: 0 auto;
    border-radius: .8rem;
    box-shadow:0px 6px 7px 0px rgba(0, 0, 0, 0.03);
    padding-left: 1.45rem;
    padding-right: 1.45rem;
    overflow: hidden;
    margin-top: 1.5rem;
    // position: relative;
    padding-bottom: 1.5rem;
    >p{
      font-size: 1.2rem;
      color: #333;
      margin-top: 1.15rem;
      border-bottom: 1px dashed rgba(221,221,221,1);
      padding-bottom: 1rem;
      line-height: 2rem;
      >img{
        width: 2.15rem;
        vertical-align: middle;
        margin-right: .8rem;
      }
      span{
        float: right;
        color: #999;
        font-size: 1.1rem;
      }
    }
    ul{
      display: flex;
      // justify-content: space-around;
      flex-wrap: wrap;
      margin-top: 1rem;
      li{
        width: 50%;
        font-size: 1.1rem;
        color: #999999;
        margin-top: 1rem;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
      }
      li:nth-of-type(2),
      li:nth-of-type(4){
        text-align:right ;
      }

    }
    .btn_reject{
      // position: absolute;
      width:6.9rem;
      height:2.9rem;
      background:rgba(246,69,66,0);
      border:1px solid rgba(246, 69, 66, 1);
      border-radius:1.45rem;
      color:rgba(246,69,66,1);
      font-size: 1.1rem;
      line-height: 2.9rem;
      text-align: center;
      margin-top: 1rem;
      margin-left: 15rem;
      display: inline-block;
    }
    .btn_pass{
      margin-left: 1rem;
      background-color: rgba(246,69,66,1);
      color: #fff;
    }
  }
}
</style>




