<template>
  <div class="user-login">
    <div class="login_header">
      <img src="../../assets/login/banner.png" alt="">
    </div>
    <div class="main_nav">
      <ul>
        <li v-for="(item,index) in navlist" :key="index" :class="{'active': login_type == index }" @click="login_type=index">{{ item }}</li>
      </ul>
    </div>
    <!-- 账号密码登录 -->
    <div class="mainForm" v-if="login_type === 0">
      <p :class="{'achive': achive_index===3}">
        <span v-if="achive_index == 3" class="input_icon_achive"></span>
        <span v-else class="input_icon"></span>
        <input type="number" v-model="loginForm.userName" @focus="achive_index = 3" @blur="achive_index = 0" placeholder="请输入众享乐账号">
        <span v-show="loginForm.userName.length !== 0" class="lay_right" @click="loginForm.userName = ''"><img src="../../assets/login/close.png"></span>
      </p>
      <p :class="{'achive': achive_index===4}">
        <span v-if="achive_index == 4" class="input_pass_achive"></span>
        <span v-else class="input_pass"></span>
        <input type="password" ref="passView" v-model="loginForm.password" @focus="achive_index = 4" @blur="achive_index = 0" v-if="passShow" placeholder="请输入密码">
        <img class="passImg" @click="passViewChange" src="../../assets/login/02.png" v-if="passShow" alt="">
        <input type="text" ref="passView" v-model="loginForm.password" @focus="achive_index = 4" @blur="achive_index = 0" v-if="!passShow" placeholder="请输入密码">
        <img class="passImg" @click="passViewChange" src="../../assets/login/01.png" v-if="!passShow"  alt="">
        <span  class="lay_right" v-show="loginForm.password.length !== 0"  @click="loginForm.password = ''"><img class="close" src="../../assets/login/close.png"></span>
      </p>
      <span :class=" remPassword ? 'rem':'rember'" @click="remPassword=!remPassword">记住密码</span>
      <span class="forget" @click="forget">忘记密码</span>
    </div>
    <!-- 手机验证码登录 -->
    <div class="mainForm" v-if="login_type === 1">
      <p :class="{'achive': achive_index===1}">
        <span v-if="achive_index == 1" class="input_phone_achive"></span>
        <span v-else class="input_phone"></span>
        <input type="number" @focus="achive_index = 1" @blur="achive_index = 0" v-model="loginForm.phone" placeholder="输入手机号">
        <span v-show="loginForm.phone.length !== 0" class="lay_right" @click="loginForm.phone = ''"><img src="../../assets/login/close.png"></span>
      </p>
      <p :class="{'achive': achive_index===2}">
        <span v-if="achive_index == 2" class="input_vscode_achive"></span>
        <span v-else class="input_vscode"></span>
        <input type="text" v-model="loginForm.vscode" @focus="achive_index = 2" @blur="achive_index = 0" placeholder="动态密码">
        <span style="color: #00A1FC" class="lay_right" v-if="!isGetVsCode" @click="getVsCode">获取验证码</span>
        <span class="lay_right" style="color: #c0c4cc" v-else>获取验证码<i>({{timelimit}})s</i></span>
      </p>
    </div>
    <!-- 授权码登录 -->
    <div class="mainForm" v-if="login_type === 2">
      <p :class="{'achive': achive_index===5}">
        <span v-if="achive_index == 5" class="input_authorizationPass_achive"></span>
        <span v-else class="input_authorizationPass"></span>
        <input type="password" @focus="achive_index = 5" @blur="achive_index = 0" v-model="loginForm.authorization" placeholder="输入授权码">
        <span v-show="loginForm.authorization.length !== 0"  class="lay_right" @click="loginForm.authorization = ''"><img src="../../assets/login/close.png"></span>
      </p>
      <p :class="{'achive': achive_index===6}">
        <span v-if="achive_index == 6" class="input_phone_achive"></span>
        <span v-else class="input_phone"></span>
        <input type="number" v-model="loginForm.authorizationPass" @focus="achive_index = 6" @blur="achive_index = 0" placeholder="商户端账号">
      </p>
    </div>
    <div class="login_btn" @click="loginauthorizationPass" v-if="login_type === 2">登录</div>
    <div class="login_btn" @click="login" v-else>登录</div>
    <p class="loginmess">登陆即代表阅读并同意<span style="color: #4B97EC">《商户服务条款》</span></p>
    <div class="login_bottom" v-show="false">
      <p>第三方账号登录</p>
      <p class="other_login">
        <span><img src="../../assets/login/QQ.png"></span>
        <span><img src="../../assets/login/wechat.png"></span>
      </p>
    </div>
  </div>
</template>
<script>
import { Toast } from 'mint-ui'
import { Base64 } from 'js-base64'
import CryptoJS from 'crypto-js'
import RSA from '@/utils/rsa.js'
import { eventBus } from '../../utils/eventBus';
import { Indicator } from 'mint-ui'
export default {
  data() {
    return {
      login_type: 1,
      achive_index: 0,
      remPassword:false,
      isGetVsCode: false,
      passShow: true,
      flag: false,
      timelimit: 60,
      formdata: {}, // 环信登录成功
      loginForm: {
        phone: '',
        vscode: '',
        userName: '',
        password: '',
        authorization: '',
        authorizationPass: ''
      },
      navlist: ['账号登录', '手机登录', '授权登录']
    }
  },
  methods: {
    // 记住账号
    getmemory() {
      localStorage.setItem('loginUserName', this.loginForm.userName)
      localStorage.setItem('loginPhone', this.loginForm.phone)
      localStorage.setItem('loginUserAuthorization', this.loginForm.authorization)
    },
    // 聊天登录
    loginChart() {
      const url = this.apiList.login.loginChart
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData('', url, true)
      }).then(({ data }) => {
        // 权限不足 直接跳转
        if(!data.result){
          return this.$router.replace('/index')
        }
        const password = data.data
        const userId = '1-' + localStorage.getItem('moduleId')
        this.$http({
          url: this.$http.adornUrl(this.apiList.shop.get),
          method: 'post',
          data: this.$http.adornData('', this.apiList.shop.get, true)
        }).then(({ data }) => {
          const nickName = data.data.name
          const avatar = data.data.logoImgUrl
          console.log("-----------------",nickName,avatar,userId,password,nickName,avatar)
          this.formdata = {
            userId: userId,
            userPassword: password,
            nickName: nickName,
            avatar: avatar
          }
          this.android.chartLogin(this.formdata)
          this.$router.replace('/index')
        })
      })
    },
    //授权登录
    loginauthorizationPass() {
      if (!this.loginForm.authorization) {
        return this.$toast('请输入授权码')
      }
      if (!this.loginForm.authorizationPass) {
        return this.$toast('您输入的联系电话有误，请确认您输入的内容')
      }
      if(!(/^1[3456789]\d{9}$/.test(this.loginForm.authorizationPass))){ 
        // 不是手机号
        if (!/^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$/.test(this.phone)) {
        // 也不是座机号码
          return this.$toast('您输入的联系电话有误，请确认您输入的内容')
        }
      }
      this.getmemory()
      this.load()
      const vm = this
      const url = this.apiList.authorization.login
      this.$http({
        url: this.$http.adornUrl(this.apiList.login.publicKey),
        method: 'post',
        data: this.$http.adornData('', '', false)
      }).then(({ data }) => {
        if (!data.result) {
          this.$message('获取公钥失败')
          return
        }
        RSA.setMaxDigits(131)
        const key = new RSA.RSAKeyPair(data.data.exponent, '', data.data.modulus)
        const loginPassword = Base64.encode(RSA.encryptedString(key, this.loginForm.authorization))
        this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({ phoneNum: this.loginForm.authorizationPass, authCode: loginPassword}, url , true)
        }).then(({ data }) => {
          this.closeLoad()
          if (data.result) {
            this.$toast('授权成功')
            localStorage.setItem('moduleId', data.data.moduleId)
            setTimeout(() => {
              const authCode = this.loginForm.authorization
              this.$router.push({name: 'authUsePayKey', params: {activityData: data.data, authCode: authCode}})
            }, 1500)
          } else {
            this.$toast(data.msg)
          }
          // setTimeout(function(){
          //   vm.goback()
          // }, 600)
        })
      })
    },
    getVsCode() {
      if (!this.loginForm.phone || !(/^1[3456789]\d{9}$/.test(this.loginForm.phone))) {
        return Toast('请输入正确的手机号码')
      }
      const url = this.apiList.login.getVsCode
      Indicator.open({
        text: '发送中',
        spinnerType: 'fading-circle'
      })
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData({ phoneNum: this.loginForm.phone }, '' , false)
      }).then(({ data }) => {
        Indicator.close()
        if (data.result) {
          // 验证码已发送
          this.isGetVsCode = true
          this.$toast('验证码已发送，请注意查收')
          this.timeList()
        } else {
          this.$toast(data.msg)
        }
      })
    },
    changeType() {
      this.login_type === 0 ? this.login_type = 1 : this.login_type = 0
    },
    //手机验证码 账号密码登录
    login() {
      // 判断登录类型
      // 验证码登录
      this.getmemory()
      if( this.flag ) return
      this.flag = true
      if (this.login_type === 1) {
        if (!this.loginForm.phone || !(/^1[3456789]\d{9}$/.test(this.loginForm.phone))) {
          this.flag = false;
          return Toast('请输入正确的手机号码')
        }else if(!this.loginForm.vscode){
           this.flag = false;
          return Toast('请输入验证码')
        }
        this.load()
        this.$http({
          url: this.$http.adornUrl(this.apiList.login.loginBySmsCode),
          method: 'post',
          data: this.$http.adornData({ phoneNum: this.loginForm.phone, code: this.loginForm.vscode, userType: 2 }, '' , false)
        }).then(({ data }) => {
          this.closeLoad()
          if (data.result) {
            // 获得密码的16位长度
            const _token = data.data.token
            // 数据加密
            const WfAesUtils = {
              _KEY: '12345678900000001234567890000000', // 16位
              _IV: '1234567890000000', // 16位
              Decrypt: function(str) {
                const key = CryptoJS.enc.Utf8.parse(this._KEY)
                const iv = CryptoJS.enc.Utf8.parse(this._IV)
                const encryptedHexStr = CryptoJS.enc.Hex.parse(str)
                const srcs = CryptoJS.enc.Base64.stringify(encryptedHexStr)
                const decrypt = CryptoJS.AES.decrypt(srcs, key, { iv: iv, mode: CryptoJS.mode.CBC, padding: CryptoJS.pad.Pkcs7 })
                const decryptedStr = decrypt.toString(CryptoJS.enc.Utf8)
                return decryptedStr.toString()
              }
            }
            const _reToken = WfAesUtils.Decrypt(_token)
            console.log(_reToken)
            // 用户token
            localStorage.setItem('token', _reToken)
            // 用户ID
            localStorage.setItem('uid', data.data.userId)
            localStorage.setItem('moduleId', data.data.moduleId)
            this.getRole()
            // 判断如果不是b端用户不允许登录
            if (data.data.userType === 2 ) {
              this.loginChart()
              // this.$router.replace('/index')
            } else {
              this.flag = false
              Toast({
                message: '此平台只允许B端账号登录',
                duration: 5000
              })
            }
          } else {
            if(data.msg == '用户未注册'){
                // 登录获取用户token
                this.$http({
                  url: this.$http.adornUrl(this.apiList.login.loginBySmsCode),
                  method: 'post',
                  data: this.$http.adornData({ phoneNum: this.loginForm.phone, code: this.loginForm.vscode, userType: 1 ,ty: 'entrya'}, false)
                }).then(({data})=>{
                  if (!data.result) {
                    this.flag = false
                    return  Toast(data.msg)
                  }
                   // 获得密码的16位长度
                  const _token = data.data.token
                  // 数据加密
                  const WfAesUtils = {
                    _KEY: '12345678900000001234567890000000', // 16位
                    _IV: '1234567890000000', // 16位
                    Decrypt: function(str) {
                      const key = CryptoJS.enc.Utf8.parse(this._KEY)
                      const iv = CryptoJS.enc.Utf8.parse(this._IV)
                      const encryptedHexStr = CryptoJS.enc.Hex.parse(str)
                      const srcs = CryptoJS.enc.Base64.stringify(encryptedHexStr)
                      const decrypt = CryptoJS.AES.decrypt(srcs, key, { iv: iv, mode: CryptoJS.mode.CBC, padding: CryptoJS.pad.Pkcs7 })
                      const decryptedStr = decrypt.toString(CryptoJS.enc.Utf8)
                      return decryptedStr.toString()
                    }
                  }
                  const _reToken = WfAesUtils.Decrypt(_token)
                  // console.log(_reToken)
                  // 用户token
                  localStorage.setItem('token', _reToken)
                  // 用户ID
                  localStorage.setItem('uid', data.data.userId)
                  localStorage.setItem('moduleId', data.data.moduleId)
                  this.getRole() 
                  // 管理员
                  if (data.data.userType === 1) {
                      // 勾选记住密码
                    if(this.remPassword){
                      localStorage.setItem('password',this.loginForm.password)
                    }else{
                      localStorage.removeItem('password')
                    }
                    this.$router.push('manage_index')
                  }
                })
              }else{
                this.flag = false
                Toast(data.msg)
              }
          }
        }).catch(() => {
          console.log('未知错误')
        })
      } else if (this.login_type === 0) {
        // 账号密码登录
        if (!this.loginForm.userName || !(/^1[3456789]\d{9}$/.test(this.loginForm.userName))) {
          this.flag = false;
          return Toast('请输入正确的账号')
        }else if(!this.loginForm.password){
           this.flag = false;
          return Toast('请输入密码')
        }
        this.load()
        const vm = this
        this.$http({
          url: this.$http.adornUrl(this.apiList.login.publicKey),
          method: 'post',
          data: this.$http.adornData('', '', false)
        }).then(({ data }) => {
          if (!data.result) {
            this.$message('获取公钥失败')
            return
          }
          RSA.setMaxDigits(131)
          const key = new RSA.RSAKeyPair(data.data.exponent, '', data.data.modulus)
          const loginPassword = Base64.encode(RSA.encryptedString(key, this.loginForm.password))
          // 登录获取用户token
          this.$http({
            url: this.$http.adornUrl(this.apiList.login.loginByPwd),
            method: 'post',
            data: this.$http.adornData({ userType: 2,userName: this.loginForm.userName, pwd: loginPassword }, false)
          }).then(({ data }) => {
             this.closeLoad()
            if (data.result) {
              // if(data.data.userType === 1){
              //   return this.$toast('此平台只允许B端账号登录')
              // }
              // 获得密码的16位长度
              const _token = data.data.token
              // 数据加密
              const WfAesUtils = {
                _KEY: '12345678900000001234567890000000', // 16位
                _IV: '1234567890000000', // 16位
                Decrypt: function(str) {
                  const key = CryptoJS.enc.Utf8.parse(this._KEY)
                  const iv = CryptoJS.enc.Utf8.parse(this._IV)
                  const encryptedHexStr = CryptoJS.enc.Hex.parse(str)
                  const srcs = CryptoJS.enc.Base64.stringify(encryptedHexStr)
                  const decrypt = CryptoJS.AES.decrypt(srcs, key, { iv: iv, mode: CryptoJS.mode.CBC, padding: CryptoJS.pad.Pkcs7 })
                  const decryptedStr = decrypt.toString(CryptoJS.enc.Utf8)
                  return decryptedStr.toString()
                }
              }
              const _reToken = WfAesUtils.Decrypt(_token)
              // console.log(_reToken)
              // 用户token
              localStorage.setItem('token', _reToken)
              // 用户ID
              localStorage.setItem('uid', data.data.userId)
              localStorage.setItem('moduleId', data.data.moduleId)
              this.getRole() 
              // 判断如果不是b端用户不允许登录
              if (data.data.userType === 2) {
                  // 勾选记住密码
                if(this.remPassword){
                  localStorage.setItem('password',this.loginForm.password)
                }else{
                  localStorage.removeItem('password')
                }
                this.loginChart()
              } 
              else {
                this.flag = false
                Toast({
                  message: '此平台只允许B端账号登录',
                  duration: 5000
                })
              }
            } else {
              // 管理员登录
              if(data.msg == '账号或密码错误1'){
                // 登录获取用户token
                this.$http({
                  url: this.$http.adornUrl(this.apiList.login.loginByPwd),
                  method: 'post',
                  data: this.$http.adornData({ userType: 1,userName: this.loginForm.userName, pwd: loginPassword,ty: 'entrya' }, false)
                }).then(({data})=>{
                  if (!data.result) {
                    this.flag = false
                    return  Toast(data.msg)
                  }
                   // 获得密码的16位长度
                  const _token = data.data.token
                  // 数据加密
                  const WfAesUtils = {
                    _KEY: '12345678900000001234567890000000', // 16位
                    _IV: '1234567890000000', // 16位
                    Decrypt: function(str) {
                      const key = CryptoJS.enc.Utf8.parse(this._KEY)
                      const iv = CryptoJS.enc.Utf8.parse(this._IV)
                      const encryptedHexStr = CryptoJS.enc.Hex.parse(str)
                      const srcs = CryptoJS.enc.Base64.stringify(encryptedHexStr)
                      const decrypt = CryptoJS.AES.decrypt(srcs, key, { iv: iv, mode: CryptoJS.mode.CBC, padding: CryptoJS.pad.Pkcs7 })
                      const decryptedStr = decrypt.toString(CryptoJS.enc.Utf8)
                      return decryptedStr.toString()
                    }
                  }
                  const _reToken = WfAesUtils.Decrypt(_token)
                  // console.log(_reToken)
                  // 用户token
                  localStorage.setItem('token', _reToken)
                  // 用户ID
                  localStorage.setItem('uid', data.data.userId)
                  localStorage.setItem('moduleId', data.data.moduleId)
                  this.getRole() 
                  // 管理员
                  if (data.data.userType === 1) {
                      // 勾选记住密码
                    if(this.remPassword){
                      localStorage.setItem('password',this.loginForm.password)
                    }else{
                      localStorage.removeItem('password')
                    }
                    this.$router.push('manage_index')
                  }
                })
              }else{
                this.flag = false
                Toast(data.msg)
              }
            }
          })
        })
      }
    },
    passViewChange(){
      this.passShow = !this.passShow
    },
    load(){
      Indicator.open({
        text: '登录中...',
        spinnerType: 'fading-circle'
      })
    },
    closeLoad(){
      Indicator.close()
    },
    timeList() {
      const time = setInterval(() => {
        this.timelimit --
        console.log(this.timelimit)
        if (this.timelimit === 0) {
          clearInterval(time)
          this.timelimit = 60
          this.isGetVsCode = false
        }
      }, 1000)
    },
    // 忘记密码
    forget(){
      this.$router.push({name:'pass',query: { forget: 'forget' }})
    }
  },
  watch:{
    passWord:function(){
      console.log(this.loginForm.passWord);
    }
  },
  mounted() {
    // if (localStorage.getItem('token')) {
    //   this.$router.replace('/index')
    // }
    // 取消跳转到更之前的页面
    const vm = this
    window.APPJS.unifyClassMethod("HtmlUpdateClass", "stopGesture", "", "")
    if(localStorage.getItem('password')){
      this.remPassword = true
    }
    this.loginForm.password = localStorage.getItem('password') ? localStorage.getItem('password') : ''
    this.loginForm.userName = localStorage.getItem('loginUserName') ? localStorage.getItem('loginUserName') : ''
    this.loginForm.phone = localStorage.getItem('loginPhone') ? localStorage.getItem('loginPhone') : ''
    this.loginForm.authorization = localStorage.getItem('loginUserAuthorization') ? localStorage.getItem('loginUserAuthorization') : ''
    eventBus.$on('android_loginSuccess', (res) => {

      // vm.$router.replace('/index')
      // if (res.success === 0) {
      //   eventBus.$off('android_loginSuccess')
        
      // }
      // if (res.success === -1) {
      //   vm.$toast(res.success)
      //   setTimeout(() => {
      //     vm.android.chartLogin(vm.formdata)
      //   }, 6000)
      // }
    })
  },
  created() {
    this.android.navColorChange('#ffffff')
    for(var key in localStorage){
      if(key != "loginUserName" && key != "password"){
        localStorage.removeItem(key)
      }
  }
  }
}
</script>
<style lang="scss">
.user-login{
  .login_header{
    text-align: center;
    margin-top: 2.2rem;
    img{
      width: 21.3rem;
      height: 15.1rem;
    }
  }
  .main_nav{
    ul{
      display: flex;
      justify-content: space-between;
      width: 100%;
      box-sizing: border-box;
      padding: 0 6.6rem;
      margin-top: 2.5rem;
      li{
        height: 1.2rem;
        color: #999;
        font-size: 1.2rem;
      }
      .active{
        color: #171717;
        position: relative;
        &::after{
          display: inline-block;
          width: 2.3rem;
          height: .3rem;
          background: #E53935;
          position: absolute;
          left: 50%;
          top: 1.8rem;
          transform: translateX(-50%);
          border-radius: .3rem;
          content: ' ',
        }
      }
    }
  }
  .lay_right{
    float: right;
    text-align: right;
    i{
      font-style: normal;
    }
  }
  .input_icon{
    width: 1.5rem; 
    margin-top: -.2rem;
    height: 1.5rem; 
    background: url('../../assets/login/user.png'); 
    background-size: 100% 100%;
    vertical-align: middle;
  }
  .input_icon_achive{
    width: 1.5rem; 
    margin-top: -.2rem;
    height: 1.5rem; 
    background: url('../../assets/login/user_achive.png'); 
    background-size: 100% 100%;
    vertical-align: middle;
  }
  .input_pass{
    width: 1.5rem; 
    margin-top: -.2rem;
    height: 1.5rem; 
    background: url('../../assets/login/pass.png'); 
    background-size: 100% 100%;
    vertical-align: middle;
  }
  .input_pass_achive{
