  width: 100%;
  box-sizing: border-box;
  .header{
    width: 100%;
    height: 4.4rem;
    background: #333;
    line-height: 4.4rem;
    text-align: center;
    box-sizing: border-box;
    padding: 0 1.6rem;
    .back{
      float: left; 
