    .achive{
      // border-bottom: solid 0.1rem #00A1FC;
    }
    .achive{
      border: solid .1rem #333;
    }
    p{
      width: 100%;
      height: 4.4rem;
      line-height: 2.8rem;
      border-radius: 4.4rem;
      line-height: 4.4rem;
      border: solid 0.1rem #D7D7D7;
      margin-top: 2.2rem;
      transition: border-color .4s cubic-bezier(.645,.045,.355,1);
      color: #333;
      font-size: 1.4rem;
      box-sizing: border-box;
      padding: 0 2.2rem 0.7rem;
      input{
        outline: 0;
        border: none;
        box-sizing: border-box;
        color: #333;
        display: inline-block;
        font-size: 1.4rem;
        width: 14rem;
        height: 80%;
        line-height: 80%;
      }
      ::-webkit-input-placeholder { /* WebKit, Blink, Edge */
        color: #d7d7d7;
        line-height: 150%;
      }
      :-moz-placeholder { /* Mozilla Firefox 4 to 18 */
