    box-sizing: border-box;
    padding: 0 3.9rem;
    text-align: right;
    color: #4B97EC;
    font-size: 1.2rem;
    margin-top: 5.5rem;
    height: 1.2rem;
  }
  .login_bottom{
    margin-top: 12rem;
    text-align: center;
    p{
      font-size: 1.2rem;
      color: #999;
      span{
        img{
          width: 4rem;
          height: 4rem;
        }
      }
    }
    .other_login{
      width: 18.3rem;
      display: flex;
      justify-content: space-between;
      margin: 2.3rem auto 0;
    }
  }
}
