<template>
    <div class="differentList">
      <div class="different_header">
        <img src="../assets/different/headerBa.png"  alt="">
        <img src="../assets/different/banner.png" alt="">
      </div>
      <dir class="nav">
        <ul>
          <li v-for="(item, index) in navList" :key="index">
            <img :src="item.imgUrl" alt=""><p>{{item.text}}</p>
          </li>
        </ul>
      </dir>
      <div class="main">
        <div class="title">
          <p>异业推荐</p>
        </div>
        <ul>
          <li v-for="(item, index) in recommendList" :key="index">
            <img :src="item.imgUrl" alt="">
            <p>{{item.title}}</p>
            <p class="bottom">
              <span>
                <span>分类：</span><span>{{item.classification}}</span>
              </span>
              <span class="view">
                <img src="../assets/different/view.png" alt=""><span class="viewNum">{{item.viewNum}}</span>
              </span>
            </p>
          </li>
        </ul>
      </div>
      <div class="content">
        <ul>
          <li v-for="(item, index) in dataList" :key="index">
            <div class="wrapper">
              <div class="header_title">
                <img src="../assets/group/block.png" alt=""><span>{{ item.name}}</span>
              </div>
              <div class="info_wrapper">
                <div class="img">
                  <img :src="item.imgUrl" alt="">
                </div>
                <div class="activity_info">
                  <p class="info">
                    <span>
                      <span>分类：</span><span>儿童</span>
                    </span>
                    <span>
                      <span>类型：</span><span>报名</span>
                    </span>
                    
                  </p>
                  <p class="info">
                    <span>
                      <span>需求：</span><span>6家</span>
                    </span>
                    <span>
                      <span>方式：</span><span>现金</span>
                    </span>
                  </p>
                  <p class="info_1">
                    <span>合作要求：</span><span>有卫生许可证资质，有卫生证 持证上岗，环境优美，风景宜人。</span>
                  </p>
                </div>
              </div>
              
            </div>
            
          </li>
        </ul>
      
      </div>
    </div>
</template>

<script>
export default {
  
  data(){
    return{
      dataList:[
        {imgUrl: require('../assets/different/02.png'), name: '宝龙城市广场'},
        {imgUrl: require('../assets/different/02.png'), name: '宝龙城市广场'},
        {imgUrl: require('../assets/different/02.png'), name: '宝龙城市广场'},
        {imgUrl: require('../assets/different/02.png'), name: '宝龙城市广场'}
      ],
      navList: [
        {imgUrl: require('../assets/different/monery.png'), text:'现金'},
        {imgUrl: require('../assets/different/resources.png'), text:'资源'},
        {imgUrl: require('../assets/different/field.png'), text:'地点'},
        {imgUrl: require('../assets/different/investment.png'), text:'招商'}
      ],
      recommendList:[
        {imgUrl: require('../assets/different/01.png'), title: '开业异业合作大优惠很', classification: '儿童', viewNum: 163},
        {imgUrl: require('../assets/different/02.png'), title: '开业异业合作大优惠很', classification: '儿童', viewNum: 163},
        {imgUrl: require('../assets/different/01.png'), title: '开业异业合作大优惠很', classification: '儿童', viewNum: 163},
        {imgUrl: require('../assets/different/02.png'), title: '开业异业合作大优惠很', classification: '儿童', viewNum: 163}

      ]
    }
  },
  methods:{
    goback() {
      this.$router.goback()
    },
    goAdd(){
      this.$router.push('different_index')
    },
    getData(){
      const url = this.apiList.differentList.list
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData({ page: 1, size: 10}, url, true)
      }).then(({data})=>{
        console.log(data);
        if(data.result){
          this.dataList = data.data.records
        }
      })
    }
  },
  created(){
    this.android.navColorChange('#5490f1')
  }
}
</script>

<style lang="scss">
  .differentList{
    
    height: 100%;
    background-color: #f5f5f5;
    
    .different_header{
      position: relative;
      img:first-child{
        width: 100%;
        height:13.75;
      }
      img:last-child{
        position: absolute;
        bottom: -2rem;
        left: 1.5rem;
        display: block;
        width: 34.5rem;
        height:11.9rem;
        margin: 0 auto;
      }
    }
    .nav{
      width: 100%;
      background: #fff;
      ul{
        width: 30.5rem;
        height: 8rem;;
        margin: 0 auto;
        padding-top: 2rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        
        
        li{
          text-align: center;
          img{
            display: block;
            width: 2.9rem;
          }
          p{
            margin-top: 0.4rem;
            color: #333;
            font-size: 1rem;
          }
        }
        
      }
    }
    .main{
      margin-top: 1.2rem;
      padding: 0 1.6rem;
      background: #fff;
      .title{
        padding: 1.2rem 0 0.5rem;
        p{
          color: #333;
          font-size: 1.4rem;
          font-weight: bold;
        }
      }
      ul{
        display: flex; 
        flex-wrap: wrap;
        justify-content: space-between;
        padding-bottom: 2rem;
        li{
          margin-top: 1rem;
          border-radius: 0.6rem 0.6rem 0rem 0rem;
          box-shadow: 0rem 0.3rem 0.35rem 0rem rgba(0, 0, 0, 0.03);
          .bottom{
            display: flex;
            justify-content: space-between;
            margin: 0 0.4rem;
            padding-bottom: 1rem; 
           
            .view{
              
              img{
                width: 1.1rem;
                height: 0.7rem;
                vertical-align: middle;
              }
              .viewNum{
                display: inline-block;
                margin-left: 0.5rem;
                vertical-align: middle; 
              }
            }
            
          }
          img{
            width: 16.85rem;
            height: 9.9rem;
          }
          p{
            color: #333;
            font-size: 1.4rem;
            font-family: "Microsoft YaHei",微软雅黑;
            margin: 0 0.4rem;
          }
          span{
            color: #999;
            font-size: 1rem;
          }
        }
      }
    }
    .content{
      background: #fff;
      margin-top: 1.2rem;
      margin-bottom: 5.8rem;
      ul{
        padding-top: 1.2rem;
        li{
          margin-bottom: 1rem;
          background-color: #F7F9FA;
          padding-bottom: 1.6rem;
          border-radius: 0.8rem;
          width: 34.3rem;
          margin: 1.2rem auto 0;
          .wrapper{
            margin: 0 1.2rem;
            .header_title{
              height: 4.2rem;
              line-height: 4.2rem;
              span{
                margin-left: 0.8rem;
              }
              img{
                width: 1.2rem;
                height: 1.2rem;
              }
              span{
                color: #333;
                font-size: 1.3rem;
                
              }
            }
            .info_wrapper{
              display: flex;
              .img{
                img{
                  display: inline-block;
                  width: 10.45rem;
                  height: 6.1rem;
                }
              }
              .activity_info{
                margin-left: 1rem;
                flex: 1;
                span{
                  color: #666;
                  font-size: 1.2rem;
                }
                .info{
                  display: flex;
                  justify-content: space-between;
                  margin-bottom: 0.5rem;
                  
                }
                .info_1{
                  span{
                    display: inline-block;
                  }
                  span:last-child{
                    width: 14.4rem;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    vertical-align: middle;
                    margin-top: -0.4rem;
                  }
                  
                }
              }
            }
            
          }
        }
      }
      
    }
  }

</style>



