<template>
  <div class="postBar">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../assets/my/back.png" alt=""  ></span>
      <span class="headText">贴吧列表</span>
      <span class="right">
        <img class="searchBtn"  src="../../assets/community/set.png" @click="showDelImg" v-if="commentList.length > 0">
        <img class="add" src="../../assets/community/edit.png" @click="goReleasePost">
      </span>
    </div>
    <div class="main">
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
        <van-list 
          v-model="loading" 
          :finished="finished" 
          finished-text="——没有更多了——"
          @load="onLoad"
          ref="listHeight"
        >
          <van-cell class="content" v-for="(item, index) in commentList" :key="item.id" @click="goPostBarDetails(moduleId, item.id)">
            <div class="left" v-if="showDel " @click.stop="opthin(index, item.id)"><img :src="num[index] === true ? opthinImgUrl : imgUrl" alt="" ></div>
            <div class="right" >
              <div :class="{portrait: true, clearMargin: showDel}">
                <img :src="item.avatar ? item.avatar : defaultImg"/>
                <span>{{ item.name ? item.name : '众享乐用户' }}</span>
              </div>
              <div :class="{text: true, clearMargin: showDel }">
                {{ item.content }}
              </div>
              <div :class="{contentImgWrapper: !showDel} ">
                <img class="contentImg" :src="items" v-for="(items, index) in item.imgUrls" :key="index">
              </div>
              
              <div :class="{footer: true, clearMargin: showDel}">
                <span>{{ item.createdAt.substr(2,8) }}</span>
                <div class="footRight">
                  <img src="../../assets/community/information.png" class="information" />
                  <span>{{ item.smallNum }}</span>
                </div>
              </div>
            </div>
          </van-cell >
        </van-list>
      </van-pull-refresh>
    </div>
    <div class="btn" v-if="showDel && commentList.length > 0">
      <span class="opthin" @click="allOpthin">
        <img :src="opthinAll === true ? opthinImgUrl : imgUrl" alt="">
        <span>全选</span>
      </span>
      <span class="button">
        <mt-button v-if="this.showMod" @click="goMod">修改</mt-button>
        <mt-button class="upper" @click="upper" v-if="arr.length < 2">{{upeerText}}</mt-button>
        <mt-button class="delBtn" @click="delPost">删除</mt-button>
      </span>
    </div>
  </div>
</template>

<script>
import { MessageBox } from 'mint-ui'
import { Indicator } from 'mint-ui'
export default {
  data() {
    return {
      id: '',
      imgUrl: require('../../assets/different/circular.png'),
      opthinImgUrl: require('../../assets/different/correct.png'),
      defaultImg:  require("../../assets/my/userAvatar.png"),
      index: '',
      moduleId: '',
      page: 0,
      size: 10,
      upeerText: '置顶',
      commentList: [],
      arr: [],
      num:[],
      commentId: [],
      opthinAll: false,
      upeerTextShow: false,
      showDel: false,
      loading: false,
      finished:false,
      isLoading: false,
      showMod: false,
      down: false
      
    };
  },
  created() {
    // this.getCommentList();
  },
  activated(){
    
  },
  methods: {
    goBack() {
      localStorage.removeItem('getDataList')
      localStorage.removeItem('scrollTop')
      localStorage.removeItem('maxPage')
      localStorage.removeItem('pageNum')
      this.$router.goback();
    },
    goReleasePost(){
      this.setScrollTop()
      this.clearScrollTop()
      this.$router.push('releasePost')
    },
    // goIssue(id) {
    //   this.$router.push({ name: 'issue', query: { moduleId: id } });
    // },
    goPostBarDetails(moduleId, id) {
      if(this.showDel) return
      this.setScrollTop()
      this.clearScrollTop()
      this.$router.push({ name: 'postBarDetails', query: { moduleId: moduleId, id: id } })
    },
    setScrollTop(){
      var scrollTop;
      scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      localStorage.setItem('scrollTop', scrollTop)
    },
    clearScrollTop(){
      document.documentElement.scrollTop = 0;
      document.body.scrollTop = 0;
    },
    goMod(){
      this.setScrollTop()
      this.clearScrollTop()
      var index = 0;
      this.num.forEach((item,i)=>{
        if(item === true){
          index = i
        }
      })
      this.$router.push({ name: 'releasePost', query: { id: this.id, index: index } });
    },
    onLoad(){
      return new Promise((resolve, reject)=>{
        this.page++;
        const url = this.apiList.community.list
        this.$http({
        url: this.$http.adornUrl(url),
        method: "post",
        data: this.$http.adornData(
          {
            page: this.page,
            size: 10,
          }, url, true)
        }).then(({ data }) => {
          if (data.data.records.length >= 0) {
            let commentList = []
            data.data.records.forEach(item=>{
              if(item.imgUrls){
                item['imgUrls'] = JSON.parse(item.imgUrls)
              }
             commentList.push(item)
            })
            this.commentList = this.commentList.concat(commentList)
            commentList = []
            localStorage.setItem('pageNum', this.page)
            localStorage.setItem('maxPage', data.data.pages)
            localStorage.setItem('getDataList', JSON.stringify(this.commentList))
            this.$nextTick(()=>{
              if ('scrollRestoration' in history) {
                history.scrollRestoration = 'manual'; //改为manual之后，就不会记录滚动位置
              }
            })
          }
          if (data.data.pages <= this.page) {
            this.finished = true
          }
          this.$nextTick(()=>{
            resolve(true)
            this.loading = false
          })
        });
      })
    },
    onRefresh(){
       return new Promise((resolve, reject)=>{
        this.page = 0;
        this.page++;
        const url = this.apiList.community.list
        this.$http({
        url: this.$http.adornUrl(url),
        method: "post",
        data: this.$http.adornData(
          {
            page: this.page,
            size: 10,
          }, url, true)
        }).then(({ data }) => {
         if (data.data.records.length >= 0) {
            let commentList = []
            data.data.records.forEach(item=>{
              if(item.imgUrls){
                item['imgUrls'] = JSON.parse(item.imgUrls)
              }
             commentList.push(item)
            })
            this.commentList = commentList
            commentList = []
          }
          this.$nextTick(()=>{
            resolve(true)
            this.$toast('刷新成功')
            this.isLoading = false;
            this.finished = false;
            this.page = 1;
          })
          
          
        });
      })
    },
    getCommentList() { 
        const url = this.apiList.community.list
        this.$http({
        url: this.$http.adornUrl(url),
        method: "post",
        data: this.$http.adornData(
          {
            page: 1,
            size: 10,
          }, url, true)
        }).then(({ data }) => {
          if (data.data.records.length >= 0) {
            let commentList = []
            data.data.records.forEach(item=>{
              if(item.imgUrls){
                item['imgUrls'] = JSON.parse(item.imgUrls)
              }
             commentList.push(item)
            })
            this.commentList = commentList
            commentList = []
          }
        });
    },
    //单选
    opthin(index, id){
      if(this.num[index] === true){
        this.num[index] = false
          this.commentId.splice(index, 1, '')
        if(this.arr.length > 1){
          this.commentId.forEach((item, i)=>{
            this.commentList.forEach((items, j)=>{
              if(items.id === item){
                if(items.upTime > 0){
                  this.upeerText = '取消置顶'
                  this.down = true
                }else{
                  this.upeerText = '置顶'
                  this.down = false
                }
              }
            })
          })
        }else{
          this.upeerText = '置顶'
          this.down = false
        }
      }else{
        this.num[index] = true
        this.commentId[index] = id
        if(this.commentList[index].upTime > 0 ){
          this.upeerText = '取消置顶'
          this.down = true
        }
      }
      this.arr = []
      this.num.forEach((item, index)=>{
        if(item === true){
          this.arr.push(item)
        }
      })
      var i;
      if(this.arr.length === 1){
        this.commentId.forEach((item, index)=>{
          if(item !== ''){
            i = index
            this.id = item
          }
        })
        if(this.commentList[i].fromC === false){
          this.showMod = true;
        }
      }else{
        this.showMod = false;
      }
      this.$forceUpdate()
    },
    // 全选
    allOpthin(){
      this.opthinAll = !this.opthinAll
      if(this.opthinAll){
        this.num = []
        this.commentList.forEach(()=>{
          this.num.push(true)
        })
        this.commentId = []
        this.commentList.forEach( item=>{
          this.commentId.push(item.id)
        })
        
      }else{
        this.num = []
        this.commentId = []
        this.upeerText = '置顶'
        this.down = false
      }
      this.arr = []
      this.num.forEach((item, index)=>{
        if(item === true){
          this.arr.push(item)
        }
      })
    },
    showDelImg(){
      this.showDel = !this.showDel
    },
    upper(){ //置顶
      Indicator.open({
        text: '正在提交...',
        spinnerType: 'fading-circle'
      })
      const url = this.apiList.community.upper
      this.commentId.forEach((item, index)=>{
        if(item !== ''){
          this.$http({
            url: this.$http.adornUrl(url),
            method: "post",
            data: this.$http.adornData(
              {
                commentId: item,
                down: this.down,
              }, url, true)
          }).then(({ data }) => {
            Indicator.close()
            if(data.result){
              this.$toast({
                message: '操作成功',
                iconClass: 'fa fa-check fa-5x'
              })
              this.getCommentList()
              this.upeerText = '置顶'
              this.down = false
              this.num = []
              this.commentId = []
              this.opthinAll = false
              this.showDel = false
              this.clearScrollTop()
            }
          });
        }
      })
    },
    delPost(){
      MessageBox.confirm('确定删除帖子？').then(()=>{
        Indicator.open({
          text: '正在删除...',
          spinnerType: 'fading-circle'
        })
        const url = this.apiList.community.del
        this.commentId.forEach((item, index)=>{
          if(item !== ''){
            this.$http({
            url: this.$http.adornUrl(url),
            method: "post",
            data: this.$http.adornData({ commentId: item }, url, true)
          }).then(({ data }) => {
            Indicator.close()
              if(data.result){
                this.$toast({
                  message: '删除成功',
                  iconClass: 'fa fa-check fa-5x'
                })
                this.commentList.forEach((items,i)=>{
                  if( items['id'] === item ){
                    this.commentList.splice(i, 1)
                  }
                })
                if(this.$refs.listHeight.$el.offsetHeight < document.documentElement.offsetHeight){
                  if(this.page <= Number(localStorage.getItem('maxPage'))){
                    this.finished = false
                  }
                  
                } 
                this.num = []
                this.commentId = []
                this.opthinAll = false
                this.showDel = false
                localStorage.setItem('getDataList', JSON.stringify(this.commentList))
              }
            });
          }
          
        })
        
      })
      
    },   
  },
  
  mounted() {
    var scrollTop = localStorage.getItem('scrollTop')
    if(scrollTop){
      document.documentElement.scrollTop = scrollTop
      document.body.scrollTop = scrollTop
    }
    // this.isIphoneX = localStorage.getItem('isIphoneX')
    // if(this.isIphoneX){
    //   document.getElementsByClassName('postBar')[0].style.minHeight = this.common.getWindowHeight()+34+ 'px'
    //   document.getElementsByClassName('messageList')[0].style.height = this.getWindowHeight()+34+ 'px'
    // }else{
    //   document.getElementsByClassName('postBar')[0].style.minHeight = this.common.getWindowHeight()+ 'px'
    // }

  },
  created(){
    const dataList = localStorage.getItem('getDataList');
    if(dataList){
      this.commentList = JSON.parse(dataList)
      this.page = Number(JSON.parse(localStorage.getItem('pageNum')))
    }
  }
};
</script>

<style lang="scss" >
.mint-toast.is-placemiddle{
  padding: 2rem;
}
.fa-5x{
  font-size: 0;
}
.fa-check:before{
  font-size: 3.2rem;
}
.postBar {
  min-height: 100%;
  background-color: #f5f5f5;

  .header {
    .headText{
      margin-left: -1rem;
    }
    .right {
      float: right;
      position: absolute;
      right: 1.6rem;
      img{
        width: 2rem;
        height: 2rem;
      }
      img:first-child{
        margin-right: 0.8rem;
      }
    }
  }
  .main {
    margin-top: 0.6rem;
    padding: 0 0.9rem 4.8rem;
    .content {
      
      position: relative;
      width: 35.65rem;
      background-color: #fff;
      padding: 0;
      padding-right: 3.75rem;
      border-radius: 0.5rem;
      overflow: hidden;
      margin-bottom: 1.9rem;
      box-sizing: border-box;
      .van-cell__value{
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
      .left{
        margin: 0 1.2rem;
        img{
          width: 2.7rem;
          height: 2.7rem;
        }
      }
      .right{
        // display: inline-block;
        display: flex;
        flex-direction: column;
        
      }
      .portrait {
        margin-left: 1.6rem;
        display: flex;
        align-items: center;
        padding: 1rem 0;
        .clearMargin{
          margin-left: 0;
        }
        img {
          width: 3.8rem;
          height: 3.8rem;
          border-radius: 50%;
        }
        span{
          margin-left: 1rem;
          color: #000;
          font-size: 2rem;
          font-family:SourceHanSansCN;
        }
      }
      .text {
        width: 28.25rem;
        margin-left: 3.65rem;
        font-size: 1.35rem;
        font-weight: 400;
        color: #8a8484;
        // line-height: 1.75rem;
        letter-spacing: 0.1rem;
      }
      .contentImgWrapper{
        margin-left: 3.65rem;
        
      }
      .contentImg{
        width: 8.5rem;
        height: 8.5rem;
        margin-left: 1rem;
        vertical-align: top;
        &:first-child{
          margin: 0;
        }
      }
      .footer {
        display: flex;
        justify-content: space-between;
        margin: 1.25rem 0.8rem 1.5rem 3.65rem;
        font-size: 1.35rem;
        font-weight: 400;
        color: #8a8484;
        line-height: 1rem;

        .information {
          width: 1.45rem;
          height: 1.45rem;
          margin-top: -0.2rem;
          vertical-align: middle;
        }
        .footRight {
          float: right;
          font-size: 1.35rem;
          font-weight: 400;
        }
      }
      .clearMargin{
        margin-left: 0;
      }
    }
  }
  .btn{
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 4.9rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #fff;
    .opthin{
      display: flex;
      align-items: center;
      margin-left: 1.6rem;
      img{
        width: 2.7rem;
        height: 2.7rem;
      }
      span{
        margin-left: 0.5rem;
        font-size: 1.5rem;
        color: #666;
        font-family:Microsoft YaHei;
      }
    }
    .button{
      height: 100%;
      font-size: 0;
      button{
        border: 0;
        box-shadow: none;
        height: 100%;
        width: 9rem;
        border-radius: 0;
        color: #fff;
        font-size: 1.5rem;
        background: #2A2A2A;

      }
      .upper{
        background: #FA5F5C;
      }
      .delBtn{
        background: #F64542;
      }
    } 
  }
}
</style>



