<template>
<div class="update">
  <!-- 通过input获取文件 ，为了美观使用v-show="false" 将input隐藏 通过input的change事件绑定fileChange-->
  <!-- <input type="file" v-show="false" accept="image" ref="input" @change="fileChange($event.target.files)" v-if="isChoose">
  <div @click="$refs.input.click()">
    <slot></slot>
  </div> -->
  <!-- 调用基座方法 -->
  <div @click="getImg">
    <slot></slot>
  </div>
  <!-- {
          path: '/addActivity',
          name: 'addActivity',
          component: addActivity
        },
        {
          path: '/activityAdd',
          name: 'activityAdd',
          component: activityAdd
        },
        {
          path: '/photos',
          name: 'photos',
          component: photos
        },
        {
          path: '/activityText',
          name: 'activityText',
          component: activityText
        },
        {
          path: '/activity_mod',
          name: 'activity_mod',
          component: activity_mod
        },
        {
          path: '/activity_map',
          name: 'activity_map',
          component: activity_map
        }, -->
  <mt-popup
    v-model="popupVisible"
    position="right">
    <div class="cut">
      <vue-cropper ref="cropper" 
      :img="option.img" 
      :output-size="option.size" 
      :output-type="option.outputType" 
      :info="true" :full="option.full"
      :can-move="option.canMove" 
      :can-move-box="option.canMoveBox" 
      :fixed-box="option.fixedBox" 
      :original="option.original"
      :auto-crop="option.autoCrop" 
      :auto-crop-width="width" 
      :auto-crop-height="height" 
      :center-box="option.centerBox"
			@real-time="realTime" 
      :high="option.high"></vue-cropper>
      <div class="show-preview" :style="{'width': previews.w + 'px', 'height': previews.h + 'px',  'overflow': 'hidden', 'margin': '5px auto'}">
        <div :style="previews.div">
          <img :src="previews.url" :style="previews.img">
        </div>
      </div>
      <div class="btnContainer">
        <mt-button type="danger" size="large"  @click="finish('base64')">确认</mt-button>
      </div>
    </div>
  </mt-popup>
</div>
</template>
<script>
import { Popup } from 'mint-ui'
import sha256 from 'sha256'
import { Indicator } from 'mint-ui'
import { eventBus } from '../../utils/eventBus.js'
export default {
  props: {
    width: {},
    height: {},
    isChoose: true,
    flag: false
  },
  data() {
    return {
      model: false,
      modelSrc: '',
      crap: false,
      previews: {},
      filesName: '',
      option: {
        img: '',
        size: 4,
        full: false,
        outputType: 'png',
        canMove: true,
        fixedBox: true,
        original: false,
        canMoveBox: true,
        autoCrop: true,
        // 只有自动截图开启 宽度高度才生效
        // autoCropWidth: 200,
        // autoCropHeight: 150,
        centerBox: false,
        high: true
      },
      style: '',
      previews: {},
      popupVisible: false
    }
  },
  methods: {
    getImg() {
      // 注册获得图片成功的方法,每次调用都刷新，防止重复相应
      const vm = this
      window.getImg_upload = function(res) {
        let reader = new FileReader()
        reader.readAsDataURL(vm.dataURLtoFile('data:image/png;base64,' + res.image))
        reader.onloadend = function () {
          vm.option.img = this.result
          vm.popupVisible = true
          // 隐藏基座loading
          vm.android.progressHUDend()
        }
      }
      
      this.android.getImg('getImg_upload')
      
    },
    dataURLtoFile(dataurl, filename = '0000img') {//将base64转换为文件
        var arr = dataurl.split(','), mime = arr[0].match(/:(.*?);/)[1],
            bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
        while(n--){
            u8arr[n] = bstr.charCodeAt(n);
        }
        return new File([u8arr], filename, {type:mime});
    },
    upload() {
      const formData = new FormData()
      console.log(this.modelSrc)
      const file = this.dataURLtoFile(this.modelSrc, this.filesName)
      formData.append('file', file)
      formData.append('userId', localStorage.getItem('uid'))
      formData.append('sign', sha256(this.apiList.upload + localStorage.getItem('token')))
      // 显示loading
      Indicator.open({
        text: '上传中....',
        spinnerType: 'fading-circle'
      })
      this.$http({
        url: this.$http.adornUrl(this.apiList.upload),
        method: 'post',
        headers: {
          'Content-Type': 'multipart/form-data'
        },
        data: formData
      }).then(({ data }) => {
        Indicator.close()
        this.popupVisible = false
        if (data.result) {
          this.$emit('fileChange', data.data)

        } else {
          this.MessageBox('提示', data.msg)
        }
      }).catch(() => {
      })
    },
    realTime(data) {
      this.previews = data
      console.log(data)
    },
    finish(type) {
      // if (type === 'blob') {
      //   console.log(this.$refs.cropper)
      //   this.$refs.cropper.getCropBlob((data) => {
      //     console.log(data)
      //     var img = window.URL.createObjectURL(data)
      //     this.model = true
      //     this.modelSrc = img
      //   })
      // } else {
      //   this.$refs.cropper.getCropData((data) => {
      //     this.model = true
      //     this.modelSrc = data
      //   })
      // }
      // console.log(this.modelSrc)
      // debugger
      if(this.flag) return 
      this.$refs.cropper.getCropData((data) => {
        this.model = true
        this.modelSrc = data
        console.log(this.modelSrc)
        this.upload()
        this.flag = true
      })
    }
  },
  created() {
    // const vm = this
    // eventBus.$on('getImgSuccess', (res) => {
    //   let reader = new FileReader()
    //   reader.readAsDataURL(vm.dataURLtoFile('data:image/png;base64,' + res.image))
    //   reader.onloadend = function () {
    //     vm.option.img = this.result
    //     vm.popupVisible = true
    //     // 隐藏基座loading
    //     vm.android.progressHUDend()
    //   }
    //   // reader.onload =(e) => { 
    //   //   let data
    //   //   if (typeof e.target.result === 'object') { 
    //   //     // 把Array Buffer转化为blob 如果是base64不需要 
    //   //     data = window.URL.createObjectURL(new Blob([e.target.result])) 
    //   //   } 
    //   //   else { 
    //   //     data = e.target.result 
    //   //   }
    //   //   vm.option.img = data
    //   //   vm.popupVisible = true
    //   // } 
    // })
  }
}
</script>
<style lang="scss">
.mint-indicator{
  position: relative;
  z-index: 999999;
}
  .update{
    .cut{
      width: 37.5rem;
      // width: 100%;
      height: 50%;
      .btnContainer{
        position: absolute;
        width: 90%;
        left: 50%;
        transform: translateX(-50%);
        box-sizing: border-box;
        bottom: 0.3rem;
        z-index: 9999;
        line-height: 4.2rem;
      }
    }
    .mint-popup{
      height: 100%;
      width: 100%;
    }
  }
</style>


