<template>
  <div class="moneryDetails" ref="moneryDetails">
    <div class="header">
      <span class="back" @click="goback">
        <img src="../assets/my/back.png" alt />
      </span>
      <span class="text">余额明细</span>
    </div>
    <!-- <div class="background"></div> -->
    <div class="main" ref="main">
      <!-- <van-tabs v-model="active"  @change="change" :sticky="true" swipeable animated color="#F64542"   ref="sticky" :offset-top="top"> -->
      <!-- <van-tab  > -->
      <monery-child :show="1"></monery-child>
      <!-- </van-tab> -->
      <!-- <van-tab title="收入">
            <monery-child :show='2'></monery-child>
          </van-tab>
          <van-tab title="支出">
            <monery-child :show='3'></monery-child>
      </van-tab>-->
      <!-- </van-tabs> -->
    </div>
  </div>
</template>
  <script>
import moneryChild from "./moneryChild.vue";
import { Dialog } from "vant";
export default {
  data() {
    return {
      active: 0,
      top: 0
    };
  },
  methods: {
    goback() {
      this.$router.goback();
    },
    change() {
      a;
    }
  },
  components: {
    moneryChild
  },
  created() {
    if (
      navigator.userAgent.match(/HONORBKK/i) == "HONORBKK" ||
      navigator.userAgent.match(/HUAWEI/i) == "HUAWEI"
    ) {
      this.top = 70;
    } else {
      this.top = 80;
    }
  }
};
</script>
<style lang="scss">
.moneryDetails {
  min-height: 100%;
  background: #f5f5f5;
  .header {
    .text {
      margin-left: -1.4rem;
    }
  }
  .van-tabs__wrap {
    margin-top: -0.8rem;
    width: 92%;
    height: 4.4rem;
    margin: 0 auto;
    z-index: 9999;
  }
  .van-hairline--top-bottom::after,
  .van-hairline-unset--top-bottom::after {
    border-width: 0 !important;
  }
  .van-tabs__line {
    background: #fff !important;
  }
  .van-tabs__nav--line {
    height: 26%;
  }
  .van-tabs__wrap {
    .van-tabs__nav {
      background: #fff;
      border-top-left-radius: 0.8rem;
      border-top-right-radius: 0.8rem;
      .van-tab--active {
        color: #333;
        font-size: 1.6rem;
      }
      .van-ellipsis {
        color: #666;
        font-size: 1.6rem;
      }
    }
  }

  .background {
    position: fixed;
    width: 100%;
    height: 5rem;
    background: #f64542;
    border-bottom-left-radius: 50%;
    border-bottom-right-radius: 50%;
    z-index: 99;
  }
  .main {
    margin-top: 1.4rem;
  }
}
</style>


