<template>
  <div class="about">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../assets/my/back.png" ></span>
      <span>设置</span>
      <span></span>
    </div>
    <div class="about_main">
       <div class="about_xzl" @click="goPass">
      <span>修改密码</span>
      <img src="../../assets/my/forward.png">
    </div>
    <!-- <div class="about_xzl">
      <span>通用</span>
      <img src="../../assets/my/back.png">
    </div> -->
    <!-- <div class="about_xzl">
      <span>欢迎评分</span>
      <img src="../../assets/my/back.png">
    </div> -->
    <div class="about_xzl">
      <router-link to='about'>
        <span>关于众享乐</span>
        <img src="../../assets/my/forward.png">
      </router-link>
    </div>
    <div class="about_xzl">
        <span>版本号 0.122501</span>
        <img src="../../assets/my/forward.png">
    </div>
    <!-- <div class="about_xzl">
      <span>隐私</span>
      <img src="../../assets/my/back.png">
    </div> -->
    <div class="about_xzl">
      <router-link to='feedback'>
        <span>意见反馈</span>
        <img src="../../assets/my/forward.png">
      </router-link>
    </div>
    <div class="about_xzl logOut">
      <p @click="logOut">退出登录</p>
    </div>
    </div>
   
    <p class="about_copyright">Copyright © 2019 众享乐</p>
  </div>
</template>

<script>
    export default {
      name: "about",
      data() {
        return{
          version: localStorage.getItem('app_version')
        }
      },
      methods:{
        goBack(){
          this.$router.goback()
        },
        logOut() {
          // localStorage.clear()
          // localStorage.removeItem("loginPhone",,,,"moduleId","loginUserAuthorization")
          // localStorage.removeItem("token")
          // localStorage.removeItem("shopName")
          // localStorage.removeItem()
          // this.$router.push({ name: 'login' })
          for(var key in localStorage){
              if(key != "loginUserName" && key != "password"){
                // console.log(key)
                localStorage.removeItem(key)
              }
          }
          this.clear()
          this.$router.push({ name: 'login' })
        },
        goPass() {
          this.$router.push({ name: 'pass' })
        }
      }
    }
</script>

<style lang="scss">
.about{
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  background-color: #fafafa;
  font-weight: 400;
  .about_main{
    margin-top: 1.4rem;
  }
  // .header{
  //   width: 100%;
  //   height: 4.4rem;
  //   background: #FAFAFA;
  //   line-height: 4.4rem;
  //   text-align: center;
  //   box-sizing: border-box;
  //   padding: 0 1.6rem;
  //   background: #fff;
  //   .back{
  //     float: left;
  //   }
  //   span{
  //     display: inline-block;
  //     text-align: center;
  //     color: #333;
  //     font-size: 1.8rem;
  //     height: 1.8rem;
  //     img{
  //       height: 100%;
  //       vertical-align: middle;
  //     }
  //   }
  // }
  .logo{
    background-color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 12.4rem;
    img{
      width: 4.8rem;
      height: 4.4rem;
      margin-bottom: 0.8rem;
    }
    p{
      color: #999;
      font-size: 1.2rem;
    }
  }
  .about_xzl{
    display: flex;
    justify-content: space-between;
    background-color: #fff;
    align-items: center;
    padding: 0 1.6rem;
    height: 4.7rem;
    a{
      display: inline-block;
      width: 100%;
      img{
        float: right;
        margin-top: 0.8rem;
        transform: translateY(-50%);
      }
    }
    span{
      font-size: 1.6rem;
      color: #333333;
    }
    img{
      width: 0.65rem;
      height: 1.2rem;
    }
  }
  .logOut{
    text-align: center;
    color: crimson;
    margin-top: 1.6rem;
    p{
      font-size: 1.6rem;
      text-align: center;
      width: 100%;
    }
  }
  .about_back{
    background-color: #fff;
    margin: 1.2rem 0;
    padding: 0 1.6rem;

    p{
      display: flex;
      justify-content: space-between;
      height: 4.7rem;
      align-items: center;
      span{
        font-size: 1.6rem;
        color: #333333;
      }
      i{
        font-size: 1.4rem;
        color: #999999;
      }
    }
  }
  .about_copyright{
    color: #999999;
    position: absolute;
    bottom: 1.2rem;
    left:50%;
    transform: translateX(-50%);
  }
  .btnContainer{
    margin-top: 1.2rem;
    width: 100%;
    text-align: center;
    box-sizing: border-box;
    padding: 1.6rem;
    background: #fff;
    p{
      height: 4.4rem;
      width: 100%;
      margin: 1.6rem auto;
      border-radius: 2.2rem;
      background: linear-gradient(to right, #00C5FC, #00AFFC);
      color: #FFF;
      font-size: 1.7rem;
      line-height: 4.4rem;
    }
  }
}
</style>



