<template>
  <div class="merchantCode">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../../assets/my/back.png" alt=""  ></span>
      <span class="text">商家码管理</span>
      <span class="add" v-show="!showDels">
        <span>
          <img src="../../../assets/auth/dels.png" @click="showDels = true">
          <img  src="../../../assets/head/add.png"  @click="show = true">
        </span>
      </span>
      <span v-show="showDels" @click="showDels = false" class="success">完成</span>
    </div>
    <div class="main">
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
        <van-list 
        v-model="loading" 
        :finished="finished" 
        finished-text="——没有更多了——"
        @load="onLoad"
        ref="listHeight"
      >
          <li v-for="(item, index) in dataList" :key="item.index" @click="select(item.id)">
            <img :src="num[item.id] == item.id ? selectImgUrl : imgUrl" class="img" v-if="showDels">
            <span :class="{left: true, margin: showDels }" >
              <p>{{ item.thirdCode }}</p>
              <p>添加时间：{{ item.updatedAt }}</p>
            </span>
            <span class="right"> 
              <span>{{ item.selled === 0 ? '未使用' : '已使用'}}</span>
              <img src="../../../assets/auth/del.png" @click="del(item.id, index)" v-show="!showDels">
            </span>
          </li>
        </van-list> 
        </van-pull-refresh>
    </div>
    <van-popup v-model="show" :style="{ width: '70%' }">
      <h3>请输入商家码</h3>
      <input type="text"  placeholder="最少四位，多个码请用英文,分隔" v-model="codes">
      <div class="btn"> 
        <mt-button @click="show = false">取消</mt-button>
        <mt-button @click="submit">确认</mt-button>
      </div>
      
    </van-popup>
    
    <div class="dels" v-show="showDels">
      <span>
        <img :src="selects ? selectImgUrl : imgUrl" alt="">
        <span @click="selectAll">选择全部</span>
      </span>
      <mt-button class="btn" @click="del" >删除</mt-button>
    </div>
  </div>
</template>
<script>
import {MessageBox} from 'mint-ui';
  export default {
    data(){
      return{
        codes: '',
        imgUrl: require('../../../assets/comment/circular.png') ,
        selectImgUrl: require('../../../assets/comment/selectCircular.png'),
        dataList: [],
        dataListIds: [],
        num: [],
        page: 0,
        show: false,
        showDels: false,
        selects: false,
        finished: false,
        loading: false,
        isLoading: false,
      }
    },
    methods:{
      goBack(){
        this.$router.goback()
      },
      goAdd(){
        this.$router.push('outCoupon_add')
      },
      select(id){
        if(this.num[id] === id){
          this.num.splice(id, 1, '')
          this.num.length + 1
        }else{
          this.$set(this.num, id, id)
        }
      },
      selectAll(){
        this.selects = !this.selects
        if(this.selects){
          this.dataList.forEach((item)=>{
            this.dataListIds.push(item.id)
            this.$set(this.num, item.id, item.id)
          })
        }else{
          this.dataListIds = [];
          this.num = []
        }
      },
      async onLoad(){
        const url = this.apiList.merchantCode.list;
        this.page++;
        let {data} = await this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData( {activityId: this.$route.query.id, activityType: 301, page: this.page, size: 10}, url, true)
        })
        if(data.result){
          if(data.data.records.length > 0){
            this.dataList = this.dataList.concat(data.data.records) 
          }
          if(this.page >= data.data.pages){
            this.finished = true;
          }
          
          this.loading = false
        }
        
      },
      async onRefresh(){
        const url = this.apiList.merchantCode.list;
        let {data} = await this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData( {activityId: this.$route.query.id, activityType: 301, page: 1, size: 10}, url, true)
        })
        if(data.result){
          if(data.data.records.length > 0){
            this.dataList = data.data.records
          }
          this.$toast('刷新成功')
          this.isLoading = false;
          this.finished = false;
          this.page = 1;
          
        }
      },
      async submit(){   
        const url = this.apiList.merchantCode.add;
        if(!this.codes) return this.$toast('请输入商家码')
        this.upload()
        let {data} = await this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData( {activityId: this.$route.query.id,activityType: 301, codes: this.codes, checkRe: true }, url, true)
        })
        this.closeUpload()
        if(data.result){
          if(data.code === 222 ){
            this.$toast("商户码重复，请重新输入")
          }else {
            this.$toast({
              message: '添加成功',
              iconClass: 'fa fa-check fa-5x'
            })
          }
          
          this.show = false;
          this.codes = '';
          this.page = 0;
          if(this.dataList.length >= 10){
            this.finished = false;
          }
          this.dataList = []
          this.onLoad()
        }else{
          this.$toast(data.msg)
        }
      },
      del(id, index){
        const url = this.apiList.merchantCode.del;
        var ids = '';
        MessageBox.confirm("确认删除？").then(async ()=>{
          if(this.showDels){
            this.num.forEach(item=>{
              if(item != ""){
                ids = ids + item + ','
              }
            })
          }else{
            ids = id
          }
          this.delActivity()
          let {data} = await this.$http({
            url: this.$http.adornUrl(url),
            method: 'post',
            data: this.$http.adornData( {ids: ids }, url, true)
          })
          this.closeUpload()
          if(data.result){
            this.$toast({
              message: '删除成功',
              iconClass: 'fa fa-check fa-5x'
            })
            this.num = []
            ids = ''
            if(this.showDels){
              this.page = 0;
              this.dataList = []
              this.selects = false;
              this.onLoad()
            }else{
              this.dataList.splice(index, 1)
            }
          }else{
            this.$toast(data.msg)
          }
        })
        
      }
    }
  }
</script>

<style lang="scss">
  .merchantCode{
    min-height: 100%;
    background: #f5f5f5;
    .header{
      .add{
        float: right;
        img{
          margin-top: -0.5rem;
          margin-right: 1rem;
          &:last-child{
            margin-right: 0;
          }
        }
      }
      .text{
        position: absolute;
        left: 50%;
        transform: translateX(-50%)
      }
      .success{
        float: right;
        font-size: 1.6rem;
      }
    }
    .main{
      margin-top: 1rem;
      .van-pull-refresh{
        overflow: visible;
      }
        li{
          display: flex;
          justify-content: space-between;
          align-items: center;
          height: 6rem;
          padding: 0 1.6rem;
          border-bottom: 0.1rem solid #eee;
          background: #fff;
          &:last-child{
            border: 0;
          }
          .img{
            width: 2rem;
          }
          .margin{
            margin-left: -8rem;
          }
          .left{
            p{
              color: #333;
              font-size: 1.6rem;
              &:last-child{
                color: #999;
                font-size: 1.1rem;
              }
            }
          }
          .right{
            span{
              color: #666;
              font-size: 1.2rem;
              margin-right: 2rem;
            }
            img{
              width: 1.3rem;
              vertical-align: middle;
            }
          }
        }
    }
    .van-popup{
      padding-bottom: 2rem;
      h3{
        text-align: center;
        font-size: 1.4rem;
        color: #333;
        margin-top: 1.6rem;
      }
      input{
        display: block;
        width: 80%;
        margin: 2rem auto 0;
        border: 0;
        
      }
      ::-webkit-input-placeholder {
        color: #ccc;
        font-size: 1.4rem;
      }
      .btn{
        margin-top: 6rem;
        text-align: center;
        button{
          width: 11.5rem;
          background: #F64542;
          color: #fff;
          font-size: 1.4rem;
          border-radius: 1.8rem;
          &:first-child{
            background: #fff;
            color: #999;
            border: 0.1rem solid #999;
            margin-right: 1rem;
          }
        }
      }
      
    }
    .dels{
      position: fixed;
      bottom: 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      height: 5rem;
      padding: 0 1.6rem;
      box-sizing: border-box;
      background: #fff;
      box-shadow:0px 0px 1.35rem 0px rgba(159,159,159,0.41);
      img{
        width: 1.8rem;
        height: 1.8rem;
        vertical-align: middle;
        margin-top: -0.6rem;
      }
      .btn{
        height: 2.4rem;
        line-height: 2.4rem;
        background: #F74543;
        border-radius: 1.2rem;
        color: #fff;
        font-size: 1.4rem;
      }
    }
  }
</style>


