<template> 
  <div class="statistical_data">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../assets/my/back.png" alt=""  ></span>
      <span class="headText">统计数据</span>
      <span class="month" @click="openPicker">
        <span>{{month}}</span>
        <img class="add" src="../../assets/statisticalData/down.png" >
      </span>
    </div>
    <div class="statistical_data_header">
      <div class="header_data">
        <span class="wrapper">
          <img src="../../assets/statisticalData/money.png" alt=""><span>销售金额(元)</span>
          <p>{{money}}</p>
        </span>
        <span class="wrapper">
          <img src="../../assets/statisticalData/transactionNum.png" alt=""><span>交易笔数(笔)</span>
          <p>{{saleNum}}</p>
        </span>
      </div>
    </div>
    <div class="statistical_Chart">
      <div class="saleNum">
        <div class="header_info">
          <span>每日销售额、交易笔数、分销金额</span><span>(元、笔)</span>
        </div>
        <div class="saleNum_Chart">
          <swiper :options="swiperOption" ref="mySwiper" v-if="!nullData">
            <swiper-slide v-for="(item, index) in chartNum" :key="index">
               <sele-chart  :num = index :startTime = startTime :endTime = endTime></sele-chart>
            </swiper-slide>
            <div class="swiper-pagination"  slot="pagination"></div>
          </swiper>
          <div class="nullData" v-else> 
            <img src="../../assets/statisticalData/nullData.png" alt="">
            <p>没有交易记录~</p>
          </div>
        </div>
      </div>
      <div class="activityNum">
        <div class="header_info">
          <span>活动比例</span><span>(%)</span>
        </div>
        <div class="activity_Chart" >
          <div v-if="!activityNumData" >
            <div  class="myActivityChart" ref="myActivityChart"></div>
          </div>
          <div class="nullData" v-else> 
            <img src="../../assets/statisticalData/nullData.png" alt="">
            <p>没有活动记录~</p>
          </div>
        </div>
      </div>
      <!-- <div class="fansNum" v-if="false">
        <div class="header_info">
          <span>粉丝数量</span><span>(人)</span>
        </div>
        <swiper :options="swiperOption" ref="mySwiper">
            
          <swiper-slide >
            <fans-chart ></fans-chart>
          </swiper-slide>
          <div class="swiper-pagination bac"  slot="pagination"></div>
        </swiper>
        
      </div> -->
      
    </div>
    <mt-datetime-picker
      @confirm='confirm'
      v-model="pickerVisible"
      ref="picker"
      type="date"
      :startDate = "date"
      :endDate = 'maxDate'
      year-format="{value} 年"
      month-format="{value} 月"
      
      >
    </mt-datetime-picker>

  </div>
</template>
<script>
import seleChart from './saleChart.vue'
import fansChart from './fansChart.vue'
import echarts from '../../utils/echarts.js'
import { eventBus } from '../../utils/eventBus.js'

  export default {
    data(){
      return{
        le: '',
        saleNum: 0,
        startTime: '',
        endTime: '',
        money: '',
        pickerVisible: new Date(), //默认当前月份
        month: '',
        realIndex: 0,
        chartNum: [],
        date: new Date(1566870627356),
        maxDate: new Date(),
        year: '',
        monthNum: '',
        arr: [],
        nullData: false,
        activityNumData: false,
        swiperOption: {
  //         notNextTick: false,
  // 　　    paginationClickable: true,
    // 　　  loop: true,
          pagination:{
            el:'.swiper-pagination',
            clickable:true,
          },//这样写小圆点就有了
          // autoplay: {
          //   delay: 3000,
          //   stopOnLastSlide: false,
          //   disableOnInteraction: false
          // },
          on:{
            slideChangeTransitionEnd(){
              self.realIndex = this.realIndex
            }
          },
          
          autoplay: false,
          speed: 600,
          direction: "horizontal",
        },
        //销售柱状图
        

        //活动图表
        activutyOption:{
          backgroundColor: '#fff',
          tooltip : { //点击悬浮黑色块  
            trigger: 'item',
            formatter: "{b} : {c} ({d}%)"
          },
          visualMap: {
            show: false,
            min: 80,
            max: 600,
            inRange: {
              // colorLightness: [0, 1]
            }
          },
          series : [
            {
              // name:'访问来源',
              type:'pie',
              color:['#EF7FAF', '#98D182','#9D7CCD','#F8CF48','#6EB2F7'],
              radius : '55%',
              center: ['50%', '50%'],
              itemStyle : {
                normal : {
                  label:{            //饼图图形上的文本标签
                    show: true,
                    position: [10, 10], //标签的位置
                    textStyle : {
                      fontSize : 12,    //文字的字体大小
                      
                    },
                    formatter:' {b}:{d}%'
                  },
                  labelLine : {
                    show : true   //隐藏标示线
                  }
                }
              },
              data:[
                // {value:335, name:'商品',},
                // {value:10, name:'拼团'},
                // {value:274, name:'活动'},
                // {value:235, name:'报名'},
                // {value:400, name:'促销'}
              ].sort(function (a, b) { return a.value - b.value; }),
              roseType: 'radius',
                animationType: 'scale',
                animationEasing: 'elasticOut',
                animationDelay: function (idx) {
                    return Math.random() * 200;
                },
                normal:{
                  label:{
                    show: true,
                    formatter: '{b} : {c} ({d}%)' //文字百分比显示
                  },
                  labelLine :{show:true}
                },
            },
          ]    
        },
        
      }
    },
    methods:{
      goBack(){
        this.$router.goback();
      },
      openPicker() {
        this.$refs.picker.open();
        //隐藏日
        this.$refs.picker.$el.getElementsByClassName('picker-slot')[2].style.display = 'none'
      },
      confirm(value){
        this.year = value.getFullYear()
        this.monthNum = value.getMonth()
        switch(value.getMonth() + 1){
          case 1:
            this.month = '一月';
          break;
          case 2:
            this.month = '二月';
          break;
          case 3:
            this.month = '三月';
          break;
          case 4:
            this.month = '四月';
          break;
          case 5:
            this.month = '五月';
          break;
          case 6:
            this.month = '六月';
          break;
          case 7:
            this.month = '七月';
          break;
          case 8:
            this.month = '八月';
          break;
          case 9:
            this.month = '九月';
          break;
          case 10:
            this.month = '十月';
          break;
          case 11:
            this.month = '十一月';
          break;
          case 12:
            this.month = '十二月';
          break;
        }
        this.startTime =  this.convertTimes(this.year, this.monthNum, 1) 
        this.endTime = this.convertTimes(this.year, this.monthNum  + 1, 1) -1
        this.getSele()
      },
      getSele(){
        const url = this.apiList.statisticalData.data
        this.loading()
        this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({ startTime: this.startTime,endTime: this.endTime   }, url, true)
        }).then(({data})=>{
          this.closeUpload()
          if(data.result){
            let arr = [];
            this.saleNum = 0;
            this.money = ''
            for(let i in data.data.num){
              arr.push(data.data.num[i])
              this.saleNum += data.data.num[i]
            }
            var num = 0
            for(let i in data.data.money){
              num += data.data.money[i]
            }
            num = (num / 100).toFixed(2)
            let result = ''; //结果
              num = num.split('.') //以小数点进行切割
              let lastNum = num[1] //得出小数点后的数值
              num = num[0] //小数点前的数值
              if(num.length > 3){
                while (num.length > 3) {
                  result = ',' + num.slice(-3) + result; //每次循环取num值的最后三位数 + 之前的结果
                  num = num.slice(0, num.length - 3); //每次切割掉num最后三位
                } 
              }
            this.money = num + result + '.' + lastNum
            var saleNum = Math.ceil(arr.length / 6) - 1
            this.chartNum = []
            this.chartNum[saleNum] = true
            if( JSON.stringify(data.data.num) === '{}'){
              this.nullData = true
            }else{
              this.nullData = false
            }
            //饼图数据
              if(data.data.activityNum.length > 0){
                this.activityNumData = false
                this.activutyOption.series[0].data = []
                data.data.activityNum.forEach((item, index)=>{
                  if(item.type == 101){
                    this.activutyOption.series[0].data.push({value: item.num, name: '报名'})
                  }else if(item.type == 301){
                    this.activutyOption.series[0].data.push({value: item.num, name: '促销'})
                  }else if(item.type == 501){
                    this.activutyOption.series[0].data.push({value: item.num, name: '活动'})
                  }else if(item.type == 601){
                    this.activutyOption.series[0].data.push({value: item.num, name: '商品'})
                  }
                })
                this.$nextTick(()=>{
                  echarts.init(this.$refs.myActivityChart).setOption(this.activutyOption)
                })
              }else{
                this.activityNumData = true
              }
          }
          
        })
      },
      convertTimes( year, month, day ){   
        let d = new Date(year, month, day)
        d.setHours(0)
        d.setMinutes(0)
        d.setSeconds(0)
        return Date.parse(d)  
      },
    },
    components:{
      seleChart,
      fansChart
    },
    mounted () {
      this.confirm(new Date())
      // this.getSele()
      // 实例化echarts对象 并 调用绘制图表的方法
      // 活动比例
     },
     created(){
       
     }
  }
</script>
<style lang="scss">
  .statistical_data{
    min-height: 100%;
    .header{
      .headText{
        font-size: 1.8rem;
        margin-left: -1.5rem;
      }
      .back{
        img{
          width: 1rem;
          height: 1.8rem;
        }
      }
      .month{
        position: absolute;
        right: 1.6rem;
        span{
          font-size: 1.3rem;
        }
      }
      img{
        width: 0.75rem;
        height: 0.4rem;
        vertical-align: middle;
      }
    }
    .statistical_data_header{
      height: 14.6rem;
      margin-top: -4rem;
      background: url('../../assets/statisticalData/dataBac.png') no-repeat;
      background-size: 100% 14.6rem;
      .header_data{
        display: flex;
        justify-content: space-around;
        align-items: center;
        color: #fff;
        font-size: 1.4rem;
        text-align: center;
        height: 14.6rem;
        width: 100%;
        .wrapper{
          margin-top: 1rem;
        }
        img{
          width: 1.6rem;
          height: 1.6rem;
          vertical-align: middle;
          margin-top: -0.5rem;
        }
        span{
          
          margin-left: 0.8rem;
        }
        p{
          margin-top: 1.5rem;
          font-size: 3rem;
        }
      }

    }
    .myActivityChart {
      width: 95%;
      height: 28rem;
      margin: 0 auto;
     }
    .statistical_Chart{
      //轮播圆点样式
      .swiper-pagination-bullet-active{
        background: #F8B46F;
      }
      .bac{
        .swiper-pagination-bullet-active{
          background: #FFAFA7;
        }
      }
      .header_info{
        display: flex;
        justify-content: space-between;
        height: 4rem;
        line-height: 4rem;
        padding: 0 1.6rem;
        border-top: 0.1rem solid #D7D7D7;
        border-bottom: 0.1rem solid #D7D7D7;
        span{
          color: #666;
          font-size: 1.3rem;
          font-weight:400;
        }
      }
      .saleNum{
        .saleNum_Chart{
          height: 30rem;
          position: relative;
          .mySaleChart{
            height: 30rem;
          }
        }
      }
      .activityNum{
        .activity_Chart{
          position: relative;
          height: 30rem;
        }
      }
      .nullData{
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        img{
          width: 30.1rem;
        }
        p{
          text-align: center;
          color: #666;
          font-size: 1.4rem;
          margin-top: 1.4rem;
        }
      }
    }
  }
//   .swiper-slide {
//     width: 100%;
//     height: 100px;
//     line-height: 100px;
//     font-size: 50px;
//     text-align: center;
//     background-color: rosybrown;
// }
</style>


