<template>
  <div class="exchangeCode">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../assets/my/back.png" alt=""  ></span>
      <span >兑换码</span>
    </div>
    <div class="main">
      <div class="background_img">
      </div>
      <div class="input">
        <span>
          <input type="text" placeholder="请输入兑换码" v-model="codeKey">
          <img src="../../assets/auth/X.png" @click="codeKey = ''" v-show="codeKey.length > 0">
        </span>
        <mt-button @click="submit" >确定</mt-button>
      </div>
      <div class="content" v-show="show">
        <span class="left">
          <img :src="data.imgUrlSquare" class="imgUrl">
          <img :src="getImg(data.type)" v-if="data.type" class="imgType">
          <p>{{ getDate(data.endUseTime) }}</p>
        </span>
        <span class="right">
          <p :class="{ title: true, coupon: data.outCouponId > 0 && data.useState === 1 }">{{ data.name }}</p>
          <p v-if="!( data.outCouponId > 0)">订单号：{{ data.outTradeNo }}</p>
          <p v-if="!( data.outCouponId > 0)">购买人：{{ data.moduleName ? data.userNickName : '众享乐用户'}}</p>
          <p :class="{ useTime: data.outCouponId > 0 && data.useState === 101 }" v-if="data.useState === 101" >兑换时间: {{setDate(data.keyUseTime)}}</p>
          <mt-button @click="cancel" :class="{marginTop : data.useState === 1} ">取消</mt-button>
          <mt-button @click="success" v-if=" data.useState === 1 ">兑换</mt-button>
          <mt-button v-else>已兑换</mt-button>
        </span>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data(){
      return{
        codeKey: '',
        time: '',
        data:{},
        show: false
      }
    },
    methods:{
      goBack(){
        this.$router.goback()
      },
      async submit(){
        if(!this.codeKey) return this.$toast("请输入兑换码")
        const url = this.apiList.active.exchangeCode;
        this.request()
        let {data} = await this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({codeKey: this.codeKey}, url, true)
        })
        this.closeUpload()
        if(data.result){
          this.show = true
          this.data = data.data
        }else{
          this.$toast(data.msg)
        }
      },
      async success(){
        const url = this.apiList.active.choose;
        this.request()
        let {data} = await this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({
            activityGetId: this.data.id, 
            activityId: this.data.activityId,
            activityType: this.data.activityType, 
            usrId: this.data.userId, 
            codeKey: this.codeKey
          }, url, true)
        })
        this.closeUpload()
        if(data.result){
          this.$toast({
            message: '兑换成功',
            iconClass: 'fa fa-check fa-5x'
          })
          this.submit()
          // this.data.useState = 101;
          // this.codeKey = ''
        }else{
          this.$toast(data.msg)
        }
      },
      cancel(){
        this.show = false
        this.codeKey = ''
      },
      getImg(type){
        switch(type){
          case 101:
            return require('../../assets/index/signUp.png');
          case 201:
            return require('../../assets/index/collage.png');
          case 301:
            return require('../../assets/index/promotion.png');
          case 501:
            return require('../../assets/index/activityImg.png');
          case 601:
            return require('../../assets/index/commodity.png');
        }
      },
      getDate(time){
        if(!time)return
        let result = '';
        let t = setInterval(() => {
          if( time - Date.now() <= 0 ){
            clearInterval(t)
            return this.time = '兑换码已过期'
          }
          let surplusTime = (time -  Date.now()) / 1000
          let d = parseInt(surplusTime / 60 / 60 / 24)
          let h = parseInt((surplusTime / 60 / 60)%24)
          let m = parseInt((surplusTime / 60) % 60)
          let s = parseInt(surplusTime % 60)
          if(h<10){
            h = `0${h}`
          }
          if(m<10){
            m = `0${m}`
          }
          if(s<10){
            s = `0${s}`
          }
          if(d<=0){
            result = `剩余时间: ${h}:${m}:${s}`
          }else{
            result = `剩余时间: ${d}天 ${h}:${m}:${s}`
          }
          this.time =  result
          clearInterval(t)
        }, 1000);
        return this.time
      }
    },
    mounted(){
    }
  }
</script>
<style lang="scss">
  .exchangeCode{
    min-height: 100%;
    background: #f5f5f5;
    font-family:Microsoft YaHei;
    .main{
      background: #fff;
      padding-bottom: 1.5rem;
      .background_img{
        width: 100%;
        height: 16.15rem;
        background: url('../../assets/group/exchangeCodeBac.png') no-repeat;
        background-size: 100% 100%; 
      }
      .input{
        display: flex;
        width: 34.5rem;
        box-sizing: border-box;
        margin: 1.5rem auto 0;
        span{
          position: relative;
          display: inline-block;
          height: 4.8rem;
          border: 0.1rem solid #d7d7d7;
          border-radius: 2.4rem 0 0 2.4rem;
          input{
            width: 25rem;
            height: 4.8rem;
            margin-left: 2.1rem;
            font-size: 1.3rem;
            border: none;
          }
          img{
            position: absolute;
            top: 50%;
            right: 5%;
            transform: translateY(-50%);
          }
        }
        
        ::-webkit-input-placeholder { /* WebKit, Blink, Edge */
          color: #D7D7D7;
        }
        :-moz-placeholder { /* Mozilla Firefox 4 to 18 */
          color: #D7D7D7;
        }
        ::-moz-placeholder { /* Mozilla Firefox 19+ */
          color: #D7D7D7;
        }
        :-ms-input-placeholder { /* Internet Explorer 10-11 */
          color: #D7D7D7;
        }
        button{
          width: 7.5rem;
          height: 5rem;;
          background: #F64542;
          color: #fff;
          font-size: 1.6rem;
          border-radius: 0 2.4rem  2.4rem 0;
        }
      }
      .content{
        display: flex;
        margin-top: 1.5rem;
        padding: 0 1.6rem;
        .left{
          position: relative;
          .imgUrl{
            width: 12.2rem;
            height: 12.52rem;
          }
          .imgType{
            position: absolute;
            top: 0;
            left: 0;
            width: 3.2rem;
            height: 3.25rem;
          }
          p{
            position: absolute;
            bottom: 0.5rem;
            width: 100%;
            text-align: center;
            background: rgba(0,0,0,0.6);
            color: #fff;
            font-size: 1rem;
          }
        }
        .right{
          margin-left: 1.2rem;
          .title{
            max-width: 19rem;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            font-size: 1.4rem;
            color: #333;
          }
          .coupon{
            margin: 3rem 0 1rem 8rem;
          }
          p{
            margin-bottom: 0.5rem;
            font-size: 1.2rem;
            color: #999;
          }
          .useTime{
            margin-bottom: 3rem;
          }
          .marginTop{
            margin-top: 2rem;
          }
          button{
            margin-top: 1rem;
            width: 6.4rem;
            height: 2.5rem;
            margin-left: 4rem;
            background: #fff;
            border-radius: 1.25rem;
            color: #333;
            font-size: 1.2rem;
            border: 0.1rem solid #d7d7d7;
            box-shadow: none;
            &:last-child{
              margin: 0;
              margin-left: 2rem;
              border: 0.1rem solid #F64542;
              color: #F64542;
            }
            &:last-child::after{
              background-color: red;
            }
          }
        }
      }
    }
  }
</style>


