<template>
  <div class="outMoeny_index">
    <div class="header">
      <span class="back" @click="goback"><img src="../../../assets/my/back.png" alt=""  ></span>
      <span>提现列表</span>
    </div>
   <div class="withdraw">
      <van-tabs  :sticky="true" swipeable animated :offset-top='48.5' color="#fff">
        <van-tab title="全部">
          <out-moeny-list  :shows="0"  ref="child1"></out-moeny-list >
        </van-tab>
        <van-tab title="已审批">
          <out-moeny-list   :shows="1"  ref="child2"></out-moeny-list >
        </van-tab>
        <van-tab title="未审批">
          <out-moeny-list   :shows="2"  ref="child3"></out-moeny-list >
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>
<script>
import outMoenyList from './list'
export default {
  data(){
    return{

    }
  },
  components:{
    outMoenyList
  },
  methods:{
    goback(){
      this.$router.goback()
    }
  }
}
</script>
<style lang="scss">
.outMoeny_index{
  min-height: 100%;
  .van-tabs__wrap{
    margin-top: -0.8rem;
  }
  .withdraw{
    min-height: 100%;
  }
}
</style>



