<template>
  <div class="roleList">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../assets/my/back.png" alt=""  ></span>
      <span>默认岗位</span>
    </div>
    <div class="auto_mian">
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
        <van-list 
          v-model="loading" 
          :finished="finished" 
          finished-text="——没有更多了——"
          @load="onLoad"
          ref="listHeight"
        >
          <van-cell v-for="(item, index) in dataList" :key="index">
            <span class="left">
              <p><img src="../../assets/auth/phone.png" alt=""><span>{{item.phoneNum}}</span></p>
            </span>
            <span class="right" v-if="isAuth('/moduleUser/breakUser')">
              <img src="../../assets/auth/set.png" alt="" @click="goSet(item.id)" v-if="apiList.role.link">
              <img src="../../assets/auth/del.png" alt="" @click="del(item.id, index)" v-if="apiList.moduleUser.del">
            </span>
          </van-cell>
        </van-list >
      </van-pull-refresh>
    </div>
  </div>
</template>
<script>
import {MessageBox} from 'mint-ui';
  export default {
    data(){
      return{
        page: 0,
        dataList: [],
        roleList: [],
        loading: false,
        finished: false,
        isLoading: false,
        
      }
    },
    methods:{
      onLoad(){
        return new Promise((resolve, reject) =>{
          this.page++
          const url = this.apiList.moduleUser.default;
          this.$http({
            url: this.$http.adornUrl(url),
            method: 'post',
            data: this.$http.adornData({ page: this.page, size: 10, id: this.$route.query.id}, url, true)
          }).then(({data})=>{
            if (data.result) {
              if (data.data.records.length >= 0) {
                this.dataList = this.dataList.concat(data.data.records)
              }
              if (data.data.pages <= this.page) {
                this.finished = true
              }
              this.$nextTick(() => {
                resolve(true)
                this.loading = false
              })
            }else if(data.msg == 'sign验证失败'){
              this.$toast('您的账户登录超时')
              setTimeout(()=>{
                localStorage.clear();
                this.$router.push({name: 'login'})
              },1000)
            }
          })
          
        })
      },
      onRefresh(){
        return new Promise((resolve, reject) =>{
          this.page = 0;
          this.page++;
          const url = this.apiList.moduleUser.default;
          this.$http({
            url: this.$http.adornUrl(url),
            method: 'post',
            data: this.$http.adornData({page: this.page, size: 10}, url, true)
          }).then(({data})=>{
            if (data.result) {
              if (data.data.records.length >= 0) {
                this.dataList = data.data.records;
              }
              this.$nextTick(() => {
                resolve(true)
                this.$toast('刷新成功')
                this.isLoading = false;
                this.finished = false;
                this.page = 1;
              })
            }
          })
          
        })
      },
      getRoleList(){
        const url = this.apiList.role.list;
          this.$http({
            url: this.$http.adornUrl(url),
            method: 'post',
            data: this.$http.adornData({searchJson: '', page: 1, size: 10000}, url, true)
          }).then(({data})=>{
            if (data.result) {
              if (data.data.records.length >= 0) {
                this.roleList = data.data.records
              }
            }else if(data.msg == 'sign验证失败'){
              this.$toast('您的账户登录超时')
              setTimeout(()=>{
                localStorage.clear();
                this.$router.push({name: 'login'})
              },1000)
            }
          })
      },
      goBack(){
        this.$router.goback()
      },
      goSet(id){
        this.$router.push({name: 'roleSet', query:{id: id}})
      },
      del(id, index){
        MessageBox.confirm('确认删除该项？').then(()=>{
          const url = this.apiList.moduleUser.del
          this.delActivity()
          this.$http({
            url: this.$http.adornUrl(url),
            method: 'post',
            data: this.$http.adornData({usrId: id}, url, true)
          }).then(({data}) =>{
            this.closeUplod()
            if(data.result){
              this.dataList.splice(index, 1)
              this.$toast({
                message: '删除成功',
                iconClass: 'fa fa-check fa-5x'
              })
            }else if(data.msg == 'sign验证失败'){
              this.$toast('您的账户登录超时')
              setTimeout(()=>{
                localStorage.clear();
                this.$router.push({name: 'login'})
              },1000)
            }
          })
        })
      },
    },
    mounted() {
      this.$refs.listHeight.$el.style.minHeight = document.documentElement.clientHeight  - 12.2 * 10 + 'px'
    },
    created(){
      
      this.getRoleList()      
      
    }
  }
</script>
<style lang="scss">
  .toast{
    z-index: 99999;
  }
  .roleList{
    background: #f5f5f5;
    min-height: 100%;
    .header{
      .add_Pro{
        float: right;
      }
    }
    .auto_mian{
      margin-top: 1.4rem;
      .van-list{
        .van-cell{
          padding: 0;
        }
        
        .van-cell__value{
          display: flex;
          justify-content: space-between;
          align-items: center;
          background: #fff;
          height: 6rem;
          padding: 0 1.6rem;
          .left{
            display: flex;
            flex-direction: column;
            font-family:"Microsoft YaHei",微软雅黑;
            span{
              color: #999999;
              font-size: 1.1rem;
            }
            p{
              font-size: 1.6rem;
              // padding-bottom: 0%
            }
            p:last-child{
              margin-top: -0.6rem;
            }
            img{
              
              width: 0.8rem;
              height: 0.8rem;
            }
          }
          .right{
            display: flex;
            img{
              width: 1.4rem;
              height: 1.4rem;
              border-right: 0.1rem solid #ddd;
              padding-right: 1.5rem;
              margin-right: 1.75rem;
            }
            img:last-child{
              border: 0;
              margin: 0;
              padding: 0;
            }
          }
        }
      }
    }
    .add_user{
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background: rgba(0, 0, 0, 0.4);
      z-index: 9999;
      .wrapper{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        height: 34.45rem;
        width: 28.4rem;
        background: #fff;
        border-radius: 0.8rem;
        .add_user_header{
          img{
            width: 28.4rem;
            height: 7.9rem;
          }
        }
        .add_user_main{
          margin-top: 2.5rem;
          margin-left: 2.8rem;
          color: #333;
          p{
            padding: 1rem 0;
            .text{
              color: #333;
              font-size: 1.4rem;
            }
            input{
              margin-left: 1rem;
              width: 12.2rem;
              border: 0;
              
            }
            ::-webkit-input-placeholder { /* WebKit, Blink, Edge */
              color: #D7D7D7;
              font-size: 1.4rem;
            }
            :-moz-placeholder { /* Mozilla Firefox 4 to 18 */
              color: #D7D7D7;
              font-size: 1.4rem;
            }
            ::-moz-placeholder { /* Mozilla Firefox 19+ */
              color: #D7D7D7;
              font-size: 1.4rem;
            }
            :-ms-input-placeholder { /* Internet Explorer 10-11 */
              color: #D7D7D7;
              font-size: 1.4rem;
            }
            .input_wrapper{
              display: inline-block;
              width: 17rem;
              margin-left: 1rem;
              padding-bottom: 0.5rem;
              border-bottom: 0.1rem solid #DDDDDD;
            }
            .code_input{
              width: 10.1rem;
              input{
                width: 8.35rem;
              }
            }
            .send_code{
              margin-left: 1rem;
              color: #5189EF;
              font-size: 1.2rem;
            }
            img{
              float: right;
              width: 1.2rem;
              height: 1.2rem;
            }
          }
        }
        .user_footer{
          position: absolute;
          bottom: 3rem;
          button{
            margin-left: 2rem;
            display: inline-block;
            width: 11.51rem;
            height: 3.6rem;
            background: #fff;
            color: #999;
            border: 0.1rem #999 solid; 
            border-radius: 1.8rem;
            font-size: 1.4rem;
          }
          button:last-child{
            border: 0;
            // margin-left: 15rem;
            background:linear-gradient(-82deg,rgba(239,83,80,1),rgba(229,57,53,1));
            color: #fff;
            
          }
        }
      }
    }
  }
</style>


