<template>
  <div class="manage_capital">
    <div class="header">
      <span>资金</span>
    </div>
    <div class="capital_li capital_li1" @click="goAuth()">
      <img src="../../assets/manage/zu.png" alt="" class="img" >
      BOSS资金
      <img src="../../assets/manage/more.png" alt="" class="img2">
    </div>
    <div class="capital_li">
      <img src="../../assets/manage/zu2.png" alt="" class="img">
      利润提现
      <img src="../../assets/manage/more.png" alt="" class="img2">
    </div>
  </div>
</template>
<script>
export default {
  data(){
    return{

    }
  },
  methods:{
    goBack(){
      this.$router.goback()
    },
    goAuth(){
      this.$router.push('capital')
    }
  }
}
</script>
<style lang="scss">
.manage_capital{
  min-height: 100%;
  background-color: #f5f5f5;
  .capital_li1{
    margin-top: 2rem;
  }
  .capital_li{
    height: 6.3rem;
    line-height: 6.3rem;
    padding-left: 2rem;
    padding-right: 2rem;
    background-color: #fff;
    position: relative;
    border-bottom: 1px solid rgba(238,238,238,1);
    .img{
      width: 4rem;
      vertical-align: middle;
      margin-right: 2rem;
    }
    .img2{
      width: 0.6rem;
      position: absolute;
      right: 2rem;
      top: 3.1rem;
    }
  }
}
</style>




