<template>
  <div class="openAccount_add">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../../assets/my/back.png" alt="" ></span>
      <span>添加商户</span>
    </div>
    <div class="form">
      <mt-field label="名称" placeholder="请输入商户名称"   v-model="shopName"/>
      <mt-field label="手机号" placeholder="请输入手机号"  type="number" v-model="phoneNum"/>
    </div>
    <div class="button_add" @click="add">
      确定
    </div>
  </div>
</template>
<script>
export default {
  data(){
    return{
      shopName: "",
      phoneNum: ''
    }
  },
  methods:{
    goBack(){
      this.$router.goback()
    },
    async add(){
      if(!this.shopName.trim()){
        return this.$toast('请输入商户名称')
      }
      if (!this.phoneNum || !(/^1[3|4|5|7|8][0-9]\d{4,8}$/.test(this.phoneNum))) {
        return this.$toast('请输入正确的手机号码')
      }
      const url = this.apiList.admin.moduleShopAdd
      let {data} = await this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData({name: this.shopName, phoneNum: this.phoneNum  }, url, true)
      })
      if(data.result){
        this.$toast({
          message: '添加成功',
          iconClass: 'fa fa-check fa-5x'
        })
        this.goBack()
      }else{
        this.$toast(data.msg)
      }
    }
  }
}
</script>
<style lang="scss">
.openAccount_add{
  min-height: 100%;
  background-color: #F5F5F5;
  .button_add{
    width:31.6rem;
    height:4.4rem;
    background:rgba(246,69,66,1);
    border-radius: .4rem;
    font-size: 1.4rem;
    line-height: 4.4rem;
    text-align: center;
    color: #fff;
    margin: 5rem auto;
  }
  .mint-field .mint-cell-title{
  width: 10rem;
}
.form{
    margin-top: 1.4rem;
  }
.mint-field{
  width: 100%;
  height: 5.5rem;
}
.mint-cell-wrapper{
  border-bottom: 0.1rem solid #EEEEEE;
  padding: 0;
  margin: 0 1.6rem;
}
.mint-cell-wrapper:last-child(){
  border: 0;
}
.mint-cell-text{
  color: #171717;
  font-size: 1.4rem;
}
.mint-cell-value{
  input{
   font-size: 1.4rem;
  }
}

::-webkit-input-placeholder { /* WebKit, Blink, Edge */
  color: #D7D7D7;
}
:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
  color: #D7D7D7;
}
::-moz-placeholder { /* Mozilla Firefox 19+ */
  color: #D7D7D7;
}
:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: #D7D7D7;
}
.mint-field:last-child{
  border: 0;
}

.mint-button--primary{
  font-size: 1.5rem;
  height: 4rem;
  border-radius: 2rem;
}
}
</style>




