<template>
  <div class="fans_Chart">
    <div class="myFansChart" ref="myFansChart"></div>
  </div>
</template>
<script>
import echarts from '../../utils/echarts.js'
  export default {
    data(){
      return{
        //粉丝折线图
        fansOption:{
          // textStyle: {color:'#999'},
          xAxis: {
            
            type: 'category',
            axisLine:{
              lineStyle:{ // x轴刻度线条颜色
                color: '#ddd'
              }
            },
            
            axisLabel: { // x轴刻度文字颜色
              textStyle: {
                color: '#666666'
              }
            },
            data: ['1日', '2日', '3日', '4日', '5日', '6日', '7日', '8日', '9日', '10日'],
            axisTick:{ 
              show: false
            },
          },
          yAxis: {
            type: 'value',
            axisLine:{
              lineStyle:{ // y轴刻度线条颜色
                color: '#ddd' 
              }
            },
            axisTick: { //隐藏y轴刻度线突出的线条
              show: false
            },
            splitLine: { //图表里面的分割线颜色
              lineStyle: {
                color: '#EEE'
              }
            },
            axisLabel : { // y轴刻度文字颜色
              textStyle: {
                color: '#999'
              }
            },
            
          },
          
          series: [{
            itemStyle:{
              color: '#FF6757'
            },
            data: [100,200,225,111, 125, 225, 255, 255,111,50],
            type: 'line',
            
          }]
        }
      }
    },
    methods:{
      getData(){
      },
    },
    mounted() {
      var max = -1;
      var value = 0;
      var data = []
      this.fansOption.series[0].data.forEach((item, index)=>{
        if( item > value){
          max = index
          value = item
        }
      })
      this.fansOption.series[0].data.splice(max,1,{value: value,label:{show: true,backgroundColor:'#FE7162',color: '#fff',padding: 4,borderRadius:6} })
      echarts.init(this.$refs.myFansChart).setOption(this.fansOption)
    }
  }
</script>
<style lang="scss">
  .fansNum{
    .fans_Chart{
      margin-left: 0.8rem;
      height: 30rem;
      .myFansChart{
        height: 30rem;
      }
    }
  }
</style>


