<template>
  <div class="detail">
    <div class="header">
      <span class="back" @click="goback()">
        <img src="../../assets/my/back.png" alt="" >
      </span>
      <span>{{title[shows]}}</span>
      <span class="add_Pro" @click="goSearch">
        <img class="searchBtn"  src="../../assets/head/search.png">
      </span>
    </div>
    <div class="detail_main">
      <van-tabs v-model="active" :sticky="true" @change="change" swipeable animated :offset-top='48.5' color="#fff">
        <van-tab title="全部">
          <detail-list :title="0" ref="child"></detail-list>
        </van-tab>
        <van-tab title="未提货">
          <detail-list :title="1" ref="child1"></detail-list>
        </van-tab>
        <van-tab title="已提货">
          <detail-list :title="2" ref="child2"></detail-list>
        </van-tab>
        <!-- <van-tab title="退款中">
          <detail-list :shows="3"></detail-list>
        </van-tab> -->
        <van-tab title="已退款">
          <detail-list :title="3" ref="child3"></detail-list>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>
<script>

import detailList from "./detailList.vue"
export default {
  data(){
    return{
      title:['促销明细','拼团明细','报名明细','活动明细','商品明细',],
      activeUrl:['pmSale','','enterName','addActivity','commodity',],
      shows:'',
      active: 0
    }
  },
  methods:{
    goback(){
      this.$router.goback()
      localStorage.removeItem('shows')
    },
    goSearch(){
      this.$router.push({name:'activity_search_details', query: { list: this.activeUrl[this.shows], id: this.$route.params.activityId}})
    },
    change(){
      if(this.active === 0 && this.$refs.child){
        this.$refs.child.change()
      }else if(this.active === 1 && this.$refs.child1){
        this.$refs.child1.change()
      }else if(this.active === 2 && this.$refs.child2){
        this.$refs.child2.change()
      }else if(this.active === 3 && this.$refs.child3){
        this.$refs.child3.change()
      }
    }
  },
  components:{
    detailList
  },
  created(){
    
    if(this.$route.params.shows || this.$route.params.shows===0){
      localStorage.setItem('shows',this.$route.params.shows)
      this.shows = this.$route.params.shows
    }else{
      this.shows = localStorage.getItem('shows')
    }
    
  },
}
</script>
<style lang="scss">
.detail{
  min-height: 100%;
  background-color: #F5F5F5;
  .add_Pro{
    float: right;
    img{
      width: 1.8rem;
      height: 1.9rem;
    }
  }
  .van-tabs__wrap{
    // position: fixed;
    // width: 100%;
    // top: 4.2rem;
    // z-index: 999;
    // margin-top: 4.2rem;
    margin-top: -0.8rem;
  }
  .van-tab {
      // flex-basis: 20% !important;
  }
}
</style>



