const android = {}
/**
 * 1.支付方法
 * @param {number} type 支付类型，101为微信支付，201为支付宝支付
 * @param {object} data 支付参数
 */
android.pay = (type, data) => {
  if (type === 101) {
    window.APPJS.unifyClassMethod('PayClass', 'wxPay', JSON.stringify(data), 'payScccess')
  } else if (type === 201) {
    const _formData = {'orderInfo': ''}
    if (Number(localStorage.getItem('app_type')) === 2) {
      _formData.orderInfo = EncodeUtf8(data)
    } else {
      _formData.orderInfo = data
    }
    window.APPJS.unifyClassMethod('PayClass', 'zfbPay', JSON.stringify(_formData), 'payScccess')
  }
}
/**
 * 2.跳转到外部链接
 * @param {string} url 外链地址
 */
android.toUrl = (url) => {
  window.APPJS.unifyClassMethod('FuctionClass', 'openUrl', changeData({openUrl: url}), ' ')
}

/**
 * 3.设置app头部颜色
 * @param {string} backgroudColor 背景颜色
 * @param {string} alpha 透明 0-1 0：完全透明1:不透明
 */
android.navColorChange = (backgroudColor, alpha = 1) => {
  const formData = { 
    color: backgroudColor
  }
  window.APPJS.unifyClassMethod('NavgationViewClass', 'setStatusBarBackgroundColor', changeData(formData), 'getHeader')
}
