/**
 * 对传入数据进行基座相应改动
 * @param {obj} data 数据
 */
function changeData (data) {
  let _data = JSON.stringify(data)
  if (Number(localStorage.getItem('app_type')) === 2) {

    return EncodeUtf8(_data)
  } else {
    return _data
  }
}

/**
 * 强制转换为utf8编码, ios使用
 * @param {*} s1 转码字符
 */
function EncodeUtf8(s1) {
	// escape函数用于对除英文字母外的字符进行编码。如“Visit W3School!”->"Visit%20W3School%21"
	var s = escape(s1);
	var sa = s.split("%");// sa[1]=u6211
	var retV = "";
	if (sa[0] != "") {
		retV = sa[0];
	}
	for ( var i = 1; i < sa.length; i++) {
		if (sa[i].substring(0, 1) == "u") {
			retV += Hex2Utf8(Str2Hex(sa[i].substring(1, 5)));
			if (sa[i].length >= 6) {
				retV += sa[i].substring(5);
			}
		} else
			retV += "%" + sa[i];
	}
	return retV;
}

/**
 * 获取基座定位
 * @param {number}
 * accuracyNum		精度要求
   distanceNum		更新距离
   backLocationType	0~2   	0:WGS-84世界标准坐标  1:GCJ-02高德地图坐标  2: BD-09百度坐标
 */
android.LocatStart = (data) => {

  window.APPJS.unifyClassMethod('LocationClass', 'locat_start', changeData(data), "locationCity")
}

android.LocatEnd = () => {
  // alert("111")
  window.APPJS.unifyClassMethod('LocationClass', 'locat_end', '', "locationEnd")

}

export default android



