<template>
    <div class="update">
      <div class="header" v-if="!notitle" ref="header">
        <span class="back" @click="goback"><img src="../assets/my/back.png" alt=""  ></span>
        <span>建设中</span>
      </div>
      <div class="content">
      <img class="update_img" src="../assets/my/update.png">
      <p>建设中...</p>
      </div>
    </div>
</template>

<script>
export default {
  name: "update",
  props: {
    notitle: {}
  },
  data(){
    return{ 
    }
  },
  methods:{
    goback() {
      this.$router.goback()
    }
  },
  mounted(){
    if(localStorage.getItem('isAdm') == '1'){
      this.$refs.header.remove()
    }
  }
}
</script>

<style lang="scss">
  .update{
    .content{
      margin-top: 11.8rem;
      text-align: center;
      .update_img{
        height: 13.7rem;
        width: 17.05rem;
        margin: 11.8rem 11.1rem  5.25rem 9.35rem;
        display: block;
      }
      p{
        font-size: 1.4rem;
        color: #666666;
      }
    }
  }

</style>



