<template>
  <div class="comment">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../../assets/my/back.png" alt=""  ></span>
      <span class="headText">用户评价</span>
      <span class="edit_wrapper" v-if="isAuth('/zComment/deleteCommentA')">
        <img  src="../../../assets/comment/edit.png">
        <span @click="showDel">编辑</span>
      </span>
    </div>
    <div class="main">
      <div class="commodity_main">
        <van-tabs v-model="active" @change="change" :sticky='true' swipeable animated color="#fff" :offset-top='48.5'>
          <van-tab :title="'全部' + `(${data.total})`">
            <index-child  :data = "data"   ref="child1"></index-child>
          </van-tab>
          <van-tab :title="'晒图' + `(${data.imgTotal})`">
            <index-child  :data = "data"  ref="child2"></index-child>
          </van-tab>
          <van-tab title="最新">
            <index-child  :data = "data"  ref="child3"></index-child>
          </van-tab>
        </van-tabs>
      </div>
    </div>
    <div class="bottom" v-if="data.showDel">
      <span>
        <img :src="data.selectAll ? selectImgUrl : imgUrl" alt="">
        <span @click="data.selectAll = !data.selectAll">删除全部</span>
      </span>
      <mt-button class="btn" @click="delAllData" >完成</mt-button>
    </div>
  </div>
</template>
<script>
import indexChild from './indexChild.vue'

export default {
  data(){
    return {
      active: 0,
      data:{
        showDel: false,
        total: 0,
        imgTotal: 0,
        num: 0,
        selectAll: false
      },
      imgUrl: require('../../../assets/comment/circular.png') ,
      selectImgUrl: require('../../../assets/comment/selectCircular.png'),
    }
  },
  methods:{
    goBack(){
      this.$router.goback();
    },
    change(){
      this.data.num = this.active
    },
    showDel(){
      return this.data.showDel = !this.data.showDel
    },
    delAllData(){
      if(this.data.selectAll){
        if(this.active === 0){
          this.$refs.child1.delAllData()
        }else if(this.active === 1){
          this.$refs.child2.delAllData()
        }else if(this.active === 2){
          this.$refs.child3.delAllData()
        }
      }else{
        this.$toast('请先勾选删除全部')
      }
    }
  },
  components:{
    indexChild
  },
  created(){
  }
}
</script>
<style lang="scss">
  .comment{
    background: #f5f5f5;
    min-height: 100%;
    .van-tabs__wrap{
      margin-top: -0.8rem;
    }
    .headText{
      margin-left: 4rem;
    }
    .edit_wrapper{
      float: right;
      img{
        width: 1.21rem;
        height: 1.2rem;
      }
      span{
        font-size: 1.2rem;
      }
    }
    .bottom{
      position: fixed;
      bottom: 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      height: 5rem;
      padding: 0 1.6rem;
      box-sizing: border-box;
      background: #fff;
      box-shadow:0px 0px 1.35rem 0px rgba(159,159,159,0.41);
      img{
        width: 1.8rem;
        height: 1.8rem;
        vertical-align: middle;
        margin-top: -0.6rem;
      }
      .btn{
        height: 2.4rem;
        line-height: 2.4rem;
        background: #F74543;
        border-radius: 1.2rem;
        color: #fff;
        font-size: 1.4rem;
      }
    }
  }
</style>


