import Vue from 'vue'
import { Indicator } from 'mint-ui'

Vue.prototype.setStorage = function(){
  
  localStorage.setItem('id', this.id ? this.id : '')
  localStorage.setItem('imgUrl',this.imgUrl)
  localStorage.setItem('imgUrlSquare',this.imgUrlSquare)
  localStorage.setItem('name',this.name)
  localStorage.setItem('price',this.price)
  localStorage.setItem('totalNum', this.totalNum ? this.totalNum : '')
  localStorage.setItem('startTime',this.startTime)
  localStorage.setItem('endTime',this.endTime)
  localStorage.setItem('startUseTime',this.startUseTime)
  localStorage.setItem('endUseTime',this.endUseTime)
  localStorage.setItem('showPrice',this.showPrice ? this.showPrice : '')
  localStorage.setItem('text',this.text)
  localStorage.setItem('value',this.value)
  // if(this.$route.name == 'activity_fullText' ||  this.$route.name == 'shopDetail_map'){
    localStorage.setItem('phoneNum', this.phoneNum )
    localStorage.setItem('lat', this.lat === null ? '' : this.lat)
    localStorage.setItem('lng',this.lng === null ? '' : this.lng)
    localStorage.setItem('doorAdd', this.doorAdd)
    localStorage.setItem('address',this.address)
    localStorage.setItem('prize', this.prizeFullText)
    localStorage.setItem('title', this.title)
    localStorage.setItem('index', this.index)
    localStorage.setItem('activityGroupIds', this.activityGroupIds)
    localStorage.setItem('groupId', this.groupId)
    localStorage.setItem('addScrollTop', this.addScrollTop)

    localStorage.setItem('poster', this.imgUrlPoster)
    localStorage.setItem('sysPrice', this.sysPrice)
    localStorage.setItem('totalCanBuy', this.totalCanBuy ? this.totalCanBuy : '')
    localStorage.setItem('sellType', this.type)
    localStorage.setItem('resellPrice', this.resellPrice)
    localStorage.setItem('resellerText', this.resellerText)
    localStorage.setItem('imgUrlResell', this.imgUrlResell)
    localStorage.setItem('resellValue', this.resellValue)
    localStorage.setItem('showResell', this.showResell)
    localStorage.setItem('resellImg', this.resellImg)
    localStorage.setItem('firstOrderPrice', this.firstOrderPrice)
    localStorage.setItem('seleceId', this.seleceId)
    localStorage.setItem('groupIdText', this.groupIdText)
    localStorage.setItem('groupIdSeleceText', this.groupIdSeleceText)
    localStorage.setItem('merchantCodeValue', this.merchantCodeValue)
    localStorage.setItem('merchantCodeText', this.merchantCodeText)
    localStorage.setItem('activityType', this.activityType)
    localStorage.setItem('moduleGetPrice', this.moduleGetPrice)
    // localStorage.setItem('photosImg', this.photosImg)

  // }
}
Vue.prototype.getStorage = function(){
  
  this.id = localStorage.getItem('id')
  this.imgUrl = localStorage.getItem('imgUrl') ? localStorage.getItem('imgUrl') : ''
  this.imgUrlSquare = localStorage.getItem('imgUrlSquare') ? localStorage.getItem('imgUrlSquare') : ''
  this.name =  localStorage.getItem('name') ? localStorage.getItem('name') : ''
  this.price = localStorage.getItem('price')  ? localStorage.getItem('price')  : ''
  this.phoneNum = localStorage.getItem('phoneNum') ? localStorage.getItem('phoneNum')  : ''
  this.totalNum =  localStorage.getItem('totalNum') ? localStorage.getItem('totalNum')  : ''
  this.startTime =  localStorage.getItem('startTime') ? localStorage.getItem('startTime') : ''
  this.endTime =  localStorage.getItem('endTime') ? localStorage.getItem('endTime') : ''
  this.startUseTime =  localStorage.getItem('startUseTime') ? localStorage.getItem('startUseTime') : ''
  this.endUseTime =  localStorage.getItem('endUseTime') ? localStorage.getItem('endUseTime') : ''
  this.showPrice =  localStorage.getItem('showPrice')
  this.text = localStorage.getItem('text')? localStorage.getItem('text') : ''
  let value =  localStorage.getItem('value') ? localStorage.getItem('value')  : this.value
  if(typeof value == 'string'){
    this.value = eval(value.toLowerCase()) // 字符串转布尔值
  }
  this.doorAdd = localStorage.getItem('doorAdd') ? localStorage.getItem('doorAdd') : ''
  this.lat = localStorage.getItem('lat') 
  this.lng = localStorage.getItem('lng') 
  this.address = localStorage.getItem('address') ? localStorage.getItem('address') : ''
  this.prizeFullText = localStorage.getItem('prize') ? localStorage.getItem('prize') : ''
  this.attention = localStorage.getItem('attention') ? localStorage.getItem('attention') : []
  this.photosImg = localStorage.getItem('photosImg') ? localStorage.getItem('photosImg') : []
  this.activityGroupIds = localStorage.getItem('activityGroupIds') ? localStorage.getItem('activityGroupIds') : '';
  this.groupId = localStorage.getItem('groupId') ? localStorage.getItem('groupId') : '';
  this.selectGroupId = this.groupId
  
  this.imgUrlPoster = localStorage.getItem('poster') ? localStorage.getItem('poster') : '';
  this.sysPrice = localStorage.getItem('sysPrice') ? localStorage.getItem('sysPrice') : 0;
  this.totalCanBuy = localStorage.getItem('totalCanBuy') ? localStorage.getItem('totalCanBuy') : '';
  this.type = localStorage.getItem('sellType') ? parseInt(localStorage.getItem('sellType')) : 1;
  this.resellPrice = localStorage.getItem('resellPrice') ? localStorage.getItem('resellPrice') : 0;
  this.resellerText = localStorage.getItem('resellerText') ? localStorage.getItem('resellerText') : '';
  this.resellPriceValue = this.resellPrice
  this.resellerTextValue = this.resellerText

  this.imgUrlResell = localStorage.getItem('imgUrlResell') ? localStorage.getItem('imgUrlResell') : '';
  this.resellValue = localStorage.getItem('resellValue') === 'true' ?  true : false;
  this.showResell = localStorage.getItem('showResell') === 'true' ?  true : false;
  this.resellImg = localStorage.getItem('resellImg') === 'true' ?  true : false;
  this.firstOrderPrice = localStorage.getItem('firstOrderPrice') ? localStorage.getItem('firstOrderPrice') : '';
  this.seleceId = localStorage.getItem('seleceId') ? localStorage.getItem('seleceId') : '';
  this.groupIdText = localStorage.getItem('groupIdText') ? localStorage.getItem('groupIdText') : '';
  this.groupIdSeleceText = localStorage.getItem('groupIdSeleceText') ? localStorage.getItem('groupIdSeleceText') : '';
  this.merchantCodeValue = localStorage.getItem('merchantCodeValue') ===  'true' ? true : false
  this.merchantCodeText = localStorage.getItem('merchantCodeText') ? localStorage.getItem('merchantCodeText') : ''
  this.title = localStorage.getItem('title') ? localStorage.getItem('title') : ''
  this.index = localStorage.getItem('index') ? localStorage.getItem('index') : ''
  this.activityType = localStorage.getItem('activityType') ? localStorage.getItem('activityType') : ''
  this.$nextTick(()=>{
    this.moduleGetPrice = localStorage.getItem('moduleGetPrice') ? localStorage.getItem('moduleGetPrice') : 0
  })  

  

}
Vue.prototype.clearStorage = function(){
  localStorage.removeItem('id')
  localStorage.removeItem('imgUrlSquare')
  localStorage.removeItem('imgUrl')
  localStorage.removeItem('name')
  localStorage.removeItem('price')
  localStorage.removeItem('phoneNum')
  localStorage.removeItem('totalNum')
  localStorage.removeItem('startTime')
  localStorage.removeItem('endTime')
  localStorage.removeItem('startUseTime')
  localStorage.removeItem('endUseTime')
  localStorage.removeItem('showPrice')
  localStorage.removeItem('text')
  localStorage.removeItem('value')
  localStorage.removeItem('doorAdd')
  localStorage.removeItem('address')
  localStorage.removeItem('lat')
  localStorage.removeItem('lng')
  localStorage.removeItem('prize')
  localStorage.removeItem('paramsId')
  localStorage.removeItem('photosImg')
  localStorage.removeItem('modId')
  localStorage.removeItem('index')
  localStorage.removeItem('attention')
  localStorage.removeItem('enterMess')
  localStorage.removeItem('group')
  localStorage.removeItem('activityGroupIds')
  localStorage.removeItem('groupId')
  localStorage.removeItem('title')
  localStorage.removeItem('addScrollTop')

  localStorage.removeItem('poster')
  localStorage.removeItem('sysPrice')
  localStorage.removeItem('totalCanBuy')
  localStorage.removeItem('sellType')
  localStorage.removeItem('resellPrice')
  localStorage.removeItem('resellerText')
  localStorage.removeItem('imgUrlResell')
  localStorage.removeItem('showResell')
  localStorage.removeItem('resellValue')
  localStorage.removeItem('resellImg')
  localStorage.removeItem('firstOrderPrice')

  localStorage.removeItem('seleceId')
  localStorage.removeItem('groupIdText')
  localStorage.removeItem('groupIdSeleceText')
  localStorage.removeItem('merchantCodeValue')
  localStorage.removeItem('merchantCodeText')
  localStorage.removeItem('activityType')
  localStorage.removeItem('moduleGetPrice')

}
Vue.prototype.setDate = function(date){
  let startDate = new Date(date);
  let Y = startDate.getFullYear();
  let M = (startDate.getMonth() + 1) < 10 ? '0' + (startDate.getMonth() + 1) : (startDate.getMonth() + 1);
  let D = startDate.getDate() < 10 ? '0' +startDate.getDate() : startDate.getDate();
  let h = startDate.getHours() < 10 ? '0' + startDate.getHours() : startDate.getHours();
  let m = startDate.getMinutes() < 10 ? '0' + startDate.getMinutes() : startDate.getMinutes();
  let s = startDate.getSeconds() < 10 ? '0' + startDate.getSeconds() : startDate.getSeconds();
  return `${Y}-${M}-${D} ${h}:${m}:${s}`;
}
Vue.prototype.upload = function(){
  Indicator.open({
    text: '正在上传...',
    spinnerType: 'fading-circle'
  })
}
Vue.prototype.request = function(){
  Indicator.open({
    text: '请求中...',
    spinnerType: 'fading-circle'
  })
}
Vue.prototype.delActivity = function(){
  Indicator.open({
    text: '正在删除...',
    spinnerType: 'fading-circle'
  })
}
Vue.prototype.loading = function(){
  Indicator.open({
    text: '加载中...',
    spinnerType: 'fading-circle'
  })
}
Vue.prototype.closeUpload = function(){
  Indicator.close();
}
// 活动添加修改滚动高度保持
Vue.prototype.setAddScrollTop = function(){
  let vm = this
  window.addEventListener('scroll', function(){
    let top = document.documentElement.scrollTop || document.body.scrollTop;
    vm.addScrollTop = top
  }, true)
  document.documentElement.scrollTop = localStorage.getItem('addScrollTop')
  document.body.scrollTop = localStorage.getItem('addScrollTop')
}



