<template>
  <div class="outMoeny_audit">
    <div class="header">
      <span class="back" @click="$router.goback()"><img src="../../../assets/my/back.png" alt=""  ></span>
      <span>提现审批</span>
    </div>
    <div class="outMoeny_info">
      <p>提现金额 <span>￥{{info.realOutNum/100}}</span></p>
      <p>申请时间 <span>{{ info.createdAt }}</span></p>
      <p>提现方式 <span v-if="info.moneyOutType == 101">微信</span> <span v-if="info.moneyOutType == 201">支付宝</span> <span v-if="info.moneyOutType == 1">银行卡</span></p>
      <p>提现单号<span>{{ info.partnerTradeNo }}</span></p>
      <!-- <span>提现单号<span>{{ info.partnerTradeNo }}</span></p> -->
      <img src="../../../assets/manage/pass.png" alt="" v-if="info.state==101">
      <img src="../../../assets/manage/reject.png" alt="" v-if="info.state==201"> 
    </div>
    <div class="outMoeny_pass" v-if="info.state == 1" @click="pass">审批通过</div>
    <div class="outMoeny_reject" v-if="info.state == 1" @click="reject">驳回申请</div>
  </div>
</template>
<script>
export default {
  data(){
    return{
      info:{}
    }
  },
  methods:{
    pass(){
      const url = this.apiList.admin.dealMoneyOut
      console.log(url)
      if(this.info.num /100 < 0.1){
        this.$toast({
          message: '提现金额要大于1毛',
        })
        return
      }
      const approvalId = this.info.id
      console.log(approvalId)
      this.$http({
        url: this.$http.adornUrl(url),
        method: "post",
        data: this.$http.adornData({ approvalId, agree: true , personOut :false}, url, true)
      }).then(({ data:res })=>{
        if(res.result){
          this.$toast({
            message: '提现成功！',
          })
          setTimeout(()=>{
            this.$router.goback()
          },1000)
        }else{
          this.$toast(res.msg)
        }
      })
    },
    reject(){
      this.$router.push('manage_reject')
    }
  },
  created(){
    this.info =JSON.parse(localStorage.getItem('outMoenyInfo')) 
    console.log(this.info)
  }
}
</script>
<style lang="scss">
.outMoeny_audit{
  min-height: 100%;
  background-color: #F5F5F5;
  .outMoeny_info{
    width: 34.3rem;
    // height: 18.2rem;
    background-color: #fff;
    margin: 0 auto;
    margin-top: 1.5rem;
    overflow: hidden;
    padding-bottom: 1.5rem;
    position: relative;
    >img{
      position: absolute;
      bottom: 0;
      right: 1rem;
      width: 8.6rem;
    }
    p{
      margin-top: 1.5rem;
      padding-left: 2.6rem;
      font-size: 1.3rem;
      color: #999999;
      span{
        margin-left: 3.5rem;
        color: #191919;
      }
    }
    p:nth-of-type(1){
      margin-top: 3.2rem;
    }
  }
  .outMoeny_pass{
    width: 33rem;
    height:4.3rem;
    background:rgba(246,69,66,1);
    border-radius:2.15rem;
    font-size: 1.6rem;
    text-align: center;
    line-height: 4.3rem;
    color: #FFFFFF;
    margin: 0 auto;
    margin-top: 7.25rem;
  }
  .outMoeny_reject{
    width: 33rem;
    height:4.3rem;
    background:rgba(255,255,255,0);
    border-radius:2.15rem;
    border:1px solid rgba(246, 69, 66, 1);
    font-size: 1.6rem;
    text-align: center;
    line-height: 4.3rem;
    color:rgba(246,69,66,1);
    margin: 0 auto;
    margin-top: 2.7rem;
  }
}
</style>




