import Vue from 'vue'
import axios from 'axios'
import qs from 'qs'
import sha256 from 'sha256'
import merge from 'lodash/merge'
const http = axios.create({
  timeout: 1000 * 60,
  headers: {
    'Content-type': 'application/x-www-form-urlencoded'
  }
})

/**
 * 请求拦截
 */
http.interceptors.request.use(config => {
  // debugger
  if (config.resTimeout) {
    config.timeout = config.resTimeout
  } else {
    config.timeout = 1000 * 60
  }
  return config
}, error => {
  return Promise.reject(error)
})

/**
 * 响应拦截
 */
http.interceptors.response.use(response => {
  if (response.data && response.data.code === 401) { // 401, token失效
    // clearLoginInfo()
    router.push({ name: 'login' })
  }
  return response
}, error => {
  return Promise.reject(error)
})

/**
 * 请求地址处理
 * @param {*} actionName action方法名称
 */
http.adornUrl = (actionName) => {
  // 自动拼接url
  return 'http://j.chnzxl.com/entrya' + actionName
}

/**
 * post请求数据处理
 * @param {*} data 数据对象
 * @param {*} openDefultdata 是否开启默认数据?
 * @param {*} contentType 数据格式
 *  json: 'application/json; charset=utf-8'
 *  form: 'application/x-www-form-urlencoded; charset=utf-8'
 */
http.adornData = (data = {}, actionName, openDefultdata = true, contentType = 'json') => {
  let moduleId = localStorage.getItem('moduleId')
  if (data.moduleId) {
    moduleId = data.moduleId
  }
  var defaults = {
    't': new Date().getTime(),
    'sign': sha256(actionName + localStorage.getItem('token')),
    'userId': localStorage.getItem('uid'),
    'moduleId': moduleId
  }
  data = openDefultdata ? merge(defaults, data) : data
  if (openDefultdata) {
    if (!data.moduleId) {
      data.moduleId = localStorage.getItem('moduleId')
    }
  }
  console.log(data)
  return qs.stringify(data)
}

export default http



