<template>
  <div class="refund_indexChild">
    <div class="indexChild_refund_main">
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
        <van-list class="ul"
          v-model="loading" 
          :finished="finished" 
          finished-text="—— 没有更多了 ——"
          @load="onLoad"
          ref="listHeight"
        >
          <van-cell v-for="(item, index) in dataList" :key="index" >
            <p class="orderNum">
              <span>订单号：</span><span>{{item.outTradeNo}}</span>
              <span class="state">{{ item.state == 101 ? '已退款' : (item.state == 201 ? '已拒绝' : '待审核')}}</span>
            </p>
            <p class="index_refund_userInfo">
              <img :src="item.userAvatar ? item.userAvatar : require('../../assets/my/userAvatar.png')" alt="">
              <span>{{item.userNickName ? item.userNickName : '众享乐用户'}}</span>
            </p>
            <p class="price">
              <span>金额：</span><span>￥{{ (item.num / 100).toFixed(2) }}</span>
            </p>
            <p class="refundTime">
              <span>退款申请时间：</span><span>{{ item.createdAt }}</span>
            </p>
            <p class="describe">
              <span>描述：</span><span>{{item.logDesc}}</span>
            </p> 
            <p class="btn" v-if="item.state === 1">
              <mt-button @click="refuse(false, item.id, index)">拒绝</mt-button><mt-button @click="order(true, item.id,index)">同意</mt-button>
            </p>
          </van-cell>
        </van-list>
      </van-pull-refresh>
    </div>
  </div>
</template>

<script>
import { MessageBox } from 'mint-ui'
  export default {
    props:['show'],
    data(){
      return{
        refuseRemark: '',
        id:'',
        page: 0,
        state: '',
        finished: false,
        loading: false,
        showRefuse: false,
        isLoading: false,
        dataList:[],
        list:[],
      }
    },
    methods:{
      goBack(){
        this.$router.goback()
      },
      onLoad(){
        return new Promise((resolve, reject)=>{
          this.page++;
          const url = this.apiList.refund.list
          if(this.show === 0){
            this.state = '';
          }else if(this.show === 1){
            this.state = 1;
          }else if(this.show === 2){
            this.state = 201;
          }else{
            this.state = 101;
          }
          this.$http({
            url: this.$http.adornUrl(url),
            method: 'post',
            data: this.$http.adornData({page: this.page, size: 10, state: this.state}, url, true)
          }).then(({data})=>{
            if(data.result){
              if (data.data.records.length >= 0) {
                
                this.dataList = this.dataList.concat(data.data.records)
              }
              if(data.data.pages <= this.page){
                this.finished = true;
              }
              this.$nextTick(() => {
                resolve(true)
                this.loading = false
              })
            }else if( data.msg == 'sign验证失败' ){
              this.$toast('您的账户登录超时')
              setTimeout(()=>{
                localStorage.clear();
                this.$router.push({name: 'login'})
              },1000)
          }
          })
        })
      },
      onRefresh(){
        return new Promise((resolve, reject) =>{
          const url = this.apiList.refund.list
          if(this.show === 0){
            this.state = ''
          }else if(this.show === 1){
            this.state = 1;
          }else if(this.show === 2){
            this.state = 201;
          }else{
            this.state = 101;
          }
          this.$http({
            url: this.$http.adornUrl(url),
            method: 'post',
            data: this.$http.adornData({page: 1, size: 10, state: this.state}, url, true)
          }).then(({data})=>{
            if (data.result) {
              if (data.data.records.length >= 0) {
                this.dataList = data.data.records
              }
              this.$nextTick(() => {
                this.$toast('刷新成功');
                this.isLoading = false;
                // if(data.data.pages > this.page){
                //    this.finished = false
                // }
                // this.finished = false;
                this.page = 1;
                resolve(true)
              })
            }else if( data.msg == 'sign验证失败' ){
              this.$toast('您的账户登录超时')
              setTimeout(()=>{
                localStorage.clear();
                this.$router.push({name: 'login'})
              },1000)
            }
          })   
        })
      },
      goDetails(){
        this.$router.push('refund_details')
      },
      order(flag, id, index){
        if(!this.isAuth('/moneyApproval/dealOrderRefund')) return this.$toast('权限不足，请联系管理员修改')
        MessageBox.confirm('确定退款？').then(()=>{
          const url = this.apiList.refund.apply
          this.upload()
          this.$http({
            url: this.$http.adornUrl(url),
            method: 'post',
            data: this.$http.adornData({approvalId: id, agree: flag  }, url, true)
          }).then(({data})=>{
            this.closeUpload()
            if(data.result){
              this.$toast('退款成功，款项将原路返回')
              this.dataList.splice(index,1)
            }
          })
        })
        
      },
      refuse(e,id,index){
        this.$emit('refuse',e,id,index)
      }
    },
    mounted(){
      // alert(22)
      this.$refs.listHeight.$el.style.minHeight = document.documentElement.clientHeight - 12.2 * 10 + 'px'
    },
    created(){
      // this.getData()
      // alert(this.show)
    }
  }
</script>
<style lang="scss">
  .refund_indexChild{
    height: 100%;
    background: #f5f5f5;
    
    .indexChild_refund_main{
      height: 100%;
      margin-top: 1.4rem;
      padding-bottom: 1.2rem;
      background: #f5f5f5;
      .van-tabs{
        height: 100%;
      }
      .van-list{
        height: 100%;
        padding: 0 1.6rem;
        .van-cell{
          height: 100%;
          background: #fff;
          padding: 1rem 1.6rem;
          box-sizing: border-box;
          margin-bottom: 1.2rem;
          border-radius: 0.8rem;
          p{
            padding: 0.2rem 0;
          }
          .orderNum{
            color: #333;
            font-size: 1.4rem;
          }
          .state{
            float: right;
            color: red;
          }
          .index_refund_userInfo{
            img{
              width: 2rem;
              height: 2rem;
              border-radius: 50%;
              vertical-align: middle;
              margin-top: -0.2rem;
            }
            span{
              color: #666;
              font-size: 1.1rem;
              margin-top: 0.2rem;
            }
          }
          .price{
            span{
              color: #666;
              font-size: 1.4rem;
            }
          }
          .refundTime, .describe{
            span{
              color: #666;
              font-size: 1.3rem;
            }
          }
          .btn{
            display: flex;
            justify-content:flex-end;
            align-items: center;
            button{
              min-width: 5.3rem;
              height: 2.7rem;
              outline: none;
              background-color: none;
              border-radius: 1.35rem;
              border: 0 solid #666;
              color: #333;
              background-color: #fff;
              margin-right: .9rem;
              font-size: 1rem;
            }
          }
        }
      }
    }
    
   
  }
  .toast{
    z-index: 99999;
  }
</style>


