<template>
  <div class="issue">
     <div class="header">
      <span class="back" @click="goBack"><img src="../../assets/my/back.png" alt=""  ></span>
      <span class="headText">{{headerTitle}}</span>
    </div>
    <div class="main">
      <div class="title">
        <input type="text" placeholder="请输入你的标题" class="iptTitle" maxlength="15" v-model="issueTitle"/>
        <div class="page">{{issueTitle.length}}/15</div>
      </div>
      <div class="content">
        <textarea placeholder="请输入你的内容..." class="iptContent" v-model="issueContent"></textarea>
        <div class="contentImg">
          <ul>
            <li v-for="(item, index) in imgList" :key="index">
              <img :src="item"  preview='1' preview-text="预览效果" alt="">
              <i @click="close(index)"></i>
            </li>
            <!-- <input type="file" ref='file' @change="change"> -->
            <div class="getImg" @click="getImg" v-if="imgList.length < 3">
              <img src="../../assets/community/addImg.png" alt="">
            </div>
            <!-- <Upload v-if="imgList.length < 3" :width="330" :height="220" class="avatar-uploader" @fileChange="fileChange">
              <i class="avatar-uploader-icon" @click="add"></i>
            </Upload> -->
          </ul>
        </div>
      </div>

    </div>
    <mt-button class="btn" @click="getAddComment()">发布</mt-button>
  </div>
</template>

<script>
import Upload from '../utils/upload.vue'
import { MessageBox } from 'mint-ui'
import sha256 from 'sha256'
import { Indicator } from 'mint-ui'
export default {
  data() {
    return {
      headerTitle: '',
      title: '',
      issueTitle: '',
      content: '',
      moduleId: '',
      issueContent: '',
      imgList: [],
      flag: false
    };
  },

  methods: {
    goBack() {
      this.$router.goback()
    },
     getImg() {
      // 注册获得图片成功的方法,每次调用都刷新，防止重复相应
      const vm = this
      window.getImg_upload = function(res) {
          // 隐藏基座loading
        vm.android.progressHUDend()
        vm.upload(vm.dataURLtoFile('data:image/png;base64,' + res.image))
      }
      this.android.getImg('getImg_upload')
      
    },
    dataURLtoFile(dataurl, filename = '0000img') {//将base64转换为文件
      var arr = dataurl.split(','), mime = arr[0].match(/:(.*?);/)[1],
          bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
      while(n--){
          u8arr[n] = bstr.charCodeAt(n);
      }
      return new File([u8arr], filename, {type:mime});
    },
    upload(file) {
      const formData = new FormData()
      
      // const file = this.dataURLtoFile(this.modelSrc, this.filesName)
      formData.append('file', file)
      formData.append('userId', localStorage.getItem('uid'))
      formData.append('sign', sha256(this.apiList.upload + localStorage.getItem('token')))
      
      // 显示loading
      Indicator.open({
        text: '上传中....',
        spinnerType: 'fading-circle'
      })
      this.$http({
        url: this.$http.adornUrl(this.apiList.upload),
        method: 'post',
        headers: {
          'Content-Type': 'multipart/form-data'
        },
        data: formData
      }).then(({ data }) => {
        Indicator.close()
        this.popupVisible = false
        if (data.result) {
          this.imgList.push(data.data)
          this.$previewRefresh();
        } else {
          this.MessageBox('提示', data.msg)
        }
      }).catch(() => {
      })
    },
    getAddComment() {
      if(this.flag) return
      this.content = this.issueContent
      this.title = this.issueTitle
      if(!this.title) return this.$toast('标题不能为空')
      if(!this.content) return this.$toast('内容不能为空')
      
      this.flag = true;
      if(this.$route.query.id){
        Indicator.open({
          text: '保存中...',
          spinnerType: 'fading-circle'
        })
        const url = this.apiList.community.mod
        this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData(
            {
              commentId: this.$route.query.id,
              content: this.content,
              moduleId: this.moduleId,
              title: this.title,
              imgUrls: JSON.stringify(this.imgList)
            }, url, true)
        }).then(({ data }) => {
          Indicator.close()
          if( data.result){
            this.$toast({
              message: '修改成功',
              iconClass: 'fa fa-check fa-5x'
            })
            let dataList = JSON.parse(localStorage.getItem('getDataList'))
            let index = this.$route.query.index
            dataList[index].title = this.title;
            dataList[index].content = this.content;
            dataList[index].imgUrls = this.imgList;  
            localStorage.setItem('getDataList', JSON.stringify(dataList))         
            setTimeout(()=>{
              this.goBack()
            },100)
            
          }
        })
      }else{
        const url = this.apiList.community.add
        Indicator.open({
          text: '正在提交...',
          spinnerType: 'fading-circle'
        })
        this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData(
            {
              content: this.content,
              moduleId: this.moduleId,
              title: this.title,
              imgUrls: JSON.stringify(this.imgList)
            }, url, true)
        }).then(({ data }) => {
          Indicator.close()
          if( data.result){
             this.$toast({
              message: '发布成功',
              iconClass: 'fa fa-check fa-5x'
            })
            localStorage.removeItem('getDataList')
            setTimeout(()=>{
                this.goBack()
            },100)
          }
        })
      }
      
    },
    // fileChange(img){
    //   this.imgList.push(img)
      
    // },
    close(index){
      // MessageBox.confirm('确定删除图片？').then(()=>{
        this.imgList.splice(index, 1)
      // })
    }

      // publish(id) {
      //   this.$router.push({ name: 'postBar', query: { id: id } })
      // }
  },
  components:{
    Upload
  },
  created() {
    if(this.$route.query.id){
      this.headerTitle = '修改帖子'
      //获取帖子
      const url = this.apiList.community.getById
      this.loading()
      this.$http({
        url: this.$http.adornUrl(url),
        method: "post",
        data: this.$http.adornData( {id: this.$route.query.id}, url, true)
      }).then(({ data }) => {
        Indicator.close()
        if(data.result){
          this.imgList = JSON.parse(data.data.imgUrls)
          this.issueTitle = data.data.title
          this.issueContent = data.data.content
        }
      });
    }else{
      this.headerTitle = '发布帖子'
    }
  }

};
</script>

<style lang="scss">
// .mint-toast.is-placemiddle{
//   padding: 2rem !important;
// }
.fa-5x{
  font-size: 0;
}
.fa-check:before{
  font-size: 3.2rem;
}
.issue {
  // background-color: #f5f5f5;
  position: absolute;
  top: 0;
  bottom: 0;
  .avatar-uploader{
    display: inline-block;
    width: 9.8rem;
    height: 9.8rem;
    margin-left: 1rem;
  }
  .main {
    color: #0D0D0D;
    padding: 0 1.75rem;
    .title {
      position: relative;
      height: 4.35rem;
      border-bottom: 0.1rem solid #D5D5D5;
      margin-bottom: 1.3rem;
      .iptTitle {
        height: 100%;
        // background: #f5f5f5;
        width: 30rem;
        font-size: 1.6rem;
        font-weight: 400;
        padding-left: .25rem;
        padding-right: 4.5rem;
        border: none;
      }
      .page {
        position: absolute;
        top: 0;
        right: 0;
        margin-top: 1.45rem;
        font-size: 1.6rem;
        font-weight: 400;
        color: #0D0D0D;
      }
    }
      .content {
        padding: 0 .25rem;
        
        .iptContent {
          // background: #f5f5f5;
            width: 100%;
            height: 10rem;
            font-size: 1.6rem;
            font-weight: 400;
            border: none;
            resize: none;
            line-height: 2.2rem;
        }
        .contentImg {
          margin-top: 0.9rem;
          ul{
            li{
              position: relative;
              margin-left: 1rem;
              display: inline-block;
              width: 9.8rem;
              height: 9.8rem;
              img{
                width: 100%;
                height: 100%;
              }
              i{
                display: inline-block;
                width: 1.8rem;
                height: 1.8rem;
                position: absolute;
                right: 0rem;
                top: 0rem;
                z-index: 7;
                background: url('../../assets/index/close.png');
                background-size: 100% 100%;
              }
            }
            .getImg{
              display: inline-block;
              margin-left: 1rem;
              margin-top: 1rem;
              img{
                width: 9.8rem;
              }
            }
          }
        }
      }
  }
  .btn {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    height: 4.9rem;
    background-color: #F64542;
    text-align: center;
    line-height: 3.8rem;
    font-size: 1.6rem;
    font-weight: 500;
    color: #fff;
    border-radius: 0;
    // margin: 27.75rem auto 2.5rem;
  }
}
</style>



