<template>
  <div class="sign_up_key">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../../../assets/my/back.png" alt=""  ></span>
      <span>信息填写管理</span>
      <span class="add">
        <img src="../../../../assets/head/add.png" @click="goAdd" v-if="isAuth('/enterNameKV/add')">
      </span>
    </div>
    <div class="main">
      <ul>
        <!--  -->
        <li v-for=" (item, index) in dataList" :key="item.id">
          <div class="left">
            <p>信息名称：{{ item.keyName }}</p>
            <span>类别：{{ item.type === '101' ? '任意类型' : (item.type === '201' ? '手机号码' : (item.type === '301' ? '时间' : '数字'))}}</span><span>是否必填：{{item.mustInput === true ? '是' : '否'}}</span>
          </div>
          <div class="right">
            <img src="../../../../assets/auth/edit.png" @click="goModify(index)">
            <img src="../../../../assets/auth/del.png" @click="del(index, item.id)">
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import { MessageBox } from 'mint-ui'
  export default {
    data(){
      return{
        title: '',
        dataList:[]
      }
    },
    methods: {
      goBack(){
        this.$router.goback()
      },
      // 跳转添加页面
      goAdd(){
        if(this.$route.query.type){
          this.$router.push({name:'key_add', query:{type: 301}})
        }else if(this.$route.query.id){
          this.$router.push({name:'key_add', query:{id: this.$route.query.id}})
        }else{
          this.$router.push('key_add')
        }
        
      },
      // 跳转修改页面
      goModify(index){
        if(this.$route.query.type){
          this.$router.push({name: 'key_add', query: {index: index}})
        }else{
          this.$router.push({name: 'key_add', params: {index: index}})
        }
        // if(localStorage.getItem('signUpInfo')){
        //   this.$router.push({name:'key_add', params:{ keyName: this.dataList[index].keyName, type: this.dataList[index].type, mustInput: this.dataList[index].mustInput, index: index }})
        // }else{
        //   this.$router.push({name:'key_add', params:{ keyName: this.dataList[index].keyName, type: this.dataList[index].type, mustInput: this.dataList[index].mustInput, id: this.dataList[index].id}})
        // }
      },
      getData(){
        let url = this.apiList.enterNameKV.list
        this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({enterNameId: this.$route.query.id}, url, true)
        }).then(({data})=>{
          if(data.result){
            this.dataList = data.data
          }else if(data.msg == 'sign验证失败'){
            this.$toast('您的账户登录超时')
            setTimeout(()=>{
              localStorage.clear();
              this.$router.push({ name:  'login' })
            },1000)
          }
        })
      
      },
      del(index, id){
       
        // let url = this.apiList.enterNameKV.del
        MessageBox.confirm('确认删除该项？').then(data=>{
          // if(localStorage.getItem('enterMess')){
            this.dataList.splice(index, 1)
            this.$toast('删除成功')
            localStorage.setItem('enterMess', JSON.stringify(this.dataList))
          // }
          // this.$http({
          //   url: this.$http.adornUrl(url),
          //   method: 'post',
          //   data: this.$http.adornData({id: id}, url, true)
          // }).then(({data})=>{
          //   if(data.result){
          //     this.dataList.splice(index, 1)
          //     this.$toast('删除成功')
          //   }
          // })
        })
      }
    },
    created(){
      // if(this.$route.query.type == 301){
      //   this.title = '促销信息管理'
      // }else{
      //   this.title = '报名信息管理'
      // }
      if(localStorage.getItem('enterMess')){
        this.dataList = JSON.parse(localStorage.getItem('enterMess'))
      }else{
        this.getData()
      }
    }
  }
</script>
<style lang="scss" scoped>
  .sign_up_key{
    min-height: 100%;
    background-color: #F5F5F5;
    .add{
      float: right;
      margin-top: -0.2rem;
    }
    .main{
      box-sizing: border-box;
      margin-top: 1rem;
      ul{
        background-color: #fff;
        li{
          display: flex;
          justify-content: space-between;
          align-items: center;
          height: 6rem;
          padding-right: 1.6rem;
          margin-left: 1.6rem;
          border-bottom: 0.1rem solid #eee;
          &:last-child{
            border: none;
          }
          .left{
            p{
              color: #333;
              font-size: 1.6rem;
            }
            span{
              color: #999;
              font-size: 1.1rem;
              &:last-child{
                margin-left: 2rem;
              }
            }
          }
          .right{
            img{
              width: 1.4rem;
              height: 1.4rem;
              &:first-child{
                border-right: 0.1rem solid #ddd;
                margin-right: 1rem;
                padding-right: 1.2rem;
              }
            }
          }
        }
      }
    }
  }
</style>



