<template>
  <div class="promotion">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../../assets/my/back.png" alt=""></span>
      <span class="headText">促销管理</span>
      <span class="add_Pro" >
        <img class="search_btn" @click="goSearch"  alt="" src="../../../assets/head/search.png">
        <img class="add" src="../../../assets/head/add.png"  alt="" @click="goAddPromotion" v-if="isAuth(apiList.template.find)">
      </span>     
    </div>
    <div class="paomotion_main">
      <van-tabs v-model="active"  @change="change" :sticky="true" swipeable animated color="#fff" :offset-top='48.5' ref="sticky" >
        <van-tab title="全部" >
          <index-child  :shows="0"  ref="child1"></index-child>
        </van-tab>
        <van-tab title="已上架">
          <index-child  :shows="1"  ref="child2"></index-child>
        </van-tab>
        <van-tab title="未上架">
          <index-child  :shows="2"  ref="child3"></index-child>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>
  <script>
  import indexChild from './indexChild.vue'
  export default {
    data(){
      return{
        scrollTop0: '',
        active: 0
      }
    },
    methods:{
      goBack(){
        this.$router.goback()
        localStorage.removeItem('dataList')
        localStorage.removeItem('pageNum')
        localStorage.removeItem('dataList2')
        localStorage.removeItem('pageNum2')
        localStorage.removeItem('dataList3')
        localStorage.removeItem('pageNum3')
        localStorage.removeItem('scrollTop0')
        localStorage.removeItem('scrollTop1')
        localStorage.removeItem('scrollTop2')
        localStorage.removeItem('activeNum')
      },
      change(){
        this.setScrollTop()
        localStorage.setItem('activeNum', this.active)
        if(this.active === 0 && this.$refs.child1){
          this.$refs.child1.change()
        }else if(this.active === 1 && this.$refs.child2){
          this.$refs.child2.change()
        }else if(this.active === 2 && this.$refs.child3){
          this.$refs.child3.change()
        }
      },
      goSearch(){
        
        this.setScrollTop()
        const url = JSON.stringify({list: 'pmSale', details: 'activity_details', buyingIns:'pro_buyingIns', mod: 'promotion_modify'})
        localStorage.setItem('searchVal', url)
        this.$router.push( {name: 'activity_search'})
      },
      goAddPromotion(){
        localStorage.removeItem('dataList')
        localStorage.removeItem('dataList')
        localStorage.removeItem('pageNum')
        localStorage.removeItem('dataList2')
        localStorage.removeItem('pageNum2')
        localStorage.removeItem('dataList3')
        localStorage.removeItem('pageNum3')
        localStorage.removeItem('scrollTop0')
        localStorage.removeItem('scrollTop1')
        localStorage.removeItem('scrollTop2')
        localStorage.removeItem('activeNum')
        // this.$router.push('promotion_temp')
        
        this.$router.push({ name: 'activity_common_template', query: {type: '301'} })

      },
      setScrollTop(){
        var scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
        let num = localStorage.getItem('activeNum')
        if(num == 0){
          localStorage.setItem('scrollTop0', scrollTop)
        }else if(num == 1){
          localStorage.setItem('scrollTop1', scrollTop)
        }else if(num == 2){
          localStorage.setItem('scrollTop2', scrollTop)
        }
      }
    },
    components:{
      indexChild,
    },
    created(){
      
      if(localStorage.getItem('activeNum')){
        this.active = Number(localStorage.getItem('activeNum'))
      }else{
        localStorage.setItem('activeNum', this.active)
      }
      
      
    },
    destoryed() {
      window.removeEventListener(
        "scroll",
        document.body.scrollTop ||
          document.documentElement.scrollTop ||
          window.pageXOffset
      );
    },
  }
</script>
<style lang="scss">
  .promotion{
    min-height: 100%;
    background-color: #F5F5F5;
    .van-tabs__wrap{
      margin-top: -0.8rem;
    }
    .headText{
      position: absolute;
      left: 50%;
      transform: translateX(-57%);
    }
    .header{
      .add_Pro{
        float: right;
        .search_btn{
          width: 1.8rem;
          height: 1.9rem;
          margin-right: 1.2rem;
          vertical-align: top;
          margin-top: 1rem;
        }
        .add{
          width: 1.5rem;
          vertical-align: top;
          margin-top: 1rem;
        }
      }
    }
  }
</style>


