<template>
  <div class="leaveMessage">
    <div class="header">
      <span>留言</span>
    </div>
    <div class="main">
      <van-tabs v-model="active"  @change="change" :sticky="true" swipeable animated color="#fff" :offset-top="60">
        <van-tab title="未回复" >
          <index-child></index-child>
        </van-tab>
        <van-tab title="全部" >
          <index-child></index-child>
        </van-tab>
      </van-tabs>
    </div>
    
  </div>
</template>

<script>
import indexChild from './indexChild'
  export default {
    data(){
      return{
        active: 0,
      }
    },
    methods: {
      change(){

      }
    },
    components:{
      indexChild
    }
  }
</script>
<style lang="scss">
  .leaveMessage{
    min-height: 100%;
    background: #f5f5f5;
    .main{
      margin-top: 1.4rem;
      .van-tabs__wrap{
        margin-top: -2rem;
      }
    }
  }
</style>


