<template>
<div class="photoshow">
  <div class="header">
    <span class="back" @click="goback"><img src="../../assets/my/back.png" alt=""  ></span>
    <span>店铺资质</span>
  </div>
  <div class="mianInfo">
    <ul>
      <li v-for="(item, index) in dataList" :key="item.id"><img :src="item.imgUrl + '!440w'" preview="1" preview-text="店铺资质"/><i class="close" @click="delData(index)"></i></li>
      <upload v-if="dataList.length < 8" @fileChange="fileChange" class="avatar-uploader" :width="300" :height="300">
        <i class="avatar-uploader-icon"></i>
      </upload>
    </ul>
  </div>
</div>
</template>
<script>
import Upload from '../utils/upload.vue'
import { Toast } from 'mint-ui'
import { MessageBox } from 'mint-ui'
import { Indicator } from 'mint-ui'
export default {
  data() {
    return{
      dataList: []
    }
  },
  components: {
    Upload
  },
  methods: {
    getDataList() {
      this.shoeLoding()
      this.$http({
        url: this.$http.adornUrl(this.apiList.photoShow.list),
        method: 'post',
        data: this.$http.adornData( '' , this.apiList.photoShow.list, true)
      }).then(({ data }) => {
        console.log(data);
        this.hideLoding()
        if (data.result) {
          this.dataList = data.data
          this.$nextTick(() => {
            this.$previewRefresh()
          })
        } else {
          if (data.msg === 'sign验证失败') {
            localStorage.clear()
            const instance = Toast({
              message: '您的账号登录超时',
              iconClass: 'icon icon-error'
            })
            setTimeout(() => {
              instance.close()
              this.$router.push({ name:  'login' })
            }, 2000)
          }
        }
      })
    },
    shoeLoding() {
      Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })
    },
    hideLoding() {
      Indicator.close()
    },
    goback() {
      this.$router.goback()
    },
    fileChange(imgurl) {
      if(!this.isAuth('/shopPhotoShow/add')) return this.$toast('权限不足，请联系管理员修改')
      this.$http({
        url: this.$http.adornUrl(this.apiList.photoShow.add),
        method: 'post',
        data: this.$http.adornData( { url: imgurl } , this.apiList.photoShow.add, true)
      }).then(({ data }) => {
        if(data.result) {
          Toast({
            message: '操作成功',
            iconClass: 'icon icon-success'
          })
          setTimeout(() => {
            this.getDataList()
          }, 600);
        }
      })
    },
    // 删除数据
    delData(index) {
      if(!this.isAuth('/shopPhotoShow/del')) return this.$toast('权限不足，请联系管理员修改')
      this.sheetVisible = false
      MessageBox.confirm('确定删除当前图片?').then(action => {
        this.$http({
          url: this.$http.adornUrl(this.apiList.photoShow.del),
          method: 'post',
          data: this.$http.adornData({id: this.dataList[index].id }, this.apiList.photoShow.del, true)
        }).then(({ data }) => {
          if(data.result) {
            Toast({
              message: '操作成功',
              iconClass: 'icon icon-success'
            })
            this.dataList.splice(index, 1)
          } else {
            Toast({
              message: data.msg,
              iconClass: 'icon icon-error'
            })
          }
        })
      })
    }
  },
  created() {
    const vm = this
    this.getDataList()
    this.getRole()
    console.log(this.isAuth('/coupon/del'), 1);
  }
}
</script>
<style lang="scss">
.photoshow{
  position: absolute;
  top: 0;
  bottom: 0;
  background: #fff;
  width: 100%;
  .mint-popup{
    background: transparent; 
  }
  .mianInfo{
    box-sizing: border-box;
    .avatar-uploader{
      display: inline-block;
      width: 9rem;
      height: 9rem;
      margin: 1.6rem;
    }
    ul{
      li{
        display: inline-block;
        width: 9rem;
        height: 9rem;
        line-height: 9rem;
        vertical-align: top;
        margin: 1.6rem;
        position: relative;
        i{
          display: inline-block;
          width: 1.8rem;
          height: 1.8rem;
          position: absolute;
          right: 0rem;
          top: 0rem;
          z-index: 7;
          background: url('../../assets/index/close.png');
          background-size: 100% 100%;
        }
        img{
          width: 100%;
          // height: 100%;
          vertical-align: middle;
        }
      }
    }
  }
  .swiperContainer{
    width: 34.3rem;
    height: 30rem;
    margin: 0 auto;
  }
}
</style>


