<template>
  <div class="add_template_BI">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../../../assets/my/back.png" alt=""  ></span>
      <span>购买须知</span>
    </div>
    <div class="main">
      <div class="name">
        <span>事项</span><input type="text" placeholder="请输入事项名称" v-model="noteKey">
      </div>
      <div class="content">
        <span>详情</span><textarea  placeholder="请输入详情" ref="textarea" @input="handleInput" v-model="noteValue"></textarea><img @click="clear" src="../../../../assets/index/close.png" alt="">
      </div>
    </div>
    <mt-button @click="addData">保存</mt-button>
  </div>
</template>
<script>
export default {
  data(){
    return{
      noteKey: '',
      noteValue: '',
      inputHeight: '',
      dataList: [],
      flag: false
    }
  },
  methods:{
    goBack(){
      this.$router.goback()
    },
    addData(){
      if(this.flag) return
      if(!this.noteKey || !this.noteValue) return this.$toast('内容不能为空')
      let id = this.$route.query.id
      if(id === 0 || id){
        this.dataList[id].noteKey = this.noteKey
        this.dataList[id].noteValue = this.noteValue
      }else{  
        this.dataList.push({noteKey: this.noteKey, noteValue: this.noteValue})
      }
      localStorage.setItem('attention', JSON.stringify(this.dataList) )
      this.flag = true
      setTimeout(()=>{
        this.$toast('已保存')
        this.goBack()
      },500)
    },
    handleInput(e){
      e.target.style.height = e.target.scrollHeight + 'px';
    },
    clear(){
      this.noteValue = '';
      this.$refs.textarea.style.height = this.inputHeight + 'px';
    }
  },
  mounted(){
    this.inputHeight = this.$refs.textarea.scrollHeight
  },
  created(){
    let data =  localStorage.getItem('attention')
    if(data){
      this.dataList = JSON.parse(data)
    }
    let id = this.$route.query.id
    if( id === 0 || id){
      this.noteKey = this.dataList[id].noteKey
      this.noteValue = this.dataList[id].noteValue
    }
  }
}
</script>
<style lang="scss">
  .add_template_BI{
    min-height: 100%;
    background: #f5f5f5;
    .main{
      background: #fff;
      span{
        color: #333;
        font-size: 1.4rem;
        margin-left: 1rem;
      }
      .name{
        padding: 1.75rem 1.6rem;
        border-bottom: 0.1rem solid #eee;
        
        input{
          outline: none;
          border: none;
          margin-left: 4.35rem;
          width: 23.3rem;
        }
      }
      .content{
        padding: 1.75rem 1.6rem;
        display: flex;
        justify-content: space-between;
        // align-items: center;
        span{
          vertical-align: top;
        }
        textarea{
          width: 23.3rem;
          height: 2rem;
          margin-left: 3rem;
          border: none; 
          resize: none;
          // overflow-y:visible;
        }
        img{
          width: 1.2rem;
          height: 1.2rem;
          margin-top: 0.4rem;
        }
      }
    }
    button{
      position: absolute;
      bottom: 0;
      width: 100%;
      background: #F64542;
      height: 4.9rem;
      color: #fff;
      border-radius: 0;
    }
  }
</style>


