<template>
  <swiper :options="swiperOption" ref="mySwiper">
    <swiper-slide>
      <img src='../../../assets/index/banner.png' style=" width: 100%"/>
    </swiper-slide>
  </swiper>
</template>
<script>
export default {
  props: {
    id: {}
  },
  data () {
    return {
      commodity: [ 
        'https://boweisou.oss-cn-shenzhen.aliyuncs.com/yy/images/911a7002e11608fb581fffcde05d5257.jpg',
        'https://boweisou.oss-cn-shenzhen.aliyuncs.com/yy/images/2_1536049430.jpg',
        'https://boweisou.oss-cn-shenzhen.aliyuncs.com/yy/images/911a7002e11608fb581fffcde05d5257.jpg',
        'https://boweisou.oss-cn-shenzhen.aliyuncs.com/yy/images/2_1536049430.jpg'
      ],
      swiperOption: {
        notNextTick: false,
　　    paginationClickable: true,
  　　  loop: true,
        // pagination:{
        //       el:'.swiper-pagination',
        //       clickable:true,
        //     },//这样写小圆点就有了
        // autoplay: {
        //   delay: 3000,
        //   stopOnLastSlide: false,
        //   disableOnInteraction: false
        // },
        autoplay: false,
        speed: 600,
        direction: "horizontal",
      }
    }
  },
  methods: {
    getDataList() {
      this.$http({
        url: this.$http.adornUrl(this.apiList.banner.get),
        method: 'post',
        data: this.$http.adornData({ bannerId: this.id })
      }).then(({ data }) => {
        if (data.result) {
          this.commodity = data.data
        }
      })
    }
  },
  created() {
    // this.getDataList()
  },
}
</script>


