<template >
  <div class="myBank">
    <div class="header">
      <span class="back" @click="goback"><img src="../../assets/my/back.png" alt="" ></span>
      <span>我的银行卡</span>
      <span class="bankRight" @click="goAddBank"><img src="../../assets/vip/append.png" alt=""></span>
    </div>
    <ul>
      <li v-for="item in dataList" :key="item.id">
        <!-- 工商银行 -->
        <img src="../../assets/my/zgyh.png" alt=""  v-if="item.bankCode == 'ICBC'">
        <span v-if="item.bankCode == 'ICBC'">中国工商银行(尾号{{ item.cardNum }})</span>
        <span class="del"  v-if="item.bankCode == 'ICBC'" @click.stop="delCard(item.id)"></span>
        <!-- 建设银行 -->
        <img src="../../assets/my/jsyh.png" alt="" v-if="item.bankCode == 'CCB'">
        <span v-if="item.bankCode == 'CCB'">中国建设银行(尾号{{ item.cardNum }})</span>
        <span class="del" v-if="item.bankCode == 'CCB'" @click.stop="delCard(item.id)"></span> 
        <!-- PSBC中国邮政 -->
        <img src="../../assets/my/zgyzyh.png" alt="" v-if="item.bankCode == 'PSBC'">
        <span v-if="item.bankCode == 'PSBC'">中国邮政银行(尾号{{ item.cardNum }})</span>
        <span class="del" v-if="item.bankCode == 'PSBC'" @click.stop="delCard(item.id)"></span>
        <!-- 农业银行 -->
        <img src="../../assets/my/nyyh.png" alt="" v-if="item.bankCode == 'ABC'">
        <span v-if="item.bankCode == 'ABC'">农业银行(尾号{{ item.cardNum }})</span>
        <span class="del" v-if="item.bankCode == 'ABC'" @click.stop="delCard(item.id)"></span>
      </li>
    </ul>
  </div>
</template>
<script>
import { MessageBox } from 'mint-ui'
export default {
  data(){
    return{
      dataList: [],
    }
  },
  created(){
    this.getDataList()
  },
  methods:{
    goback() {
      this.$router.goback()
    },
    goAddBank(){
      this.$router.push({name:'addBank'})
    },
    // 获取银行卡
    getDataList() {
      const url = this.apiList.bankCard.list
      this.loading()
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData('', url, true)
      }).then(({ data }) => {
        console.log("------------",data.data)
        this.closeUpload()
        if (data.result) {
          data.data.map((v, i, a) => {
            v.cardNum = v.cardNum.slice(v.cardNum.length - 4, v.cardNum.length)
          })
          this.dataList = data.data
          console.log(this.dataList)
        } else {
          this.$toast(data.msg)
        }
      })
    },
    // 删除当前银行卡
    delCard(id) {
      const url = this.apiList.bankCard.del
      // 弹出询问
      MessageBox.confirm('确认删除？').then(() =>{
        this.delActivity()
        this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({ bankCardId: id }, url, true)
        }).then(({ data }) => {
          this.closeUpload()
          if (data.result) {
            this.$toast({
              message: '删除成功',
              iconClass: 'fa fa-check fa-5x'
            })
            this.getDataList()
          } else {
            this.$toast(data.msg)
          }
        })
      }).catch(() => {
        console.log(1111)
      })
    },
  }
}
</script>
<style lang="scss"  >
.myBank{
  min-height: 100%;
  background-color:#F5F5F5;
  .bankRight{
    float: right;
  }
  ul {
    width: 34.5rem;
    margin: 0 auto;
    margin-top: 1.55rem;
    background-color: #fff;
    border-radius:.8rem ;
    box-sizing: border-box;
    padding-left: 3.15rem;
    padding-right: 0.96rem;
    li:last-child{
        border-bottom: none
      }
    li {
      // margin: 2.4rem 1.5rem;
      box-sizing: border-box;
      // margin: 0 2.4rem;
      border-bottom: solid 0.1rem #eeeeee;
      // border-radius: .2rem;
      height: 6rem;
      box-sizing: border-box;
      line-height: 6rem;
      // padding: 0 1.5rem;
      input {
        vertical-align: middle;
      }
      img {
        width: 3.4rem;
        height: 3.4rem;
        vertical-align: middle;
        // margin-left: 1.2rem;
        margin-right: 0.8rem;
      }
      &:last-child {
        margin-bottom: 0;
      }
      .del {
        color: #34aff6;
        float: right;
        display: inline-block;
        width: 1.3rem;
        height: 1.3rem;
        background: url("../../assets/vip/del.png");
        background-size: 100% 100%;
        margin-top: 3rem;
        transform: translateY(-50%);
      }
    }
  }
}
</style>



