<template>
  <div class="item">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../../../assets/my/back.png" alt=""  ></span>
      <span>小组管理</span>
      <span class="signUp_add">
        <img  src="../../../../assets/head/add.png" @click="goAdd" v-if="isAuth('/enterNameItem/add')">
      </span>
    </div>
    <div class="main">
      <ul>
        <li v-for=" (item, index) in dataList" :key="item.id">
          <div class="indexChild">
            <div class="content">
              <div class="commodityInfo" >
                <div class="head">
                  <span class="title"> {{ item.name }} </span>
                </div>
                <div class="wrapper">
                  <div class="item_main" >
                    <div class="commodityInfo">
                      <div class="info">
                        <span class="price">
                          <span>
                            <span class="text">价格：</span>
                            <span class="text">￥{{ (item.price / 100).toFixed(2) }}</span>
                          </span>
                          <span>
                            <span class="text">总数：</span>
                            <span class="total">{{ item.totalNum }}</span>
                          </span>
                        </span>
                      </div>
                      <div class="time">
                        <span>开始时间：</span><span>{{ setDate(item.beginTime) }}</span><br>
                        <span>结束时间：</span><span>{{ setDate(item.endTime) }}</span>
                      </div>
                    </div>
                  </div>
                  <div class="foot">
                    <!-- <button class="seeData" @click="seeData" >查看数据</button> -->
                    <mt-button class="modifyBtn" @click="goModify(index)">修改</mt-button>
                    <mt-button @click="del(index)" >删除</mt-button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import { MessageBox } from 'mint-ui'
  export default {
    
    data(){
      return{
        name:'',
        id:'',
        totalNum: '',
        price: '',
        startTime:'',
        endTime:'',
        num:'',
        dataList:[]

      }
    },
    methods:{
      goBack(){
        this.$router.goback()
      },
      goAdd(){
        this.$router.push( {name:'signUp_item_add', query:{ id: this.$route.query.id} })
      },
      goModify(index){
        this.$router.push({name: 'signUp_item_add', params:{ index: index}})
        // signUp_item_modify
      },
      getDate(){
        if(localStorage.getItem('group')){
          this.dataList = JSON.parse(localStorage.getItem('group'))
        }
        // let url = this.apiList.enterNameItem.list
        // this.$http({
        //   url: this.$http.adornUrl(url),
        //   method: 'post',
        //   data: this.$http.adornData( {enterNameId: this.$route.query.id}, url, true)
        // }).then( ({data})=>{
        //   if(data.result){
        //     this.list = data.data
        //   }else if(data.msg == 'sign验证失败'){
        //     this.$toast('您的账户登录超时')
        //     setTimeout(()=>{
        //       localStorage.clear();
        //       this.$router.push({ name:  'login' })
        //     },1000)
        //   }
        // })
        
      },
      del(index, id){
        let url = this.apiList.enterNameItem.del
        MessageBox.confirm('确认删除该项？').then(data=>{
          this.dataList.splice(index, 1)
          this.$toast({
            message: '删除成功',
            iconClass: 'fa fa-check fa-5x'
          })
          localStorage.setItem('group', JSON.stringify(this.dataList))
          // this.$http({
          //   url: this.$http.adornUrl(url),
          //   method: 'post',
          //   data: this.$http.adornData( {id: id, moduleId: moduleId, userId: userId}, url, true)
          // }).then( ({data})=>{
          //   if(data.result){
          //     this.list.splice(index, 1)
          //     this.$toast('删除成功')
          //   }
          // })
        })
        
      },
      
    },
    created(){
      this.getDate()
    }
  }
</script>
<style lang="scss">
  .item{
    // height: 100%;
    background-color: #F5F5F5;
    min-height: 100%;
    .signUp_add{
      float: right;
      margin-top: -0.2rem;
    }
    .main{
      margin-top: 1.4rem;
      li{
        margin-bottom: 1.2rem;
      }
      .indexChild{
        
        .conChange{
          margin-bottom: 0.6rem;
        }
        .content{
          width: 34.3rem;
          margin: 0 auto;
          background-color: #fff;
          border-radius: 0.8rem;
          .commodityInfo{
            .item_main{
              margin: 0 1.6rem;
              display: flex;
              .commodityInfo{
                flex:1;
                .info{
                  .price{
                    display: flex;
                    justify-content: space-between;
                    .text{
                      font-size: 1.3rem;
                      color: #333;
                    }
                  }
                }
                .time{
                    margin-top: 0.5rem;
                    span{
                      display: inline-block;
                      color: #333;
                      font-size: 1.3rem;
                      margin: 0.5rem 0
                    }
                  }
                  .place{
                    margin-bottom: 1.2rem;
                  }
                  .phone{
                    margin-bottom: 1.2rem;
                  }
              }
            }
            .foot{
              height: 5.2rem;
              display: flex;
              justify-content:flex-end;
              align-items: center;
              button{
                width: 6.3rem;
                height: 2.7rem;
                outline: none;
                background-color: none;
                border-radius: 1.35rem;
                color: #333;
                background-color: #fff;
                margin-right: 1rem;
                font-size: 1.2rem;
              }
            }
          }
          
        }
      }
    }
  }
</style>




