<template>
  <span>
    {{time}}
  </span>
</template>
<script>
export default {
  data(){
    return{
      time:""
    }
  },
  props:['endTime'],
  mounted(){
    setInterval(()=>{
      if(this.endTime-Date.now()<0){
        this.time = '活动已结束'
        return
      }
      let dif = parseInt((this.endTime-Date.now())/1000)
      let d = parseInt(dif/60/60/24) 
      let h = parseInt((dif/60/60)%24) 
      let m = parseInt((dif/60)%60)
      let s = parseInt(dif%60)
      // console.log(d,h,m,s)
      if(h<10){
        h = `0${h}`
      }
      if(m<10){
        m = `0${m}`
      }
      if(s<10){
        s = `0${s}`
      }
      if(d<=0){
        this.time = `剩余  ${h}:${m}:${s}`
      }else{
        this.time = `剩余${d}天 ${h}:${m}:${s}`
      }
      // console.log(this.endTime)
      // console.log(Date.now())
    },1000)
  }
}
</script>
<style lang="scss">

</style>




