<template>
  <div class="activiity_ul">
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <van-list 
        v-model="loading" 
        :finished="finished" 
        finished-text="——没有更多了——"
        @load="onLoad"
        ref="listHeight"
      >
        <van-cell class="conChange" v-for="(item, index) in dataList" :key="index" v-show=" shows==0 ? true :(shows==1?item.showToC: !item.showToC) ">
          <div class="promotion_child" >
            <div class="content">
              <div class="promotionInfo" >
                <div class="head">
                  <span class="title"> {{ item.name }} </span>
                  <span class="right">
                    <span class="upper">{{ item.showToC === true  ?  ( item.alreadyNum === item.totalNum ? '已抢光' : (Date.now() >= item.endTime ? '已过期' : '已上架'))   : '已下架'  }}</span>
                  </span>
                </div>
                <div class="wrapper">
                  <div class="promotion_main" >
                    <div class="commodityImg">
                      <img :src="item.imgUrlSquare" alt="">
                    </div>
                    <div class="commodityInfo">
                      <div class="info">
                        <span class="price">
                          <span class="text">价格：</span><span class="text">￥{{ (item.price / 100).toFixed(2) }}</span><span class="original">￥{{ (item.showPrice/ 100).toFixed(2) }}</span> 
                        </span>
                        <span class="number"><span>人数：</span><span class="num">{{ item.totalNum }}</span></span> 
                      </div>
                      <div class="time">
                        <span>活动开始时间：</span><span>{{ setDate(item.startTime) }}</span>
                      </div>
                      <div class="time">
                        <span>活动结束时间：</span><span>{{ setDate(item.endTime) }}</span>
                      </div>
                    </div>
                  </div>
                  <div class="foot">
                    <!-- <img  @click.stop="showBtn(index)" src="../../assets/signUp/buttonGroup.png" alt="" v-if="isAuth('/activityNote/listAll') || isAuth('/activityPhotoShow/listByTypeAndTargetId')"> -->
                    <mt-button @click="goDetails(item.activityId)" v-if="isAuth('/activityPlan/listOrderData')">明细</mt-button>
                    <mt-button @click="upperClick(item.activityId, index, item.approvalId)" v-if="isAuth('/activityPlan/mod')">{{ item.showToC == true ? '下架' :  '上架' }}</mt-button>
                    <mt-button @click="goModify(item.activityId, index)" v-if="isAuth('/activityPlan/mod')">修改</mt-button>
                    <mt-button @click="delData(item.activityId,index)" v-if="isAuth('/activityPlan/del')">删除</mt-button>
                  </div>
                  <!-- <div class="hideBtn" v-if="showBtnNum == index">
                    <img src="../../assets/signUp/hideBtn.png" alt="">
                    <mt-button @click="goBuyingIns(item.activityId)" v-if="isAuth('/activityNote/listAll')">注意事项</mt-button>
                    <mt-button @click="goPhoto(item.activityId)" v-if="isAuth('/activityPhotoShow/listByTypeAndTargetId')">风采展示</mt-button>
                  </div> -->
                </div>
              </div>
            </div>
          </div>
        </van-cell>
    </van-list>
  </van-pull-refresh>
</div>
</template>
<script>
import {MessageBox} from 'mint-ui';
  export default {
    props:['shows'],
    data(){
      return{
        dataList:[],
        page: 0,
        showBtnNum: -1,
        showToC: true,
        value: true,
        error: false,
        modifyShow: false,
        loading: false,
        finished:false,
        isLoading: false,
        count: 0,
      }
    },
    methods:{
      onLoad(){
        let showToC;
        if(this.shows === 0){
          showToC = ''
        }else if(this.shows === 1){
          showToC = true
        }else{
          showToC = false
        }
        return new Promise((resolve, reject) =>{
          this.page++
          const url = this.apiList.addActivity.list
          this.$http({
            url: this.$http.adornUrl(url),
            method: 'post',
            data: this.$http.adornData({ showToC: showToC, page: this.page, size: 10}, url, true)
          }).then(({data})=>{
            if (data.result) {
              if (data.data.records.length > 0) {
                if(localStorage.getItem('dataList')){
                  let arr = data.data.records
                    this.dataList.forEach((item, index)=>{
                      arr.forEach(items=>{
                        if(item.activityId === items.activityId){
                          this.dataList.splice(index, 1, items)
                        }
                      })
                    })
                  let list = this.dataList.concat(arr)
                  this.dataList = Array.from(new Set([...list, ...list]))
                  localStorage.setItem('dataList', JSON.stringify(this.dataList))
                }else{
                  this.dataList = data.data.records
                  localStorage.setItem('dataList', JSON.stringify(this.dataList))
                }
                
              }
              if (data.data.pages <= this.page) {
                this.finished = true
              }
              if(this.shows === 0){
                localStorage.setItem('pageNum', this.page)
              }else if(this.shows === 1){
                localStorage.setItem('pageNum2', this.page)
              }else if(this.shows === 2){
                localStorage.setItem('pageNum3', this.page)
              }
              this.$nextTick(() => {
                resolve(true)
                this.loading = false
                if ('scrollRestoration' in history) {
                  history.scrollRestoration = 'manual'; //改为manual之后，就不会记录滚动位置
                }
              })
            }else if( data.msg == 'sign验证失败' ){
              this.$toast('您的账户登录超时')
              setTimeout(()=>{
                localStorage.clear();
                this.$router.push({name: 'login'})
              },1000)
            }
          })
          
        })
      },
      onRefresh(){
        let showToC;
        if(this.shows === 0){
          showToC = ''
        }else if(this.shows === 1){
          showToC = true
        }else{
          showToC = false
        }
        return new Promise((resolve, reject) =>{
          this.page = 0;
          this.page++;
          const url = this.apiList.addActivity.list
          this.$http({
            url: this.$http.adornUrl(url),
            method: 'post',
            data: this.$http.adornData({ showToC: showToC, page: this.page, size: 10}, url, true)
          }).then(({data})=>{
            if (data.result) {
              if (data.data.records.length >= 0) {
                let arr = data.data.records
                  this.dataList.forEach((item, index)=>{
                    arr.forEach(items=>{
                      if(item.activityId === items.activityId){
                        this.dataList.splice(index, 1, items)
                      }
                    })
                  })
                let list = this.dataList.concat(arr)
                this.dataList = Array.from(new Set([...list, ...list]))
                localStorage.setItem('dataList', JSON.stringify(this.dataList))
              }
              this.$nextTick(() => {
                this.$toast('刷新成功')
                this.isLoading = false;
                this.finished = false;
                this.page = 1;
                // if(this.$refs.listHeight.$el.offsetHeight < document.documentElement.offsetHeight){
                //   this.finished = false
                //   this.loading = false
                //   this.onLoad()
                // } 
                localStorage.setItem('pageNum', this.page)
                resolve(true)
              })
            }
          })
          
        })
      },
      goModify(id, index){
        this.setScrollTop()
        // this.$router.push({name:'activity_mod', params:{modId: id, index: index, type: 501}})
        this.$router.push({name:'activity_common_edit', params:{modId: id, index: index, type: 501}})
        // localStorage.removeItem('dataList')

      },
      goDetails(id){
        // this.$router.push( { name: 'activity_details', query: {id: id, list: 'addActivity'}} )
        const url = this.apiList.addActivity.orderList
        if(!this.isAuth(url)){
          return this.$toast("权限不足，请联系管理员修改")
        }
        this.setScrollTop()
        this.$router.push({name:'detail',params:{'activityId':id,url,'shows': 3}})
      },
      // goBuyingIns(id){
      //   this.$router.push({name: 'activity_buyingIns_index', query:{id: id, type: 501}})
      // },
      // goPhoto(id){
      //   this.$router.push( {name:'activity_photos2', query: {id: id, type: 501}})
      // },
      delData(id, index){
        MessageBox.confirm('确认删除该项？').then(()=>{
          let url = this.apiList.addActivity.del
          this.delActivity()
          this.$http({
            url: this.$http.adornUrl(url),
            method: 'post',
            data: this.$http.adornData({ id: id,}, url, true)
          }).then(({data})=>{
            this.closeUpload()
            if(data.result){
              this.dataList.splice(index,1)
              localStorage.setItem('dataList', JSON.stringify(this.dataList) )
              this.$toast({
                message: '删除成功',
                iconClass: 'fa fa-check fa-5x'
              })
              if(this.$refs.listHeight.$el.offsetHeight < document.documentElement.offsetHeight){
                this.finished = false
                this.loading = false
                this.onLoad()
              }
            }else{
              this.$toast(data.msg)
            }
          })
        })
      },
      change(){
        this.dataList = JSON.parse(localStorage.getItem('dataList'));
        this.getScrollTop()
      },
      setScrollTop(){
        var scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
        let num = localStorage.getItem('activeNum')
        if(num == 0){
          localStorage.setItem('scrollTop0', scrollTop)
        }else if(num == 1){
          localStorage.setItem('scrollTop1', scrollTop)
        }else if(num == 2){
          localStorage.setItem('scrollTop2', scrollTop)
        }
        document.documentElement.scrollTop = 0;
        document.body.scrollTop = 0;
      },
      getScrollTop(){
        if(this.shows == 0){
          this.$nextTick(()=>{
            document.documentElement.scrollTop = localStorage.getItem('scrollTop0')
            document.body.scrollTop = localStorage.getItem('scrollTop0')
          })
        }else if(this.shows == 1){
          this.$nextTick(()=>{
            document.documentElement.scrollTop = localStorage.getItem('scrollTop1')
            document.body.scrollTop = localStorage.getItem('scrollTop1')
          })
        }else{
          this.$nextTick(()=>{
            document.documentElement.scrollTop = localStorage.getItem('scrollTop2')
            document.body.scrollTop = localStorage.getItem('scrollTop2')
          })
        }
      },
      // showBtn(index){
      //   this.dataList.forEach((item, i)=>{
      //     if(index == i){
      //       this.showBtnNum = this.showBtnNum == i ? -1 : i
      //     }
      //   })
      // },
      // hideBtn(){
      //   this.showBtnNum = -1
      // },
      upperClick(id, index){
        let url = this.apiList.addActivity.edit
        if(this.dataList[index].showToC== true){
          var upperText = '确认下架？'
        }else{
          var upperText = '确认上架？'
        }
        MessageBox.confirm(upperText).then( ()=>{
          this.upload()
          this.$http({
            url:this.$http.adornUrl(url),
            method:'post',
            data: this.$http.adornData( { id: id, showToC: !this.dataList[index].showToC}, url, true )
          }).then(({ data }) => {
            this.closeUpload()
            if(data.result){
              this.dataList[index].showToC = !this.dataList[index].showToC
              this.$toast({
                message: '操作成功',
                iconClass: 'fa fa-check fa-5x'
              })
              if(this.$refs.listHeight.$el.offsetHeight < document.documentElement.offsetHeight){
                this.finished = false
                this.loading = false
                this.onLoad()
              } 
              localStorage.setItem('dataList', JSON.stringify(this.dataList))
            }else if(data.msg == 'sign验证失败'){
              this.$toast('您的账户登录超时')
              setTimeout(()=>{
                localStorage.clear();
                this.$router.push({name: 'login'})
              },1000)
            }else{
              this.$toast(data.msg)
            }
          })
        })
      },
    },
    mounted(){
      this.$refs.listHeight.$el.style.minHeight = document.documentElement.clientHeight  - 12.2 * 10 + 'px'
    },
    created(){
      if(window.history.length > 3){
        if(localStorage.getItem('dataList')){
          this.change()
          if(localStorage.getItem('pageNum')){
            this.page = Number(localStorage.getItem('pageNum'))
          }
        }else if( this.shows === 1){
          this.change()
          if(localStorage.getItem('pageNum2')){
            this.page = Number(localStorage.getItem('pageNum2'))
          }
        }else if(this.shows === 2){
          this.change()
          if(localStorage.getItem('pageNum3')){
            this.page = Number(localStorage.getItem('pageNum2'))
          }
        }else{
          this.getScrollTop()
        }
      }
    }

  }
</script>
<style lang="scss">

.indexChild .van-cell {
  padding: 0;
  width: 34.3rem !important; 
  overflow: visible;
}
  .activiity_ul{
    box-sizing: border-box;
    padding-top: 1.2rem;
    background-color: #F5F5F5;
    
    .conChange{
      margin: 0 auto;
      margin-bottom: 1.6rem;
      width: 34.3rem;
      border-radius: .8rem;
      overflow: visible;
      padding: 0;
    }
    .content{
      width: 100%;
      background-color: #fff;
      .promotionInfo{
        .promotion_main{
          box-sizing: border-box;
          margin: 0 1.2rem;
          display: flex;
          .commodityImg{
            img{
              display: inline-block;
              width: 6.7rem;
              height: 7rem;
            }
          }
          .commodityInfo{
            flex:1;
            margin-left: 1.4rem;
            .info{
              display: flex;
              justify-content: space-between;
              .text{
                font-size: 1.4rem;
                color: #333;
              }
              .original{
                margin-left: 0.5rem;
                color: #999;
                font-size: 1.1rem;
                text-decoration: line-through;
              }
              .num{
                color: #333;
                font-size: 1.4rem;
                text-align: right;
              }
              
            }
            .time{
              span{
                color: #333;
                font-size: 1.4rem;
              }
            }
          }
          .modifyBtn{
            margin-right: 1.2rem;
          .foot{
            height: 5.2rem;
            margin-right: 1.6rem;
            display: flex;
            justify-content:flex-end;
            align-items: center;
            button{
              height: 2.7rem;
              line-height: 2.7rem;
              background: #fff;
              border-radius: 1.35rem;
              border: 0.1rem solid #666;
              color: #333;
              margin-right: 1.2rem;
              font-size: 1.2rem;
              user-select:none;
            }
            button:last-child{
              margin: 0;
            }
            .modifyBtn{
              margin-right: 1.2rem;
            }
          }
        }
        }
        .foot{
          height: 5.2rem;
          margin-right: 1rem;
          display: flex;
          justify-content:flex-end;
          align-items: center;
          img{
            width: 2rem;
            height: 2rem;
            margin-right: 9.2rem;
          }
          button{
            min-width: 6.3rem;
            height: 2.7rem;
            border-radius: 1.35rem;
            color: #333;
            background-color: #fff;
            margin-right: .9rem;
            font-size: 1rem;
          }
          button:last-child{
            margin: 0;
          }
        }
      
      }
    }
    
}
</style>



