<template>
  <div class="saleNum_Chart">
    <div class="mySaleChart" ref="mySaleChart"></div>
  </div>
</template>
<script>
// 
import echarts from '../../utils/echarts.js'
export default {
  props: [
    'num',
    'startTime',
    'endTime'
  ],
  watch:{
    startTime:function(){
      this.getData()
    }
  },
  data(){
    return{
      chartNum: '',
      saleOption: {
          tooltip : {
            trigger: 'axis'
          },
          itemStyle:{
            barBorderRadius: 12,
            borderWidth: 24
          },
          calculable : true,
          xAxis : [
            {
              type: 'category',
              data : [],
              barWidth : 12,
              axisLine:{
                show: false
              },
              axisTick:{
                show: false
              }
              
            }
          ],
          yAxis : [
            
            {
              type : 'log',
              show: false
            }
          ],
          series : [
            {
              name:'销售额',
              type:'bar',
              barWidth : 12,
             
              label:{show: true, position: 'top',color: '#666',fontSize: 12},
              data:[],
              itemStyle:{
                normal: {
                  color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [{
                    offset: 0,
                    color: "#EE795A" // 0% 处的颜色
                  }, {
                    offset: 1,
                    color: "#FF9E37" // 50% 处的颜色
                  }], false)
                }
              },
            },
              {
                name:'交易笔数',
                type:'bar',
                barWidth : 12,
                barGap: '50%',
                label:{show: true, position: 'top',color: '#666',fontSize: 12},
                data:[],
                itemStyle:{
                  normal: {
                    
                    color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [{
                      offset: 0,
                      color: "#5A8EEE" // 0% 处的颜色
                    }, {
                      offset: 1,
                      color: "#37B1FF" // 50% 处的颜色
                    }], false)
                  }
              }
              },
              // {
              //   name:'分销金额',
              //   type:'bar',
              //   barWidth : 12,
              //   barGap: '50%',
              //   label:{show: true, position: 'top',color: '#666',fontSize: 12},
              //   data:[10,100,100,255,355,10],
              //   itemStyle:{
              //     normal: {
                    
              //       color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [{
              //         offset: 0,
              //         color: "#5AC9EE" // 0% 处的颜色
              //       }, {
              //         offset: 1,
              //         color: "#47FDFF" // 50% 处的颜色
              //       }], false)
              //     }
              //   }
              // }
          ]
        },
    }

  },
  methods:{
    getData(){
    this.saleOption.xAxis[0].data = []
    this.saleOption.series[0].data = []
    this.saleOption.series[1].data = []
    const url = this.apiList.statisticalData.data
    this.$http({
      url: this.$http.adornUrl(url),
      method: 'post',
      data: this.$http.adornData({ startTime: this.startTime, endTime: this.endTime}, url, true)
    }).then(({data})=>{
      if(data.result){
        
        if(JSON.stringify(data.data.num) !== '{}'){
          for(let i in data.data.money){
            this.saleOption.xAxis[0].data.push(i+ '日')
            if(data.data.money[i]=== 0){
              this.saleOption.series[0].data.push('null')
            }else{
              this.saleOption.series[0].data.push((data.data.money[i] / 100).toFixed(2))
            }
          }
          for(let i in data.data.num){
            this.saleOption.series[1].data.push(data.data.num[i])
          }
          let startVal = '';
          let endVal = '';
          switch (this.num) {
            case 0:
              startVal = 0
              endVal = 6;
              break;
            case 1:
              startVal = 6
              endVal = 12;
              break;
            case 2:
              startVal = 12
              endVal = 18;
              break;
            case 3:
              startVal = 18
              endVal = 24;
              break;
            case 4:
              startVal = 24
              endVal = 30;
              break;
            case 5:
              startVal = 30
              endVal = 32
              break;
         }
          this.saleOption.xAxis[0].data = this.saleOption.xAxis[0].data.slice(startVal,endVal)
          this.saleOption.series[0].data = this.saleOption.series[0].data.slice(startVal,endVal)
          this.saleOption.series[1].data = this.saleOption.series[1].data.slice(startVal,endVal)

          echarts.init(this.$refs.mySaleChart).setOption(this.saleOption)
        }
        
      }
      
    })
    },
    convertTimes( year, month, day ){   
        let d = new Date()
        d.setFullYear(year)
        d.setMonth(month)
        d.setDate(day)
        if(day = 0){
          d.setHours(23)
          d.setMinutes(59)
          d.setSeconds(59)
        }else{
          d.setHours(0)
          d.setMinutes(0)
          d.setSeconds(0)
        }
        return Date.parse(d)  
    },
  },

  mounted(){
    this.getData()
     
  },
  created(){
    // var date = new Date()
    // this.startTime = this.convertTimes(date.getFullYear(), date.getMonth(), 1)
    // this.endTime = this.convertTimes(date.getFullYear(), date.getMonth() + 1, 0)

  }
}
</script>
<style lang="scss">
  .saleNum_Chart{
    height: 30rem;
    .mySaleChart{
      height: 30rem;
    }
    
  }
</style>


