<template>
  <div class="commodity">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../../assets/my/back.png" alt=""  ></span>
      <span>商品管理</span>
      <span class="add_Pro" >
        <img class="searchBtn" @click="goSearch" src="../../../assets/head/search.png">
        <img class="add" src="../../../assets/head/add.png" @click="goAdd" v-if="isAuth(apiList.template.find)">
      </span>
    </div>
    <div class="commodity_main">
      <van-tabs v-model="active" @change="change" :sticky='true' swipeable animated color="#fff" :offset-top='48.5'>
        <van-tab title="全部">
          <index-child  :shows="0"  ref="child1"></index-child>
        </van-tab>
        <van-tab title="已上架">
          <index-child  :shows="1"  ref="child2"></index-child>
        </van-tab>
        <van-tab title="未上架">
          <index-child  :shows="2"  ref="child3"></index-child>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>
  <script>
  import indexChild from './indexChild.vue'
  export default {
    data(){
      return{
        active: 0,
      }
    },
    methods:{
      goBack(){
        localStorage.removeItem('dataList')
        localStorage.removeItem('pageNum')
        localStorage.removeItem('dataList2')
        localStorage.removeItem('pageNum2')
        localStorage.removeItem('dataList3')
        localStorage.removeItem('pageNum3')
        localStorage.removeItem('scrollTop0')
        localStorage.removeItem('scrollTop1')
        localStorage.removeItem('scrollTop2')
        localStorage.removeItem('activeNum')
        this.$router.goback()
      },
      change(){
        this.setScrollTop()
        localStorage.setItem('activeNum', this.active)
        if(this.active === 0 && this.$refs.child1){
          this.$refs.child1.change()
        }else if(this.active === 1 && this.$refs.child2){
          this.$refs.child2.change()
        }else if(this.active === 2 && this.$refs.child3){
          this.$refs.child3.change()
        }
      },
      goSearch(){
        this.setScrollTop()
        let url = JSON.stringify({list: 'commodity', details: 'commodity_details', buyingIns:'commodity_buyingIns', mod: 'commodity_mod'})
        localStorage.setItem('searchVal', url)
        this.$router.push( {name: 'activity_search'})
      },
      delData(index, id){
        MessageBox.confirm('确定删除促销商品?').then( data => {
          let url = this.apiList.commodity.del
          this.$http({
            url:this.$http.adornUrl(url),
            method:'post',
            data: this.$http.adornData({ id: id }, url, true)
          }).then(({ data }) => {
            if( data.result ){
              
              this.$toast('删除成功')
            }else{
              this.$toast(data.msg)
            }
          })
        })

      },
      goAdd(){
        // this.setScrollTop()
        localStorage.removeItem('dataList')
        localStorage.removeItem('activeNum')
        // this.$router.push('commodity_temp')
        this.$router.push({ name: 'activity_common_template', query: { type: '601' } })
      },
      setScrollTop(){
        var scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
        let num = localStorage.getItem('activeNum')
        if(num == 0){
          localStorage.setItem('scrollTop0', scrollTop)
        }else if(num == 1){
          localStorage.setItem('scrollTop1', scrollTop)
        }else if(num == 2){
          localStorage.setItem('scrollTop2', scrollTop)
        }
      }
    },
    components:{
      indexChild
    },
    created(){
      if(localStorage.getItem('activeNum')){
        this.active = Number(localStorage.getItem('activeNum'))
      }else{
        localStorage.setItem('activeNum', this.active)
      }
    }
  }
</script>
<style lang="scss">
  .commodity{
    min-height: 100%;
    background-color: #F5F5F5;
    .van-tabs__wrap{
      margin-top: -0.8rem;
    }
     .header{
      .add_Pro{
        position: absolute;
        right: 1.6rem;
        .searchBtn{
          width: 1.8rem;
          height: 1.9rem;
          margin-right: 1.2rem;
          margin-top: -0.5rem;
        }
        .add{
          width: 1.5rem;
          margin-top: -0.5rem;
        }
      }
    }
  }
</style>




