<template>
  <div class="auth_add">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../assets/my/back.png" alt=""  ></span>
      <span>添加角色</span>
    </div>
    <div class="auth_add_main">
      <div class="add_role">
        <div class="role_header">
          <img src="../../assets/auth/addRole.png" alt="">
          <span>添加角色</span>
        </div>
        <div class="role_name">
          <span>名称</span>
          <input type="text" placeholder="请输入角色名称" v-model="roleName">
          <img src="../../assets/auth/X.png" alt="" v-if="roleName !== ''" @click="roleName = ''">
        </div>
        <div class="role_describe">
          <span>描述</span>
          <input type="text" placeholder="请输入角色描述" v-model="describe">
          <img src="../../assets/auth/X.png" alt="" v-if="describe !== ''" @click="describe= ''">
        </div>
      </div>
      <div class="set_auth">
        <div class="set_auth_header">
          <img src="../../assets/auth/setAuth.png" alt="">
          <span>设置权限</span>
        </div>
        <div class="set_auth_content">
          <ul>
            <li v-for="(item, index) in dataList" :key="index">
              <div :class="{module_dome: true, dome_border: num[index]}" >
               <span> <span class="line"></span><span>{{item.label}}</span></span><van-switch v-model="num[index]" size="20px"   active-color="#00A1FC" inactive-color="#C0C0C0" />
              </div>
              <div class="liChild" v-if="num[index]" >
                <span v-for="(childred_item, childred_index) in item.children" :class="{active : childred_item.checked} "    :key="childred_index" @click="getUrl(index, childred_index, childred_item.id)">{{ childred_item.label}}</span>
              </div>
            </li>
          </ul>
          
        </div>
      </div>
      <div class="btn">
        <mt-button @click="clearAuth">重置权限</mt-button><mt-button @click="addData">确定</mt-button>
      </div>
    </div>  
  </div>
  
</template>
<script>
import { MessageBox } from 'mint-ui'
  export default {
    data(){
      return{
        num: [],
        roleName: '',
        describe: '',
        url: '',
        dataList: [],
        flag: false
      }
    },
    
    methods:{
      goBack(){
        if(this.roleName || this.describe || this.url) {
          MessageBox.confirm('确认返回？返回将清空已填写的数据').then(()=>{
            this.$router.goback()
          })
        }else{
          this.$router.goback()
        }
      },
      getUrl(i,index){
        if(this.dataList[i].children[index].checked == true){
          this.dataList[i].children[index].checked = false
        }else{
          this.dataList[i].children[index].checked = true
        }
        this.$forceUpdate();
          // var url = 0;
          if(this.url.indexOf(this.dataList[i].children[index].value) !== -1){
            if(this.url.indexOf(',' + this.dataList[i].children[index].value  ) !== -1){
              this.url = this.url.replace( ',' + this.dataList[i].children[index].value , '')
            }else{
              this.url = this.url.replace(this.dataList[i].children[index].value, '')
            }
          }else{
            this.url = this.url === '' ?   this.dataList[i].children[index].value  :   this.url + ',' + this.dataList[i].children[index].value 
          }
      },
      clearAuth(){
        MessageBox.confirm('确认重置权限？').then(()=>{
          this.num = []
          this.url = ''
          this.dataList.forEach((item,index)=>{
            item.children.forEach((children_item, index)=>{
              children_item.checked = false
            })
          })
          if(this.num.length===0 &&  this.url === ''){
            this.$toast({
              message: '重置成功',
              iconClass: 'fa fa-check fa-5x'
            })
          }
        })
       
      },
      getData(){
        const url = this.apiList.role.api;
        this.loading()
        this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({roleId: 2, page: 1, size: 10000}, url, true)
        }).then(({data})=>{
          this.closeUpload()
          if(data.result){
            const arr = [];
            const pushDataList = data.data.records;
            pushDataList.forEach((ele, index, array) =>{
              //  ele.checked === 1 ? this.defaultchecked.push(index) : ''
             const idx = arr.findIndex(item => item.label === ele.groupName )
              // console.log(arr);
              
              if(idx === -1){
                arr.push({
                  id: index,
                  label: ele.groupName,
                  children: [{
                    id: index,
                    label: ele.name,
                    value: ele.url,
                    desc: ele.apiDesc
                  }]
                  
                })
              }else{
                arr[idx].children.push({
                  id: index,
                  label: ele.name,
                  value: ele.url,
                  desc: ele.apiDesc
                })
              }
            })
            this.dataList = arr
            console.log(this.dataList);
          }
        })
      },
      addData(){
        if(this.flag) return
        if(!this.roleName || !this.describe) return this.$toast('请检查您填写的内容是否为空')
        if(this.url === '') return this.$toast('请给该角色设置相关权限')
        this.flag = true
        const url = this.apiList.role.add;
        this.upload()
        this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({roleName: this.roleName, describe: this.describe, }, url, true)
        }).then(({data})=>{
          this.closeUpload()
          if(data.result){
            const apiUrl = this.apiList.role.update;
            this.$http({
              url: this.$http.adornUrl(apiUrl),
              method: 'post',
              data: this.$http.adornData({roleId: data.data.id, apiUrls: this.url }, apiUrl, true)
            }).then(({data})=>{
              if(data.result){
                this.$toast({
                  message: '添加成功',
                  iconClass: 'fa fa-check fa-5x'
                })
                setTimeout(()=>{
                  this.$router.goback()
                }, 500)
              }else if(data.msg == 'sign验证失败'){
                this.$toast('您的账户登录超时')
                setTimeout(()=>{
                  localStorage.clear();
                  this.$router.push({name: 'login'})
                },1000)
              }
            })
           
          }else{
            this.$toast(data.msg)
            this.flag = false
          }
        })
      },
    },
    created(){
      this.getData()
    }
  }
</script>
<style lang="scss">
  .auth_add{
    position: relative;
    min-height: 100%;
    background: #f5f5f5;
    .auth_add_main{
      margin-top: 1.4rem;
      .add_role{
        background: #fff;
        .role_header{
          height: 4.9rem;
          display: flex;
          align-items: center;
          padding-left: 1.6rem;
          border-bottom: 0.1rem solid #ededed;
          img{
            width: 1.5rem;
            height: 1.5rem;
          }
          span{
            color: #666;
            font-size: 1.6rem;
            margin-left: 0.5rem;
          }
        }
        .role_name{
          
          border-bottom: 0.1rem solid #ededed;
        }
        .role_name, .role_describe{
          padding: 1.4rem 1.6rem;
          margin-left: 1.8rem;
          font-size: 1.4rem;
          img{
            width: 1.2rem;
            float: right;
            margin-top: 0.4rem;
          }
          span{
            color: #333;

          }
          input{
            margin-left: 3rem;
            color: #666;
            border: 0;
          }
           ::-webkit-input-placeholder { /* WebKit, Blink, Edge */
            color: #D7D7D7;
          }
          :-moz-placeholder { /* Mozilla Firefox 4 to 18 */
            color: #D7D7D7;
          }
          ::-moz-placeholder { /* Mozilla Firefox 19+ */
            color: #D7D7D7;
          }
          :-ms-input-placeholder { /* Internet Explorer 10-11 */
            color: #D7D7D7;
          }
        }
      }
      .set_auth{
        margin-top: 1.4rem;
        padding-bottom: 8rem;
        background: #fff;
        .set_auth_header{
          display: flex;
          height: 4.9rem;
          align-items: center;
          // line-height: 4.9rem;
          padding: 0 1.6rem;
          border-bottom: 0.1rem solid #ededed;
          img{
            width: 1.5rem;
            height: 1.5rem;
          }
          span{
            color: #666;
            font-size: 1.6rem;
            margin-left: 0.5rem;
          }
          
        }
        .set_auth_content{
          ul{
            li{
              border-bottom: 0.1rem solid #EEEEEE;
              padding: 0 1.9rem;
              .line{
                display: inline-block;
                width: 0.2rem;
                height: 1.2rem;
                margin-right: 0.8rem;
                background: #00A1FC;
              }
              .van-switch--on .van-switch__node{
                height: 98%;
                width: 2rem;
                transform: translateX(1.9rem);
              }
              .van-switch__node {
                width: 2rem;
                height: 98%;
              }
              .module_dome{
                display: flex;
                justify-content: space-between;
                align-items: center;
                height: 4.4rem;
              }
              .dome_border{
                // padding-right: 1.6rem;
                border-bottom: 0.1rem solid #eee;
              }
              .liChild{
                padding: 1rem 0;
                span{
                  display: inline-block;
                  height: 2.5rem;
                  line-height: 2.5rem;
                  padding: 0 1rem;
                  margin: 0.5rem 0.5rem;
                  color: #666;
                  font-size: 1.2rem;
                  background: #ECECEC;
                  border-radius: 0.4rem;
                }
                .active{
                  background:#00A1FC;
                  color: #fff;
                }
              }
            }
          }
        }
      }
    }
    .btn{
      position: fixed;
      left: 0;
      bottom: 0;
      width: 100%;
      z-index: 9999;
      button{
        width: 50%;
        height: 4.9rem;
        font-size: 1.6rem;
        color: #fff;
        outline: none;
        border-radius: 0;
      }
      button:first-child{
        background: #FA5F5B;
      }
      button:last-child{
        background: #F64542;
      }
    }
  }
</style>


