<template>
  <div class="search">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../../../assets/my/back.png" alt="" ></span>
      <span class="search_box">
        <img src="../../../../assets/head/header_search.png" alt="">
        <input type="text" placeholder="请输入标题进行搜索" ref="iphone"  v-model="keyword">
        <img src="../../../../assets/auth/X.png" v-if="keyword.length > 0" @click="keyword = ''">
      </span>
      <span class="text" @click="searchData">搜索</span>
    </div>
    <div class="main">
      
      <div class="history" v-if="showHistory">
        <span class="searchHis">
          <span class="historyText">搜索历史</span>
          <img src="../../../../assets/search/del.png" @click="delHistory" alt="">
        </span>
        <span class="textContent">
          <span class="wrapper" v-for=" (item, index) in historyList" :key="index">
            <span class="text" @click="getText(index)" @touchstart="star(index)" @touchend="end">{{item}}</span>
          </span>
        </span>
      </div>
      <van-list
      v-model="loading" 
      :finished="finished" 
      finished-text="—— 没有更多了 ——"
      @load="onLoad"
      :immediate-check="false"
      ref="list"
      >
      <van-cell class="conChange" v-for="(item, index) in searchList" :key="index">
        <div class="promotion_child" >
          <div class="content">
            <div class="promotionInfo" >
              <div class="head">
                <span class="title" v-html="searchText(item.name, keyword)"></span>
                <span class="right">
                  <span class="upper" style="color: #333">{{ item.showToC === true  ?  ( item.alreadyNum === item.totalNum ? '已抢光' : (Date.now() >= item.endTime ? '已过期' : '已上架'))   : '已下架' }}</span>
                </span>
              </div>
              <div class="wrapper">
                <div class="promotion_main" >
                  <div class="commodityImg">
                    <img :src="item.imgUrlSquare" alt="">
                  </div>
                  <div class="commodityInfo">
                    <div class="info">
                      <span class="price">
                        <span class="text">价格：</span><span class="text">￥{{ (item.price / 100).toFixed(2) }}</span><span class="original">￥{{ (item.showPrice/ 100).toFixed(2) }}</span> 
                      </span>
                      <span class="number"><span>数量：</span><span class="num">{{ item.totalNum }}</span></span> 
                    </div>
                    <div class="time">
                      <span>活动开始时间：</span><span>{{ setDate(item.startTime) }}</span>
                    </div>
                    <div class="time">
                      <span>活动结束时间：</span><span>{{ setDate(item.endTime) }}</span>
                    </div>
                  </div>
                </div>
                <div class="foot">
                  <mt-button @click="goMerchantCode(item.activityId)" v-if="item.useThirdCode && isAuth('/thirdCode/list')" >商家码</mt-button>
                  <mt-button @click="goDetails(item.activityId)" v-if="orderBtn">明细</mt-button>
                  <mt-button @click="upperClick(item.activityId, index)" v-if="modBtn">{{item.showToC == true ? '下架' : '上架'}}</mt-button>
                  <mt-button @click="goModify(item.activityId, index)" v-if="modBtn">修改</mt-button>
                  <mt-button @click="delData(item.activityId,index)" v-if="delBtn">删除</mt-button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </van-cell>
    </van-list>
    </div>
  </div>
</template>
<script>
  import { MessageBox } from 'mint-ui'
  import {Indicator} from 'mint-ui'
  export default {
    data(){
      return{
        type:'',
        page: 1,
        dataList:[],
        keyword:'',
        keywords:'',
        showBtnNum: -1,
        keywordsIndex:'',
        historyList:[],
        searchList:[],
        loop:'',
        activeUrl:  JSON.parse(localStorage.getItem('searchVal')),
        showHistory: true,
        loading: false,
        finished: false,
        orderBtn: false,
        modBtn: false,
        delBtn: false,
        imgBtn: false
      }
    },
    methods:{
      goBack(){
        this.$router.goback()
        localStorage.removeItem('searchVal')
      },
      goKey(id){
        this.$router.push({name:'key', query:{id: id}})
      },
      goItem(id){
        this.$router.push({ name:'signUp_item', query: { id: id }} )
      },
      goMerchantCode(id){
        this.$router.push({name: 'merchantCode', query:{id: id}})
        localStorage.removeItem('dataList')
      },
      // showBtn(index){
      //   this.searchList.forEach((item, i)=>{
      //     if(index == i){
      //       this.showBtnNum = this.showBtnNum == i ? -1 : i
      //     }
      //   })
      // },
      // hideBtn(){
      //   this.showBtnNum = -1;
      // },
      onLoad(){
        return new Promise((resolve, reject)=>{
          const activeUrl = this.activeUrl.list
          const url = this.apiList[activeUrl].list
          this.page++
          this.$http({
            url:this.$http.adornUrl(url),
            method:'post',
            data: this.$http.adornData({page: this.page, size: 10, nameLike: this.keyword }, url, true)
          }).then(({ data }) => {
            if( data.result ){
              if(data.data.records.length > 0){
                this.searchList = this.searchList.concat(data.data.records);
              }
              
            }
            if (data.data.pages <= this.page) {
                this.finished = true
            }
            this.$nextTick(() => {
              resolve(true)
              this.loading = false
            })
          })
        }) 
      },
      star(id){
        this.loop = setTimeout(()=>{
          MessageBox.confirm('确认删除此记录？').then(()=>{
            this.historyList.splice(id,1)
            const his =  this.activeUrl.list + '_' + 'historyList'
            localStorage.setItem(his, JSON.stringify(this.historyList) )
          })
        },500)
      },
      end(){
        clearTimeout(this.loop)
      },
      upperClick(id, index){
        const activeUrl = this.activeUrl.list
        let url = this.apiList[activeUrl].edit
        if(this.searchList[index].showToC== true){
          var upperText = '确认下架？'
          var upeer = '下架成功'
        }else{
          var upperText = '确认上架？'
          var upeer = '上架成功'
        }
        MessageBox.confirm(upperText).then( ()=>{
          this.upload()
          this.$http({
            url:this.$http.adornUrl(url),
            method:'post',
            data: this.$http.adornData( { id: id, showToC: !this.searchList[index].showToC}, url, true )
          }).then(({ data }) => {
            this.closeUpload()
            if(data.result){
              this.searchList[index].showToC = !this.searchList[index].showToC
              this.$toast({
                message: '操作成功',
                iconClass: 'fa fa-check fa-5x'
              })
            }else if(data.msg == 'sign验证失败'){
              this.$toast('您的账户登录超时')
              setTimeout(()=>{
                localStorage.clear();
                this.$router.push({name: 'login'})
              },1000)
            }
          })
        })
      },
      searchText(val, keyword){
        val = val + '';
        if (val.indexOf(keyword) !== -1 && keyword !== '') {
          return val.replace(keyword, '<m style="color: red">' + keyword + '</m>')
        } else {
          return val
        }
      },
      goModify(id, index){
        if(this.activeUrl.list == 'commodity' || this.activeUrl.list == 'pmSale'){
          if(this.activeUrl.list == 'pmSale'){
            this.$router.push({name: 'promotion_add' , params: {modId: id, index: index}})
          }else{
            this.$router.push({name: this.activeUrl.mod , params: {modId: id, index: index}})
          }
        }else if(this.activeUrl.list == 'addActivity' || this.activeUrl.list == 'enterName'){
          this.$router.push({name: this.activeUrl.mod , params: {modId: id, index: index}})
        }
      },
      goDetails(id){
        if(this.activeUrl.list == 'commodity'){
          const url = this.apiList.commodity.orderList
          if(!this.isAuth(url)){
            return this.$toast("权限不足，请联系管理员修改")
          }
          this.$router.push({name:'detail',params:{'activityId':id,url,'shows': 4}})
        }else if(this.activeUrl.list == 'addActivity'){
          const url = this.apiList.addActivity.orderList
          if(!this.isAuth(url)){
            return this.$toast("权限不足，请联系管理员修改")
          }
          this.$router.push({name:'detail',params:{'activityId':id,url,'shows': 3}})
        }else if(this.activeUrl.list == 'enterName'){
          const url = this.apiList.enterName.orderList
          if(!this.isAuth(url)){
            return this.$toast("权限不足，请联系管理员修改")
          }
        this.$router.push({name:'detail',params:{'activityId':id,url,'shows': 2}})
        }else if(this.activeUrl.list == 'pmSale'){
          const url = this.apiList.pmSale.orderList
          if(!this.isAuth(url)){
            return this.$toast("权限不足，请联系管理员修改")
          }
          this.$router.push({name:'detail',params:{'activityId':id,url,'shows': 0}})
        }
        
        
      },
      // goBuyingIns(id){
      //   this.$router.push( {name: 'activity_buyingIns_index', query: {id: id, type: this.type}})
      // },
      // goPro_photo(id){
      //   if(this.activeUrl.list == 'commodity'){
      //     this.$router.push( {name:'activity_photos', query: {id: id, type: 601}})
      //   }else if(this.activeUrl.list == 'addActivity'){
      //     this.$router.push( {name:'activity_photos2', query: {id: id, type: 501}})
      //   }else if(this.activeUrl.list == 'enterName'){
      //     this.$router.push( {name:'activity_photos2', query: {id: id}})
      //   }else if(this.activeUrl.list == 'pmSale'){
      //     this.$router.push( {name:'activity_photos', query: {id: id, type: 301}})
      //   }
        
      // },
      delData(id, index){
        MessageBox.confirm('确定删除?').then( data => {
          this.delActivity()
          const activeUrl = this.activeUrl.list
          let url = this.apiList[activeUrl].del
          this.$http({
            url:this.$http.adornUrl(url),
            method:'post',
            data: this.$http.adornData({id: id}, url, true)
          }).then(({ data }) => {
            this.closeUpload()
            if( data.result ){
              this.$toast('删除成功')
              this.searchList.splice(index,1)
            }else{
              this.$toast(data.msg)
            }
          })
        })
      },
      searchData(){
        return new Promise((resolve, reject)=>{
          const activeUrl = this.activeUrl.list
          const url = this.apiList[activeUrl].list
          Indicator.open({
            text: '搜索中...',
            spinnerType: 'fading-circle'
          })
          this.$http({
            url:this.$http.adornUrl(url),
            method:'post',
            data: this.$http.adornData({page: this.page, size: 10, nameLike: this.keyword }, url, true)
          }).then(({ data }) => {
            Indicator.close()
            if( data.result ){
              this.dataList = data.data.records;
              this.$nextTick(()=>{
                resolve(true)
                this.searchList = []
                this.keywords = ''
                let key = this.keyword.trim()
                if(key == '') return this.$toast('关键词不能为空')
                this.dataList.filter( item=>{
                  if(item.name.indexOf(this.keyword) != -1){
                    this.showHistory = false
                    return this.searchList.push(item)
                  }
                })
                if(this.searchList.length == 0){
                  this.showHistory = true
                  this.$toast('暂时没有数据')
                  this.$refs.list.$el.style.display = 'none';
                }
                //判断数据中有没有重复的值
                if(this.historyList.length !== 0){
                    this.historyList.forEach((item, index)=>{
                    if(this.historyList[index] === this.keyword){
                      this.keywords = item
                      this.keywordsIndex = index
                    }
                  })
                }
                if(this.historyList.length < 6){
                  if(this.keywords != ''){
                    //替换重复的值
                    this.historyList.splice(this.keywordsIndex, 1)
                    this.historyList.unshift(this.keywords)
                  }else{
                    this.historyList.unshift(this.keyword)
                  } 
                }else{
                  if(this.keywords != ''){
                    this.historyList.splice(this.keywordsIndex, 1)
                    this.historyList.unshift(this.keywords)
                  }else{
                    this.historyList.pop()
                    this.historyList.unshift(this.keyword)
                  }
                }
                const his =  this.activeUrl.list + '_' + 'historyList'
                localStorage.setItem( his , JSON.stringify(this.historyList) )
              })
            }else if(data.msg == 'sign验证失败'){
              this.$toast('您的账户登录超时')
              setTimeout(()=>{
                localStorage.clear();
                this.$router.push({name: 'login'})
              },1000)
            }
          })
        })
        
      },
      delHistory(){
        MessageBox.confirm('确定删除全部记录？').then(()=>{
          const his =  this.activeUrl.list + '_' + 'historyList'
          localStorage.removeItem(his)
            this.historyList = []
            this.showHistory = false
          })
      },
      //点击历史记录进行搜索
      getText(index){
        this.historyList.forEach((item, i)=>{
          if(index == i){
            this.keyword = item
            this.searchData()
          }
        })
      },
      getBtnAuthority(){
        if(this.activeUrl.list == 'commodity'){
          this.type = 601
          this.orderBtn = this.isAuth('/product/listOrderData')
          this.modBtn = this.isAuth('/product/mod')
          this.delBtn = this.isAuth('/product/del')
        }else if(this.activeUrl.list == 'addActivity'){
          this.type = 501
          this.orderBtn = this.isAuth('/activityPlan/listOrderData')
          this.modBtn = this.isAuth('/activityPlan/mod')
          this.delBtn = this.isAuth('/activityPlan/del')
        }else if(this.activeUrl.list == 'enterName'){
          this.type = 101
          this.orderBtn = this.isAuth('/enterName/listOrderData')
          this.modBtn = this.isAuth('/enterName/mod')
          this.delBtn = this.isAuth('/enterName/del')
         return this.imgBtn = this.isAuth("/activityPhotoShow/listByTypeAndTargetId") || this.isAuth( "/activityNote/listAll") || this.isAuth('/enterNameItem/list') || this.isAuth('/enterNameKV/list')
        }else if(this.activeUrl.list == 'pmSale'){
          this.type = 301
          this.orderBtn = this.isAuth('/pmSale/listOrderData')
          this.modBtn = this.isAuth('/pmSale/mod')
          this.delBtn = this.isAuth('/pmSale/del')
        }
        this.imgBtn = this.isAuth("/activityPhotoShow/listByTypeAndTargetId") || this.isAuth( "/activityNote/listAll")
      }
    },
    watch:{
      keyword: function(){
        if(this.keyword == ''){
          this.showHistory = true;
          this.searchList = []
          this.page = 1;
          this.$refs.list.$el.style.display = 'none';
        }else{
          this.$refs.list.$el.style.display = 'block'
        }
      }
    },
    mounted(){
      this.$refs.list.$el.style.display = 'none'
      if(this.activeUrl.list == 'enterName'){
        this.$refs.list.$el.style.paddingBottom = 9.4 + 'rem'
      }
      var u = navigator.userAgent
      var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
      // 判断是否为苹果手机
        if(isiOS){
          this.$refs.iphone.style.marginTop = 0
        }
    },
    created(){
      this.getBtnAuthority()
      const his =  this.activeUrl.list + '_' + 'historyList'
      if(localStorage.getItem(his)){
        this.historyList = JSON.parse(localStorage.getItem(his)) 
      }
      
      
    }
  }
</script>
<style lang="scss">
.search{
  min-height: 100%;
  background: #F5F5F5;
  .header{
    // line-height: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .back{
      line-height: 0;
      img{
        height: 1.8rem;
      }
    }

    .search_box{
      width: 20.9rem;
      padding: 0.5rem 0;
      margin-left: 3rem;
      background: #F5F5F5;
      border-radius: 1.45rem;
      display: flex;
      align-items: center;
      overflow: hidden;
      padding-right: 2rem;
      img{
        width: 1.8rem;
        height: 1.9rem;
        vertical-align: top;
        margin-left: 1rem;
      }
      input{
        outline: none;
        border: none;
        line-height: 2rem;
        margin-left: 0.5rem;
        font-size: 1.3rem;
        color: #333;
        margin-top: 0.5rem;
        width: 100%;
        background: #f5f5f5;
      }
       ::-webkit-input-placeholder {
        color: #666;
        line-height: 2rem;
        font-size: 1.3rem;
        
      }
      ::-webkit-input-placeholder  {
        color: #666;
        line-height: 2rem;
        font-size: 1.3rem;
        
      }
      :-moz-placeholder {
        color: #666;
        line-height: 2rem;
        font-size: 1.3rem;
      }
      ::-moz-placeholder {
        color: #666;
        line-height: 2rem;
        font-size: 1.3rem;
      }
      :-ms-input-placeholder {
        color: #666;
        line-height: 2rem;
        font-size: 1.3rem;
      }
    }
    .text{
        margin-left: 1rem;
        color: #fff;
        font-size: 1.4rem;
        height: 0;
        line-height: 0;
      }
  }
  .main{
    margin-top: 1.4rem;
    .history{
      margin: 0 1.6rem;
      .searchHis{
        display: flex;
        justify-content: space-between;
        .historyText{
          color: #333;
          font-size: 1.4rem;
        }
        img{
          width: 1.4rem;
          height: 1.5rem;
          vertical-align: middle;
          margin-top: 0.5rem;
        }
      }
      .textContent{
        .wrapper{
          display: inline-block;
          margin-top: 1.2rem;
          .text{
            display: inline-block;
            padding: 0 1rem;
            height: 3.3rem;
            margin-right: 1rem;
            line-height: 3.3rem;
            text-align: center;
            border-radius: 1.65rem;
            font-size: 1.2rem;
            background: #EBEBEB;
            user-select: none;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            max-width: 7.5rem;
            min-width: 5rem;
          }
        }
      }
    }
    .conChange{
      margin: 0 auto;
      margin-bottom: 1rem;
      width: 34.3rem;
      border-radius: .8rem;
      background-color: #fff;
      padding: 0;
      overflow: visible;
    }
    
    .content{
      
      .promotionInfo{
        .promotion_main{
          box-sizing: border-box;
          margin: 0 1.2rem;
          display: flex;
          .commodityImg{
            img{
              display: inline-block;
              width: 6.7rem;
              height: 7rem;
            }
          }
          .commodityInfo{
            flex:1;
            margin-left: 1.4rem;
            .info{
              display: flex;
              justify-content: space-between;
              .text{
                font-size: 1.4rem;
                color: #333;
              }
              .original{
                margin-left: 0.5rem;
                color: #999;
                font-size: 1.1rem;
                text-decoration: line-through;
              }
              .num{
                color: #333;
                font-size: 1.4rem;
                text-align: right;
              }
              
            }
            .time{
              span{
                color: #333;
                font-size: 1.4rem;
              }
            }
          }
        }
        .foot{
          position: relative;
          height: 5.2rem;
          margin-right: 1rem;
          display: flex;
          justify-content:flex-end;
          align-items: center;
          img{
            position: absolute;
            left: 1rem;
            width: 2rem;
            height: 2rem;
            margin-right: 2.4rem;
          }
          button{
            min-width: 5.8rem;
            height: 2.7rem;
            outline: none;
            background-color: none;
            border-radius: 1.35rem;
            color: #333;
            background-color: #fff;
            margin-right: .9rem;
            font-size: 1rem;
          }
          button:last-child{
            margin: 0;
          }
        }

      }
    }
  }
}
</style>


