<template>
	<div class="photoshow">
		<div class="header">
			<span class="back" @click="goback"><img src="../../assets/my/back.png" alt=""></span>
			<span>{{titleName}}</span>
		</div>
		<div class="mianInfo">
			<ul>
				<li v-for="(item, index) in dataList" :key="item.id">
					<img :src="item.imgUrl  + '!440w'" preview="1" preview-text="风采展示" />
					<i class="close" @click="delData(index)"></i>
				</li>
				<upload v-if="dataList.length < 8" @fileChange="fileChange" class="avatar-uploader" :width="300" :height="300">
					<i class="avatar-uploader-icon"></i>
				</upload>
			</ul>
		</div>
	</div>
</template>
<script>
	import Upload from '../utils/upload.vue'
	import {
		Toast
	} from 'mint-ui'
	import {
		MessageBox
	} from 'mint-ui'
	import {
		Indicator
	} from 'mint-ui'
	export default {
		data() {
			return {
				queryId: "",
				dataList: [],
				titleName: '',
				dataname: ''
			}
		},
		components: {
			Upload
		},
		methods: {
			getDataList() {
				this.shoeLoding()
				this.dataList = []
				const url = eval("this.apiList." + this.queryId + ".list")
				this.$http({
					url: this.$http.adornUrl(url),
					method: 'post',
					data: this.$http.adornData('', url, true)
				}).then(({
					data
				}) => {
					this.hideLoding()
					if (data.result) {
						let dataList = data.data
						console.log(dataList)
						for (var i in dataList) {
							if (dataList[i].grouping == this.dataname) {
								var obj = {
									id: dataList[i].id,
									imgUrl: dataList[i].imgUrl,
								}
								this.dataList.push(obj)
							}
						}
					}
				})
			},
			shoeLoding() {
				Indicator.open({
					text: '加载中...',
					spinnerType: 'fading-circle'
				})
			},
			hideLoding() {
				Indicator.close()
			},
			goback() {
				this.$router.goback()
			},
			fileChange(imgurl) {
				const url = eval("this.apiList." + this.queryId + ".add")
				if (!this.isAuth(url)) return this.$toast('权限不足，请联系管理员修改')
				this.$http({
					url: this.$http.adornUrl(url),
					method: 'post',
					data: this.$http.adornData({
						url: imgurl,
						grouping:this.dataname
					}, url, true)
				}).then(({
					data
				}) => {
					if (data.result) {
						Toast({
							message: '上传成功',
							iconClass: 'fa fa-check fa-5x'
						})
						setTimeout(() => {
							this.getDataList()
						}, 600);
					}
				})
			},
			// 删除数据
			delData(index) {
				const url = eval("this.apiList." + this.queryId + ".del")
				if (!this.isAuth(url)) return this.$toast('权限不足，请联系管理员修改')
				this.sheetVisible = false
				MessageBox.confirm('确定删除当前图片?').then(action => {
					this.$http({
						url: this.$http.adornUrl(url),
						method: 'post',
						data: this.$http.adornData({
							id: this.dataList[index].id
						}, url, true)
					}).then(({
						data
					}) => {
						if (data.result) {
							Toast({
								message: '操作成功',
								iconClass: 'fa fa-check fa-5x'
							})
							this.dataList.splice(index, 1)
						} else {
							Toast({
								message: data.msg,
								iconClass: 'icon icon-error'
							})
						}
					})
				})
			}
		},
		created() {
			const vm = this
			console.log(this.$route.query.data)
			this.queryId = this.$route.query.id
			this.dataList = this.$route.query.data.dataList
			this.dataname = this.$route.query.data.name
			if (this.$route.query.id == 'photoShow') {
				this.titleName = '风采展示'
			}
			if (this.$route.query.id == 'aptitude') {
				this.titleName = '店铺资质'
			}
			if (this.$route.query.id == 'promise') {
				this.titleName = '店铺承诺'
			}
			this.getDataList()
			this.getRole()
		}
	}
</script>
<style lang="scss">
	.mint-toast.is-placemiddle {
		padding: 2rem;
	}

	.fa-5x {
		font-size: 0;
	}

	.fa-check:before {
		font-size: 3.2rem;
	}

	.photoshow {
		position: absolute;
		top: 0;
		bottom: 0;
		background: #fff;
		width: 100%;

		.mint-popup {
			background: transparent;
		}

		.mianInfo {
			box-sizing: border-box;

			.avatar-uploader {
				display: inline-block;
				width: 9rem;
				height: 9rem;
				margin: 1.6rem;
			}

			ul {
				li {
					display: inline-block;
					width: 9rem;
					height: 9rem;
					line-height: 9rem;
					vertical-align: top;
					margin: 1.6rem;
					position: relative;

					i {
						display: inline-block;
						width: 1.8rem;
						height: 1.8rem;
						position: absolute;
						right: 0rem;
						top: 0rem;
						z-index: 7;
						background: url('../../assets/index/close.png');
						background-size: 100% 100%;
					}

					img {
						width: 100%;
						// height: 100%;
						vertical-align: middle;
					}
				}
			}
		}

		.swiperContainer {
			width: 34.3rem;
			height: 30rem;
			margin: 0 auto;
		}
	}
</style>



