          </span>
          <span class="user_info">
            <span class="name">{{ item.userName }}</span>
            <span class="phoneNum">{{ item.phoneNum }}</span>
          </span>
          
        </span>
        <div class="content">
          {{ item.content}}
        </div>
        <div class="time">
          {{ item.time}}
        </div>
        <div class="btn" v-if="item.message"> 
          <mt-button >回复</mt-button>
        </div>
        
      </li>
    </ul>
  </div>
</template>
<script>
  export default {
    data(){
      return{
        dataList:[
          {imgurl: '', userName: '用户名字', phoneNum: 18455555555, content: '大男孩的发放开发大客户奋达科技啊哈黑科技大法黑科技大法黑科技大法大厦可能哈哈刀山火海啊', time: '2019.12.19 17:38:20', message: true},
          {imgurl: '', userName: '用户名字', phoneNum: 18455555555, content: '大男孩的发放开发大客户奋达科技啊哈黑科技大法黑科技大法黑科技大法大厦可能哈哈刀山火海啊', time: '2019.12.19 17:38:20', message: false},
          {imgurl: '', userName: '用户名字', phoneNum: 18455555555, content: '大男孩的发放开发大客户奋达科技啊哈黑科技大法黑科技大法黑科技大法大厦可能哈哈刀山火海啊', time: '2019.12.19 17:38:20',  message: true},

        ]
      }
    }
  }
</script>
<style lang="scss">
  .leaveMessage_indexchild{
    ul{
      padding-bottom: 5rem;
      li{
        padding: 0.5rem 0 1rem;
        background: #fff;
        box-shadow:0px 0.15rem 0.7rem 0px rgba(0, 0, 0, 0.08);
        margin-bottom: 1.4rem;
        .leaveMessage_userInfo{
          display: flex;
          align-items: center;
          
          img{
            width: 3.4rem;
            height: 3.4rem;
            margin-left: 1.6rem;
            background: green;
            border-radius: 50%;
          }
          .user_info{
            flex: 1;
            display: flex;
            justify-content: space-between;
            align-items: center;
            height: 4rem;
            border-bottom: 0.1rem solid #eee;
            padding-right: 1.6rem;
            margin-left: 2rem;
            .name{
              
              color: #333;
              font-size: 1.4rem;
            }
            .phoneNum{
              color: #999;
              font-size: 1.2rem;
            }
          }
          
        }
        .content{
          margin: 1.7rem 2.45rem 0 6.2rem;
          color: #333;
          font-size: 1.3rem;
        }
        .time{
          padding: 1.65rem 1.75rem 0.5rem;
          text-align: right;
          color: #999;
          font-size: 1.1rem;
          
        }
        .btn{
          padding-top: 0.85rem;
          border-top: 0.1rem solid #eee;
          button{
            display: block;
            height: 2.4rem;
            margin: 0 auto;
            border: 0.1rem solid #F64542;
            color: #F64542;
            background: none;
            border-radius: 1.2rem;
            font-size: 1.2rem;
          }
        }
        
      }
    }
  }
</style>


