<template>
<div class="indexChild_ul" >
  <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
    <van-list
      v-model="loading" 
      :finished="finished" 
      finished-text="—— 没有更多了 ——"
      @load="onLoad"
      ref="listHeight"
    >
      <van-cell class="conChange" v-for="(item, index) in dataList" :key="index" v-show=" shows==0 ? true :(shows==1?item.showToC: !item.showToC) ">
        <div class="promotion_child" >
          <div class="content">
            <div class="promotionInfo" >
              <div class="head">
                <span class="title"> {{ item.name }} </span>
                <span class="right">
                  <span class="upper">{{ item.showToC === true  ?  ( item.alreadyNum === item.totalNum ? '已抢光' : (Date.now() >= item.endTime ? '已过期' : '已上架'))   : (item.approvalId ? '审批中':'已下架') }}</span>
                </span>
              </div>
              <div class="wrapper">
                <div class="promotion_main" >
                  <div class="commodityImg">
                    <img :src="item.imgUrlSquare" alt="">
                  </div>
                  <div class="commodityInfo">
                    <div class="info">
                      <span class="price">
                        <span class="text">价格：</span><span class="text">￥{{ (item.price / 100).toFixed(2) }}</span><span class="original">￥{{ (item.showPrice/ 100).toFixed(2) }}</span> 
                      </span>
                      <span class="number"><span>数量：</span><span class="num">{{ item.totalNum }}</span></span> 
                    </div>
                    <div class="time">
                      <span>促销开始时间：</span><span>{{ setDate(item.startTime) }}</span>
                    </div>
                    <div class="time">
                      <span>促销结束时间：</span><span>{{ setDate(item.endTime) }}</span>
                    </div>
                  </div>
                </div>
                <div class="foot">
                  <!-- <img  @click.stop="showBtn(index)" src="../../assets/signUp/buttonGroup.png" alt="" v-if="isAuth('/activityNote/listAll') || isAuth('/activityPhotoShow/listByTypeAndTargetId')"> -->
                  <mt-button @click="goMerchantCode(item.activityId)" v-if="item.useThirdCode && isAuth('/thirdCode/list')" >商家码</mt-button>
                  <mt-button @click="goDetails(item.activityId)" v-if="isAuth('/pmSale/listOrderData')">明细</mt-button>
                  <mt-button @click="upperClick(item.activityId, index, item.approvalId)" v-if="isAuth('/pmSale/mod')">{{item.showToC == true ? '下架' : (item.approvalId ? '取消审批' : '上架' )}}</mt-button>
                  <mt-button @click="goModify(item.activityId, index)" v-if="isAuth('/pmSale/mod')">修改</mt-button>
                  <mt-button @click="delData(item.activityId,index)" v-if="isAuth('/pmSale/del')">删除</mt-button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </van-cell>
    </van-list>
  </van-pull-refresh>
</div>
</template>
<script>                                
import { MessageBox } from 'mint-ui'
export default {
props:['shows'],
  data(){
    return{
      dataList:[],
      page: 0,
      showBtnNum: -1,
      check: true,
      value: true,
      modifyShow: false,
      loading: false,
      finished:false,
      isLoading: false
    }
  },
  methods:{
    /**上拉加载 */
    onLoad(){
      let showToC ='';
      if(this.shows === 0){
        showToC = ''
      }else if(this.shows === 1){
        showToC = true
      }else if(this.shows === 2){
        showToC = false
      }
     return new Promise((resolve, reject) =>{
        this.page++
        const url = this.apiList.pmSale.list
        this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({  showToC: showToC,  page: this.page, size: 10}, url, true)
        }).then(({data})=>{
          if (data.result) {
            if (data.data.records.length >= 0) {
              if(localStorage.getItem('dataList')){
                let arr = data.data.records
                  this.dataList.forEach((item, index)=>{
                    arr.forEach(items=>{
                      if(item.activityId === items.activityId){
                        this.dataList.splice(index, 1, items)
                      }
                    })
                  })
                let list = this.dataList.concat(arr)
                this.dataList = Array.from(new Set([...list, ...list]))
                localStorage.setItem('dataList', JSON.stringify(this.dataList))
              }else{
                this.dataList = data.data.records
                localStorage.setItem('dataList', JSON.stringify(this.dataList))
              }
              
            }
            if (data.data.pages <= this.page) {
              this.finished = true
            }
            if(this.shows === 0){
              localStorage.setItem('pageNum', this.page)
            }else if(this.shows === 1){
              localStorage.setItem('pageNum2', this.page)
            }else if(this.shows === 2){
              localStorage.setItem('pageNum3', this.page)
            }
            this.$nextTick(() => {
              resolve(true)
              this.loading = false
              if ('scrollRestoration' in history) {
                history.scrollRestoration = 'manual'; //改为manual之后，就不会记录滚动位置
              }
            })
          }else if( data.msg == 'sign验证失败' ){
            this.$toast('您的账户登录超时')
            setTimeout(()=>{
              localStorage.clear();
              this.$router.push({name: 'login'})
            },1000)
          }
        })   
      })
    },
     /** 下拉刷新 */
    onRefresh(){
      this.page = 1
      let showToC;
      if(this.shows === 0){
        localStorage.setItem('pageNum', this.page)
        showToC = ''
      }else if(this.shows === 1){
        localStorage.setItem('pageNum2', this.page)
        showToC = true
      }else{
        localStorage.setItem('pageNum3', this.page)
        showToC = false
      }
      return new Promise((resolve, reject) =>{
        const url = this.apiList.pmSale.list
        this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({ showToC: showToC, page: 1, size: 10}, url, true)
        }).then(({data})=>{
          if (data.result) {
            if (data.data.records.length >= 0) {
              let arr = data.data.records
                this.dataList.forEach((item, index)=>{
                  arr.forEach(items=>{
                    if(item.activityId === items.activityId){
                      this.dataList.splice(index, 1, items)
                    }
                  })
                })
              let list = this.dataList.concat(arr)
              this.dataList = Array.from(new Set([...list, ...list]))
              localStorage.setItem('dataList', JSON.stringify(this.dataList))
            }
            console.table(data.data.records, "id");
            this.$nextTick(() => {
              this.$toast({
                message: '刷新成功'
              })
              this.isLoading = false
              this.finished = false
              
              
              // if(this.$refs.listHeight.$el.offsetHeight < document.documentElement.offsetHeight){
              //   this.finished = false
              //   this.loading = false
              //   this.onLoad()
              // } 
              resolve(true)
              
            })
          }else if( data.msg == 'sign验证失败' ){
            this.$toast('您的账户登录超时')
            setTimeout(()=>{
              localStorage.clear();
              this.$router.push({name: 'login'})
            },1000)
          }
        })   
      })
    },
    //上下架
    upperClick(id, index, approvalId){
      let url;
      var upperText;
      if(this.dataList[index].showToC== true){
        upperText = '确认下架？'
      }else if(approvalId){
        upperText = '取消审批？'
      }else{
        upperText = '确认上架？'
      }
      var showToC;
      if(approvalId){ //取消审批
        showToC = false
        url = this.apiList.showToC.cancel
      }else{ // 上下架
        showToC = !this.dataList[index].showToC;
        url  = this.apiList.showToC.showToC
      }
      
      MessageBox.confirm(upperText).then( ()=>{
        this.upload()
        this.$http({
          url:this.$http.adornUrl(url),
          method:'post',
          data: this.$http.adornData( { approvalId: approvalId, activityId: id, activityType: 301, showToC: showToC}, url, true )
        }).then(({ data }) => {
          this.closeUpload()
          if(data.result){
            
            // this.$nextTick(()=>{
                this.page = 0;
                this.loading = false;
                this.finished = false;
                this.onLoad();
              
            // })
            
            
            if(this.dataList[index].showToC){
              this.dataList[index].showToC = showToC
            }
              this.$toast({
                message: '操作成功',
                iconClass: 'fa fa-check fa-5x'
              })
              if(this.$refs.listHeight.$el.offsetHeight < document.documentElement.offsetHeight){
                this.finished = false
                this.loading = false
                this.onLoad()
              } 
              localStorage.setItem('dataList', JSON.stringify(this.dataList) )
            }else if(data.msg == 'sign验证失败'){
              this.$toast('您的账户登录超时')
              setTimeout(()=>{
                localStorage.clear();
                this.$router.push({name: 'login'})
              },1000)
            }else{
              this.$toast(data.msg)
            }
          })
      })
    },
    getScrollTop(){
      if(this.shows == 0){
        this.$nextTick(()=>{
           document.body.scrollTop = document.documentElement.scrollTop = localStorage.getItem('scrollTop0')
        })
      }else if(this.shows == 1){
        this.$nextTick(()=>{
         document.body.scrollTop =  document.documentElement.scrollTop = localStorage.getItem('scrollTop1')
        })
      }else{
        this.$nextTick(()=>{
          document.body.scrollTop =  document.documentElement.scrollTop = localStorage.getItem('scrollTop2')
        })
      }
    },
    setScrollTop(){
      var scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
      let num = localStorage.getItem('activeNum')
      if(num == 0){
        localStorage.setItem('scrollTop0', scrollTop)
      }else if(num == 1){
        localStorage.setItem('scrollTop1', scrollTop)
      }else if(num == 2){
        localStorage.setItem('scrollTop2', scrollTop)
      }
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
    },
    change(){
      this.dataList = JSON.parse(localStorage.getItem('dataList'));
      this.getScrollTop()
    },
    delData(id,index){
      if(!this.isAuth('/pmSale/del')) return this.$toast('权限不足，请联系管理员修改')
      MessageBox.confirm('确定删除促销商品?').then( () => {
        let url = this.apiList.pmSale.del
        this.upload()
        this.$http({
          url:this.$http.adornUrl(url),
          method:'post',
          data: this.$http.adornData({id: id}, url, true)
        }).then(({ data }) => {
          this.closeUpload()
          if( data.result ){
            this.dataList.splice(index,1)
            localStorage.setItem('dataList', JSON.stringify(this.dataList))
            this.$toast({
              message: '删除成功',
              iconClass: 'fa fa-check fa-5x'
            })
            if(this.$refs.listHeight.$el.offsetHeight < document.documentElement.offsetHeight){
              this.finished = false
              this.loading = false
              this.onLoad()
            }  
          }else if(data.msg == '未下架'){
            this.$toast('未下架')
          }else if(data.msg == 'sign验证失败'){
            this.$toast('您的账户登录超时')
            setTimeout(()=>{
              localStorage.clear();
              this.$router.push({name: 'login'})
            },1000)
          }else{
            this.$toast(data.msg)
          }
        })
      })
    },
    goMerchantCode(id){
      this.$router.push({name: 'merchantCode', query:{id: id}})
      localStorage.removeItem('dataList')
    },
    goModify(id, index){
      var scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
      this.setScrollTop()
      // this.$router.push( {name: 'promotion_add', params:{ modId: id, index: index, type: 301 }})
      this.$router.push( {name: 'activity_common_edit', params:{ modId: id, index: index, type: 301 }})
      // localStorage.removeItem('dataList')
    },
    goDetails(id){
      const url = this.apiList.pmSale.orderList
      if(!this.isAuth(url)){
        return this.$toast("权限不足，请联系管理员修改")
      }
      this.setScrollTop()
      this.$router.push({name:'detail',params:{'activityId':id,url,'shows': 0}})
    },
    // goBuyingIns(id){
    //   this.$router.push( {name:'activity_buyingIns_index', query: {id: id, type: 301}})
    // },
    // goPro_photo(id){
    //   this.$router.push( {name:'activity_photos', query: {id: id, type: 301}})
    // },
  },
    mounted(){
      this.$refs.listHeight.$el.style.minHeight = document.documentElement.clientHeight   - 11.2 * 10 + 'px'
    },
    created() {
      if(window.history.length > 3){
        if(localStorage.getItem('dataList')){
          if(this.shows === 0){
            this.change()
            if(localStorage.getItem('pageNum')){
              this.page = Number(localStorage.getItem('pageNum'))
            }
          }else if( this.shows === 1){
            this.change()
            if(localStorage.getItem('pageNum2')){
              this.page = Number(localStorage.getItem('pageNum2'))
            }
          }else if(this.shows === 2){
            this.change()
            if(localStorage.getItem('pageNum3')){
              this.page = Number(localStorage.getItem('pageNum2'))
            }
          }
        }else{
          this.getScrollTop()
        }
      }
    }
     
  }
  
</script>
<style lang="scss" >
.indexChild_ul{
  box-sizing: border-box;
  padding-top: 1.4rem;
  background-color: #F5F5F5;
  .conChange{
    margin: 0 auto;
    margin-bottom: 1rem;
    width: 34.3rem;
    padding: 0;
    overflow: visible;
    background-color: #fff;
    border-radius: .8rem;
  }
  
  .content{
    width: 100%;
    
    .promotionInfo{
      .promotion_main{
        box-sizing: border-box;
        margin: 0 1.2rem;
        display: flex;
        .commodityImg{
          img{
            display: inline-block;
            width: 6.7rem;
            height: 7rem;
          }
        }
        .commodityInfo{
          flex:1;
          margin-left: 1.4rem;
          .info{
            display: flex;
            justify-content: space-between;
            .text{
              font-size: 1.4rem;
              color: #333;
            }
            .original{
              margin-left: 0.5rem;
              color: #999;
              font-size: 1.1rem;
              text-decoration: line-through;
            }
            .num{
              color: #333;
              font-size: 1.4rem;
              text-align: right;
            }
            
          }
          .time{
            span{
              color: #333;
              font-size: 1.4rem;
            }
          }
        }
      }
      .foot{
        position: relative;
        height: 5.2rem;
        margin-right: 1rem;
        display: flex;
        justify-content:flex-end;
        align-items: center;
        img{
          position: absolute;
          left: 1rem;
          width: 2rem;
          height: 2rem;
          margin-right: 2.4rem;
        }
        button{
          min-width: 5.8rem;
          height: 2.7rem;
          outline: none;
          background-color: none;
          border-radius: 1.35rem;
          color: #333;
          background-color: #fff;
          margin-right: .9rem;
          font-size: 1rem;
        }
        button:last-child{
          margin: 0;
        }
      }
    }
  }
  
}
</style>


