<template>
  <div class="template">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../../assets/my/back.png" alt="" ></span>
      <span>新建{{ title }}</span>
    </div>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh"  >
    <div class="template_mian" ref="li">
      <div class="top"  v-if="getAuth">
        <div class="background"></div>
          <p>快来新建{{ title }}吧~</p>
          <p>新上保存模板功能啦，模板助你高效发布！</p>
          <img src="../../../assets/template/add.png" @click="goAdd">
      </div>
      <ul>
        
        <li v-for="(item , index) in templateList" :key="item.id" @click="goSubmit(item.id)">
          <img src="../../../assets/template/one.png" alt="" v-if="(index+1)%3 == 1">
          <img src="../../../assets/template/two.png" alt="" v-if="(index+1)%3 == 2 ">
          <img src="../../../assets/template/three.png" alt="" v-if="(index+1)%3 == 0 ">
          <div>
            <p>模板{{index+1}}   :{{ item.name }}</p>
            <span>选择模版建立{{ title }}</span>
            <div class="btn">
              <img src="../../../assets/template/write.png" alt="" class="write" @click.stop="goWrite(item.id)"> 
              <img src="../../../assets/template/string.png" alt="" class="str">
              <img src="../../../assets/template/del.png" alt="" class="del" @click.stop="del(item.id)">
            </div>
          </div>
        </li>
        <!-- <li>
          <img src="../../assets/template/two.png" alt="">
          <div>
            <p>模板二</p>
            <span>选择模建立促销</span>
            <div class="btn">
              <img src="../../assets/template/write.png" alt="" class="write"> 
              <img src="../../assets/template/string.png" alt="" class="str">
              <img src="../../assets/template/del.png" alt="" class="del">
            </div>
          </div>
        </li>
        <li>
          <img src="../../assets/template/three.png" alt="">
          <div>
            <p>模板三</p>
            <span>选择模建立促销</span>
            <div class="btn">
              <img src="../../assets/template/write.png" alt="" class="write"> 
              <img src="../../assets/template/string.png" alt="" class="str">
              <img src="../../assets/template/del.png" alt="" class="del">
            </div>
          </div>
        </li> -->
      </ul>
    </div>
    </van-pull-refresh>
  </div>
</template>
<script>
import { MessageBox } from 'mint-ui'
import { Indicator } from 'mint-ui'
export default {
  data(){
    return{
      title: '',
      templateList:[],
      loading: false,
      finished:false,
      isLoading: false
    }
  },
  methods:{
    goBack(){
      this.$router.goback()
    },
    goAdd(){
      this.$router.push({name:'activity_common_edit', params :{ type: this.type }})
    },
    goSubmit(id){
      // this.$router.push({name: 'promotion_add' , params :{templateAddId: id, type: 301}})
      this.$router.push({name: 'activity_common_edit' , params :{templateAddId: id, type: this.type}})
    },
    goWrite(id){
      if(!this.isAuth(this.apiList.template.update)){
        return this.$toast("权限不足，请联系管理员修改")
      }
      // this.$router.push({name: 'promotion_add' , params :{ templateEditId: id, type: 301}})
      this.$router.push({name: 'activity_common_edit' , params :{ templateEditId: id, type: this.type}})

    },
    // 获取模板列表
    getTempList(){
      
      const url = this.apiList.template.find
      Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })
      this.$http({
        url:this.$http.adornUrl(url),
        method:'post',
        data: this.$http.adornData({ type: this.type, page: 1, size: 10000 }, url, true)
      }).then(({data : res})=>{
        this.closeUpload()
        if(res.result){
          this.templateList = res.data.records
        }
      })
    },
    // 删除模板
    del(id){
      if(!this.isAuth(this.apiList.template.del)){
        return this.$toast("权限不足，请联系管理员修改")
      }
      MessageBox.confirm('确认删除模板？').then(()=>{
        const url = this.apiList.template.del
        this.$http({
          url:this.$http.adornUrl(url),
          method:'post',
          data: this.$http.adornData({id : id }, url, true)
        }).then(({data : res})=>{
          if(res.result){
            this.getTempList()
            this.$toast({
              message: '删除成功',
              iconClass: 'fa fa-check fa-5x'
            })
          }
        })
      })
    },
    onRefresh(){
      return new Promise((resolve, reject) =>{
        const url = this.apiList.template.find
        this.$http({
          url:this.$http.adornUrl(url),
          method:'post',
          data: this.$http.adornData({type: this.type, page: 1, size: 10000}, url, true)
        }).then(({data : res})=>{
          if(res.result){
            this.templateList = res.data.records
          }
        })
        this.$nextTick(() => {
          this.$toast('刷新成功')
          this.isLoading = false
          this.finished = false
          resolve(true)
        })
      })
    },
    getType(){
      this.type = this.$route.query.type
      console.log(this.type);
      switch(this.type){
        case '101':
          this.title = "报名";
          break;
        case '301':
          this.title = "促销";
          break;
        case '501':
          this.title = "活动";
          break;
        case '601':
          this.title = "商品";
          break;
      }
    },
    getAuth(){
      switch(this.type){
        case '101':
          return this.isAuth(this.apiList.enterName.add);
        case '301':
          return this.isAuth(this.apiList.pmSale.add)
        case '501':
          return this.isAuth(this.apiList.addActivity.add)
        case '601':
          return this.isAuth(this.apiList.commodity.add)
      }
    },
    
  },
  mounted(){
    this.$refs.li.style.minHeight = document.documentElement.clientHeight -  12.2 * 10 + 'px'
  },
  created(){
    this.getType()
    this.getTempList()
  }
}
</script>
<style lang="scss">
.template{
  min-height: 100%;
  background-color: #F5F5F5;
  .template_mian{
    min-height: 100%;
    .top{
      margin-top: 4rem;
      .background{
        width: 100%;
        height: 16.4rem;
        background: url('../../../assets/template/background.png') no-repeat center;
        background-size: 28rem 16.4rem;
      }
      p{
        text-align: center;
        padding: 1rem 0;
        color: #333;
        font-size: 1.8rem;
        font-family:Microsoft YaHei;
        &:nth-child(3){
          color: #666;
          font-size: 1.2rem;
        }
      }
      img{
        display: block;
        width: 4.5rem;
        height: 5rem;
        margin: 4.5rem auto 5.5rem;

      }
    }
    ul{
      width: 100%;
      margin-top: 4rem;
      padding: 0 1.5rem;
      box-sizing: border-box;
      
      li{
        width: 100%;
        height: 7.5rem;
        margin-top: 2rem;
        display: flex;
        background:rgba(255,255,255,1);
        border-radius: .8rem;
        >img{
          height: 100%;
        }
        div{
          flex: 1;
          padding: 2rem 0;
          position: relative;
          // text-align: center;
          // line-height: 7.5rem;
          >p{
            color:rgba(51,51,51,1);
            font-size: 1.4rem;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            width: 18rem;
          }
          >span{
            font-size: 1.1rem;
            color:rgba(153,153,153,1);  
          }
          .btn{
            width: 5.8rem;
            height: 1.5rem;
            position: absolute;
            margin: 0;
            padding: 0;
            top: 3rem;
            right: 1.55rem;
            span{
              // margin-top: 0.2rem;
              position: absolute;
              top: .2rem;
              height: 1.2rem;
              width: .05rem;
              color:rgba(221,221,221,1);
              margin-left: .8rem;
              margin-top: 0.1rem;
              font-size: 1.4rem;
            }
            >img{
              height: 1.4rem;
              // vertical-align: middle;
            }
            .str{
              margin-left: .8rem;
            }
            .del{
              margin-left: .8rem;
            }
          }
        }

      }
    }
  }
}
</style>



