<template>
  <div class="template_buying_ins">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../../../assets/my/back.png" alt=""  ></span>
      <span>购买须知</span>
      <span class="add_Pro">
        <img class="add" src="../../../../assets/auth/add.png" @click="goAdd">
      </span>
    </div>
    <div class="main">
      <ul>
        <li v-for="(item, index) in dataList" :key="index">
          <span class="left">
            <p>{{ item.noteKey }}</p>
            <span>{{ item.noteValue}}</span>
          </span>
          <span class="right">
            <img src="../../../../assets/auth/edit.png"  @click="goEdit(index)">
            <img src="../../../../assets/auth/del.png" @click="del(index)" alt="">
          </span>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import { MessageBox } from 'mint-ui'
export default {
  data(){
    return{
      dataList: []
    }
  },
  methods:{
    goBack(){
      this.$router.goback()
    },
    goAdd(){
      this.$router.push('templateBuyingIns_add')
    },
    del(index){
      MessageBox.confirm('确认删除？').then(()=>{
        this.dataList.splice(index, 1)
        this.$toast('已删除')
        localStorage.setItem('attention', JSON.stringify(this.dataList) )
      })
    },
    goEdit(index){
      this.$router.push({name: 'templateBuyingIns_add', query:{id: index} })
    },
  },
  created(){
    let data = localStorage.getItem('attention')
    if(data){
      this.dataList =  JSON.parse(data)
    }
  }


}
</script>
<style lang="scss">
  .template_buying_ins{
    min-height: 100%;
    background: #f5f5f5;
    .header{
      .add_Pro{
        float: right;
        margin-top: -0.2rem;
      }
    }
    .main{
      margin-top: 1.4rem;
      background: #fff;
      ul{
        li{
          display: flex;
          justify-content: space-between;
          align-items: center;
          height: 6rem;
          padding-right: 1.6rem;
          margin-left: 1.6rem;
          border-bottom: 0.1rem solid #eee;
          &:last-child{
            border: none;
          }
          .left{
            p{
              color: #333;
              font-size: 1.6rem;
            }
            span{
              display: inline-block;
              width: 28rem;
              color: #999;
              font-size: 1.1rem;
              white-space: nowrap;
              overflow: hidden;
              
              text-overflow: ellipsis;
            }
          }
          .right{
            img{
              width: 1.4rm;
              height: 1.4rem;
              &:nth-child(1){
                border-right: 0.1rem solid #ddd;
                padding-right: 1rem;
                margin-right: 0.5rem;
              }
            }
            
          }
        }
      }
    }
  }
</style>


