<template>
  <div class="small_add">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../../../assets/my/back.png" alt=""  ></span>
      <span>{{ title }}</span>
    </div>
    <div class="main">
      <p>
        <span class="star">*</span><span class="left">小组名称</span><input type="text" placeholder="请输入小项名称" v-model="name">
      </p>
      <p>
        <span class="star">*</span><span class="left">报名价格</span><input type="number" placeholder="请输入小项价格" v-model.number="price" oninput="this.value = this.value.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3')" >
        <!-- oninput="this.value=this.value.replace(/[^0-9.]+/,'')" -->
      </p>
      <p>
          <span class="left" style="color: #ccc; marginLeft: 0.6rem">商户结算价</span><input
            type="text"
            placeholder="商户结算价"
            :value="moduleGetPrice"
            readonly
            style="color: #ccc"
          >
        </p>
      <p>
        <span class="star">*</span><span class="left">报名总数</span><input type="number" placeholder="请输入小项总数" v-model.number="totalNum"  oninput="this.value=this.value.replace(/[^0-9.]+/,'')">
      </p>
      <mt-datetime-picker ref="picker" type="dateTime" @confirm="confirm" :startDate = "date"></mt-datetime-picker>
      <p @click="openPicker(1)">
        <span class="star">*</span><span class="left">开始时间</span><input type="text" readonly placeholder="请选择" v-model="startTime">
      </p>
      <p @click="openPicker(2)">
        <span class="star">*</span><span class="left">结束时间</span><input type="text" readonly placeholder="请选择" v-model="endTime">
      </p>
      
    </div>
    <div class="explain">说明：标“<span class="star">*</span>”为必填信息。</div>
    <mt-button @click="addData">保存</mt-button>
  </div>
</template>
<script>
  export default {
    data(){
      return{
        title: '',
        name:'',
        price: '',
        totalNum:'',
        startTime:'',
        endTime:'',
        mintOpen:'',
        moduleGetPrice: 0,
        dataList: [],
        date: new Date(),
        flag: false
      }
    },
    watch:{
      price:function(){
        this.moduleGetPrice = (this.price - this.price * localStorage.getItem('getModuleChou') / 10000).toFixed(2)
      }
    },
    methods:{
      goBack(){
        this.$router.goback()
      },
       openPicker(val){
        this.$refs.picker.open()
        this.mintOpen = val
      },
      confirm(val){
        switch(this.mintOpen){
          case 1:
            this.startTime = this.setDate(val);
            break;
          case 2:
            this.endTime = this.setDate(val);
            break;
        }
      },
      addData(){ 
        if(this.flag) return 
        let startTime = new Date(this.startTime).getTime()  || new Date(this.startTime.replace(/\-/g, "/")).getTime()
        let endTime = new Date(this.endTime).getTime() ||  new Date(this.endTime.replace(/\-/g, "/")).getTime()
        if(!this.name || this.price === '' || !this.totalNum || !startTime || !endTime) {
          return this.$toast('请填写必填项*')
        }
        if(endTime<startTime ){
          this.endTime = ''
          this.startTime = ''
          return this.$toast('结束时间无法小于开始时间')
        }      
        this.flag = true
        if( JSON.stringify(this.$route.params ) !== '{}'){
          //修改
          let index = this.$route.params.index
          this.dataList[index].name = this.name
          this.dataList[index].price = Math.round(this.price * 10000) / 100
          this.dataList[index].totalNum = this.totalNum
          this.dataList[index].beginTime = startTime
          this.dataList[index].endTime = endTime
          this.dataList[index].moduleGetPrice =  Math.round(this.moduleGetPrice * 10000) / 100
          localStorage.setItem( 'group', JSON.stringify(this.dataList))
          this.$toast({
            message: '修改成功',
            iconClass: 'fa fa-check fa-5x'
          })
          setTimeout(()=>{
            this.goBack()
          },500)
        }else{
          // 添加
          this.dataList.unshift({
            name: this.name, price: Math.round(this.price * 10000) / 100, moduleGetPrice: Math.round(this.moduleGetPrice * 10000) / 100, totalNum: this.totalNum, beginTime: startTime, endTime: endTime})
          localStorage.setItem('group', JSON.stringify(this.dataList))
          this.$toast({
            message: '添加成功',
            iconClass: 'fa fa-check fa-5x'
          })
          setTimeout(()=>{
            this.goBack()
          }, 500)
        }
      }
    },
    created(){
      if(localStorage.getItem('group')){
        if( JSON.stringify(this.$route.params ) !== '{}'){
          this.title = '修改小项'
          let index = this.$route.params.index
          this.dataList = JSON.parse(localStorage.getItem('group'))
          this.name = this.dataList[index].name
          this.price = (this.dataList[index].price / 100).toFixed(2)
          this.totalNum = this.dataList[index].totalNum
          this.startTime = this.setDate(this.dataList[index].beginTime) 
          this.endTime = this.setDate(this.dataList[index].endTime)
          this.$nextTick(()=>{
            this.moduleGetPrice = (this.dataList[index].moduleGetPrice / 100).toFixed(2)
          })
        }else{
          this.title = '添加小项'
          this.dataList = JSON.parse(localStorage.getItem('group'))
        }
      }else{
        this.title = '添加小项'
      }
      
    }
  }
</script>
<style lang="scss" scoped>
  .small_add{
    min-height: 100%;
    background-color: #F5F5F5;
    .main{
      margin-top: 1.2rem;
      padding: 0 1.6rem;
      background-color: #fff;
      p{
        height: 5rem;
        // line-height: 5rem;
        box-sizing: border-box;
        padding: 1.4rem 0;
        border-bottom: 0.1rem solid #EDEDED;
        .left{
          display: inline-block;
          width: 8.9rem;
          font-size: 1.4rem;
          color: #333;
        }
        input{
          width: 22rem;
          margin-left: 2rem;
          border: none;
          outline: none;
          font-size: 1.4rem;
          color: #333;  
        }
      }
      .star{
          color:#FF3B30;
        }
        ::-webkit-input-placeholder { /* WebKit, Blink, Edge */
          color: #ccc;
        }
        :-moz-placeholder { /* Mozilla Firefox 4 to 18 */
          color: #ccc;
        }
        ::-moz-placeholder { /* Mozilla Firefox 19+ */
          color: #ccc;
        }
        :-ms-input-placeholder { /* Internet Explorer 10-11 */
          color: #ccc;
        }
    }
    .explain{
      margin: 0.2rem 1.6rem;
      color: #999;
      font-size: 1.1rem;
      .star{
        color: #FF3B30;
      }
    }
    button{
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 4.75rem;
      margin-top: 4rem;
      background-color: #E63C39;
      color: #fff;
      font-size: 1.4rem;
      border-radius: 0;
    }
  }
</style>



