<template>
  <div class="feedback">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../assets/my/back.png" alt=""  ></span>
      <span>意见反馈</span>
    </div>
    <div class="feedback_main">
      <div class="problem">
        <div class="wrapper">
          <textarea v-model="fadeData" placeholder="请描述你的问题，以便我们尽快为您处理（必填）"></textarea>
        </div>
      </div>
      <div class="info">
        <p>请输入联系信息(选填)</p>
        <div class="form">
          <div class="Contacts">
            <span>联系人</span>
            <label>
              <input type="text" v-model="name" placeholder="请输入联系人名称">
            </label>
          </div>
          <div class="tel">
            <span>联系电话</span>
            <label>
              <input type="text" v-model="phone" placeholder="请输入联系人电话">
            </label>
          </div>
          <p>联系人信息将为您加密传输，保护您的隐私</p>
          
        </div>  
      </div>
    </div>
    <mt-button class="sub" @click="submitFade">提交反馈</mt-button>
  </div>
</template>
<script>
export default {
  data() {
    return {
      phone: '',
      name: '',
      fadeData: ''
    }
  },
  methods: {
    goBack(){
      this.$router.goback()
    },
    submitFade() {
      if (!this.phone) {
        return this.$toast('您输入的联系电话为空，请确认您要提交的内容')
      }
      if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.phone))){ 
        // 不是手机号
        if (!/^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$/.test(this.phone)) {
        // 也不是座机号码
          return this.$toast('您输入的联系电话有误，请确认您要提交的内容')
        }
      } 
      const url = this.apiList.fadeBack.add
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData({phoneName: this.name, fromType: 2, phoneNum: this.phone, content: this.fadeData }, url , true)
      }).then(({ data }) => {
        if (data.result) {
          this.$toast('您的问题已经为您提交成功，我们会马上进行核实处理')
        } else {
          this.$toast(data.msg)
        }
        setTimeout(() => {
          this.goBack()
        }, 600);
      })
    }
  }
}
</script>
<style lang="scss">
  .feedback{
    position: relative;
    width: 100%;
    height: 100%;
    font-size: 1.8rem;
    margin: 0 auto;
    .feedback_main{
      margin-top: 1.4rem;
      .wrapper{
        width: 92%;
        height: 12rem;
        margin: 0 auto;
        background-color: #e8e8e8;
        border-radius: 1rem;
        overflow: hidden;
        text-align: center;
        textarea{
          width: 100%;
          height: 100%;
          overflow-y:visible;
          font-size: 1.1rem;
          outline: none;
          border: none;
          box-sizing: border-box;
          padding: 1.2rem;
          margin: 0 auto;
          background-color: #e8e8e8;
          font-size: 1.4rem;
          resize: none;
          color: #666;
        }
      }
    }
    ::-webkit-input-placeholder {
      font-size: 1.4rem;
      color: #666;
    }
    :-moz-placeholder { 
      font-size: 1.4rem;
      color: #666;
    }
    ::-moz-placeholder { 
      font-size: 1.4rem;
      color: #666;
    }
    :-ms-input-placeholder {
      font-size: 1.4rem;
      color: #666;
    }
    .info{
      width: 100%;
      box-sizing: border-box;
      padding: 1rem;
      p:nth-child(1){
        padding-bottom: 1rem;
      }
      .form{
        div{
          border-bottom:1px solid #ccc;
        }
        span{
          width: 18%;
          display: inline-block;
        } 
        label{
          width: 70%;
          text-align: center;
          display: inline-block;
        }
        label:nth-child(2) {
          width: 66%;
        }
        input {
          border: none;
          padding: 1rem;
          font-size: 1.5rem;
          outline: none;
        }
        
        p{
          font-size: 1.2rem;
          color: #ccc;
          padding: 0.375rem 0 1.2rem;
        }
        
      }
    }
    .sub{
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 4.75rem;
      background-color: #F64542;
      color: #fff;
      font-size: 1.4rem;
      border-radius: 0;
    }
  }
</style>


