<template>
  <div class="roleSet">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../assets/my/back.png" alt=""  ></span>
      <span>变更岗位</span>
    </div>
    <div class="roleSet_main">
      <ul>
        <li v-for="(item, index) in roleList" :key="index" @click="changeImg(index)">
          <span><img :src="index === i ? imgUrl : imgUrl2" alt=""></span>
          <span>
            <p>{{item.roleName}}</p>
            <span>{{item.roleDesc}}</span>
          </span>
        </li>
      </ul>
    </div>
    <mt-button @click="submit">确认</mt-button>
  </div>
</template>
<script>
  export default {
    data(){
      return{
        i: -1,
        imgUrl: require('../../assets/auth/Checked.png'),
        imgUrl2: require('../../assets/auth/noChecked.png'),
        roleList: []
      }
    },
    methods:{
      goBack(){
        this.$router.goback()
      },
      getRoleList(){
        
        const url = this.apiList.role.list;
        this.loading()
        this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({searchJson: '', page: 1, size: 10000}, url, true)
        }).then(({data})=>{
          this.closeUpload()
          if (data.result) {
            if (data.data.records.length >= 0) {
              this.roleList = data.data.records
            }
          }else if(data.msg == 'sign验证失败'){
            this.$toast('您的账户登录超时')
            setTimeout(()=>{
              localStorage.clear();
              this.$router.push({name: 'login'})
            },1000)
          }
        })
          
      },
      changeImg(index){
        this.i = index
      },
      submit(){
        const url = this.apiList.role.link;
        this.upload()
        this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({usrId: this.$route.query.id , roleId: this.roleList[this.i].id}, url, true)
        }).then(({data})=>{
          this.closeUpload()
          if (data.result) {
            this.$toast({
              message: '操作成功',
              iconClass: 'fa fa-check fa-5x'
            })
            setTimeout(()=>{
              this.$router.goback()
            },500)
            
          }else if(data.msg == 'sign验证失败'){
            this.$toast('您的账户登录超时')
            setTimeout(()=>{
              localStorage.clear();
              this.$router.push({name: 'login'})
            },1000)
          }
        })
      }
    },
    created(){
      this.getRoleList()
    }
  }
</script>
<style lang="scss">
  .roleSet{
    min-height: 100%;
    background: #f5f5f5;
    .roleSet_main{
      margin-top: 1.4rem;
      ul{
        
        background: #fff;
        li{
          padding: 0 1.6rem;
          height: 6rem;
          border-bottom: 0.1rem solid #eee;
          display: flex;
          align-items: center;
          span{
            margin-right: 1.2rem;
            img{
              width: 1.6rem;
              height: 1.6rem;
            }
          }
          span:last-child{
            p{
              color: #333;
              font-size: 1.6rem;
            }
            span{
              color: #999;
              font-size: 1.1rem;
            }
          }
          
        }
        li:last-child{
          border: 0;
        }
      }
    }
    button{
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 4.75rem;
      margin-top: 4rem;
      background-color: #E63C39;
      color: #fff;
      font-size: 1.6rem;
      border-radius: 0;
    }
  }
</style>


