<template>
  <div class="outCoupon_add">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../../assets/my/back.png" alt=""  ></span>
      <span> {{ title }}导出销售</span>
    </div>
    <div class="main">
      <p>
        <span class="star">*</span><span class="adp_left">名称</span><input type="text" placeholder="请输入名称" v-model="name">
      </p>
      <p class="coverImgWrapper">
        <span class="adp_left">主图</span>
        <img class="cover_img" ref="cover_img" :src="coverImgUrl" alt="">
        <Upload class="coverImg" :width="242" :height="252" @fileChange="coverFileChange">
          <i class="avatar-uploader-icon" ref="cover" ></i>
        </Upload>
      </p>
    </div>
    <div class="explain">说明：标“<span class="star">*</span>”为必填信息。</div>
    <mt-button class="button" @click="submit">{{btn}}</mt-button>
  </div>
</template>
<script>
import Upload from '../../utils/upload.vue'
  export default {
    data(){
      return{
        btn: '',
        id: '',
        url: '',
        title: '',
        name: '',
        mainImgUrl: '',
        coverImgUrl: '',
        selected: require('../../../assets/signUp/selected.png'),
        noSelected: require('../../../assets/signUp/noSelected.png'),
        value: false
      }
    },
    methods:{
      goBack(){
        this.$router.goback()
      },
      coverFileChange(image){
        this.coverImgUrl = image
        this.imgShow()
      },
      async submit(){
        var url = this.url
        if(!this.name)return this.$toast('必填项*不能为空')
        this.loading()
        let {data} = await this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData( {
            id: this.id, 
            name: this.name, 
            imgUrl: this.coverImgUrl,
            imgUrlSquare: this.coverImgUrl, 
            showToC: this.value
          }, url, true)
        })
        this.closeUpload()
        if(data.result){
          if(this.$route.params.item){
            this.$toast({
              message: '修改成功',
              iconClass: 'fa fa-check fa-5x'
            })
          }else{
            this.$toast({
              message: '添加成功',
              iconClass: 'fa fa-check fa-5x'
            })
          }
          setTimeout(()=>{
            this.goBack()
          }, 500)
        }else{
          this.$toast(data.msg)
        }
        
      },
      imgShow(){
        if(this.coverImgUrl){
          this.$refs.cover.style.opacity = 0
          this.$refs.cover_img.style.display = 'inline-block'
        }
      },
    },
    components:{
      Upload
    },
    created(){
      if(this.$route.params.item){
        this.url = this.apiList.outCoupon.mod
        this.title = "修改"
        this.btn = '保存'
        this.name = this.$route.params.item.name
        this.coverImgUrl = this.$route.params.item.imgUrlSquare
        this.value = this.$route.params.item.showToC
        this.id = this.$route.params.item.id
        this.$nextTick(()=>{
          this.imgShow()
        })
      }else{
        this.title = "添加"
        this.btn = '提交'
        this.url = this.apiList.outCoupon.add
      }
    },

  }
</script>

<style lang="scss">
  .outCoupon_add{
    background: #f5f5f5;
    min-height: 100%;
    .main{
      margin-top: 2rem;
      background: #fff;
      p{
        display: flex;
        align-items: center;
        margin: 0 1.6rem;
        padding: 2rem 0;
        border-bottom: 0.1rem solid #eee;
        .star{
          color: #E63C39;
        }
        input{
          border: 0;
        }
        .adp_left{
          display: inline-block;
          width: 8.9rem;
          font-size: 1.4rem;
          color: #333;
        }
      }
      .coverImgWrapper{
        position: relative;
        height: 9rem;
        line-height: 4.2rem;
        padding: 2rem 0;
        .cover_img{
          display: inline-block;
          width: 6.8rem;
          height: 6.8rem;
          margin-left: 1.2rem;
          margin-top: .1rem;
          vertical-align: middle;
          border: 1px dashed #d9d9d9;

        }
        .coverImg{
          position: absolute;
          top: 23%;
          left: 26%;
          width: 6.8rem;
          height: 6.8rem;
          margin-left: 1.2rem;
          // i{
          //   margin-top: 23%;
          //   width: 4rem;
          //   height: 4rem;
          // }
        }
      }
    }
    .button{
      position: absolute;
      bottom: 0;
      width: 100%;
      margin-top: 4rem;
      background-color: #E63C39;
      color: #fff;
      font-size: 1.6rem;
      height: 4.75rem;
      border-radius: 0;

    }
  }
</style>


