<template>
<div class="nameChange">
  <div class="header">
      <span class="back" @click="goback">
        <img src="../../assets/my/back.png" alt="">
      </span>
      <span>绑定支付宝</span>
    </div>
    <div class="marin_body">
      <div class="item">
        <span>姓名</span>
        <input type="text" v-model="realName" placeholder="请输入真实姓名">
      </div>
      <div class="item">
        <span>支付宝账号</span>
        <input  v-model="aliAccount" @change="change" placeholder="请输入支付宝账号">
      </div>
      <!-- <div class="item">
        <span>手机</span>
        <input type="number" v-model="phoneNum" @change="change" placeholder="请输入手机号">
      </div>
      <div class="item">
        <span>验证码</span>
        <input type="number" v-model="code"  placeholder="请输入验证码">
        <span class="code">获取验证码</span>
      </div> -->
    </div>
    <mt-button @click="addBank" class="addbtn">绑定</mt-button>
</div>
</template>
<script>
export default {
  data() {
    return{
      realName: '',
      aliAccount: '',
      phoneNum:'',
      code: ''
    }
  },
  methods: {
    goback() {
      this.$router.goback()
    },
    change(){
      //  /^(?:\w+\.?)*\w+@(?:\w+\.)+\w+|\d{9,11}$/ 
      if(this.aliAccount.replace(/^(?:\w+\.?)*\w+@(?:\w+\.)+\w+|\d{9,11}$/)){
        console.log(1)
      }
    },
    addBank() {
      if (!this.realName) {
        return this.$toast('请确认您要输入的提款人姓名')
      }
      if (!this.aliAccount) {
        return this.$toast('请确认您要输入的支付宝账号')
      }
      const vm = this
      const url = this.apiList.money.bindAliInfo
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData({aliAccount: this.aliAccount, realName: this.realName}, url , true)
      }).then(({ data }) => {
        if (data.result) {
          this.$toast({
            message: '修改成功',
            iconClass: 'fa fa-check fa-5x'
          })
        } else {
          this.$toast(data.msg)
        }
        setTimeout(function(){
          vm.goback()
        }, 600)
      })
    }
  }
}
</script>
<style lang="scss">
.nameChange{
    // position: fixed;
    // top: 0;
    // bottom: 0;
    min-height: 100%;
    background: #f5f5f5;
    .marin_body{
      margin-top: 1.5rem;
      background: #fff;
      width: 100%;
      .item{
        // height: 5.5rem;
        // line-height: 5.5rem;
        background: #fff;
        padding:1.4rem 0;
        margin: 0 2rem;
        border-bottom: solid #EEEEEE .1rem;
        box-sizing: border-box;
        span{
          display: inline-block;
          color: #171717;
          font-size: 1.4rem;
          text-align: left;
          width: 8rem;
        }
        input{
          border: none;
          color: #666666;
          font-size: 1.4rem;
          outline: none;
          // height: 95%;
          // height: 9rem;
          height: 2.7rem;
        }
        .code{
          position: absolute;
          font-size: 1.2rem;
          color: #00A1FC;
          margin-top: .5rem;
        }
         input::-webkit-input-placeholder {
          /* placeholder颜色  */
          color: #aab2bd;
        }
      }
      .item:last-child{
        border: none;
      }
    }
    .addbtn{
      display: block;
      width: 31.6rem;
      height: 4.4rem;
      line-height: 4.4rem;
      text-align: center;
      font-size: 1.4rem;
      color: #fff;
      background: #F64542;
      margin: 9.7rem auto 0;
      border-radius: 0;
    }
  }
</style>


