<!-- 首页 -->
<template>
  <div class="main_index">
    <img src="../assets/index/pull.jpg" alt="" class="pull" >
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh" >
      <div class="main_header" >
        <p><img src="../assets/index/logo_index.png" alt="">众享乐商铺信息管理系统
          <get-scancode  class="lay_right"  />
        </p>
      </div>
      <div class="main_banner">
        <banner-list/>
      </div>
      <!-- 主操作界面 -->
      <div class="itemList">
        <ul>
          <li v-for="(item, index) in utilList" :key="index" @click="goTopage(index)">
            <img :src="item.imgUrl" alt="">
            <p>{{ item.name }}</p>
          </li>
        </ul>
        <ul>
          <li v-for="(item, index) in utilList_2" :key="index" @click="goTopage_2(index)" >
            <img :src="item.imgUrl" alt="">
            <p>{{ item.name }}</p>
          </li>
        </ul>
      </div>
      <div class="fill"></div>
      <!-- 商户功能 -->
      <div class="main_body" v-if="isAuth(this.apiList.money.getDetail)">
        <p class="body_header">商户数据</p>
        <div class="item_continer">
          <div class="item" v-for="(item, index) in dataList" :key="index" @click="goTopage_3(index)">
            <p>
              <img src="../assets/index/info_icon.png" alt="">
            </p>
            <p>{{ item.name }}</p>
            <p>{{ item.value }}</p>
          </div>
        </div>
      </div>
       <!-- 退款 -->
      <div class="refundMsg" v-if="isAuth('/moneyApproval/listOrderRefund')">
        <div class="title">
          <span>
            <span class="titleText">退款申请</span>
          </span>
          <span class="more" @click="goRefund">
            <span>更多</span><img src="../assets/index/more.png" alt="">
          </span>
        </div>
        <div class="refund_main" >
          <div class="refund_img" v-if="!showMessage">
            <img src="../assets/index/refund.png" alt="">
          </div>
          <div class="refund_content"  v-show="showMessage" v-for="(item, index) in refundList" :key="index">
            <div class="refund_header">
              <span class="user_info">
                <img :src="item.userAvatar" alt="">
                <span>{{item.userNickName}}</span>
              </span>
              <span class="time">
                <span>申请时间：</span><span>{{ item.createdAt}}</span>
              </span>
            </div>
            <div class="refund_main_content">
              <div class="activity_img">
                <img :src="item.activityImgUrl" alt="">
                <img :src="getImg(item.activityType)" alt="">
              </div>
              <div class="activity_info">
                <p title="name">{{item.logDesc}}</p>
                <p class="price">￥<span>{{(item.num / 100).toFixed(2)}}</span></p>
                <p class="state">
                  <span>状态：</span><span>{{item.useState == 101 ? '已提货' : '未提货'}}</span>
                </p>
                <p class="order">
                  <span>订单号：</span><span>{{item.outTradeNo}}</span>
                </p>
              </div>
            </div>
            <div class="btn">
              <mt-button @click="refund(false, item.id)">拒绝</mt-button>
              <mt-button @click="refund(true, item.id)">同意</mt-button>
            </div>
          </div>
          
        </div>
      </div>
      <div class="refuse" v-if="showRefuse">
        <div class="wrapper">
          <p>拒绝理由</p>
          <div class="text">
            <textarea v-model="refuseRemark" placeholder="请填写拒绝理由（必填）"></textarea>
          </div>
          <mt-button @click="showRefuse = false">取消</mt-button><mt-button @click="refuse(false)">确定</mt-button>
        </div>
      </div>
    </van-pull-refresh>
  </div>
</template>
<script>
import BannerList from './utils/banner/index.vue'
import GetScancode from './utils/getScanCode.vue'
import { MessageBox } from 'mint-ui'
import { eventBus } from '../utils/eventBus'
// import { Actionsheet } from 'vux'
export default {
  data () {
    return {
      isLoading: false,
      nowIndex: 0,
      saleNum: 0,
      username: '',
      time: '',
      refuseRemark:'',
      id:'',
      showMessage: false,
      showRefuse: false,
      refundList:[],
      dataList: [
        {name: '活动数量', value: '0'},
        {name: '员工列表', value: '0'},
        {name: '关注数量', value: '0'},
        {name: '数据统计', value: 'DATA STATISTICS'}
      ],
      show1: false,
      isUserId: false, // 苹果上架审批账号
      notice: '',
      utilList: [
        { imgUrl: require('../assets/index/item_0.png'), name: '信息修改'},
        { imgUrl: require('../assets/index/item_1.png'), name: '会员卡管理'},
        { imgUrl: require('../assets/index/item_2.png'), name: '公告管理'},
        { imgUrl: require('../assets/index/item_3.png'), name: '社区管理'}
      ],
      utilList_2: [
        { imgUrl: require('../assets/index/item_4.png'), name: '发布异业'},
        { imgUrl: require('../assets/index/item_5.png'), name: '兑换码'},
        { imgUrl: require('../assets/index/item_6.png'), name: '发布活动'},
        { imgUrl: require('../assets/index/item_7.png'), name: '权限管理'}
      ],
      notice_title: '',
      menus1: {
        menu1: '分享给朋友',
        menu2: '取消'
      },
      click (key) {
        console.log(key)
      },
      swiperOption: {
        notNextTick: false,
        paginationClickable: true,
        loop: false,
        on: {
          slideChange: () => {
            this.nowIndex = this.$refs.mySwiper.swiper.realIndex
          }
        }
      }
    }
  },
  components: {
    BannerList,
    GetScancode
  },
  methods: {
    tabClick(index) {
      this.$refs.mySwiper.swiper.slideTo(index, 300, true)
    },
    goCommodity(){
      if(!this.isAuth('/product/list')) return this.$toast('权限不足，请联系管理员修改')
      this.$router.push('/commodity')
    },
    goCoupon(){
      // this.$router.push('/coupon')
      this.$router.push('/nodata')
    },
    goCollage(){
      // this.$router.push('/collage')
      this.$router.push('/nodata')
    },
    goVip(){
       if(!this.isAuth('/collectPerson/listUserByPage')) return this.$toast('权限不足，请联系管理员修改')
      this.$router.push('/vipMember')
    },
    goRelease(){
      if(!this.isAuth('/product/list') && !this.isAuth('/pmSale/list') && !this.isAuth('/enterName/list') && !this.isAuth('/activityPlan/list') ) return this.$toast('权限不足，请联系管理员修改')
      this.$router.push('/release')
    },
    goDifferent(){
      this.$router.push('/different_index')
      // this.$router.push('/nodata')
    },
    goRefund(){
      this.$router.push('/refund')
    },
    goActivity_num(){
      // 促销 拼团（待添加） 报名 活动 商品 权限验证
      if(!this.isAuth("/pmSale/list") && !this.isAuth("/enterName/list") && !this.isAuth("/activityPlan/list") && !this.isAuth("/product/list")){
        return this.$toast('权限不足，请联系管理修改')
      }
      this.$router.push("/activity_num")
    },
     goShopDetail(){
      if(!this.isAuth('/moduleShop/getById')) return this.$toast('权限不足，请联系管理员修改')
      this.$router.push('/shopDetail')
    },
    goPhotoshow(){
      if(!this.isAuth('/shopPhotoShow/listByTypeAndTargetId')) return this.$toast('权限不足，请联系管理员修改')
      this.$router.push('/photoshow')
    },
    goTopage(index) {
      switch(index) {
        case 0:
           this.goShopDetail()
          break;
        case 1:
           this.goCollage()
          break;
        case 2:
          if(!this.isAuth('/moduleShop/getById')) return this.$toast('权限不足，请联系管理员修改')
          this.$router.push('notice')
          break;
        case 3:
          if(!this.isAuth('/comment/listComment')) return this.$toast('权限不足，请联系管理员修改')
          this.$router.push('postBarList')  
          break;
      }
    },
    goTopage_2(index) {
      switch(index) {
        case 0:
          // this.goDifferent()
          this.goCollage()
          break;
        case 1:
          if(!this.isAuth('/payKey/getOrderInfoByGetCodeKey')) return this.$toast('权限不足，请联系管理员修改')
          this.$router.push('exchangeCode')
          break;
        case 2:
          this.goRelease()
          // this.$toast('项目部署中，敬请期待！')
          break;
        case 3:
          if(!this.isAuth('/role/list')) return this.$toast('权限不足，请联系管理员修改')
          this.$router.push('auth')
          // this.$router.push('statistical_data')
          // this.$router.push('/nodata')
          break;
      }
    },
    goTopage_3(index){
      switch(index){
        case 0:
          this.goActivity_num()
          break;
        case 1:
          if(!this.isAuth('/role/list')) return this.$toast('权限不足，请联系管理员修改')
          this.$router.push('auth')
          break;
		    case 2:
          if(!this.isAuth('/collectPerson/listUserByPage')) return this.$toast('权限不足，请联系管理员修改')
          this.goVip()
          break;
        case 3:
          if(!this.isAuth('/statistics/moduleData')) return this.$toast('权限不足，请联系管理员修改')
          this.$router.push('statistical_data')
          break;
      }
    },
    /**
     * 判断是否修改登录密码
     */
    hasPass() {
      if (localStorage.getItem('hasPasssConfim')) {
        return
      }
      const url = this.apiList.login.getPass
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData('', url , true)
      }).then(({ data }) => {
        if (data.result) {
          console.log(data.data)
          localStorage.setItem('hasPasssConfim', true)
          if (data.data) {
          } else {
            MessageBox.confirm('您尚未设置密码,请及时前往设置').then(() => {
              this.$router.push({
                name: 'pass'
              })
            })
          }
        }
      })
    },
    noticeDetail() {
      this.$emit('goMessage')
    },
    /**
     * 获得店铺活动信息 
    */
    getDetail() {
      const url = this.apiList.money.getDetail
      if(!this.isAuth(url)){
        return 
      }
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData('', url, true)
      }).then(({ data }) => { 
        this.dataList = [
          {name: '活动数量', value: data.data.activityNum },
          {name: '员工列表', value: data.data.moduleUserCount  },
          {name: '关注数量', value: data.data.collectNum },
          {name: '数据统计', value: 'DATA STATISTICS'}
        ]
        localStorage.setItem('getDetail', JSON.stringify(this.dataList))
      })
    },
    /** 获取员工数量 */
    // getStaffNum(){
    //   const url = this.apiList.moduleUser.list
    //   this.$http({
    //     url: this.$http.adornUrl(url),
    //     method: 'post',
    //     data: this.$http.adornData({page: 1, size: 10000}, url, true)
    //   }).then(({ data }) => {
    //     this.saleNum = data.data.records.length
    //   })
    // },
    /** 获取店铺类型分类id */
    getGroupIds(){
      const url = this.apiList.shop.get
      if( !this.isAuth(url) ){
        return 
      }
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData('', url, true)
      }).then(({ data }) => {
        if (data.result) {
          let groupId = data.data.groupIds
          let str = groupId.slice(1, groupId.length - 1)
          localStorage.setItem('groupIds', JSON.stringify(str.split(',')))
        }
      })
    },
    /** 获取退款消息*/
    getMessage(){
      const url = this.apiList.refund.message
      if (!this.isAuth(url)){
        return 
      }
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData( '', url, true)
      }).then(({data})=>{
        if(data.result){
          if(data.data.length > 0){
            this.refundList = []
            this.refundList.push(data.data[0]) 
            this.showMessage = true
          }else{
            this.showMessage = false
          }
        }else if(data.msg == 'sign验证失败'){
          this.$toast('您的账户登录超时')
          setTimeout(()=>{
            localStorage.clear()
            this.$router.push({name:  'login' })
          },1000)
        }
      })
    },
    refund(flag, id){
      if(!this.isAuth('/moneyApproval/dealOrderRefund')) return this.$toast('权限不足，请联系管理员修改')
      if(flag == false) {
        this.id = id
        return this.showRefuse = true
      }
      MessageBox.confirm('确定退款？').then(()=>{
        const url = this.apiList.refund.apply
        this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({approvalId: id, agree: flag  }, url, true)
        }).then(({data})=>{
            if(data.result){
              this.$toast('退款成功，款项将原路返回')
              this.getMessage()
            }
        })
      })
      
    },
    refuse(){
      if(!this.isAuth('/moneyApproval/dealOrderRefund')) return this.$toast('权限不足，请联系管理员修改')
      if(this.refuseRemark == ''){
        return this.$toast({
            message: '拒绝理由不能为空',
            position: 'bottom',
            className: 'toast'
          })
      } 
      MessageBox.confirm('确定拒绝？').then(()=>{
        const url = this.apiList.refund.apply
        this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({approvalId: this.id, agree: false, refuseRemark: this.refuseRemark  }, url, true)
        }).then(({data})=>{
          console.log(data);
            if(data.result){
              this.showRefuse = false
              this.$toast({
                message: '已拒绝',
              })
              this.getMessage()
            }
        })
      })
    },
    getImg(type){
      switch(type){
        case 101:
          return require('../assets/index/signUp.png');
        case 201:
          return require('../assets/index/collage.png');
        case 301:
          return require('../assets/index/promotion.png');
        case 501:
          return require('../assets/index/activityImg.png');
        case 601:
          return require('../assets/index/commodity.png');
      }
    },
    onRefresh(){
      // this.getStaffNum()
      this.getDetail()
      this.getMessage()
      this.getGroupIds()
      setTimeout(() => {
        this.$toast('刷新成功');
        this.isLoading = false;
        this.count++;
      }, 1000)
    }
  },
  created() {
    this.getMessage()
    this.hasPass()
    if(!localStorage.getItem('groupIds')){
      this.getGroupIds()
    }
    
    if(localStorage.getItem('getDetail')){
      this.dataList = JSON.parse(localStorage.getItem('getDetail'))
    }else{
      this.getDetail()
    }
    this.android.navColorChange('#F64542')
    if (localStorage.getItem('uid') === '42') {
      this.isUserId = true
    }
    this.getRole().then(res=>{
      // console.log(res)
      // console.log( res.indexOf(this.apiList.active.detail) !== -1  )
      // console.log( this.apiList.active.detail ) 
    })
    // console.log("wwwwwwww",this.isAuth(this.apiList.active.detail))
    // this.getAuth()
    // MessageBox.confirm('您尚未设置密码,请及时前往设置').then(() => {
    //           this.$router.push({
    //             name: 'pass'
    //           })
    //         })
  },
}
</script>
<style lang="scss">
.mint-msgbox-wrapper .mint-msgbox {
    position: fixed;
    top: 50%;
    left: 50%;
    width: 28.4rem ;
    // height: 20.3rem ;
    padding: 0 1.5rem;
    box-sizing: border-box;
    -webkit-transform: translate3d(-50%, -50%, 0);
    transform: translate3d(-50%, -50%, 0);
    background-color: #fff;
    border-radius: .8rem;
    font-size: 16px;
    -webkit-user-select: none;
    overflow: hidden;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    -webkit-transition: .2s;
    transition: .2s;
    .mint-msgbox-content{
      border: none;
      margin-top: 2.6rem;
      .mint-msgbox-message{
        line-height: none;
      }
    }
    .mint-msgbox-btns{
      display: block;
      .mint-msgbox-cancel,.mint-msgbox-confirm{
        width:11.6rem;
        height:3.6rem;
        background:rgba(255,255,255,1);
        border:1px solid rgba(153, 153, 153, 1);
        border-radius:1.8rem;
        margin-top: 2.1rem;
        margin-bottom: 2.1rem;
        float: left;
        color: #999;
      }
      .mint-msgbox-confirm{
        float: right;
        background:linear-gradient(-82deg,rgba(239,83,80,1),rgba(229,57,53,1));
        color: #fff;
        border: none;
      }
    }
  }
.main_index::-webkit-scrollbar {
  display: none;
}
.toast{
  z-index: 99999;
}
.main_index{
  position: absolute;
  width: 100%;
  top: 0;
  bottom: 5rem;
  background: #f5f5f5;
  overflow: scroll;
  // -webkit-overflow-scrolling: touch;
  
  .van-pull-refresh__head,.van-loading__text,.van-loading__spinner {
    color: #fff !important;    
  }
  .pull{
    position: fixed;
    width: 100%;
  }
  .main_header{
    width: 100%;
    height: 10.9rem;
    background-color: #f64542;
    // background: -webkit-gradient(linear, left top, right top, from(#F23B39), to(#F64542));
    p{
      font-size: 1.2rem;
      color: #fff;
      margin-top: 1.8rem;
      width: 100%;
      box-sizing: border-box;
      padding: 0 1.5rem 0 2.4rem;
      float: left;
      img{
        width: 2.1rem;
        height: 1.9rem;
        vertical-align: middle;
        margin-right: .7rem;
      }
      .lay_right{
        float: right;
      }
    }
  }
  .main_banner{
    width: 34.5rem;
    height: 11.9rem;
    overflow: hidden;
    border-radius: .6rem;
    position: absolute;
    top: 5.5rem;
    left: 1.5rem;
  }
  .itemList{
    width: 100%;
    background: #fff;
    height: 22.9rem;
    box-sizing: border-box;
    padding: 6.5rem 3.5rem 0;
      ul{
        display: flex;
        justify-content: space-between;
        padding-top: 2.1rem;
        li{
          text-align: center;
          width: 5.2rem;
          img{
            display: block;
            margin: auto;
            width: 2.9rem;
            height: 2.9rem;
          }
          p{
            color: #333;
            font-size: 1rem;
            font-weight: 400;
            text-align: center;
            margin-top: .7rem;
          }
        }
      }
      .data{
        li {
          width: 7.7rem;
          background: #f6f6f6;
          border-radius: .5rem;
          box-sizing: border-box;
          padding: 1rem;
          text-align: left;
          h2{
            font-size: 1.8rem;
            color: #333333;
            padding: .3rem 0 .5rem;
            &::after{
              content: '';
              display: block;
              width: 1.1rem;
              height: .2rem;
              background: #FF7178;
            }
          }
          p{
            text-align: left;
            color: #999;
            margin-top: 0;
            height: 1rem;
          }
        }
      }
    }
  .fill{
    height: 1rem;
    width: 100%;
    background-color: #f5f5f5;
  }
  .main_body{
    width: 100%;
    height: 24.3rem;
    // margin: 1rem 0;
    margin-bottom: 1rem; 
    // padding:1rem 0 ;
    background: #FFF;
    box-sizing: border-box;
    .body_header{
      padding-left: 1.6rem;
      padding-top: 1.1rem;
      color: #333333;
      font-weight: 800;
      line-height: 1.4rem;
    }
    .item_continer{
      box-sizing: border-box;
      padding: 1rem 2.2rem;
      .item{
        display: inline-block;
        width: 16.1rem;
        height: 9.1rem;
        box-sizing: border-box;
        padding: 1rem 1.5rem;
        margin-bottom: 1rem;
        &:nth-child(odd){
          margin-right: .9rem;
        }
        &:nth-child(1){
          background: url("../assets/index/info_item0.png");
          background-size: 100% 100%;
        }
        &:nth-child(2){
          background: url("../assets/index/info_item1.png");
          background-size: 100% 100%;
        }
        &:nth-child(3){
          background: url("../assets/index/info_item2.png");
          background-size: 100% 100%;
        }
        &:nth-child(4){
          background: url("../assets/index/info_item3.png");
          background-size: 100% 100%;
        }
        p{
          color: #666;
          margin-top: .5rem;
          font-size: 1.5rem;
          line-height: 1.5rem;
          font-family:"Microsoft YaHei",微软雅黑;
          &:nth-child(1){
            margin-top: 0;
            line-height: 1.5rem;
            img{
              width: 1.5rem;
              height: 1rem;
            }
          }
          &:nth-child(3){
            font-size: 1.6rem;
            color: #333;
            font-weight: bold;
            margin-top: 2.8rem;
          }
        }
      }
      .item:last-child p:last-child{
        color: #999;
        font-size: 1rem;
        font-weight:bold;
      }
    }
  }
  .refundMsg{
    font-family: "Microsoft YaHei",微软雅黑;
    padding: 0 1.6rem 0.8rem;
    background-color: #fff;
    margin-bottom: 1rem;
    .title{
      display: flex;
      justify-content: space-between;
      padding: 1rem 0;
      .titleText{
        color: #333;
        font-size: 1.4rem;
        font-weight: bold;
      }
      .more{
        img{
          width: 0.7rem;
        }
        span{
          color: #999;
          font-size: 1rem;
        }
      }
    }
    .refund_main{
      
      .refund_img{
        img{
          display: block;
          width: 22.15rem;
          height: 15.2rem;
          margin: 0 auto;
          // border: 0.1rem dashed 
          
        }
      }
      .refund_content{
        background: #F7F9FA;
        padding-bottom: 1.2rem;
        border-radius: 0.8rem;
        .refund_header{
          display: flex;
          justify-content: space-between;
          padding: 1rem;
          border-bottom: 0.1rem dashed #fff;
          .user_info{
            img{
              display: inline-block;
              width: 2rem;
              height: 2rem;
              border-radius: 50%;
              vertical-align: middle;
            }
            span{
              color: #333;
              font-size: 1.2rem;
            }
          }
          .time{
            span{
              font-size: 1rem;
              color: #999;
              font-family: "Microsoft YaHei",微软雅黑;
            }
          }
        }
        .refund_main_content{
          display: flex;
          margin: 1.6rem 1.1rem 0;
          .activity_img{
            position: relative;
            img{
              border-radius: 0.8rem 0px 0px 0.8rem;
              display: inline-block;
              width: 11.05rem;
              height: 11.45rem;
              &:nth-child(2){
                position: absolute;
                top: 0;
                left: 0;
                width: 2.9rem;
                height: 2.9rem;
              }
            }

          }
          
          .activity_info{
            flex: 1;
            padding-left: 1.2rem;
            background: #fff;
            border-radius:  0px 0.8rem 0.8rem 0px; 
            height: 11.5rem;
            p{
              margin: 0.7rem 0;
              color: #333;
              font-size: 1.3rem;
              width: 20rem;
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
            }
            .name{
              font-size: 1.4rem;
              
            }
            .price{
              font-size: 1.1rem;
              span{
                font-weight:bold;
                font-size: 1.7rem;
              }
            }

          }
        }
        .btn{
          display: flex;
          justify-content: flex-end;
          margin-top: 1.2rem;
          button{
            width: 6.3rem;
            height: 2.7rem;
            margin-right: 1.1rem;
            background: #fff;
            color: #333;
            font-size: 1.2rem;
            border-radius: 1.35rem;
          }
          button:first-child{
            margin-right: 1.2rem;
          }
        }
      }
    }
  }
  .refuse{
      position: fixed;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background: rgba(0,0,0,0.4);
      z-index: 999;
      .wrapper{
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 10;
        transform: translate(-50%, -50%);
        background: #fff;
        width: 25rem;
        height: 20rem;
        p{
          text-align: center;
          padding: 1.2rem 0;
        }
        .text{
          width: 80%;
          height: 11rem;
          border-radius: 1.1rem;
          border: 0.1rem solid #ededed;
          margin: auto;
          outline: none;
        }
        textarea{
          width: 94%;
          height: 90%;
          margin: 0.5rem 0.8rem 0.5rem;
          color: #333;
          font-size: 1.3rem;
          border: none;
          resize: none;
        }
        button{
          float: right;
          margin: 1rem 0;
          min-width: 5.3rem;
          height: 2.7rem;
          outline: none;
          background-color: none;
          border-radius: 1.35rem;
          border: 0 solid #666;
          color: #333;
          background-color: #fff;
          margin-right: .9rem;
          font-size: 1rem;
        }
      }
    }
}
</style>


