<template>
  <div class="photos">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../../assets/my/back.png" alt=""  ></span>
        <span>风采展示</span>
    </div>
    <div class="main">
      <ul>
        <li v-for="(item, index ) in imgList" :key="index"><img :src="type === 301 ? item.url : item" preview='1' preview-text="预览效果"  alt=""><i class="close" @click="delImg(index)"></i></li>
        <Upload v-if="imgList.length < 8" class="img-uploader" :width="300" :height="300" @fileChange="fileChange">
          <i class="avatar-uploader-icon" ></i>
        </Upload>
      </ul>
      <mt-button class="btn" @click="addData">保存</mt-button>
    </div>
  </div>
</template>

<script>
import Upload from '../../utils/upload.vue'
import { MessageBox } from 'mint-ui'
  export default {
    data(){
      return{
        type: '',
        imgUrl:'',
        imgList:[]
      }
    },
    methods:{
      goBack(){
        this.$router.goback()
      },
      fileChange(image){
        if(this.$route.query.type === 301){
          this.imgList.unshift({url: image})
        }else{
          this.imgList.unshift(image)
        }
        this.$previewRefresh();
      },
      delImg(index){
        MessageBox.confirm('确定删除当前图片?').then(()=>{
          this.imgList.splice(index,1)
          this.$toast({
            message: '删除成功',
            iconClass: 'fa fa-check fa-5x'
          })
        })
        
      },
      
      addData(){
        if(this.imgList.length > 0){
          this.$toast({
            message: '添加成功',
            iconClass: 'fa fa-check fa-5x'
          })
          
        }
        setTimeout(()=>{
          this.goBack()
        },500)
        localStorage.setItem('photosImg', JSON.stringify(this.imgList))
        // var url;
        // if(this.$route.query.type == 501){
        //     url = this.apiList.addActivity.detail;
        // }else{
        //     url = this.apiList.enterName.detail;
        // }
        // if( this.imgList.length > 0 ){
        //   this.$http({
        //   url:this.$http.adornUrl(url),
        //   method:'post',
        //   data: this.$http.adornData( { id: this.$route.query.id , photos: JSON.stringify(this.imgList) }, url, true)
        // }).then(({ data }) => {
        //   console.log(data);
        //   if(data.result){
        //     this.$toast('修改成功')
        //     setTimeout(()=>{
        //       this.$router.goback()
        //     },600)
            
        //   }else{
        //     this.$toast(data.msg)
        //   }
        //   })
        // }else{
        //   this.$toast('提交失败，内容不能为空')
        // }
      },
      getData(){
        if(localStorage.getItem('photosImg')){
          this.imgList = JSON.parse(localStorage.getItem('photosImg'))
        }
        if(this.$route.query.type === 301){
          this.type = 301
        }
      //   var url;
      //  if(this.$route.query.type == 501){
      //     url = this.apiList.addActivity.detail;
      //  }else{
      //     url = this.apiList.enterName.detail;
      //  }
      //   this.$http({
      //     url: this.$http.adornUrl(url),
      //     method:'post',
      //     data: this.$http.adornData( {  id: this.$route.query.id }, url, true)

      //     }).then(({ data }) => {
      //       if(data.result){
      //         if(data.data.photos){
      //           JSON.parse(data.data.photos).forEach(item =>{
      //             this.imgList.push(item)
      //           })
      //         }
      //       }
      //     })
        
      },
    },
    components:{
      Upload
    },
    created(){
      this.getData()
    }
  }
</script>
<style lang="scss">
  .photos{
    min-height: 100%;
      // background-color: #f5f5f5;
    .main{
      // background-color: #fff;
      // margin: 1.6rem;

      ul{
        li{
          position: relative;
          display: inline-block;
          width: 9rem;
          height: 9rem;
          margin: 1.6rem;
          img{
            width: 100%;
            height: 100%;
          }
          i{
            position: absolute;
            top: 0;
            right: 0;
            width: 1.8rem;
            height: 1.8rem;
            background: url('../../../assets/index/close.png');
            background-size: 100% 100%;
          }
        }
        .img-uploader{
          display: inline-block;
          width: 9rem;
          height: 9rem;
          margin-left: 1.35rem;
          border: 1px dashed #d9d9d9;
          border-radius: 6px;
          cursor: pointer;
          position: relative;
          overflow: hidden;
          margin-top: 1.4rem;
        }
      }
      .btn{
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 4.75rem;
        margin-top: 4rem;
        background-color: #E73E3B;
        color: #fff;
        font-size: 1.4rem;
        border-radius: 0;
      }
      
    }
  }
</style>


