<template>
  <div class="passChange">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../assets/my/back.png" ></span>
      <span>修改密码</span>
      <span></span>
    </div>
    <div class="form">
      <mt-field v-if="isPass && !isFirst" label="原密码" placeholder="请输入原密码"  type="password" v-model="dataForm.oldpassword"/>
      <mt-field v-if="!isPass" label="手机号" placeholder="请输入手机号"   v-model="dataForm.phone"/>
      <mt-field v-if="!isPass" label="验证码" placeholder="请输入验证码" v-model="dataForm.code">
        <mt-button size="small" plain v-if="!isGetVsCode" @click="getVsCode">获取验证码</mt-button>
        <mt-button size="small" plain v-else :disabled="true">获取验证码{{ timelimit }}(s)</mt-button>
      </mt-field>
      <mt-field  label="新密码" placeholder="请输入新密码"  type="password" v-model="dataForm.password"/>
      <mt-field  label="确认新密码" placeholder="再次输入新密码"  type="password" v-model="dataForm.repasWord"/>
    </div>
    <div class="submit">
      <mt-button class="btn_primary" @click="submit" v-if="isPass">确认修改</mt-button>
      <mt-button class="btn_primary" @click="forget" v-if="!isPass">确认修改</mt-button>
    </div>
  </div>
</template>
<script>
import RSA from '@/utils/rsa.js'
import { Base64 } from 'js-base64'
export default {
  data() {
    return{
      dataForm: {
        phone: '',
        password: '',
        repasWord: '',
        oldpassword: '',
        code:''
      },
      isFirst: false,
      isPass: true,
      isGetVsCode: false,
      timelimit: '60'
    }
  },
  methods: {
    goBack() {
      this.$router.goback()
    },
    getVsCode() {
      if (!this.dataForm.phone || !(/^1[3|4|5|7|8][0-9]\d{4,8}$/.test(this.dataForm.phone))) {
        return this.$toast('请输入正确的手机号码')
      }
      const url = this.apiList.login.getVsCode
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData({ phoneNum: this.dataForm.phone }, '' , false)
      }).then(({ data }) => {
        if (data.result) {
          // 验证码已发送
          this.isGetVsCode = true
          this.$toast('验证码已发送，请注意查收')
          this.timeList()
        } else {
          this.$toast(data.msg)
        }
      })
    },
    timeList() {
      const time = setInterval(() => {
        this.timelimit --
        console.log(this.timelimit)
        if (this.timelimit === 0) {
          clearInterval(time)
          this.timelimit = 60
          this.isGetVsCode = false
        }
      }, 1000)
    },
    submit() {
      if (this.dataForm.password !== this.dataForm.repasWord) {
        return this.$toast('两次输入密码不一致')
      }
      const url = this.apiList.login.passManager
      this.$http({
        url: this.$http.adornUrl(this.apiList.login.publicKey),
        method: 'post',
        data: this.$http.adornData('', '', false)
      }).then(({ data }) => {
         if (!data.result) {
          this.$message('获取公钥失败')
          return
        }
        RSA.setMaxDigits(131)
        const key = new RSA.RSAKeyPair(data.data.exponent, '', data.data.modulus)
        const pwd = Base64.encode(RSA.encryptedString(key, this.dataForm.password))
        const oldPwd = Base64.encode(RSA.encryptedString(key, this.dataForm.oldpassword))
        this.upload()
        this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({userType: 2, pwd: pwd, oldPwd: oldPwd}, url, true)
        }).then(({ data }) => {
          this.closeUpload()
          if (data.result) {
            this.$toast({
              message: '修改成功',
              iconClass: 'fa fa-check fa-5x'
            })
            setTimeout(() => {
              this.$router.goback()
            }, 1000);
          } else {
            this.$toast({
              message: data.msg
            })
          }
        })
      })
    },
    // 判断是否有密码
    getPass() {
      const url = this.apiList.login.getPass
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData('', url , true)
      }).then(({ data }) => {
        if (data.result) {
          this.isFirst = !data.data
        }
      })
    },
    // 忘记密码提交
    forget(){
      if (!this.dataForm.phone || !(/^1[3|4|5|7|8][0-9]\d{4,8}$/.test(this.dataForm.phone))) {
        return this.$toast('请输入正确的手机号码')
      }
      if(!this.dataForm.code){
        return this.$toast('请输入验证码')
      }
      if(!this.dataForm.password){
        return this.$toast('请输入新密码')
      }
      if(!this.dataForm.repasWord){
        return this.$toast('请确认新密码')
      }
      if (this.dataForm.password !== this.dataForm.repasWord) {
        return this.$toast('两次输入密码不一致')
      }
      const url = this.apiList.login.passManager
      this.$http({
        url: this.$http.adornUrl(this.apiList.login.publicKey),
        method: 'post',
        data: this.$http.adornData('', '', false)
      }).then(({ data })=>{
        if (!data.result) {
          this.$message('获取公钥失败')
          return
        }
        RSA.setMaxDigits(131)
        const key = new RSA.RSAKeyPair(data.data.exponent, '', data.data.modulus)
        const pwd = Base64.encode(RSA.encryptedString(key, this.dataForm.password))
        this.$http({
          url: this.$http.adornUrl('/user/resetPwdByPhone'),
          method: 'post',
          data: this.$http.adornData({phoneNum:this.dataForm.phone,userType: 2, pwd: pwd, code : this.dataForm.code}, url, true)
        }).then(({ data }) => {
          if (data.result) {
            this.$toast({
              message: '修改成功',
              iconClass: 'icon-success'
            })
            setTimeout(() => {
              this.$router.goback()
            }, 1000);
          } else {
            // 管理员账号修改密码
            if(data.msg == '用户未注册'){
              const url = this.apiList.login.passManager
              this.$http({
                url: this.$http.adornUrl(this.apiList.login.publicKey),
                method: 'post',
                data: this.$http.adornData('', '', false)
              }).then(({ data })=>{
              if (!data.result) {
                this.$message('获取公钥失败')
                return
              }
              RSA.setMaxDigits(131)
              const key = new RSA.RSAKeyPair(data.data.exponent, '', data.data.modulus)
              const pwd = Base64.encode(RSA.encryptedString(key, this.dataForm.password))
              this.$http({
                url: this.$http.adornUrl('/user/resetPwdByPhone'),
                method: 'post',
                data: this.$http.adornData({phoneNum:this.dataForm.phone,userType: 1, pwd: pwd, code : this.dataForm.code , ty: 'entrya'}, url, true)
              }).then(({data})=>{
                if (data.result) {
                  this.$toast({
                    message: '修改成功',
                    iconClass: 'icon-success'
                  })
                  setTimeout(() => {
                    this.$router.goback()
                  }, 1000);
                }else{
                  this.$toast({
                  message: data.msg
                })
                }
              })
            })}else{
              this.$toast({
                message: data.msg
              })
            }
          }
        })
      })
      //  this.$http({
      //   url: this.$http.adornUrl('/user/resetPwdByPhone'),
      //   method: 'post',
      //   data: this.$http.adornData({userType: 2, pwd: pwd, code : this.dataForm.code}, url, true)
      // })
    }
  },
  created() {
    this.android.navColorChange('#f64542')
    this.getPass()
    // 判断是否为忘记密码入口进入
    if(this.$route.query.forget){
      this.isPass = false
    }
  }
}
</script>
<style lang="scss">
.passChange{
  background: #F5F5F5;
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  font-weight: 400;
  .form{
    margin-top: 1.4rem;
  }
  // .btn_primary{
  //   display: block;
  //   width: 31.6rem;
  //   height: 4.5rem;
  //   margin:  0 auto;
  //   line-height: 4.5rem;
  //   font-size: 1.4rem;
  //   color: #fff;
  //   background: #F64542;
  // }
  .submit{
    position: absolute;
    bottom: 0;
    width: 100%;  
    button{
      display: block;
      width: 100%;
      height: 4.4rem;
      margin:  0 auto;
      color: #fff;
      font-size: 1.4rem;
      background: #F64542;
      border-radius: 0;
      box-shadow: none;
    }
    // box-sizing: border-box;
  }

}
.mint-field .mint-cell-title{
  width: 10rem;
}
.mint-field{
  width: 100%;
  height: 5.5rem;
}
.mint-cell-wrapper{
  border-bottom: 0.1rem solid #EEEEEE;
  padding: 0;
  margin: 0 1.6rem;
}
.mint-cell-wrapper:last-child(){
  border: 0;
}
.mint-cell-text{
  color: #171717;
  font-size: 1.4rem;
}
.mint-cell-value{
  input{
   font-size: 1.4rem;
  }
}

::-webkit-input-placeholder { /* WebKit, Blink, Edge */
  color: #D7D7D7;
}
:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
  color: #D7D7D7;
}
::-moz-placeholder { /* Mozilla Firefox 19+ */
  color: #D7D7D7;
}
:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: #D7D7D7;
}
.mint-field:last-child{
  border: 0;
}

.mint-button--primary{
  font-size: 1.5rem;
  height: 4rem;
  border-radius: 2rem;
}


</style>


