<template>
<div class="iframe">
  <iframe :src="linkSrc" frameborder="0" :scrolling='scrolling' ></iframe>
</div>
</template>
<script>
export default {
  data() {
    return{
      linkSrc: '',
      scrolling: 'yes'
    }
  },
  created() {
    // this.linkSrc = "http://f.chnzxl.com/#/shopPets?shopId="+ localStorage.getItem('moduleId') + '&type=entryb'
    // this.linkSrc = "http://192.168.0.116:10087/#/shopPets?shopId="+ localStorage.getItem('moduleId') + '&type=entryb'
    this.linkSrc = "http://t.chnzxl.com/#/shopPets?shopId="+ localStorage.getItem('moduleId') + '&type=entryb'
    // this.linkSrc = "https://www.baidu.com"
    // this.linkSrc = "http://localhost:8082/#/shop"
    var u = navigator.userAgent
    var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
    // 判断是否为苹果手机
    if(isiOS){
      this.scrolling = 'no'
    }
  },
  mounted(){

  }
}
</script>
<style lang="scss">
  .iframe{
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: absolute;
    top: 0;
    overflow: auto;
    -webkit-overflow-scrolling:touch;
    width:100%;
    // min-height: 100%;
    iframe{
      width: 1px; 
      min-width: 100%; 
      *width: 100%;
      // width: 100%;
      // height: 500%;
      height: 100%;
    }
  }
</style>


