<template>
<div class="address">
  <div class="header">
    <span class="back" @click="goback"><img src="../../assets/my/back.png" alt=""  ></span>
    <span>修改地址</span>
    <span class="sure"><div @click="finish">确定</div></span>
  </div>
  <div class="main_container">
    <p class="main_container_item" @click="changeAddress">
      <span>地址</span>
      <span :class="{'achive': address !== '未填写' }">{{ address }}</span>
      <span><img src="../../assets/my/more.png" alt=""></span>
    </p>
    <p class="main_container_item">
      <span>详细地址</span>
      <span><input type="text" v-model="doorAdd" placeholder="请输入详细地址"></span>
    </p>
  </div>
</div>
</template>
<script>
import { MessageBox } from 'mint-ui'
import { eventBus } from '../../utils/eventBus.js'
export default {
  name:'mapbox',
  data () {
    return {
      lng: '',
      lat: '',
      address: '',
      doorAdd: '',
      stree: ''
    }
  },
  methods: {
    changeAddress() {
      this.$router.push({name: 'shopDetail_map', params: {address: this.address, lng: this.lng, lat: this.lat}})
    },
    finish() {
      if (this.address === '未填写') {
        return this.$toast('您尚未选择店铺地址，请确认您要修改的店铺地址')
      }
      if (this.doorAdd === '') {
        return this.$toast('您尚未填写店铺详细地址，请确认您要修改的店铺地址')
      }
      const vm = this
      const url = this.apiList.shop.edit
      this.upload()
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData({address: this.address, lng: this.lng, lat: this.lat, doorAdd: this.doorAdd }, url , true)
      }).then(({ data }) => {
        this.closeUpload()
        if (data.result) {
          this.$toast({
            message: '修改成功',
            iconClass: 'fa fa-check fa-5x'
          })
          eventBus.$emit('addressChangeSuccess', vm.address + vm.doorAdd)
        } else {
          if (data.msg === 'sign验证失败' || data.msg === '请检查userId或sign参数') {
            localStorage.clear()
            const instance = this.$toast({
              message: '您的账号登录超时',
              iconClass: 'icon icon-error'
            })
            setTimeout(() => {
              instance.close()
              this.$router.push({ name:  'login' })
            }, 1000)
          } else {
            this.$toast(res.msg)
          }
        }
        setTimeout(function(){
          vm.goback()
        }, 600)
      })
    },
    goback() {
      this.$router.goback()
    },
    getDataList() {
      this.$http({
        url: this.$http.adornUrl(this.apiList.shop.get),
        method: 'post',
        data: this.$http.adornData('', this.apiList.shop.get, true)
      }).then(({ data }) => {
        if (data.result) {
          this.address = data.data.address ? data.data.address : '未填写'
          this.doorAdd = data.data.doorAdd
          this.lat = data.data.lat
          this.lng = data.data.lng
        } else {
          if (data.msg === 'sign验证失败' || data.msg === '请检查userId或sign参数') {
            localStorage.clear()
            const instance = this.$toast({
              message: '您的账号登录超时',
              iconClass: 'icon icon-error'
            })
            setTimeout(() => {
              instance.close()
              this.$router.push({ name:  'login' })
            }, 1000)
          }
        }
      })
    }
  },
  mounted:function () {
    // 0
    if (this.$route.params.address) {
      this.address = this.$route.params.address
      this.doorAdd = this.$route.params.doorAdd
      this.lat = this.$route.params.lat
      this.lng = this.$route.params.lng
    } else {
      this.getDataList()
    }
  }
}
</script>
<style lang="scss">
.mapbox{
  width: 100%;
  height: 100%;
  .BMap_stdMpZoom{
    // margin-top: 4.4rem;
  }
  .anchorBL{
    img{
      display: none
    }
  }

}
.address{
  position: absolute;
  top: 0;
  bottom: 4.4rem;
  width: 37.5rem;
  .sure{
    float: right; 
    margin-top: .1rem;
    color: #fff;
  }
}
.address{
  .main_container{
    width: 100%;
    background: #fff;
    margin-top: 2rem;
    box-sizing: border-box;
    padding: 0 1.5rem;
  }
    .main_container_item{
      border-bottom: solid .1rem #f5f5f5;
      width: 100%;
      height: 5rem;
      line-height: 5rem;
      span{
        display: inline-block;
        font-size: 1.4rem;
        vertical-align: middle;
        text-align: left;
        i{
          color: #FF3B30;
        }
        input{
          outline: none;
          width: 100%;
          height: 3.6rem;
          border: none;
          color: #666;
        }
      }
      span:nth-child(1){
        width: 7.1rem;
        // margin-right: 3.8rem;
        padding-left: 1rem;
        margin-right: 1.8rem;
      }
      span:nth-child(2){
        color: #ccc;
        width: 18.8rem;
        white-space: nowrap;
        text-overflow:ellipsis;
        overflow: hidden;
      }
      .achive{
        color: #666666!important;
      }
      span:nth-child(3){
        float: right;
        width: .8rem;
        height: 1.4rem;
        img{
          width: 100%;
          height: 100%;
          transform: translateY(.4rem);
        }
      }
    }
}
</style>


