<template>
  <div class="comment_datail" >
    <div class="header">
      <span class="back" @click="goBack"><img src="../../../assets/my/back.png" ></span>
      <span class="headText">评论详情</span>
      <span class="edit_wrapper" @click="showDels = !showDels" v-if="isAuth('/zComment/deleteReplyA')">
        <img  src="../../../assets/comment/edit.png">
        <span>编辑</span>
      </span>
      
    </div>
    <div class="main" v-if="dataList.length > 0">
      <!-- 评论详情 -->
      <div class="box" v-for="(item, index) in dataList" :key="index">
        <div class="wrapper" >
          <img :src="item.userEntity.avatar" class="avatar">
          <div class="info">
            <span>{{ item.userEntity.name ? item.userEntity.name: '众享乐用户' }}</span>
            <span>{{ item.createdAt.substring(5,10).replace('-', '月') + '日'}}</span>
          </div>
          <div class="evaluate">
            <van-rate
              allow-half
              v-model="item.starNum"
              readonly
              color="#34BFFF"
            />
            <span>消费后评价</span>
          </div>
          <div class="content">
            <span>{{ item.content }}</span>
          </div>
          <div class="img">
            <img :src="items" v-for="(items, i) in item.imgUrls" :key="i">
          </div>
          <div class="comment_box">
            <span class="browse">浏览{{ item.browseNum }}次</span>
            <div class="comment_info">
              <span class="comment_del" @click="detComment(item.id)" v-if="isAuth('/zComment/deleteCommentA')"  >
                <img src="../../../assets/comment/del.png">
                <span>删除</span>
              </span>
              <span class="comment_num">
                <img src="../../../assets/comment/message.png">
                <span>{{ item.replyNum }}</span>
              </span>
              <span class="zan">
                <img src="../../../assets/comment/zan.png" alt="">
                <span>{{ item.zanNum }}</span>
              </span>
            </div>
            </div>
        </div>
      </div>
      <!-- 全部评论 -->
      <div class="reply">
        <h3>全部评论</h3>
        <ul>
          <van-list
            v-model="loading" 
            :finished="finished" 
            finished-text="—— 没有更多了 ——"
            @load="onLoad"
            ref="listHeight"
          >
            <li v-for="(item, index) in replyList" :key="index">
              <img :src="item.fromC ? item.cuserEntity.avatar ? item.cuserEntity.avatar: require('../../../assets/my/userAvatar.png') : item.bmoduleEntity.logo ? item.bmoduleEntity.logo : require('../../../assets/my/userAvatar.png')" class="avatar">
              <div class="reply_info">
                <span class="right">
                  <p>{{ item.fromC ? item.cuserEntity.name ? item.cuserEntity.name: '众享乐用户' : item.bmoduleEntity.name ? item.bmoduleEntity.name : '众享乐用户' }}</p>
                  <p>{{ item.createdAt.substring(5,10).replace('-', '月') + '日'}}</p>
                </span>
                <span class="left">
                  <span class="del" @click="del(item.id, item.commentId, item.fromUid, item.replyType, index)" v-if="isAuth('/zComment/deleteReplyA')">
                    <img src="../../../assets/comment/del.png" >
                    <span>删除</span>
                  </span>
                  <span class="message" @click="openReply(item.id)" v-if="isAuth('/zComment/addReply')">
                    <img src="../../../assets/comment/message.png" >
                    <span>回复</span>
                  </span>
                </span>
              </div>
              <img src="../../../assets/comment/dels.png" alt="" class="dels" v-show="showDels" @click="del(item.id, item.commentId, item.fromUid, item.replyType, index)">
              <div class="reply_content"> 
                {{ item.response }}
              </div>
              <div class="reply_child" v-show="!showReplyS[item.id]" v-if="item.zreplySmallEntity">
                <div class="reply_child_box">
                  <span class="reply_child_info">
                    <!-- <span>{{item.zreplySmallEntity.bmoduleEntity.name}}</span> -->
                    <img :src="item.zreplySmallEntity.fromC ? item.zreplySmallEntity.cuserEntity.avatar ? item.zreplySmallEntity.cuserEntity.avatar: require('../../../assets/my/userAvatar.png') : item.zreplySmallEntity.bmoduleEntity.logo ? item.zreplySmallEntity.bmoduleEntity.logo : require('../../../assets/my/userAvatar.png')">
                    <span>{{ item.zreplySmallEntity.fromC ? item.zreplySmallEntity.cuserEntity.name ? item.zreplySmallEntity.cuserEntity.name: '众享乐用户' : item.zreplySmallEntity.bmoduleEntity.name ? item.zreplySmallEntity.bmoduleEntity.name : '众享乐用户' }}</span>
                  </span>
                  <span class="reply_child_del" @click="del(item.zreplySmallEntity.id, item.zreplySmallEntity.commentId, item.zreplySmallEntity.fromUid, item.zreplySmallEntity.replyType, item.id )" v-if="isAuth('/zComment/deleteReplyA')">
                    <img src="../../../assets/comment/del.png" >
                    <span>删除</span>
                  </span>
                </div>
                <div class="reply_child_content">
                  回复@{{  item.fromC ? item.cuserEntity.name ? item.cuserEntity.name: '众享乐用户' : item.bmoduleEntity.name ? item.bmoduleEntity.name : '众享乐用户' }}：{{ item.zreplySmallEntity.response }}
                </div>
              </div>
              <div class="reply_child" v-for="(items, i) in replySList[item.id]" :key="i" v-show="showReplyS[item.id]">
                <div class="reply_child_box">
                  <span class="reply_child_info">
                    <img :src="items.fromC ? items.cuserEntity.avatar ? items.cuserEntity.avatar: require('../../../assets/my/userAvatar.png') : items.bmoduleEntity.logo ? items.bmoduleEntity.logo : require('../../../assets/my/userAvatar.png')">
                    <span>{{ items.fromC ? items.cuserEntity.name ? items.cuserEntity.name: '众享乐用户' : items.bmoduleEntity.name ? items.bmoduleEntity.name : '众享乐用户' }}</span>
                  </span>
                  <span class="reply_child_del" @click="del(items.id, items.commentId, items.fromUid, items.replyType, index, item.id, i)" v-if="isAuth('/zComment/deleteReplyA')">
                    <img src="../../../assets/comment/del.png" >
                    <span>删除</span>
                  </span>
                </div>
                <div class="reply_child_content">
                  回复@{{ item.fromC ? item.cuserEntity.name ? item.cuserEntity.name: '众享乐用户' : item.bmoduleEntity.name ? item.bmoduleEntity.name : '众享乐用户' }}：{{ items.response }}
                </div>
              </div>
               <!-- v-if="item.zreplySmallEntity" -->
              <div class="openMore" v-show="item.zreplySmallNum > 1">
                <span @click="openReplyS(item.id)" v-show="!showReplyS[item.id]"> {{showReplyS[item.id]  ? '收起' : '展开'}} </span>
                <span @click="hideComment(item.id)" v-show="showReplyS[item.id]"> {{showReplyS[item.id]  ? '收起' : '展开'}} </span>
                <img src="../../../assets/comment/open.png" v-show="!showReplyS[item.id]">
                <img src="../../../assets/comment/open.png" v-show="showReplyS[item.id]" class="retract">
              </div>
              <div class="replyInput" v-show="showReplyInput[item.id]" >
                <input type="text" placeholder="说说你的看法" v-model="replyContent" @focus="focus" @blur="blur">
                <mt-button @click="addReply(item.fromUid, item.replyType, item.id, item.zreplySmallNum )" :class="{btn: replyContent.length > 0}">发表</mt-button>
              </div>
            </li>
          </van-list>
        </ul>
      </div>
      <div class="addComment" v-show="showInput" v-if="!showDels && isAuth('/zComment/addReply')">
        <span class="input_wrapper">
          <img src="../../../assets/comment/input.png" alt="">
          <input type="text" placeholder="说说你的看法" v-model="response"  >
          <img src="../../../assets/comment/face.png" alt="">
        </span>
        <mt-button @click="addReply()" :class="{btn: response.length > 0}" >发布</mt-button>
      </div>
      <div class="bottom" v-if="showDels">
        <span>
          <img :src="selectAll ? selectImgUrl : imgUrl" alt="">
          <span @click="selectAll = !selectAll">删除全部</span>
        </span>
        <mt-button class="btn" @click="delAllData">完成</mt-button>
      </div>
    </div>
  </div>
</template>
<script>
import {MessageBox} from 'mint-ui';
  export default {
    data(){
      return{
        imgUrl: require('../../../assets/comment/circular.png') ,
        selectImgUrl: require('../../../assets/comment/selectCircular.png'),
        page: 0,
        replyContent: '',
        response: '',
        dataList: [],
        replyList: [],
        replySList: [],
        replyOneSList: [],
        showReplyInput: [],
        finished: false,
        loading: false,
        showInput: true,
        replyInput: true,
        showDels: false,
        selectAll: false,
        showReplyS: []
      }
    },
    methods:{
      goBack(){
        this.$router.goback()
      },
      getData(){
        const url = this.apiList.comment.browse;
        this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({commentId: this.$route.query.id  }, url, true)
        }).then(({data})=>{
          if(data.result){
            this.dataList = []
            let arr = []
            JSON.parse( data.data.imgUrls).forEach(items => {
              arr.push(items)
            })
            data.data['imgUrls'] = arr;
            data.data.starNum = data.data.starNum / 2
            this.dataList.push(data.data)
          }
        })
      },
      async onLoad(add){
        this.page++
        const url = this.apiList.comment.replyList;
        let {data} = await this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({ 
            commentId: this.$route.query.id,
            page: this.page, 
            size: 10,  
            replyType: 100 
          }, url, true)
        })
        if(data.result){
          if(data.data.records.length >= 0){
            if (data.data.page <= this.page) {
              this.finished = true
            }else{
              this.finished = false
            }
            data.data.records.forEach(item=>{
              this.showReplyInput[item.id] = false;
            })
            this.replyList = this.replyList.concat(data.data.records)
            this.loading = false;
          }
        }else{
          this.$toast(data.msg)
        }
      },
      async getReplyList(id){
        const url =  this.apiList.comment.replySList
        let {data} = await this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({replyId: id, page: 1, size: 9999, replyType: 101 }, url, true )
        })
        if(data.result){
          if(data.data.records.length >= 0){
            this.$set(this.replySList, id, data.data.records);
          }
        }
      },
      openReplyS(id){
        this.showReplyS[id] = true
        this.getReplyList(id)
      },
      hideComment(id){
        this.showReplyS[id] = false
        this.$forceUpdate()
      },
      del(id, commentId, uid, type, index, itemId, i){
        const url = this.apiList.comment.delReply
        MessageBox.confirm('确认删除？').then(async ()=>{
          this.delActivity()
          let {data} =  await this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({responseId: id, replyType: type, commentId: commentId, fromUid:uid }, url, true)
        })
        this.closeUpload()
          if(data.result){
            this.$toast({
              message: '删除成功',
              iconClass: 'fa fa-check fa-5x'
            })
            if(type === 100){
              this.replyList.splice(index, 1)
              this.getData()
            }else{
              if(i || i === 0){
                this.replySList[itemId].splice(i, 1)
                if(this.replySList[itemId].length <= 1){
                  this.page -= 1;
                  this.replyList = []
                  this.onLoad()
                }
              }else{
                this.page -= 1;
                this.replyList = []
                this.onLoad()
              }
            }
          }
        })
      },
      detComment(id){
        const url = this.apiList.comment.del
        MessageBox.confirm('确认删除？').then(async ()=>{
          this.delActivity()
          let {data} =  await this.$http({
            url: this.$http.adornUrl(url),
            method: 'post',
            data: this.$http.adornData({commentId: this.$route.query.id}, url, true)
          })
          this.closeUpload()
          if( data.result ){
            this.$toast({
              message: '删除成功',
              iconClass: 'fa fa-check fa-5x'
            })
            this.dataList.splice(0, 1)
          }
        })
        
      },
      async addReply(uid, type, id, smallNum){
        const url = this.apiList.comment.addReply
        var content;
        if(type === 100){
          type = 101
          content = this.replyContent;
        }else{
          type = 100
          uid = this.dataList[0].fromUid;
          content = this.response;
          id = 0
        }
        if(!content){ return this.$toast('发布的内容不能为空')}
        this.upload()
        let {data} =  await this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({ replyId: id,  toUid: uid,  replyType: type, response: content, commentId: this.$route.query.id}, url, true)
        })
        this.closeUpload()
        if(data.result){
          if(this.showReplyS[id] && type === 101 ){
            this.getReplyList(id)
            this.replyContent = '';
          }else{
            this.page = 0;
            this.response = '';
            this.replyContent = '';
            this.replyList = []
            this.onLoad();
            this.getData()
          }
          
        } 
      },
      openReply(id){
        this.showReplyInput.forEach((item, index)=>{
          if(id === index){
            if(this.showReplyInput[id] === false){
              this.$set(this.showReplyInput, id, true);
            }else{
              this.$set(this.showReplyInput, id, false);
            }
          }
          else{
            this.$set(this.showReplyInput, index , false);
          }
        })
      },
      focus(){
        this.showInput= false;
      },
      blur(){
        this.showInput= true;
        this.replyInput= false;
      },
      delAllData(){
        if(!this.selectAll){
          return this.$toast('请勾选删除全部按钮')
        }
        
        let flag = true;
        const url = this.apiList.comment.delAll
        MessageBox.confirm('确认删除？').then(async ()=>{
          let arr = []
          this.delActivity()
          this.replyList.forEach(  (item, index)=>{
            arr.push(item.id)
          })
          let {data} =  await this.$http({
              url: this.$http.adornUrl(url),
              method: 'post',
              data: this.$http.adornData({
                responseId: JSON.stringify(arr), 
                commentId: this.dataList[0].id, 
              }, url, true)
            })
            this.closeUpload()
            if(data.result){
              if(flag){
                this.$toast({
                  message: '删除成功',
                  iconClass: 'fa fa-check fa-5x'
                })
                flag = false;
                this.showDels = false;
                this.replyList = []
                this.page -= 1;
                this.onLoad()
                this.getData()
              }
            }
        })
        
       
        
      }
    },
    created() {
      
      this.getData()   
    }
  }
</script>
<style lang="scss">
  .van-rate__icon--half {
    position: absolute;
    top: 0;
    left: 0;
    width: .5em;
    overflow: hidden;
  }
  .van-list{
    padding-bottom: 4.4rem;
  }
  .comment_datail{
    background: #f5f5f5;
    min-height: 100%;
    font-family:Microsoft YaHei;
    .van-tabs__wrap{
      margin-top: -0.8rem;
    }
    .headText{
      margin-left: 4rem;
    }
    .edit_wrapper{
      float: right;
      img{
        width: 1.21rem;
        height: 1.2rem;
      }
      span{
        font-size: 1.2rem;
      }
    }
    .main{
      margin-top: 2rem;
      .box{
        font-family:Microsoft YaHei;
        position: relative;
        width: 33.45rem;
        margin: 0 auto 2rem;
        background: #fff;
        border-radius: 0.4rem;
        padding: 1rem 0;
        .wrapper{
          margin: 0 2.35rem;
          .avatar{
            position: absolute;
            left: -0.8rem;
            top: -0.8rem;
            width: 3.2rem;
            height: 3.2rem;
            border-radius: 50%;
          }
          .info, .evaluate{
            display: flex;
            justify-content: space-between;
            padding: 0.2rem 0;
          }
          .info{
            padding-top: 1rem;
            span{
              &:first-child{
                color: #666;
                font-size: 1.4rem;
              }
              &:last-child{
                color: #999;
                font-size: 1.1rem;
              }
            }
            
          }
          .evaluate{
            span{
              color: #999;
              font-size: 1.1rem;
            }
          }
          .content{
            margin-top: 1rem;
            span{
              color: #171717;
              font-size: 1.4rem;
            }
          }
          .img{
            margin-top: 1rem;
            img{
              width: 9.1rem;
              height: 9.1rem;
              margin-left: 0.5rem;
              &:first-child{
                margin: 0;
              }
              &:nth-child(4){
                margin: 0;
              }
            }
            
          }
          .comment_box{
            display: flex;
            justify-content: space-between;
            margin-top: 1rem;
            .browse{
              color: #929191;
              font-size: 1.1rem;
            }
            .comment_info{
              .comment_num, .zan, .comment_del{
                display: inline-block;
                height: 2rem;
                line-height: 2rem;
                border: 0.1rem solid #d7d7d7;
                border-radius: 1rem;
                padding: 0 1rem;
                span{
                  color: #333;
                  font-size: 1.1rem;
                }
                img{
                  vertical-align: middle;
                  width: 1.3rem;
                  height: 1.3rem;
                }
              }
              .comment_del{
                padding: 0 0.6rem;
              }
              
            }
          }
        }
        
      }
      // 回复
      .reply{
        width: 35rem;
        margin: 0 auto;
        h3{
          font-family:Microsoft YaHei;
          font-weight:400;
          color: #333;
          font-size: 1.4rem;
          
        }
        ul{
          margin-top: 2rem;
          li{
            position: relative;
            width: 33.45rem;
            margin: 0 auto 3rem;;
            padding: 1rem 0 1rem;
            border-radius: 0.4rem;
            background: #fff;
            &:last-of-type{
              margin: 0;
            }
            .avatar{
              position: absolute;
              top: -0.8rem;
              left: -0.8rem;
              width: 3.2rem;
              height: 3.2rem;
              border-radius: 50%;
            }
            .dels{
              position: absolute;
              top: -0.95rem;
              right: -0.95rem;
              width: 1.9rem;

            }
            .reply_info{
              display: flex;
              justify-content: space-between;
              align-items: center;
              margin: 1rem 2.35rem 0;
              .right{
                p:first-child{
                  color: #666;
                  font-size: 1.4rem;
                }
                p:last-child{
                  margin-top: 0.3rem;
                  color: #999;
                  font-size: 1.1rem;
                }
                
              }
              .left{
                .del{
                  img{
                    width: 1.3rem;
                    height: 1.3rem;
                    vertical-align: middle;
                    margin-top: -0.4rem;
                  }
                }
                .message{
                  img{
                    width: 1.2rem;
                    height: 1.05rem;
                    vertical-align: middle;
                    margin-top: -0.1rem;
                  }
                }
                span{
                  color: #333;
                  font-size: 1.1rem;
                }
                .del, .message{
                  height: 2rem;
                  line-height: 2rem;
                  border: 0.1rem solid #D7D7D7;
                  border-radius: 1rem;
                  padding: 0.2rem 0.8rem;
                }
              }
            }
            .reply_content{
              margin: 0.8rem 2.35rem 0;
              color: #171717;
              font-size: 1.4rem;
            }
            .reply_child{
              margin-top: 1rem;
              .reply_child_box{
                display: flex;
                justify-content: space-between;
                align-items: center;
                width: 100%;
                box-sizing: border-box;
                padding: 0 0.95rem;
                
                .reply_child_info{
                  span{
                    color: #666;
                    font-size: 1.4rem;
                  }
                  img{
                    width: 2.4rem;
                    height: 2.4rem;
                    vertical-align: middle;
                    margin-top: -0.3rem;
                    border-radius: 50%;
                  }
                }
                .reply_child_del{
                  height: 2rem;
                  line-height: 2rem;
                  border: 0.1rem solid #D7D7D7;
                  border-radius: 1rem;
                  padding: 0 0.8rem;
                  img{
                    width: 1.3rem;
                    height: 1.3rem;
                    vertical-align: middle;
                  }
                  span{
                    display: inline-block;
                    color: #333;
                    font-size: 1.1rem;
                  }
                }
                
              }
              .reply_child_content{
                width: 23rem;
                margin-left: 4.15rem;
                color: #171717;
                font-size: 1.2rem;
              }
              
            }
            .openMore{
              margin-top: 0.5rem;
              text-align: center;
              span{
                color: #999;
                font-size: 1.2rem;
              }
              img{
                width: 0.8rem;
              }
              .retract{
                transform: rotate(180deg);
              }
            }
            .replyInput{
              position: relative;
              float: right;
              width: 100%;
              margin-left: -0.5rem;
              box-shadow: 0.2rem 0.15rem 0.4rem  0.1rem rgba(0, 0, 0, 0.2);
              border-radius: 1.1rem;
              input{
                width: 80%;
                margin-left: 0.5rem;
                border: none;
                line-height: 2.5rem;
                border-radius: 1.1rem;
                font-size: 1.1rem;
              }
              button{
                position: absolute;
                top: 0;
                right: 0;
                height: 1.85rem;
                background-color: #909699;
                border-radius: 1rem;
                line-height: 1.85rem;
                margin: 0.35rem 0.75rem 0 0;
                font-size: 1.1rem;
                color: #fff;
              }
              .btn{
                background: #F74543;
              }
            }
          }
        }
      }
      .addComment{
        position: fixed;
        bottom: 0;
        width: 100%;
        height: 5rem;
        line-height: 5rem;
        background: #fff;
        .input_wrapper{
          position: relative;
          display: inline-block;
          width: 28.5rem;
          height: 3.6rem;
          line-height: 3.6rem;
          margin-left: 2rem;
          border-radius: 1.8rem;
          background: #f5f5f5;
          input{
            width: 22rem;
            border: 0;
            background: #f5f5f5;
          }
          img{
            width: 1.2rem;
            margin-left: 1rem;
            &:last-child{
              position: absolute;
              width: 1.8rem;
              vertical-align: middle;
              right: 1rem;
              top: 50%;
              transform: translateY(-50%);
            }
          }
          
        }
        button{
          height: 2.4rem;
          line-height: 2.4rem;
          // background: #F74543;
          background-color: #909699;
          font-size: 1.4rem;
          color: #fff;
          vertical-align: middle;
          border-radius: 1.2rem;
        }
        .btn{
          background: #F74543;
        }
      }
      .bottom{
        position: fixed;
        bottom: 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        height: 5rem;
        padding: 0 1.6rem;
        box-sizing: border-box;
        background: #fff;
        box-shadow:0px 0px 1.35rem 0px rgba(159,159,159,0.41);
        img{
          width: 1.8rem;
          height: 1.8rem;
          vertical-align: middle;
          margin-top: -0.6rem;
        }
        .btn{
          height: 2.4rem;
          line-height: 2.4rem;
          background: #F74543;
          border-radius: 1.2rem;
          color: #fff;
          font-size: 1.4rem;
        }
      }
    }
  }
