<template>
  <div class="bindWechat">
    <div class="header">
      <span class="back" @click="goback">
        <img src="../../assets/my/back.png" alt="">
      </span>
      <span>绑定微信</span>
    </div>
    <div class="marin_body">
      <div class="item">
        <span>姓名</span>
        <input type="text" v-model="realName" placeholder="请输入姓名">
      </div>
      <!-- <div class="item">
        <span>手机</span>
        <input type="number" v-model="phoneNum" @change="change" placeholder="请输入手机号">
      </div>
      <div class="item">
        <span>验证码</span>
        <input type="number" v-model="code"  placeholder="请输入验证码">
        <span class="code">获取验证码</span>
      </div> -->
    </div>
    <mt-button @click="addBank" class="addbtn">绑定</mt-button>
  </div>
</template>
<script>
import { eventBus } from '../../utils/eventBus.js'
import { Toast } from 'mint-ui'
export default {
  data(){
    return{
      realName:'',
      phoneNum:'',
      code:'',
      openid:'',
      accessToken:''
    }
  },
  created(){
    // 调用微信绑定安卓基座的回调监听
    const vm = this
    eventBus.$on("android_loginThird", (data) => {
      vm.openid = data.openid
      vm.accessToken = data.accessToken
      vm.bindWxRequest()
    })
  },
  beforeDestroy () {  //清除eventBus 防止多次触发
    eventBus.$off('android_loginThird')
  },
  methods:{
    goback() {
      this.$router.goback()
    },
    change(){
      if (!this.phoneNum || !(/^1[3|4|5|7|8][0-9]\d{4,8}$/.test(this.phoneNum))) {
        Toast('请输入正确的手机号码')
        this.phoneNum =''
      }
    },
    addBank(){
      if(this.realName){
        this.android.loginThird(0)
      }else{
        this.$toast('姓名不能为空')
      }
    },
    bindWxRequest() {
      const url = this.apiList.money.bindWxInfo
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData({ openId: this.openid, accessToken: this.accessToken, realName: this.realName }, url, true)
      }).then(({ data }) => {
        if (data.result) {
          this.$toast('绑定提现微信号成功')
          this.goback() 
        } else {
          this.$toast(data.msg)
        }
      })
    }
  }
}
</script>
<style lang="scss">
.bindWechat{
    min-height: 100%;
    // position: fixed;
    // top: 0;
    // bottom: 0;
    background: #f5f5f5;
    .marin_body{
      margin-top: 1.5rem;
      background: #fff;
      width: 100%;
      .item{
        background: #fff;
        padding:1.4rem 0;
        margin: 0 2rem;
        border-bottom: solid #EEEEEE .1rem;
        box-sizing: border-box;
        span{
          display: inline-block;
          color: #171717;
          font-size: 1.4rem;
          text-align: left;
          width: 8rem;
        }
        input{
          border: none;
          color: #666666;
          font-size: 1.4rem;
          outline: none;
          // height: 95%;
          // height: 9rem;
          height: 2.7rem;
        }
        .code{
          position: absolute;
          font-size: 1.2rem;
          color: #00A1FC;
        }
         input::-webkit-input-placeholder {
          /* placeholder颜色  */
          color: #aab2bd;
        }
      }
      .item:last-child{
        border: none;
      }
    }
    .addbtn{
      display: block;
      width: 31.6rem;
      height: 4.4rem;
      line-height: 4.4rem;
      text-align: center;
      font-size: 1.4rem;
      color: #fff;
      background: #F64542;
      margin: 9.7rem auto 0;
      border-radius: 0;
    }
  }
</style>




