<template>
<div class="mapbox">
  <baidu-map :center="center" :zoom="zoom" @ready="handler" style="height:100%; width: 100%" @moveend="moveend">
    <!-- <bm-navigation anchor="BMAP_ANCHOR_TOP_RIGHT"></bm-navigation> -->
      <bm-geolocation ></bm-geolocation>
    <bm-control>
      <div><img src="../../assets/shopDetail/address.png" alt=""></div>
    </bm-control>
    <!-- <bm-marker :position="{lng: clickPoint.lng, lat: clickPoint.lat}" :dragging="true" @dragend="getClickInfo" animation="BMAP_ANIMATION_BOUNCE"></bm-marker> -->
  </baidu-map>
</div>
</template>
<script>
import { eventBus } from '../../utils/eventBus.js' 
import { MessageBox } from 'mint-ui'
export default {
  name:'mapbox',
  props: {
    address: {}
  },
  data () {
    return {
      lng: '',
      lat: '',
      center: {lng: 119.303686, lat: 26.080883},
      clickPoint: {
        lng: '',
        lat: ''
      },
      firstAddress: '', //
      isready: false,
      zoom:19,
      myGeo: {},
      // 指向百度地图的实例
      Bmap:{},
      local: {}
    }
  },
  methods: {
    // getAddPoint() {
    //   this.android.getAdd()
    // },
    moveend(e) {
      // this.$nextTick(()=>{
        const vm = this
        vm.center = e.target.getCenter()
        if (vm.isready) {
          vm.myGeo.getLocation( vm.center, (res) => {
            vm.clickPoint.address = res.address
            vm.$emit('changePoint', {lng: vm.center.lng, lat: vm.center.lat, address: res})
          })
        }
      // })
      
    },
    changeAdd(lng, lat) {
      this.center.lng = lng
      this.center.lat = lat
    },
    async handler({BMap, map}){
      if(this.$route.params.lng){
        this.center.lng = this.$route.params.lng
        this.center.lat = this.$route.params.lat
      }else{
        const url = this.apiList.shop.get
        let {data} = await this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData('', url, true)
        })
        if (data.result) {
        // 更改lng、lat放在 handler函数下不然第一次进来会报错
          this.center.lng = data.data.lng ? data.data.lng : 119.303686
          this.center.lat = data.data.lat ? data.data.lat : 26.080883
          // this.firstAddress = data.data.address
          eventBus.$emit('firstAddress', {address: data.data.address})
          console.log(data.data.lng);
          console.log(data.data.lat);
        } else {
          if (data.msg === 'sign验证失败' || data.msg === '请检查userId或sign参数') {
            localStorage.clear()
            const instance = this.$toast({
              message: '您的账号登录超时',
              iconClass: 'icon icon-error'
            })
            setTimeout(() => {
              instance.close()
              this.$router.push({ name:  'login' })
            }, 2000)
          }
        }
      }
      
      const vm = this
      vm.isready = true
      vm.Bmap = BMap
      vm.myGeo = new BMap.Geocoder()
      var options = {
        onSearchComplete: function(results){
          // 判断状态是否正确
          if (vm.local.getStatus() == BMAP_STATUS_SUCCESS){
            var s = [];
            for (var i = 0; i < results.getCurrentNumPois(); i ++){
              s.push(results.getPoi(i).title + ", " + results.getPoi(i).address + ',' + results.getPoi(i).point.lat + ',' + results.getPoi(i).point.lng)
            }
            vm.$emit('getDataList', s)
          }
        }
      }
      vm.local = new BMap.LocalSearch(map, options)
    },
    getadd(val) {
      const vm = this
      this.myGeo.getPoint(val, function(point) {
        if (point) {
          vm.center = point
          vm.clickPoint = point
        } else {
          vm.$toast('您的地址没有解析到结果')
        }
      }, '福建省')
    },
    getAddList(val) {
      const vm = this
      vm.local.search(val)
    }
    
  },
  mounted() {
    // const vm = this
    // eventBus.$on('getAddressSuccess', function(data) {
    //   vm.center = {lng: data.longitude, lat: data.latitude}
    //   vm.clickPoint = {lng: data.longitude, lat: data.latitude}
    // })
  },
  
}
</script>
<style lang="scss">
.mapbox{
  .anchorTL{
    left: 50%!important;
    top: 50%!important;
    transform: translate(-50%, -100%);
  }
  .BMap_stdMpZoom{
    margin-top: 4.4rem;
  }
  .anchorBL{
    img{
      display: none
    }
  }
}
</style>


