<template>
  <div class="postBarDetails" >
    <!-- @click="hideNews"  -->
    <div class="header">
      <span class="back" @click="goBack"><img src="../../assets/my/back.png" alt=""  ></span>
      <span class="headText">{{ title }}</span>
    </div>
    <div class="main" ref="main">
      <div class="userImg">
        <img :src="comment.avatar ? comment.avatar : defaultImg" :onerror="defaultImg" />
      </div>
      <div class="userId">{{ comment.name ? comment.name : '众享乐用户' }}</div>
      <div class="content">
        <div class="title">{{ comment.title }}</div>
        <div class="text">{{ comment.content }}</div>
        <div class="showImg" v-for="(item, index) in imgList" :key="index">
          <img :src="item" />
        </div>
        <p class="issueDate">{{ comment.createdAt ? comment.createdAt.substr(2,8) : '' }}</p>
      </div>
    </div>
    <div class="commentTitle">评论</div>

    <!-- <replyModule
      :avatar = "item.avatar"
      :name = "item.name"
      :content = "item.content"
      :nextSmall = "item.nextSmall">
    </replyModule> -->
    <van-list 
        v-model="loading" 
        :finished="finished" 
        finished-text="——已显示所有评论——"
        @load="onLoad"
        ref="listHeight"
      >
      <van-cell v-for="(item,index) in newCommentList" :key="item.id">
        <div class="commentBar" >
          <div class="commentImg">
            <img :src="item.avatar ? item.avatar : defaultImg" :onerror="defaultImg" />
          </div>
          <div class="commentId">{{ item.name ? item.name : '众享乐用户' }}<img  @click="delCommentBar(item.id, index)" src="../../assets/vip/del.png" alt=""></div>
          <div class="commentContent">{{ item.content }}</div>
          <div class="replyBox" v-if="item.nextSmall" >
            <div class="replyBoxJunior" v-show="!showFalg[item.id]">
              <div class="replyImg">
                <img :src="item.avatar ? item.avatar : defaultImg" :onerror="defaultImg" />
              </div>
              <div class="replyId">{{ item.nextSmall.name ? item.nextSmall.name : '众享乐用户' }} <img   @click="delCommentBar(item.nextSmall.id, index, 1)" src="../../assets/vip/del.png" alt=""></div>
              <div class="replyContent" >
                <span>回复@</span> 
                <span>{{ item.name ? item.name : '众享乐用户' }}</span>:
                <span>{{ item.nextSmall.content }}</span>
              </div>
            </div>
            <div class="unfold">
              <div
                class="onUnfold"
                v-if="item.nextSmall.smallNum > 1"
                @click="unfoldShow(item.id, item.name, item.avatar)"
              >
                <span v-show="!showFalg[item.id]">展开评论</span>
                <!-- <span v-show="replyFalg">收起评论</span> -->
                <span class="triangleDown" v-show="!showFalg[item.id]">
                  <img src="../../assets/community/triangle.png" />
                </span>
              </div>
              <div class="moreReply" v-show="showFalg[item.id]">
                <div class="moreReplyList" v-for="items in unfoldShowList[item.id]" :key="items.id">
                  <div class="secondaryBox">
                    <div class="secondaryImg">
                      <img :src="avatar ? avatar : defaultImg" :onerror="defaultImg" />
                    </div>
                    <div class="secondaryName">{{ items.name ? items.name : '众享乐用户 ' }} <img  @click="delCommentBar(items.id, index, 2, item.id, item.name, item.nextSmall.avatar, item.nextSmall.smallNum)" src="../../assets/vip/del.png" alt=""></div>
                    <div class="secondaryContent">
                      <span>回复@</span> 
                      <span>{{ masterName ? masterName : '众享乐用户'}}</span>:
                      <span>{{ items.content }}</span>
                    </div>
                  </div>
                </div>
                <div class="packUp" @click="packUpShow(item.id)">
                  <!-- <span class="more" v-if=" total === 20">
                    <span @click="packUpShow(item.id)">加载更多</span><img src="../../assets/community/triangle.png" />
                  </span> -->
                  
                  <span v-show="showFalg[item.id]" @click="hideComment">收起评论</span>
                  <span class="triangleUp">
                    <img src="../../assets/community/triangle.png" />
                  </span>
                </div>
              </div>
            </div>
          </div>
          <div class="commentFoot">
            <span class="commentTime">
              <i>{{ item.createdAt | timeDay }}</i>
            </span>
            <div class="footRight">
              <span class="likeQuantity">{{ zanNum[index] }}</span>
              <span class="like">
                <img
                  src="../../assets/community/like.png"
                  v-show="!zan[index]"
                  @click="like(item.commentId,zan[index],index)"
                />
                
                <img
                  src="../../assets/community/dianzan.png"
                  v-show="zan[index]"
                  @click="like(item.commentId,zan[index],index)"
                />
              </span>
              <span class="stealth"></span>
              <span class="news" @click.stop="replyInput(item.id)">
                <img src="../../assets/community/reply.png" />
              </span>
            </div>
          </div>
          <div class="replyComment" v-show="showFalgList[item.id]">
            <input type="text" placeholder="这一刻的想法" class="commentIpt" @focus="replyPublishFocus" @blur="replyPublishBlur" v-model="replyContent" />
            <div
              class="commentButton"
              @click="replyPublish(item.id, replyContent)"
              :class="{'commentButtonTrue':commentButtonTrue}"
            >发表</div>
          </div>
        </div>
      </van-cell>

    </van-list> 
    <div v-show="inputShow" :class="[{'fixed': original},{'upspring': focus}]" style="position: fixed">
      <input type="text" class="ipt" placeholder="这一刻的想法" v-model="commentContent" @focus="upspring" @blur="layDown" />
      <div class="button" @click="replyComment" :class="{'buttonTrue':buttonTrue}">发表</div>
    </div>
  </div>
</template>

<script>
// import replyModule from './replyModule'
import { Indicator } from 'mint-ui'
import {MessageBox} from 'mint-ui'
export default {
  data() {
    return {
      buttonTrue: false,
      commentButtonTrue: false,
      loading: false,
      finished: false,
      page: 1,
      commentPage: 0,
      size: 20,
      id: "",
      moduleId: "",
      comment: {},
      imgList: [],
      name: "",
      title: "",
      total: '',
      content: "",
      commentContent: "",
      superSmallCommentId: 0,
      smallCommentId: '',
      defaultImg: 
        require("../../assets/my/userAvatar.png"),
      newCommentList: [],
      falg: false,
      replyContent: "",
      showFalg: [],
      unfoldShowList: [],
      masterName: "",
      showFalgList: [],
      replyFalg: false,
      showLike: false,
      zan: [],
      zanNum: [],
      avatar: "",
      focus: false,
      original: true,
      inputShow: true,
      replyContentId: '',
      isUnfold:[],
    };
  },
  filters:{
    timeDay: function(createdAt) {
      let time = createdAt.replace(/\-/g, "/");
      let createTime = Math.round(new Date(time).getTime() / 1000);
      let nowTime = Math.round(new Date().getTime() / 1000);
      let deltaSecond = nowTime - createTime + 5;
      let result = "";
      if (deltaSecond < 60) {
        result = "刚刚";
      } else if (deltaSecond < 3600) {
        result = Math.floor(deltaSecond / 60) + "分钟前";
      } else if (deltaSecond < 86400) {
        result = Math.floor(deltaSecond / 3600) + "小时前";
      } else {
        result = Math.floor(deltaSecond / 86400) + "天前";
      }
      return result;
    }
  },
  // components: {
  //   replyModule
  // },
   watch: {
    commentContent() {
      if (this.commentContent !== "") {
        this.buttonTrue = true;
      } else {
        this.buttonTrue = false;
      }
    },
    replyContent(){
      if(this.replyContent !== ''){
        this.commentButtonTrue = true
      }else{
        this.commentButtonTrue = false
      }
    },
    // showFalgList(){
    //   this.getSmallComment();
    //   this.newComment();
    // }
  },
  methods: {
    goBack() {
      this.$router.goback();
    },
    upspring() {
      this.focus = true
      this.original = false
      // document.body.scrollTop = document.documentElement.scrollTop = this.$refs.main.offsetHeight
    },
    layDown() {
      this.focus = false
      this.original = true
    },
    getSmallComment() { //获取帖子
      const url = this.apiList.community.getById
      this.$http({
        url: this.$http.adornUrl(url),
        method: "post",
        data: this.$http.adornData( {id: this.$route.query.id}, url, true)
      }).then(({ data }) => {
        if(data.result){
          this.comment = data.data;
          this.title = data.data.title
          if(data.data.hasOwnProperty('imgUrls')){
            this.imgList = JSON.parse(data.data.imgUrls)
          }
        }else{
          this.$toast(data.msg)
        }
        
      });
    },
    replyComment() { //评论帖子
      const url = this.apiList.community.smallAdd
      this.content = this.commentContent.trim();
      
      if (this.content.length > 0) {
        Indicator.open({
          text: '发表中...',
          spinnerType: 'fading-circle'
        })
        this.$http({
          url: this.$http.adornUrl(url),
          method: "post",
          data: this.$http.adornData(
            {
              superSmallCommentId: 0,
              commentId: this.$route.query.id,
              content: this.content
            },
            url,
            true
          )
        }).then(({ data }) => {
          Indicator.close()
          if(data.result){
            document.body.scrollTop = document.documentElement.scrollTop = this.$refs.main.offsetHeight
            this.newComment()

          }
        });
        this.commentContent = "";
      }
    },
    newComment() { //获取评论
      const url = this.apiList.community.smallList
                
      this.$http({
        url: this.$http.adornUrl(url),
        method: "post",
        data: this.$http.adornData(
          {
            page: this.page,
            size: this.size,
            commentId: this.$route.query.id
          },
          url,
          true
        )
      }).then(({ data }) => {
        console.log(data);
        this.newCommentList = data.data.records;
        this.newCommentList.forEach((item, i) => {
        this.showFalg[item.id] = false;
        this.showFalgList[item.id] = false;
        this.zan.push(item.zaned);
        this.zanNum.push(item.zanNum);

        })
      });
    },
    onLoad(){
      return new Promise((resolve, reject)=>{
        this.commentPage++;
        const url = this.apiList.community.smallList
        this.$http({
          url: this.$http.adornUrl(url),
          method: "post",
          data: this.$http.adornData(
            {
              page: this.commentPage,
              size: 20,
              commentId: this.$route.query.id
            },
            url,
            true
          )
        }).then(({ data }) => {
          if(data.result){
            if (data.data.records.length >= 0) {
              this.newCommentList = this.newCommentList.concat(data.data.records)
            }
            if (data.data.pages <= this.commentPage) {
              this.finished = true
            }
            this.$nextTick(()=>{
              resolve(true)
              this.loading = false
              this.newCommentList.forEach((item, i) => {
                this.showFalg[item.id] = false;
                this.showFalgList[item.id] = false;
                this.zan.push(item.zaned);
                this.zanNum.push(item.zanNum);
              })
            })
          }
        })
      })
    },
    replyInput(id) {
      this.showFalgList.forEach((item,i) => {
        if(id === i){
          if(this.showFalgList[id] === false ){
            this.$set(this.showFalgList, id, true);
          }else{
            this.$set(this.showFalgList, id, false);
          }
        }else{
          this.$set(this.showFalgList, i, false);
        }
        
      });
      this.replyContent = ''
      // if(this.replyContentId === id ){

      // }else{
        //  this.$set(this.showFalgList, id, !this.showFalgList[id]);
      // }
      // this.replyContentId = id
      // console.log(this.showFalgList);
    },
    hideNews(){
      this.showFalgList.forEach((item, i)=>{
        this.$set(this.showFalgList, i, false);
      })
    },
    replyPublish(id) { //回复评论
      this.superSmallCommentId = id;
      //alert(id)
      this.content = this.replyContent.trim();
      if (this.content.length > 0) {
        Indicator.open({
          text: '发表中...',
          spinnerType: 'fading-circle'
        })
        const url = this.apiList.community.smallAdd
        this.$http({
          url: this.$http.adornUrl(url),
          method: "post",
          data: this.$http.adornData(
            {
              superSmallCommentId: this.superSmallCommentId,
              commentId: this.$route.query.id,
              content: this.content
            },
            url,
            true
          )
        }).then(({ data }) => {
          Indicator.close()
          if(data.result){
            this.newComment()
            // this.unfoldShow(id, avatar, name)
          }
        });
        this.replyContent = "";
        this.falg = false;
      } 
    },
    replyPublishFocus(){ //input聚焦
      this.inputShow = false
    },
    replyPublishBlur(){ //input失去焦点
      this.inputShow = true
    },
    like(id, zaned, index) { //点赞
      let url = this.apiList.community.zan;
      this.$http({
        url: this.$http.adornUrl(url),
        method: "post",
        data: this.$http.adornData(
          {
            commentId: id,
            zan: !zaned
          },
          url,
          true
        )
      }).then(({ data }) => {
        if (data.result) {
          this.$set(this.zan, index, !zaned);
          if (zaned) {
            this.zanNum[index]--;
          } else {
            this.zanNum[index]++;
          }
          this.$set(this.zanNum, index, this.zanNum[index]);
        }
      });
    },
    unfoldShow(id, name, avatar, msgNum) { //回复评论列表
      // this.page++;
      this.avatar = avatar;
      this.replyFalg = !this.replyFalg;
      this.showFalg[id] = !this.showFalg[id];
      this.masterName = name;
      this.smallCommentId = id
      const url = this.apiList.community.smallCommentList
      Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })
      this.$http({
        url: this.$http.adornUrl(url),
        method: "post",
        data: this.$http.adornData(
          {
            smallCommentId: this.smallCommentId,
            page: 1,
            size: 99999
          },
          url,
          true
        )
      }).then(({ data }) => {
        this.closeUpload()
        if(data.result){
          //this.unfoldShowList = data.data.records;
          this.$set(this.unfoldShowList, id, data.data.records);
          this.total = data.data.tatal;
          console.log(this.unfoldShowList);
         // this.isUnfold[id] = true
          this.$set(this.isUnfold, id, true);
          //this.unfoldShowList = this.unfoldShowList;
          console.log(this.showFalg);
          if(msgNum === 2){
            this.newComment()
          }
        }

      });
    },
    hideComment(){
      // this.page = 0
    },
    packUpShow(id) {
      this.replyFalg = !this.replyFalg;
      this.showFalg[id] = !this.showFalg[id];
      const url = this.apiList.community.smallCommentList
      this.$http({
        url: this.$http.adornUrl(url),
        method: "post",
        data: this.$http.adornData(
          {
            smallCommentId: this.smallCommentId,
            page: this.page,
            size: this.size
          },
          url,
          true
        )
      }).then(({ data }) => {
        if(data.result){
          console.log(data);
          //this.unfoldShowList = data.data.records;
          this.$set(this.unfoldShowList, id, data.data.records);
          //this.isUnfold = false
          this.$set(this.isUnfold, id, false);
        }
      });
    },
    delCommentBar(id, index, num, mid, name, avatar, msgNum){ //删除评论
    MessageBox.confirm('确认删除评论？').then(()=>{
      const url = this.apiList.community.smallDel
      this.$http({
        url: this.$http.adornUrl(url),
        method: "post",
        data: this.$http.adornData(
          {
            smallCommentId: id,
          },
          url,
          true
        )
      }).then(({ data }) => {
        if(data.result){
          if(num === 2){
            // this.unfoldShowList.splice(index, 1)
            this.unfoldShow(mid, name, avatar, msgNum)
          }else{
            this.newComment()
          }
          this.$toast({
            message: '删除成功',
            iconClass: 'fa fa-check fa-5x'
          })
        }
      });
    })
    }
  },
  mounted(){

  },
  created() {
    this.getSmallComment();
    // this.newComment();
    
  },
}
</script>

<style lang="scss">
.postBarDetails {
  position: relative;
  background-color: #fff;
  padding-bottom: 10.15rem;

  .main {
    padding: 1.3rem 1.95rem 0 1.7rem;
    border-bottom: 0.65rem solid #eaeaea;
    overflow: hidden;
    .userImg {
      float: left;
      width: 2.95rem;
      height: 2.95rem;
      img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
      }
    }
    .userId {
      margin: 0.9rem 0 0 3.75rem;
      font-size: 1.2rem;
      font-weight: 400;
      color: #000;
    }
    .content {
      margin: 1.7rem 0 0 0.25rem;
      .title {
        font-size: 2rem;
        font-weight: 400;
        color: #000;
        margin-bottom: 1rem;
        word-break: normal;
        word-break: break-all;
      }
      .text {
        font-size: 1.35rem;
        font-weight: 400;
        color: #8a8484;
        line-height: 1.75rem;
        margin-bottom: 1.8rem;
        word-break: normal;
        word-break: break-all;
      }
      .showImg {
        //padding: 0 .8rem 0 .3rem;
        width: 32.5rem;
        margin: 0 auto;
        img {
          width: 100%;
          height: 100%;
          border-radius: 0.5rem;
        }
      }
      img{
        width: 100%;
        margin-bottom: 1rem;
      }
      .issueDate {
        font-size: 1.3rem;
        font-weight: 400;
        color: #8a8484;
        margin: 1.8rem 0 1rem 27.5rem;
      }
    }
  }
  .commentTitle {
    font-size: 1.8rem;
    font-weight: 400;
    color: #000;
    margin: 2.15rem 0 0 2.05rem;
  }
   .commentBar {
    margin: 1.3rem 1.6rem 2.85rem;
    .commentImg {
      float: left;
      //margin-top: 1.3rem;
      width: 5.3rem;
      height: 5.3rem;
      img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
      }
    }
    .commentId {
      margin: 0 0 0 6.25rem;
      font-size: 1rem;
      font-weight: 400;
      color: #7c7777;
      img{
        float: right;
        width: 1.2rem;
        height: 1.2rem;
        margin-top: 0.6rem;
      }
    }
    .commentContent {
      margin: 0.8rem 0 0 6.25rem;
      font-size: 1.2rem;
      font-weight: 400;
      color: #000;
      line-height: 1.8rem;
    }
    .replyBox {
      width: 26.35rem;
      background-color: #f1f1f1;
      border-radius: 1rem;
      margin: 1.85rem 0 0 5rem;
      overflow: hidden;
      
      .replyBoxJunior {
        .replyImg {
          float: left;
          margin: 0 0 0 1.15rem;
          width: 2.3rem;
          height: 2.3rem;
          img {
            width: 100%;
            height: 100%;
            border-radius: 50%;
          }
        }
        .replyId {
          margin: 0.95rem 0 0 3.95rem;
          font-size: 1rem;
          font-weight: 400;
          color: #7c7777;
          img{
            float: right;
            width: 1rem;
            height: 1rem;
            margin-top: 0.7rem;
            margin-right: 1rem;
          }
        }
        .replyContent {
          color: #000;
          margin: 0.4rem 0 0 3.95rem;
          line-height: 1.3rem;
          font-size: 1rem;
        }
      }
      .unfold {
        margin: 1rem 0 0.95rem 0;
        
        font-weight: 400;
        color: #7c7777;
        .onUnfold {
          width: 7rem;
          margin-left: 1.8rem;
          font-size: 1rem;
        }
        .triangleDown {
          display: inline-block;
          width: 0.6rem;
          height: 0.6rem;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .moreReply {
          margin-top: 1rem;
          
          .moreReplyList {
            margin-bottom: 0.5rem;
           
            .secondaryBox {
              span{
                font-size: 1rem;
              }
               
              .secondaryImg {
                float: left;
                margin: 0 0 0 1.15rem;
                width: 2.3rem;
                height: 2.3rem;
                img {
                  width: 100%;
                  height: 100%;
                  border-radius: 50%;
                }
              }
              .secondaryName {
                margin: 0.95rem 0 0 3.95rem;
                font-size: 1rem;
                font-weight: 400;
                color: #7c7777;
                img{
                  float: right;
                  width: 1rem;
                  height: 1rem;
                  margin-right: 1rem;
                  margin-top: 0.6rem;
                }
              }
              .secondaryContent {
                color: #000;
                margin: 0.4rem 0 0 3.95rem;
                line-height: 1.3rem;
                font-size: 1rem;
              }
            }
          }
          .packUp {
            width: 7rem;
            margin-left: 1.8rem;
            margin-top: 1.5rem;
            font-size: 1rem;
          }
          .more{
            
            img {
              margin-left: 0.3rem;
              width: 0.6rem;
              height: 0.6rem;
            }
          }
          .triangleUp {
            display: inline-block;
            width: 0.6rem;
            height: 0.6rem;
            img {
              width: 100%;
              height: 100%;
              transform: rotate(180deg);
            }
          }
        }
      }
    }
    .commentFoot {
      margin: 1.5rem 0 0 6.25rem;
      overflow: hidden;
      .commentTime {
        color: #7c7777;
        font-size: 1rem;
      }
      .footRight {
        float: right;
        .likeQuantity {
          font-size: 1.2rem;
          color: #999;
          margin-right: 0.3rem;
        }
        .like {
          display: inline-block;
          width: 1.2rem;
          height: 1.2rem;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .stealth {
          display: inline-block;
          width: 0.1rem;
          height: 0.9rem;
          background-color: #d7d7d7;
          margin: 0 1.2rem;
        }
        .news {
          float: right;
          width: 1.2rem;
          height: 1.2rem;
          margin-top: 0.2rem;
          img {
            width: 100%;
            height: 100%;
          }
        }
      }
    }
    .replyComment {
      position: relative;
      width: 26.35rem;
      // height: 2.5rem;
      // margin: 1rem 0 0 5rem;
