<template>
  <div class="vip">
    <div class="header">
      <span class="back" @click="goback"><img src="../../assets/login/back.png" alt="" ></span>
      <span>购买会员</span>
    </div>
    <div class="header_botttom clearfix">
      <div class="left"><img :src="logo" alt=""></div>
      <div class="right">
        <p><span>{{name}}</span><span v-if="showtoc"><img v-if="tag === '官方'" src="../../assets/my/vip_4.png" alt=""><img v-else src="../../assets/my/vip_1.png" alt="">{{tag}}</span></p>
        <p v-if="isvip">有效期至{{ timeChange('Y年m月d日', dateStart) }}</p>
        <p v-else>尚未开启vip</p>
      </div>
    </div>
    <div class="priceContainer">
      <!-- <div class="priceItem" :class="{'achive': viptype === 1}" @click="viptype = 1;  dateEnd = dateStart + viptype === 1? 60*60*24*31*1000 : 60*60*24*365*1000"> -->
      <div class="priceItem" :class="{'achive': viptype === 1, 'disable': yearVip  }" @click="chooseType(1)">
        <p>月度会员</p>
        <p><i>￥</i>{{ vip_1 }}</p>
        <p>原价<del>￥{{ vip_1_pro }}</del></p>
      </div>
      <div class="priceItem recommend" :class="{'achive': viptype === 2}" @click="chooseType(2)">
        <p>年度会员</p>
        <p><i>￥</i>{{ vip_2 }}</p>
        <p>原价<del>￥{{ vip_2_pro }}</del></p>
      </div>
    </div>
    <div class="btnContainer">
      <div class="about_xzl" >
        <p>
          <span>有效期限</span>
          <span>
            {{ timeChange('Y-m-d', dateEnd) }}
          </span>
        </p>
      </div>
      <div class="about_xzl">
        <p>
          <span>支付金额</span>
          <span style="color: #F6343E">
            ￥{{ viptype === 2 ? vip_2 : vip_1 }}
          </span>
        </p>
      </div>
    </div>
    <div class="btnContainer" style="margin-top: 1.2rem;">
      <div class="about_xzl" @click="payType = 301">
        <p v-show="!isUserId">
          <span><img class="List_icon" src="../../assets/vip/1.png">余额支付</span>
          <span v-if="payType === 301"><img src="../../assets/vip/choose.png" alt="" class="choose"></span>
        </p>
      </div>
      <div class="about_xzl" @click="payType = 101">
        <p>
          <span><img class="List_icon" src="../../assets/vip/3.png">微信支付</span>
          <span v-if="payType === 101"><img src="../../assets/vip/choose.png" alt="" class="choose"></span>
        </p>
      </div>
      <div class="about_xzl" @click="payType = 201">
        <p>
          <span><img class="List_icon" src="../../assets/vip/2.png">支付宝支付</span>
          <span v-if="payType === 201"><img src="../../assets/vip/choose.png" alt="" class="choose"></span>
        </p>
      </div>
    </div>
    <div class="foot_btn" @click="order">立即购买</div>
  </div>
</template>
<script>
import { eventBus } from '../../utils/eventBus.js'
export default {
  data() {
    return{
      name: '',
      logo: '',
      isUserId: false,
      tag : '',
      showtoc: '',
      viptype: 2,
      vip_1: 666.00,
      vip_1_pro: 999,
      vip_2: 999.00,
      vip_2_pro: 1000,
      dateStart: 0,
      dateEnd: 0,
      yearVip:false,
      isvip: false,
      payType: 301
    }
  },
  methods: {
    goback() {
      this.$router.goback()
    },
    // 店铺详情
    getData() {
      this.loading()
      this.$http({
        url: this.$http.adornUrl(this.apiList.shop.get),
        method: 'post',
        data: this.$http.adornData('', this.apiList.shop.get, true)
      }).then(({ data }) => {
        this.closeUpload()
        if (data.result) {
          localStorage.setItem('logoUrl', data.data.logoImgUrl)
          localStorage.setItem('shopName', data.data.name)
          this.logo = data.data.logoImgUrl
          this.name = data.data.name
          this.id = data.data.id
          this.collectNum = data.data.collectNum
          this.showtoc = data.data.showed
          const tags = data.data.tags ? data.data.tags : []
          tags.forEach(element => {
            if (element.name === '官方' || element.name === '企业官方') {
              this.tag = '官方'
            }
          })
        } else {
          if (data.msg === 'sign验证失败') {
            localStorage.clear()
            this.$toast({
              message: '您的账号登录超时',
              iconClass: 'icon icon-error'
            })
            setTimeout(() => {
              instance.close()
              this.$router.push({ name:  'login' })
            }, 2000)
          }
        }
      })
    },
    chooseType(type) {
      if (type === 1) {
        if (! this.yearVip) {
          this.viptype = 1;
          this.dateEnd = this.dateStart + 60*60*24*31*1000 
        } else {
          this.$toast('您已经是年费会员，请等待会员到期')
        }
      }
      if (type === 2) {
        this.viptype = 2;
        this.dateEnd = this.dateStart + 60*60*24*365*1000
      }
    },
    // 点击支付
    order() {
      let url = this.apiList.vip.year
      if (this.viptype === 1) {
        url = this.apiList.vip.month
      }
      // this.upload()
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData('', url, true)
      }).then(({ data }) => {
        const url = this.apiList.vip.order
        const orderId = data.data
        this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({ orderId: orderId, payType : this.payType }, url, true)
        }).then(({ data }) => {
          this.closeUpload()
          console.log(data)
          if (data.result) {
            if (this.payType !== 301) {
              this.android.pay(this.payType, data.data)
            } else {
              this.$toast({
                  message: '支付成功',
                iconClass: 'fa fa-check fa-5x'
              })
              this.getdataList()
              this.getData()
              setTimeout(() => {
                this.goback()
              }, 600)
            }
          }else {
            this.$toast(data.msg)
          }
        })
      })
    },
    // 会员详情
    getdataList() {
      const url = this.apiList.vip.state
      if( !this.isAuth(url) ){
        return console.log('会员详情列表权限不足')
      }
      this.loading()
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData('', url, true)
      }).then(({ data }) => {
        this.closeUpload()
        const timeadd = this.viptype === 1? 60*60*24*31*1000 : 60*60*24*365*1000
        this.yearVip = data.data.vipType === 101
        console.log(this.yearVip)
        let now = 0
        if (data.data.vipEndTime == 0) {
          this.isvip = false
          now = (new Date()).getTime()
        } else {
          this.isvip = true
          now = data.data.vipEndTime
        }
        this.dateStart = now
        this.dateEnd = now + timeadd
        console.log(this.dateStart)
      })
    }
  },
  created() {
    if (localStorage.getItem('uid') === '42') {
      this.isUserId = true
      this.payType = 101
    }
    this.getData()
    this.getdataList()
    // 设置为黑色
    this.android.navColorChange('#282828')
    eventBus.$on('payScccess', ({ res }) => {
      if(Number(res.code) === 0){
        this.$toast({
          message: '支付成功',
          iconClass: 'fa fa-check fa-5x'
        })
        setTimeout(() => {
          this.goback()
        }, 600)
      }else{
        this.$toast('支付失败')
      }
   })
  },
  destroyed() {
    this.android.navColorChange('#ffffff')
  }
}
</script>
<style lang="scss">
.vip{
  position: absolute;
  width: 100%;
  top: 0;
  bottom: 0;
  background: #f5f5f5;
  .header{
    background: #282828;
    span{
      color: #E1BB8C;
    }
  }
  .header_botttom{
    background: #282828;
    height: 7.2rem;
    line-height: 7.2rem;
    box-sizing: border-box;
    padding: 1.5rem 2.8rem;
    .left{
      float: left;
      width: 3.8rem;
      height: 3.8rem;
      img{
        width: 100%;
        height: 100%;
        border-radius: 100%;
        vertical-align: top;
      }
    }
    .right{
      float: left;
      margin-left: 1rem;
      p{
        color: #fff;
        font-size: 1.6rem;
        text-align: left;
        line-height: 1.6rem;
        span{
          color: #fff;
          font-size: 1.6rem;
          font-weight: 600;
        }
        span:nth-child(2){
          font-size: 1rem;
          height: 1.2rem;
          vertical-align: top;
          margin-left: .7rem;
          font-weight: 400;
          img{
            width: 1.4rem;
            height: 1.4rem;
            vertical-align: top;
          }
        }
      }
      p:nth-child(2){
        font-size: 1.1rem;
        margin-top: .4rem;
        color: #999;
      }
    }
  }
  .priceContainer{
    width: 100%;
    box-sizing: border-box;
    padding: 2.5rem 1.9rem;
    background: #fff;
    .priceItem{
      width: 14.8rem;
      height: 8.4rem;
      border: solid .1rem #D7D7D7;
      border-radius: .8rem;
      display: inline-block;
      text-align: center;
      box-sizing: border-box;
      padding-top: 1.1rem;
      background: #fff;
      margin-right: 3rem;
      p{
        color: #333;
        font-size: 1.2rem;
      }
      p:nth-child(2) {
        color: #A18068;
        font-size: 2.4rem;
        font-weight: 600;
        letter-spacing: .1rem;
        margin-top: .8rem;
        i{
          font-size: 1.4rem;
          font-style: normal;
        }
      }
      p:nth-child(3){
        font-size: 1rem;
        color: #999;
      }
    }
    .priceItem:last-child{
      margin-right: 0;
    }
    .recommend{
      position: relative;
      &::before{
        content: '推荐';
        position: absolute;
        display: inline-block;
        top: -.7rem;
        left: 0;
        color: #F6E2AC;
        width: 4rem;
        height: 1.7rem;
        line-height: 1.7rem;
        text-align: center;
        border-radius: 0 .5rem 0 .5rem;
        background: #535353;
        font-size: 1.1rem;
      }
    }
    .achive{
      background: #FFFDF1;
      border: solid .1rem #F3D9B8;
    }
    .disable{
      background: #eee;
    }
  }
  .btnContainer{
    // margin-top: 1.2rem;
    width: 100%;
    text-align: center;
    box-sizing: border-box;
    // background: #fff;
    .about_xzl{
      background-color: #fff;
      align-items: center;
      padding: 0 1.6rem;
      height: 5rem;
      p{
        width: 100%;
        height: 100%;
        line-height: 5rem;
        border-bottom: solid .1rem #EEEEEE;
        display: flex;
        justify-content: space-between;
        box-sizing: border-box;
        span{
          font-size: 1.4rem;
          color: #666;
          .List_icon{
            width: 2.4rem;
            height: 2.4rem;
            vertical-align: middle;
            margin-right: 1.1rem;
          }
          .choose{
            width: 1.6rem;
            height: 1.1rem;;
          }
        }
        span:nth-child(2){
          color: #999;
          line-height: 5rem;
        }
      }
    }
    .about_xzl:last-child{
      p{
        border: none;
      }
    }
  }
  .foot_btn{
    margin: 2.5rem 3rem;
    border-radius: .4rem;
    height: 4.4rem;
    background: -webkit-gradient(linear, left top, right top, from(#EDD19B), to(#E7BA79));
    // background: linear-gradient(left, #EDD19B, #E7BA79);
    font-size: 1.4rem;
    text-align: center;
    line-height: 4.4rem;
  }
}
</style>


