<template>
  <div class="layout">
    <div class="container">
      <page-index v-if="target === 0" @goMessage="target = 1"></page-index>
      <my-message v-if="target === 1"></my-message>
      <different-list v-if="target === 2"></different-list>
      <my-info v-if="target === 3"></my-info>
    </div>
    <div class="foot_nav">
      <ul>
        <li @click="changeTabs(0)"><img v-if="target === 0" src="../assets/index/home-achive.png" alt=""><img v-else src="../assets/index/home.png"><p :class="{'achive': target === 0}">首页</p></li>
        <li @click="changeTabs(1)"><img v-if="target === 1" src="../assets/index/mess-achive.png" alt=""><img v-else src="../assets/index/mess.png" alt=""><p :class="{'achive': target === 1}">消息</p></li>
        <li @click="changeTabs(2)"><img v-if="target === 2" src="../assets/index/diff-achive.png" alt=""><img v-else src="../assets/index/diff.png" alt=""><p :class="{'achive': target === 2}">异业</p></li>
        <li @click="changeTabs(3)"><img v-if="target === 3" src="../assets/index/my-achive.png" alt=""><img v-else src="../assets/index/my.png" alt=""><p :class="{'achive': target === 3}">我的</p></li>
      </ul>
    </div>
  </div>
</template>
<script>
import PageIndex from './index.vue'
// import MyMessage from './myMessage.vue'
import MyMessage from './mess/list.vue'
// import NoData from './nodata.vue'
import DifferentList from './differentList.vue'
// 建设中
import MyInfo from './myInfo.vue'
export default {
  data() {
    return {
      target: 0
    }
  },
  components: {
    PageIndex,
    MyMessage,
    MyInfo,
    DifferentList
  },
  methods: {
    changeTabs(val) {
      localStorage.setItem('layout_index', val)
      this.target = val
      
      // if (val === 3 || val === 0 ) {
      //   this.android.navColorChange('#f64542')
      // } else {
        // this.android.navColorChange('#ffffff')
      // }
      if(val === 2){
        this.android.navColorChange('#5490f1')
      }else{
        this.android.navColorChange('#f64542')
      }
    }
  },
  mounted() {
    
    const _idx = Number(localStorage.getItem('layout_index'))
    this.changeTabs(_idx)
    if (!localStorage.getItem('token')) {
      this.$toast({
        message: '您的账号登录超时',
        iconClass: 'icon icon-error'
      })
      setTimeout(() => {
        this.$router.push({name: 'login'})
      }, 1500);
    }
  },
  destroyed() {
    // this.android.navColorChange('#ffffff')
  },
  created() {
   
  }
}
</script>

<style lang="scss">
.layout{
  height: 100%;
  .container{
    height: 100%;
  }
  .foot_nav{
    z-index: 3;
    display: block;
    position: fixed;
    height: 5rem;
    bottom: 0;
    width: 100%;
    color: #999;
    border-top: 2px solid #00000014;
    box-shadow: #00000014 10px 10px 20px 5px ;
    background-color: #fff;
    .achive{
      color: #e51d27
    }
    ul {
      display: flex;
      justify-content: space-between;
      margin: 0.7rem 1.7rem 0.5rem;
      li {
        width: 5.2rem;
        text-align: center;
        img {
          width: 2.2rem;
          height: 2.2rem;
        }
        p {
          width: 100%;
          text-align: center;
          font-size: 1.2rem;
          color: #999;
          // margin-top: 0.2rem;
        }
      }
    }
  }
}
</style>



