<template>
  <div class="getScanCode" :class="{'center': moduleId }">
    <div class="imgCode" @click="getQr">
      <img v-if="moduleId" src="../../assets/index/btn.png" alt="">
      <img v-else src="../../assets/index/QR.png" alt="">
    </div>
    <!-- <div @click="isShow = !isShow">点击</div> -->
    <!-- 确认活动信息的弹窗 -->
    <transition name="fade">
      <div v-show="isShow">
        <!-- <div v-show="true"> -->
        <!-- 促销 -->
        <div class="orderBox" v-if="activityType === 301">
          <div class="orderBoxheader">确认出货</div>
          <div class="body clearfix">
            <div class="left">
              <img :src="img" alt="">
              <!-- <span>{{ tag }}</span> -->
            </div>
            <div class="right">
              <p>单号 : {{ orderId }}</p>
              <p>促销名称 : {{ name }}</p>
              <p>促销价格 : ￥{{ price }}</p>
              <p>用户昵称 : {{ nickName }}</p>
              <!-- <p v-if="price"><i>￥</i>{{ price }}</p> -->
            </div>
          </div>
          <div class="footer">
            <div @click="isShow = false">取消</div>
            <div @click="success">确认</div>
          </div>
        </div>
        <!-- 报名 -->
        <div class="orderBox orderBox_sign" v-if="activityType === 101">
          <div class="orderBoxheader">确认收货</div>
          <div class="body clearfix">
            <div class="left">
              <img :src="img" alt="">
              <!-- <span>{{ tag }}</span> -->
            </div>
            <div class="right">
              <p>单号 : {{ orderId }}</p>
              <p>昵称 : {{ nickName }}</p>
              <p>组别 : {{ itemName }}</p>
              <p>开始时间 : {{ timeChange('Y.m.d h:i', startUseTime) }}</p>
              <!-- 报名信息 -->
              <!-- <p v-for="item in enterInfo" :key="item.id">{{ item.keyName }} : {{ item.value }}</p> -->
            </div>
          </div>
          <div class="footer">
            <div @click="isShow = false">取消</div>
            <div @click="success">确认</div>
          </div>
        </div>
        <!-- 活动 -->
        <div class="orderBox" v-if="activityType === 501">
          <div class="orderBoxheader">活动确认</div>
          <div class="body clearfix">
            <div class="left">
              <img :src="img" alt="">
              <!-- <span>{{ tag }}</span> -->
            </div>
            <div class="right">
              <p>单号 : {{ orderId }}</p>
              <p>活动名称 : {{ name }}</p>
              <p>活动价格 : ￥{{ price }}</p>
              <p>用户昵称 : {{ nickName }}</p>
              <!-- <p v-if="price"><i>￥</i>{{ price }}</p> -->
            </div>
          </div>
          <div class="footer">
            <div @click="isShow = false">取消</div>
            <div @click="success">确认</div>
          </div>
        </div>
        <!-- 商品 -->
        <div class="orderBox" v-if="activityType === 601">
          <div class="orderBoxheader">出货确认</div>
          <div class="body clearfix">
            <div class="left">
              <img :src="img" alt="">
              <!-- <span>{{ tag }}</span> -->
            </div>
            <div class="right">
              <p>单号 : {{ orderId }}</p>
              <p>商品名称 : {{ name }}</p>
              <p>商品价格 : ￥{{ price }}</p>
              <p>用户昵称 : {{ nickName }}</p>
              <!-- <p v-if="price"><i>￥</i>{{ price }}</p> -->
            </div>
          </div>
          <div class="footer">
            <div @click="isShow = false">取消</div>
            <div @click="success">确认</div>
          </div>
        </div>
        <div class="orderBox" v-if="activityType === 701">
          <div class="orderBoxheader">抽奖确认</div>
          <div class="body clearfix">
            <div class="left">
              <img :src="img" alt="">
            </div>
            <div class="right">
              <p>单号 : {{ orderId }}</p>
              <p>抽奖名称 : {{ name }}</p>
              <p>用户昵称 : {{ nickName }}</p>
              <p>奖品 : {{ winningPrize }}</p>
            </div>
          </div>
          <div class="footer">
            <div @click="isShow = false">取消</div>
            <div @click="success">确认</div>
          </div>
        </div>
        <!-- 其他 -->
        <!-- <div  v-if="activityType != 601">
          5555555555
        </div> -->
      </div>
    </transition>
    <!-- 遮罩 -->
    <transition name="fade">
      <div v-show="isShow">
        <div class="shadow"></div>
      </div>
    </transition>

    <!-- 报名信息详情 -->
    <!-- <transition name="fade">
      <div v-show="activeMess"  @click="activeMess = false">
        <div class="enterInfo">
          <h2>报名信息：</h2>
          <p>单号 : {{ orderId }}</p>
          <p>昵称 : {{ nickName }}</p>
          <p>组别 : {{ itemName }}</p>
          <p>开始时间 : {{ timeChange('Y.m.d h:i', startUseTime) }}</p> -->
          <!-- 报名信息 -->
          <!-- <p v-for="item in enterInfo" :key="item.id">{{ item.keyName }} : {{ item.value }}</p> -->
        <!-- </div>
      </div> -->
    <!-- </transition> -->
    <!-- 遮罩 -->
    <transition name="fade">
      <div v-show="activeMess" @click="activeMess = false">
        <div class="shadow"></div>
      </div>
    </transition>
  </div>
</template>
<script>
import { MessageBox } from 'mint-ui'
import { eventBus } from '../../utils/eventBus.js'
import { Base64 } from 'js-base64'
export default {
  props: {
    moduleId: {},
    sign: {},
    sign_detail: {}
  },
  data() {
    return{
      isShow: false,
      img: 'https://zxlmain.oss-cn-hangzhou.aliyuncs.com/main/c1b36620d523423f95d0891ef0f309cdblob',
      tag: '',
      orderId: '',
      nickName: '',
      userAvatar: '',
      enterInfo: [],
      activeMess: false,
      price: '',
      activityType: 101,
      winningPrize: '',
      formData: {},
      itemName: '',
      startUseTime: '',
      info: "2",
      // 促销名称
      name : '',
      leaveMoney: "",
    }
  },
  methods: {
    getQr() {
      this.android.getScanCode()
    },
    dataChange(data) {
      // function alertObj(obj){
      //   var output = "";
      //   for(var i in obj){
      //     var property=obj[i];
      //     output+=i+" = "+property+"\n";
      //   }
      //   alert(output);
      // }
      if(data){
        // 判断接收到的数据是否为对象格式
        if(!(data.substring(0,1) === '{')){
          return this.$toast('请您扫描众享乐客户端出示的二维码')
          // alert(111)
        }
        // alert(data.substring(0,1) === '{')
      }
      // alertObj(JSON.parse(data))
      let formData = JSON.parse(data)
      this.formData = formData
      const activityId = formData.activityId
      const activityType = formData.activityType
      this.activityType = formData.activityType
      
      // let url = 'http://j.chnzxl.com/entryc/activitys/getById'
      // 授权登录获得商品详情
      if (this.sign) {
        const url = this.apiList.active.detail
        this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({ activityGetId: formData.getId, activityId: activityId, activityType: activityType, sign: this.sign_detail, moduleId: this.moduleId }, url , false)
        }).then(({data}) => {
           if (data.result) {
            this.itemName = data.data.itemName
            this.name = data.data.name
            this.leaveMoney = data.data.leaveMoney / 100
            
            this.img = data.data.imgUrlSquare
            this.price = data.data.price / 100
            this.isShow = true
            this.orderId = data.data.outTradeNo
            this.nickName = data.data.userNickName ? data.data.userNickName : '众享乐用户'
            this.userAvatar = data.data.userAvatar
            this.enterInfo = data.data.enterInfo
            this.isShow = true
          } else {
            this.$toast(data.msg)
          }
        })
      } else {
        // 扫码获得商品详情
        const url = this.apiList.active.moduleDetail 
        this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({ getId: formData.getId}, url , true)
        }).then(({data}) => {
          if (data.result) {
            this.info = data.data
            this.name = data.data.name
            this.leaveMoney = data.data.leaveMoney / 100
            // this.info  = data.data
            this.enterInfo = data.data.enterInfo
            this.img = data.data.imgUrlSquare
            this.price = data.data.price / 100
            this.isShow = true
            this.orderId = data.data.outTradeNo
            this.nickName = data.data.userNickName ? data.data.userNickName : '众享乐用户'
            this.userAvatar = data.data.userAvatar
            this.isShow = true
            this.itemName = data.data.itemName
            this.startUseTime = data.data.startUseTime
            // alert(this.timeChange('Y.m.d h:i', this.startUseTime))
            switch(data.data.hitState){
              case 1:
                this.winningPrize = '未开奖'
                break;
              case 101:
                this.winningPrize = '一等奖'
                break;
              case 201:
                this.winningPrize = '二等奖'
                break;
              case 301:
                this.winningPrize = '三等奖'
                break;
              case 401:
                this.winningPrize = '未中奖'
                break;
            }

          } else {
            this.$toast(data.msg)
          }
        })
      }
    }, 
    success() {
      let url = this.apiList.active.choose
      const formData = {
        activityGetId: this.formData.getId,
        activityId: this.formData.activityId,
        activityType: this.formData.activityType,
        usrId: this.formData.userId,
        shaPayKey: this.formData.shaPayKey
      }
      if (this.moduleId) {
        url = this.apiList.authorization.qr
        formData.moduleId = this.moduleId
        formData.sign = this.sign
      }
      this.loading()
      this.$http({
        url: this.$http.adornUrl(url),
        method: 'post',
        data: this.$http.adornData(formData, url, true)
      }).then(({ data }) => {
        this.closeUpload()
        if (data.result) {
          // this.$toast('')
          this.$emit('success', data.data)
          this.isShow = false
          // MessageBox.alert('兑换成功')
          this.$toast({
            message: '兑换成功',
            iconClass: 'fa fa-check fa-5x'
          })
          if (this.activityType === 101) {
            // this.activeMess = true
          }
        } else {
          this.$toast(data.msg)
        }
      })
    }
  },
  created() {
    const vm = this
    eventBus.$on('getScanCode', (res) => {
      vm.dataChange(Base64.decode(res.result))
      // vm.dataChange(Base64.decode(res))
    })
  },
  destroyed() {
    eventBus.$off('getScanCode')
  }
}
</script>
<style lang="scss" scope>
.getScanCode{
  .imgCode{
    width: 2.4rem;
    height: 2.4rem;
    img{
      width: 100%;
      height: 100%;
    }
  }
}
.center{
  // position: fixed;
  // right: 50%;
  // bottom: 50%;
  // transform: translate(50%, 50%);
  position: static;
  // top: 3.4rem;
  margin: 3.4rem auto;
  .imgCode{
    width: 18.8rem;
    height: 18.8rem;
    margin: 3.4rem auto;
  }
}
.orderBox{
  width: 28.4rem;
  height: 36rem;
  background: url('../../assets/index/orderBac.png');
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-size: 100% 100%;
  z-index: 7;
  .orderBoxheader{
    font-size: 1.8rem;
    height: 1.8rem;
    line-height: 1.8rem;
    color: #333;
    margin-top: 14.5rem;
    text-align: center;
    width: 100%;
    font-weight: 600;
  }
  .body{
    width: 25.4rem;
    height: 9.4rem;
    // height: 100%;
    text-align: center;
    margin: auto;
    border-radius: .8rem;
    overflow: hidden;
    margin-top: 2rem;
    .left{
      width: 4.7rem;
      height: 4.9rem;
      margin-right: 1rem;
      margin-left: 1.45rem;
      float: left;
      img{
        width: 100%;
        height: 100%;
      }
      span{
        position: absolute;
        top: .4rem;
        right: .4rem;
        display: inline-block;
        width: 2.7rem;
        height: 1.3rem;
        border-radius: 1.3rem;
        text-align: center;
        background: #7145E9;
        color: #fff;
        font-size: .8rem;
      }
    }
    .right{
      width: 18rem;
      background: #fff;
      padding: 0;
      height: 100%;
      // overflow: scroll;
      box-sizing: border-box;
      float: left;
      p{
        overflow: hidden;
        white-space: nowrap;
        text-overflow:ellipsis;
        font-size: 1.2rem;
        color: #999999;
        line-height: 1.4rem;
        margin-top: 0;
        padding: 0;
        margin-bottom: .8rem;
        text-align: left;
        word-break: break-all;
        float: none;
      }
      p:nth-child(1){
        color: #333333;
        margin-bottom: .9rem;
        font-size: 1.2rem;
        font-weight: 600;
      }
      // p:last-child{
      //   color: #FA2D4F;
      //   font-size: 1.5rem;
      //   height: 1.5rem;
      //   margin-top: 1.5rem;
      //   i{
      //     font-style: normal;
      //     font-size: .9rem;
      //     vertical-align: bottom;
      //   }
      // }
    }
  }
  .footer{
    width: 100%;
    height: 3.4rem;
    margin-top: 2.8rem;
    div{
      height: 3.4rem;
      border-radius: 3.4rem;
      border: solid .1rem #999999;
      color: #999999;
      width: 9rem;
      box-sizing: border-box;
      line-height: 3.4rem;
      float: left;
      margin-left: 3rem;
      text-align: center;
    }
    div:nth-child(2){
      margin-right: 3rem;
      color: #fff;
      float: right;
      background: #E53935;
      border: solid .1rem #E53935;
    }
  }
}
.orderBox_sign{
  height: 41.2rem;
  background: url('../../assets/index/orderBac_sign.png');
  background-size: 100% 100%;
  background-repeat: no-repeat;
  .body {
    height: 14.5rem;
    .left{
      width: 4.7rem;
      height: 4.9rem;
      margin-right: 1rem;
      margin-left: 1.45rem;
    }
    .right{
      width: 18rem;
      background: #fff;
      padding: 0;
      height: 15rem;
      overflow: scroll;
      -webkit-overflow-scrolling: touch;
      &::-webkit-scrollbar{
        width: 0;
      }
      &::-webkit-scrollbar-thumb{
        width: 0;
      }
      p{
        font-size: 1.2rem;
      }
      p:last-child{
        font-size: 1.2rem;
        color: #999;
        height: auto;
        margin-top: 0;
      }
    }
  }
}
.enterInfo{
  width: 28.4rem;
  height: 36rem;
  position: fixed;
  z-index: 2;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 7;
  text-align: center;
  color: #fff;
}
.shadow{
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #000;
  opacity: 0.6;
  z-index: 2;
}
</style>


