<template>
  <div class="manage_openAccount">
    <div class="header">
      <span class="back" @click="$router.goback()"><img src="../../../assets/my/back.png" alt="" ></span>
      <span>开户管理</span>
      <span class="add_Pro" ><img src="../../../assets/manage/open.png" class="searchBtn" @click="$router.push('openAccount_add')"></span>
    </div>
    <div class="manage_openAccount_title">
      <span>ID</span>
      <span>店名</span>
      <span>开店时间</span>
      <!-- <span>手机</span> -->
    </div>
    <!--       -->
    <div class="ul_box">
      <ul ref="ul">
        <van-pull-refresh v-model="isLoading" @refresh="onRefresh"> 
          <li v-for="item in info" :key='item.adminId'><span>{{item.adminId}}</span>   <span>{{item.name}}</span>  <span>{{item.updatedAt | time}}</span>         </li>
        </van-pull-refresh>
      </ul>
    </div>
        
      
    <!--  -->
  </div>
</template>
<script>
export default {
  data(){
    return{
      isLoading: false,
      info:[]
    }
  },
  filters:{
    time(val){
      return val.slice(0,10)
    }
  },
  methods:{
    onRefresh(){
      const url = this.apiList.admin.shopList
      this.$http({
        url: this.$http.adornUrl(url),
        method: "post",
        data: this.$http.adornData({ page: 1, size: 9999 , }, url, true)
      }).then(({data : res})=>{
        console.log(res)
        if(res.result){
          this.info = []
          this.info = res.data.records  
          this.isLoading = false  
          this.$toast('刷新成功')      
        }else{
          this.$toast(res.msg)
        }
      })  
    },
    getinfo(){
      const url = this.apiList.admin.shopList
      this.$http({
        url: this.$http.adornUrl(url),
        method: "post",
        data: this.$http.adornData({ page: 1, size: 9999 , }, url, true)
      }).then(({data : res})=>{
        console.log(res)
        if(res.result){
          this.info = res.data.records          
        }else{
          this.$toast(res.msg)
        }
      })
    }
  },
  mounted(){
    this.$refs.ul.style.height = document.documentElement.clientHeight -100 +'px'
  },
  created(){
    this.getinfo()
  }
}
</script>
<style lang="scss">
.manage_openAccount{
  height: 100%;
  background-image: url('../../../assets/manage/openbgi.png');
  background-size: 100%;
  .add_Pro{
    float: right;
  }
  .manage_openAccount_title{
    position: relative;
    z-index: 5;
    width:  36rem;
    height: 3.3rem;
    background:linear-gradient(0deg,rgba(246,125,66,1) 0%,rgba(246,163,74,1) 100%);
    border-radius:1.65rem;
    margin: 0 auto;
    color: #FFFEFF;
    font-size: 1.2rem;
    font-weight:bold;
    line-height: 3.3rem;
    span:nth-of-type(1){
      margin-left: 2.45rem;
    }
    span:nth-of-type(2){
      margin-left: 5.8rem;
      margin-left: 8.8rem;
    }
    span:nth-of-type(3){
      margin-left: 7.1rem;
      margin-left: 10.6rem;
    }
    // span:nth-of-type(4){
    //   margin-left: 4.3rem;
    // }
  }
  .ul_box{
    width: 33rem;
    margin: 0 auto;
    overflow: hidden ;
    // background-color: pink;
    border-bottom-left-radius: 1.55rem;
    border-bottom-right-radius: 1.55rem;
  }
  ul{
    // width: 33.5rem;
    // margin: 0 auto;
    // height: 100%;
    // height: 66rem;
    // position: absolute;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    // border-radius: 1.55rem;
    // margin-top: 7.5rem;
    li{
      width: 100%;
      line-height: 4.5rem;
      height: 4.5rem;
      font-size: 1.2rem;
      color: #333333;
      background-color: #fff;
      box-sizing: border-box;
      padding-left: 1.35rem;
      // padding-right: 1.15rem;
      position: relative;
      span{
        display: inline-block;
      }
      span:nth-of-type(1){
        width: 10%;
        overflow: hidden;
      }
      span:nth-of-type(2){
        width: 60%;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
        text-align: center;
      }
      span:nth-of-type(3){
        width: 20%;
        margin-left: 1rem;
        overflow: hidden;
      }
      span:nth-of-type(4){
        position: absolute;
        right: 1.15rem;
      }
    }
    li:nth-of-type(even){
      background:rgba(247,247,247,1);
    }
  }
}
</style>




