<template>
  <div class="key_add">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../../../assets/my/back.png" alt=""  ></span>
      <span>{{ title }}</span>
    </div>
    <div class="main">
      <p>
        <span class="left">信息名称</span><input type="text" placeholder="请输入信息名称" v-model="keyName">
      </p>
      <p class="next" @click="showPopup">
        <span class="left" >信息类型</span><input type="请选择信息类型" placeholder="请选择信息类型" readonly v-model="typeText">
      </p>
      <!-- 弹出层 -->
      <van-popup
        v-model="show"
        position="bottom"
        :style="{ height: height }"
      >
        <div class="title">
          <img src="../../../../assets/signUp/closeT.png" @click="show = false">
          <span>选择信息类型</span>
        </div>
        <div class="selectType" v-for="(item, index) in typeSelect" :key="index" @click="getType(item.type)">
          <span>{{ item.text }}</span><img src="../../../../assets/signUp/select.png" v-if="selectType === item.type">
        </div>
        <div class="popupBtn">
          <mt-button @click="submitType">确认</mt-button>
        </div>
      </van-popup>
      <p>
        <span class="left">是否必填</span>
        <span class="yes" @click="select(true)">
          <img :src="selectTarget === true ? selected : noSelected" ><span class="text">是</span>
        </span>
        <span class="no" @click="select(false)">
          <img :src="selectTarget === false ? selected : noSelected" ><span class="text">否</span>
        </span>
      </p>
    </div>
    <mt-button @click="modData" class="btn" v-if="title === '修改报名信息' || title === '修改促销信息' ">提交</mt-button>
    <!-- <mt-button @click="addData" class="btn" v-else-if="id !== ''">提交</mt-button> -->
    <mt-button @click="addTmplateData" class="btn" v-else>提交</mt-button>
  </div>
</template>
<script>
  export default {
    data(){
      return{
        id: '',
        title: '',
        keyName:'',
        height: '',
        selectType: '101',
        selected: require('../../../../assets/signUp/selected.png'),
        noSelected: require('../../../../assets/signUp/noSelected.png'),
        typeText:'',
        selectTarget: true,
        show: false,
        flag: false,
        typeSelect: [
          {text: '任意类型', type: '101'},
          {text: '手机号码', type: '201'},
          // {text: '时间', type: '301'},
          {text: '数字', type: '401'}, 
        ],
        dataList: [],
      }
    },
    methods:{
      goBack(){
        this.$router.goback()
      },
      select(value){
        this.selectTarget = value
      },
      showPopup(){
        this.show = true
      },
      getType(type){
        this.selectType = type
      },
      //选择类型确认
      submitType(){
        this.show = false;
        switch (this.selectType) {
          case '101':
            this.typeText = '任意类型'
            break;
          case '201':
            this.typeText = '手机号码'
            break;
          case '301':
            this.typeText = '时间'
            break;
          case '401':
            this.typeText = '数字'
            break;
          case '501':
            this.typeText = '身份证'
            break;          
        }
        
      },
      //修改
      modData(){
        if(this.flag) return
        if(!this.keyName || !this.selectType) return this.$toast('内容不能为空')
        var index;
        if(this.$route.params.index == 0 || this.$route.params.index){
          index = this.$route.params.index
        }else{
          index = this.$route.query.index
        }
        this.flag = true;
        if(index !== ''){
          this.dataList[index].keyName = this.keyName
          this.dataList[index].type = this.selectType
          this.dataList[index].mustInput = this.selectTarget
          localStorage.setItem('enterMess', JSON.stringify(this.dataList))
          this.$toast({
            message: '修改成功',
            iconClass: 'fa fa-check fa-5x'
          })
          setTimeout(()=>{
            this.goBack()
          },500)
        }
        
      },
      //添加
      addTmplateData(){
        if(this.flag) return
        
        if(!this.keyName || !this.selectType) return this.$toast('内容不能为空')
        this.dataList.unshift({keyName: this.keyName, type: this.selectType, mustInput: this.selectTarget})
        localStorage.setItem('enterMess', JSON.stringify(this.dataList))
        this.flag = true;
        this.$toast({
          message: '添加成功',
          iconClass: 'fa fa-check fa-5x'
        })
        setTimeout(()=>{
          this.goBack()
        }, 500)
      }
    },
    created(){
      if(this.$route.query.type === 301){
        this.title = '添加填写信息'
        this.typeSelect.push({text: '身份证', type: '501'})
        this.height = '43%'
        if(localStorage.getItem('enterMess')){
          this.dataList = JSON.parse(localStorage.getItem('enterMess'))
        }
        return;
      }else if(this.$route.query.index || this.$route.query.index == 0){
        this.title = '修改填写信息'
        this.typeSelect.push({text: '身份证', type: '501'})
        this.height = '43%'
        if(localStorage.getItem('enterMess')){
          this.dataList = JSON.parse(localStorage.getItem('enterMess'))
          let index = this.$route.query.index
          this.keyName = this.dataList[index].keyName
          this.selectType = this.dataList[index].type
          this.selectTarget = this.dataList[index].mustInput
          this.submitType()
        }
        return;
      }
      this.height = '36%'
      if( localStorage.getItem('enterMess')){
        this.dataList = JSON.parse(localStorage.getItem('enterMess'))
        if(this.$route.params.index !== undefined){
          this.title = '修改填写信息'
          let index = this.$route.params.index
          this.keyName = this.dataList[index].keyName
          this.selectType = this.dataList[index].type
          this.selectTarget = this.dataList[index].mustInput
          this.submitType()
        }else{
          this.title = '添加填写信息'
        }
      }else{
        this.title = '添加填写信息'
      }
    }
  
  }
</script>
<style lang="scss">
  .key_add{
    min-height: 100%;
    background-color: #F5F5F5;
    .main{
      margin-top: 1.2rem;
      padding: 0 1.6rem;
      background-color: #fff;
      .van-popup{
        .title{
          height: 4.4rem;
          line-height: 4.4rem;
          text-align: center;
          margin: 0 1.6rem;
          border-bottom: 0.1rem solid #eee;
          img{
            float: left;
            width: 1.2rem;
            height: 1.2rem;
            margin-top: 1.5rem;
          }
          span{
            display: inline-block;
            
          }
        }
        .selectType{
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin: 0 1.6rem;
          height: 4.4rem;
          border-bottom: 0.1rem solid #eee;
          span{
            color: #333;
            font-size: 1.4rem;
          }
          img{
            width: 1.6rem;
            height: 1.1rem;
          }
        }
        .popupBtn{
          text-align: center;
          margin-top: 0.5rem;
          button{
            width: 34.3rem;
            height: 4.4rem;
            line-height: 4.4rem;
            color: #fff;
            font-size: 1.4rem;
            background: #F64542;
            font-size: 1.7rem;
            border-radius: 2.2rem;
            text-align: center;
          }
        }
        
      }
      p{
        height: 5rem;
        line-height: 5rem;
        border-bottom: 0.1rem solid #EDEDED;
        img{
          width: 1.6rem;
          height: 1.6rem;
          vertical-align: middle;
        }
        .text{
          vertical-align: middle;
          margin-left: 0.5rem;
          color: #333;
          font-size: 1.4rem;
        }
        .no{
          margin-left: 6rem;
        }
        .type{
          color: #999;
        }
        .left{
          display: inline-block;
          margin-left: 1.6rem;
          padding-right: 2rem;
          color: #333;
          font-size: 1.4rem;
          
        }
        input{
          height: 4rem;
          border: none;
          outline: none;
          font-size: 1.4rem;
          color: #333;
        }
      }
      .next{
        background: url('../../../../assets/my/next.png') no-repeat  right center;
        background-size: 0.75rem 1.35rem;
      }
    ::-webkit-input-placeholder { /* WebKit, Blink, Edge */
      color: #ccc;
      font-size: 1.4rem;
    }
    :-moz-placeholder { /* Mozilla Firefox 4 to 18 */
      color: #ccc;
      font-size: 1.4rem;
    }
    ::-moz-placeholder { /* Mozilla Firefox 19+ */
      color: #ccc;
      font-size: 1.4rem;
    }
    :-ms-input-placeholder { /* Internet Explorer 10-11 */
      color: #ccc;
      font-size: 1.4rem;
    }
    }
    .btn{
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 4.9rem;
      background-color: #F64542;
      color: #fff;
      font-size: 1.4rem;
      border-radius: 0;
    }
  }
</style>



