<template>
  <div class="sales_index">
    <div class="header">
      <span class="back" @click="$router.goback()"><img src="../../../assets/my/back.png" alt=""  ></span>
      <span>分销商提现列表</span>
    </div>
    <div class="withdraw">
      <van-tabs  :sticky="true" swipeable animated :offset-top='48.5' color="#fff">
        <van-tab title="全部">
          <sales-list  :shows="0"  ref="child1"></sales-list >
        </van-tab>
        <van-tab title="已审批">
          <sales-list  :shows="1"  ref="child1"></sales-list >
        </van-tab>
        <van-tab title="未审批">
          <sales-list  :shows="2"  ref="child1"></sales-list >
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>
<script>
import salesList from './list'
export default {
  data(){
    return{

    }
  },
  
  components:{
    salesList
  }
}
</script>
<style lang="scss">
.sales_index{
  min-height: 100%;
  background-color: #F5F5F5;
  .van-tabs__wrap{
    margin-top: -0.8rem;
  }
  .withdraw{
    min-height: 100%;
  }
}
</style>



