<template>
<div class="cut">
  <div class="header">
    <span class="back" @click="goback"><img src="../../../assets/my/back.png" alt=""  ></span>
    <span>{{ title }}</span>
    <span class="sure"><div @click="finish">确定</div></span>
  </div>
  <vue-cropper ref="cropper" 
    :img="option.img" 
    :output-size="option.size" 
    :output-type="option.outputType" 
    :info="true" :full="option.full"
    :can-move="option.canMove" 
    :can-move-box="option.canMoveBox" 
    :fixed-box="option.fixedBox" 
    :original="option.original"
    :auto-crop="option.autoCrop" 
    :auto-crop-width="width" 
    :auto-crop-height="height" 
    :center-box="option.centerBox"
    :high="option.high"
  />
</div>
</template>
<script>
import { Popup } from 'mint-ui'
import sha256 from 'sha256'
import { Indicator } from 'mint-ui'
import { eventBus } from '../../../utils/eventBus.js'
export default {
  data() {
    return {
      width: 388,
      height: 180,
      model: false,
      modelSrc: '',
      crap: false,
      filesName: '',
      title: '',
      option: {
        img: '',
        size: 2,
        full: false,
        outputType: 'png',
        canMove: true,
        fixedBox: true,
        original: false,
        canMoveBox: true,
        autoCrop: true,
        centerBox: false,
        high: true
      },
      style: '',
      popupVisible: false,
      imgUrl:''
    }
  },
  methods: {
    goback() {
      this.$router.goback()
      
    },
    getImg() {
      // 注册获得图片成功的方法,每次调用都刷新，防止重复相应
      const vm = this
      const imgdata = this.$route.params.imgdata
      let reader = new FileReader()
      reader.readAsDataURL(vm.dataURLtoFile('data:image/png;base64,' + imgdata.image))
      reader.onloadend = function () {
        vm.option.img = this.result
        vm.popupVisible = true
        // 隐藏基座loading
        vm.android.progressHUDend()
      }
    },
    dataURLtoFile(dataurl, filename = '0000img') {//将base64转换为文件
        var arr = dataurl.split(','), mime = arr[0].match(/:(.*?);/)[1],
            bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
        while(n--){
            u8arr[n] = bstr.charCodeAt(n);
        }
        return new File([u8arr], filename, {type:mime});
    },
    upload() {
      const formData = new FormData()
      console.log(this.modelSrc)
      const file = this.dataURLtoFile(this.modelSrc, this.filesName)
      formData.append('file', file)
      formData.append('userId', localStorage.getItem('uid'))
      formData.append('sign', sha256(this.apiList.upload + localStorage.getItem('token')))
      // 显示loading
      Indicator.open({
        text: '上传中....',
        spinnerType: 'fading-circle'
      })
      this.$http({
        url: this.$http.adornUrl(this.apiList.upload),
        method: 'post',
        headers: {
          'Content-Type': 'multipart/form-data'
        },
        data: formData
      }).then(({ data }) => {
        Indicator.close()
        this.popupVisible = false
        if (data.result) {
          // this.$emit('fileChange', data.data)
          this.changeLogo(data.data)
        } else {
          this.MessageBox('提示', data.msg)
        }
      }).catch((e) => {

      })
    },
    finish(type) {
      this.$refs.cropper.getCropData((data) => {
        this.model = true
        this.modelSrc = data
        this.upload()
      })
    },
    changeLogo(imgurl) {
      setTimeout(()=>{
        if(this.$route.params.title === 'resell'){
          localStorage.setItem('imgUrlResell',imgurl)
        }else if(this.$route.params.title === 'poster'){
          localStorage.setItem('poster', imgurl)
        }else{
          localStorage.setItem('imgUrl', imgurl)
        }
        this.$toast('已保存')
        this.goback()
      },500)
        
      
      // const vm = this
      // const url = this.apiList.shop.edit
      // this.$http({
      //   url: this.$http.adornUrl(url),
      //   method: 'post',
      //   data: this.$http.adornData({logoUrl: imgurl}, url , true)
      // }).then(({ data }) => {
      //   if (data.result) {
        
      //     eventBus.$emit('logoChangeSuccess', imgurl)
      //   } else {
      //     this.$toast(data.msg)
      //   }
      //   setTimeout(function(){
      //     vm.goback()
      //   }, 600)
      // })
    }
  },
  mounted() {
    this.getImg()
    
    // console.log(this.$route.params);
    // alert(this.$route.params.id)
  },
  created() {
    if(this.$route.params.title === 'resell' || this.$route.params.title === 'poster'){
      this.width = 375
      this.height = 667
      this.title = '海报'
    }else{
      this.title = '主图'
    }
  },
}
</script>
<style lang="scss" scoped>
  .cut{
    width: 37.5rem;
    height: 100%;
    .sure{
      float: right; 
      margin-top: .1rem;
      color: #fff;
    }
  }
</style>


