<template>
<div class="nameChange">
  <div class="header">
    <span class="back" @click="goback"><img src="../../assets/my/back.png" alt=""  ></span>
    <span>授权扫码</span>
    <span class="sure"><div @click="finish">确定</div></span>
  </div>
  <div class="nameInput">
    <input type="password" v-model="num">
    <p>输入您获得的授权码</p>
  </div>
  <div class="nameInput">
    <input type="text" v-model="phone">
    <p>输入商户端手机号</p>
  </div>
</div>
</template>
<script>
import { Base64 } from 'js-base64'
import CryptoJS from 'crypto-js'
import RSA from '@/utils/rsa.js'
export default {
  data() {
    return{
      num: '',
      phone: ''
    }
  },
  methods: {
    goback() {
      this.$router.goback()
    },

  },
  created() {
  }
}
</script>
<style lang="scss">
.nameChange{
  width: 37.5rem;
  height: 100%;
  .sure{
    float: right; 
    margin-top: .1rem;
    color: #FF3B30;
  }
  .nameInput{
    width: 90%;
    margin: 1rem auto;
    p{
      font-size: 1.2rem;
      color: #999;
      margin-top: 1rem;
    }
    input{
      width: 100%;
      height: 4rem;
      border: none;
      outline: none;
      font-size: 1.4rem;
      border-bottom: solid .1rem #FF3B30;
    }
    input:focus{color:#FF3B30}//光标颜色
    input{
      text-shadow: 0px 0px 0px #333;//文字颜色
      -webkit-text-fill-color: transparent;
    }
  }
}
</style>


