<template>
<div class="myMessage" >
  <div class="mess_header">
    <span>消息</span>
  </div>
  <div class="main">
    <div v-if="isLogin">
     <li @click="goMessItem(1)">
       <div class="left" >
         <img src="../../assets/mess/1.png">
         <span>系统消息</span>
       </div>
       <div class="right">
         <img src="../../assets/mess/icon.png" >
       </div>
     </li>
    <li @click="goMessItem(2)">
      <div class="left">
        <img src="../../assets/mess/2.png">
        <span>用户消息</span>
      </div>
      <div class="right">
        <img src="../../assets/mess/icon.png" >
      </div>
    </li>
    </div>
  </div>
</div>
</template>
<script>
export default {
  data() {
    return {
      nodata: true,
      selected: 0,
      noMessItem : true,
      isLogin:false
    }
  },
  methods: {

  },
  created(){
    // this.android.navColorChange("#00A1FC")

    if(localStorage.getItem('uid')){
      this.isLogin = true
    }else{
      this.isLogin = false
    }
  },
  methods:{
    goMessItem(type){
      if (type === 1) {
        this.$router.push({
          name:'messItem',
          query:{messItem:type}
        })
      } else {
        let formData = {
          url_title: '消息列表',
          navBackgroudColor: '#f54340'
        }
        this.android.emGoToChatList(formData)
      }
    },
    goLogin() {
      this.$router.push('/login')
    },
  },
  mounted(){
    
  },
  activated() {
    if(localStorage.getItem('uid')){
      this.islogin = true
    }else{
      this.islogin = false
    }
  },
  deactivated() {
    this.android.navColorChange("#ffffff")
  }
}
</script>
<style lang="scss">
.myMessage{
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  background: #fafafa;
 // min-height: 100%;
  overflow: scroll;
  .mess_header{
    height: 4.4rem;
    line-height: 4.4rem;
    background: #F64542;
    text-align: center;
    color: #fff;
    font-size: 1.8rem;
    font-family:"Microsoft YaHei",微软雅黑;
  }
  .main{
    margin-top: 1.6rem;
    border-radius: 0.8rem;
    background-color: #ffffff;
    li{
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 1.2rem 0;
      margin: 0 1.6rem 0 2rem;
      border-bottom: 0.05rem solid #EEEEEE;
      .left{
        display: flex;
        align-items: center;
        img{
          width: 4rem;
          height: 4rem;
          margin-right:1.2rem ;
        }
        span{
          font-size: 1.6rem;
          color: #333333;
        }
      }
      .right{
        display: flex;
        align-items: center;
        img{
          width: 0.5rem;
          height: 1rem;
          margin-left: 1.2rem;
        }
        span{
          /*width:60px;*/
          height:1.8rem;
          background:rgba(253,75,99,1);
          border-radius:0.9rem;
          color: #fff;
          font-size: 1.2rem;
          padding: 0 0.5rem;
          line-height: 1.8rem;
        }

      }
    }
    li:last-child{
      border-bottom: none;
    }
    .login{
      text-align: center;
      background-color: #fafafa;

      img{
        width:16.8rem;
        height: 17.1rem;
        display: block;
        margin: 6.9rem auto 6.4rem;
      }
      button{
        width:12.2rem;
        height:4rem;
        background:linear-gradient(42deg,rgba(0,197,252,1),rgba(0,175,252,1));
        border-radius:2rem;
        border: none;
        outline:none;
        font-size: 1.6rem;
        color: #fff;
        margin-bottom: 1rem;
      }
      p{
        color: #999999;
        font-size: 1.2rem;
      }
    }
  }

}
</style>



