<template>
  <div class="outMoney_list">
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh"  ref="listHeight">
       <van-list v-model="loading" :finished="finished" finished-text="——没有更多了——" @load="onLoad" ref="li" >
      <ul>
      <li v-for="( item , index) in list" :key="index" @click="goAudit(item)">
        <img :src="item.logoImgUrl">
        <div>
          <span class="shopName">{{ item.name }}</span>
          <span class="shopTime">{{ item.createdAt }}</span>
          <span class="bank" v-if="item.moneyOutType == 1">银行卡</span>
          <span class="bank" v-if="item.moneyOutType == 101">微信</span>
          <span class="bank" v-if="item.moneyOutType == 201">支付宝</span>
          <span class="shopNum">{{ item.num/100 }}</span>
          
          <span class="category" v-if="item.state == 1">未审批</span>
          <span class="category" v-if="item.state == 101 ? item.moneyOutState == 101 :false">打款中</span>
          <span class="category" v-if="item.state == 101 ? item.moneyOutState == 201 :false">已打款</span>
          <span class="category" v-if=" item.state == 101 ? item.moneyOutState == 1 :false ">未打款</span>
          <span class="category" v-if="item.state == 201">已拒绝</span>
        </div>
      </li>
    </ul>
    </van-list>
    </van-pull-refresh>
  </div>
</template>
<script>
export default {
  data(){
    return{
      isLoading: false,
      loading: false,
      finished: false,
      page: 0,
      list: [],
    }
  },
  props:['shows'],
  methods:{
    // 下拉刷新
    onRefresh(){
      this.list = []
      this.page = 0
      this.onLoad()
      setTimeout(()=>{
        this.$toast('刷新成功')
        this.isLoading = false
        this.finished = false
      },2000)
    },
    // 获取商户提现列表 
    onLoad(){
      this.page++
      const url = this.apiList.admin.outMoneyList
      // 未审核
      if(this.shows == 2){
        var state = 1
      }
      this.$http({
        url: this.$http.adornUrl(url),
        method: "post",
        data: this.$http.adornData({ page: this.page, size: 10 ,state}, url, true)
      }).then(({ data : res })=>{
        console.log(res)
        if (!res.result) {
          console.log(res.msg)
        }
        if (res.data.records.length >= 0) {
          // 已审核  筛选数据
          if( this.shows == 1){
            const data = res.data.records
            for (var i = 0; i < data.length; i++){
              if(data[i].state == 1){
                data.splice(i,1)
              }
            }
            this.list = this.list.concat(data)
          }else{
            // 全部
            this.list = this.list.concat(res.data.records)
          }
        }
        if (res.data.pages <= this.page) {
          this.finished = true
        }
        this.$nextTick(() => {
          this.loading = false
        })
      })
    },
    // 获取商户提现列表
    getMoenyOutList(){
      // const url = this.apiList.admin.outMoneyList
      // this.$http({
      //   url: this.$http.adornUrl(url),
      //   method: "post",
      //   data: this.$http.adornData({ page: 1, size: 10 }, url, true)
      // }).then(({ data : res })=>{
      //   console.log(res)
      //   if (!res.result) {
      //     console.log(res.msg)
      //   }else{
      //     this.list = this.list.concat(res.data.records)
      //   }
      // })
    },
    // 审核页面
    goAudit(item){
      this.$router.push('manage_outMoeny_audit')
      localStorage.setItem('outMoenyInfo' , JSON.stringify(item))
    }
  },
  created(){
    this.getMoenyOutList()
  },
  mounted(){
    this.$refs.li.$el.style.minHeight = document.documentElement.clientHeight -  12.2 * 10 + 'px'
  }
}
</script>
<style lang="scss">
.outMoney_list{
  min-height: 100% ;
  ul{
    li{
      height: 7rem;
      width: 100%;
      display: flex;
      // background-color: aqua;
      >img{
        width: 3.5rem;
        height: 3.5rem;
        // background-color: pink;
        margin-top: 1.75rem;
        margin-left: 1.55rem;
        border-radius:50%;
      }
      >div{
        flex: 1;
        position: relative;
        margin-left: 1.35rem;
        border-bottom: 1px solid #eee;
        span{
          position: absolute;
        }
        .shopName{
          font-size: 1.4rem;
          color:rgba(51,51,51,1);
          top: 1.9rem;
        }
        .shopTime{
          font-size:1rem;
          color:rgba(153,153,153,1);
          top: 4.25rem;
        }
        .bank{
          height: 1.5rem;
          width: 3.8rem;
          background:rgba(255,251,238,1);
          border-radius:0.2rem;
          font-size:1rem; 
          color:rgba(187,174,133,1);
          text-align: center;
          line-height: 1.5rem;
          top: 4.1rem;
          left: 10.6rem;
        }
        .shopNum{
          top: 1.9rem;
          right: 1.9rem;
          font-size: 1.4rem;
          color:rgba(51,51,51,1);
        }
        .category{
          top: 4.05rem;
          right: 2rem;
          font-size:1.1rem;
          color:rgba(153,153,153,1);
        }
      }
    }
  }
}
</style>



