// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import '@/styles/index.scss'
import apiList from '@/utils/api'
import devClass from '@/utils/type'
import httpRequest from '@/utils/request'
import { timeChange } from '@/utils/timeChange.js'
import VueCookie from 'vue-cookie'
import androidApi from '@/utils/android'
import VueAwesomeSwiper from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.min.css'
import Mint from 'mint-ui'
import 'mint-ui/lib/style.css'
import BaiduMap from 'vue-baidu-map'
import VueCropper from 'vue-cropper'
import appJS from '@/utils/appJS'
import { DatetimePicker } from 'mint-ui'
import '@/utils/storage'
import { Tab, Tabs, List, Cell, Field, Popup, Picker, PullRefresh , Dialog, Switch, Swipe, SwipeItem, Rate  } from 'vant'
Vue.use(Tab).use(Tabs).use(List).use(Cell).use(Picker)
.use(Field ).use(Popup).use(PullRefresh).use(Dialog)
.use(Switch).use(Swipe).use(SwipeItem)
.use(Rate)
Vue.component(Switch.name, Switch);
// 使用权限
import { isAuth, getRole, clear } from '@/utils/isAuth'
// 初始化挂载
Vue.prototype.isAuth = isAuth
Vue.prototype.getRole = getRole
Vue.prototype.clear = clear



Vue.component(DatetimePicker.name, DatetimePicker)
Vue.use(Mint)
Vue.use(VueCropper)
Vue.prototype.android = androidApi
Vue.use(BaiduMap, {
  ak: '57aw6AtBaydVkb5N3k4zOSU8wV3OtbEO'
})

// 富文本控件
// import VueHtml5Editor from 'vue-html5-editor'
import VueHtml5Editor from '@/utils/vue-html5-editor.js'
const options = {
  name: "vue-html5-editor",
  showModuleName: true,
  language: "zh-cn",
  i18n: {
    //specify your language here
    "zh-cn": {
        "subscript": "下标",
        "superscript": "上标",
    }
},

  image: {
    sizeLimit: 1024 * 1024,
    upload: {
      url: apiList.baseUrl + apiList.upload_full,
      fieldName: 'file'
    },
    compress: {
      width: document.documentElement.offsetWidth - 20 / 750 *document.documentElement.offsetWidth*3.2 - 20,
      height: 355,
      quality: 100
    },
    // 上传返回处理
    uploadHandler(res) {
      const data = JSON.parse(res)
      return data.data
    }
  },
  // 自定义显示模块
  visibleModules: [
    'text',
    'color',
    'font',
    'align',
    'list',
    'link',
    'unlink',
    'tabulation',
    'image',
    'hr',
    'eraser',
    'undo'
  ]
}
Vue.use(VueHtml5Editor,options)
// 使用图片预览
import preview from 'vue-photo-preview'
import 'vue-photo-preview/dist/skin.css'

var previewoptions = {
  fullscreenEl:false, //关闭全屏按钮
  history: true
}
Vue.use(preview, previewoptions)
Vue.prototype.timeChange = timeChange
Vue.prototype.apiList = apiList
Vue.prototype.devClass = devClass
Vue.prototype.$http = httpRequest
Vue.use(VueCookie)
Vue.use(VueAwesomeSwiper)

appJS()
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>',
})



