<template>
  <div class="moneryChild">
    <div class="main">
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh" >
        <van-list 
          v-model="loading" 
          :finished="finished" 
          finished-text="——没有更多了——"
          @load="onLoad"
          ref="listHeight"
        >
          <van-cell v-for="(item, index) in dataList" :key="index" :class="{lastChild : dataList.length - 1 === index, radius: dataList.length === 1}">
            <img :src="imgType(item.type)" alt="">
            <span class="wrapper">
              <span class="top">
                <span>{{ item.logDesc }}</span>
              </span>
              <span class="bottom">
                <span>{{ item.createdAt}}</span>
              </span>
            </span>
            <span>{{ (item.type === 101 || item.type === 401 ? '-'  : '+') + item.num / 100}}</span>
          </van-cell>
        </van-list>
      </van-pull-refresh>
    </div>
  </div>
</template>
<script>
  export default {
    props:['show'],
    data(){
      return{
        page: 0,
        dataList: [],
        loading: false,
        finished:false,
        isLoading: false,
      }
    },
    methods:{
      /**上拉加载 */
      async onLoad(){
        this.page++
        const url = this.apiList.logModule.logModule
        let {data} = await this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({   page: this.page, size: 10}, url, true)
        })
        if (data.result) {
          if (data.data.records.length >= 0) {
            this.dataList = this.dataList.concat(data.data.records); 
          }
          if (data.data.pages <= this.page) {
            this.finished = true
          }
          this.loading = false;
        }else if( data.msg == 'sign验证失败' ){
          this.$toast('您的账户登录超时')
          setTimeout(()=>{
            localStorage.clear();
            this.$router.push({name: 'login'})
          },1000)
        }
      },
      /** 下拉刷新 */
      async onRefresh(){
        this.page = 1
        const url = this.apiList.logModule.logModule
        let {data} = await this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({ page: 1, size: 10}, url, true)
        })
          if (data.result) {
            if (data.data.records.length >= 0) {
              this.dataList = data.data.records
            }
            this.$nextTick(() => {
              this.$toast({
                message: '刷新成功'
              })
              this.isLoading = false
              this.finished = false
            })
          }else if( data.msg == 'sign验证失败' ){
            this.$toast('您的账户登录超时')
            setTimeout(()=>{
              localStorage.clear();
              this.$router.push({name: 'login'})
            },1000)
          }
      },
      imgType(type){
        switch(type){
          case 101:
            return require('../assets/my/withdrawal.png');
          case 301:
            return require('../assets/my/301.png');
          case 111:
            return require('../assets/my/111.png');
          case 401:
          return require('../assets/my/111.png');
        }
      }
    },
    mounted(){
      this.$refs.listHeight.$el.style.minHeight = document.documentElement.clientHeight   - 11.2 * 10 + 'px'
    },
    created(){
    }
    

  }
</script>
<style lang="scss">
  .moneryChild{
    width: 92%;
    margin: 0 auto;
    .main{
      .van-list {
        .van-cell{
          padding: 0;
          box-shadow:0 0.15rem 0.35rem 0 rgba(3,7,11,0.07);
          &:first-child{
            border-radius: 0.8rem 0.8rem 0 0;
          }
        }
        .lastChild{
          border-radius: 0 0 0.8rem 0.8rem;
        }
        .radius{
          border-radius: 0.8rem !important;
        }
        .van-cell__value{
          display: flex;
          align-items: center;
          margin: 0 1rem;
          height: 6.8rem;
          
          img{
            width: 3.2rem;
            height: 3.2rem;
          }
          .wrapper{
            margin-left: 1.4rem;
            flex: 1;
            .top, .bottom{
              display: flex;
              justify-content: space-between;
              
            }
            .top{
              padding-bottom: 0.2rem;
              color: #333;
              font-size: 1.4rem;
            }
            .bottom{
              padding-top: 0.2rem; 
              color: #999;
              font-size: 1.2rem;
            }

          }
        }
      }
    }
  }
</style>


