<template>
  <div class="detailList">
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh"  >
      <van-list v-model="loading" :finished="finished" finished-text="——没有更多了——" @load="onLoad"  ref="listHeight">
        <ul>
          <li v-for="(item,index) in list" :key="item.id">
            <div class="number">
              <p>订单号：{{item.outTradeNo}} <span>{{item.useState === 1 ? '待使用' : (item.useState === 101 ? '已提货': '已退款')}}</span></p>
            </div>
            <div class="detailList_userInfo">
              <div class="name"> <img :src="item.userAvatar?item.userAvatar:require('../../assets/my/userAvatar.png')" alt=""> {{item.userNickName?item.userNickName:'众享乐用户'}}</div>
              <div class="specific">
                <p>付款时间：{{item.createdAt}}</p>
                <p>金额：￥{{item.price/100}}</p>
                <p v-show="item.useState==101">提货时间：{{item.updatedAt}}</p>
              </div>             
              <div class="but"  v-show="item.useState==1" v-if="isAuth(apiList.businessRefund.refund)">
                <mt-button class="refund" @click="refund(item.orderId,index)" >办理退款</mt-button>
                <!-- <div class="takeshop">提货</div> -->
              </div>
            </div>
          </li>
        </ul>
      </van-list>
    </van-pull-refresh>
  </div>  
</template>
<script>
import { MessageBox } from 'mint-ui'
export default {
  data(){
    return{
      activityId: this.$route.params.activityId,
      url: this.$route.params.url,
      loading: false,
      finished: false,
      list:[],
      page:0,
      isLoading: false,
    }
  },
  props:['title'],
  created(){
    console.log(this.$route.params.url)
  },
  methods:{
    onLoad(){
      var useState;
      if(this.title === 0){
        useState =''
      }else if(this.title === 1){
        useState = 1
      }else if(this.title === 2){
        useState = 101
      }else{
        useState = 201
      }
      return new Promise((resolve, reject)=>{
        this.page++
        // const url = this.apiList.pmSale.orderList
         this.$http({
          url: this.$http.adornUrl(this.url),
          method: "post",
          data: this.$http.adornData({ activityId:this.activityId ,page: this.page, size: 10, useState: useState }, this.url, true)
        }).then(({data:res})=>{
          console.log(res)
          if (!res.result) {
              return console.log("请求接收失败")
            }
            if (res.data.records.length >= 0) {
                this.list = this.list.concat(res.data.records)
              // var arr = res.data.records
              // // 未提货 
              // if(this.title==1){
              //   for(var i=0 ; i<arr.length; i++){
              //     if(arr[i].useState==1){
              //       this.list = this.list.concat(arr[i])
              //     }
              //   }
              // }
              // // 已提货 
              // if(this.title==2){
              //   for(var i=0 ; i<arr.length; i++){
              //     if(arr[i].useState==101){
              //       this.list = this.list.concat(arr[i])
              //     }
              //   }
              // }
              // // 已退款 
              // if(this.title==3){
              //   for(var i=0 ; i<arr.length; i++){
              //     if(arr[i].useState==201){
              //       this.list = this.list.concat(arr[i])
              //     }
              //   }
              // }
            }
            if (res.data.pages <= this.page) {
              this.finished = true
            }
            this.$nextTick(() => {
              resolve(true)
              this.loading = false
           })
        })
      })
    },
    refund(id,index){
      let url = this.apiList.businessRefund.refund;
      let orderList = []
      MessageBox.confirm('确认退款？').then( ()=>{
        this.upload()
        this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({ orderId: id }, url, true)
        }).then(({data})=>{
          this.closeUpload()
          if(data.result){
            this.$toast({
              message: '退款成功',
              iconClass: 'fa fa-check fa-5x'
            })
            this.list[index].useState = 201
            this.orderList.push(this.list[index])
            localStorage.setItem('orderList', JSON.stringify(orderList) )
            if(this.title === 1){
              this.list.splice(index, 1)
            }
            
          }else if(data.msg == 'sign验证失败'){
            this.$toast('您的账户登录超时')
            setTimeout(()=>{
              localStorage.clear()
              this.$router.push({name: 'login'})
            },1000)
          }
        })
      })
    },
    onRefresh(){
      var useState;
      if(this.title === 0){
        useState =''
      }else if(this.title === 1){
        useState = 1
      }else if(this.title === 2){
        useState = 101
      }else{
        useState = 201
      }
      return new Promise((resolve, reject)=>{
        // const url = this.apiList.pmSale.orderList
        this.list =[]
         this.$http({
          url: this.$http.adornUrl(this.url),
          method: "post",
          data: this.$http.adornData({ activityId:this.activityId ,page: 1, size: 10, useState: useState}, this.url, true)
        }).then(({data:res})=>{
          if (!res.result) {
              return console.log("请求接收失败")
            }
            if (res.data.records.length >= 0) {
                this.list = res.data.records
            //   var arr = res.data.records
            //   // 未提货 
            //   if(this.title==1){
            //     for(var i=0 ; i<arr.length; i++){
            //       if(arr[i].useState==1){
            //         this.list = this.list.concat(arr[i])
            //       }
            //     }
            //   }
            //   // 已提货 
            //   if(this.title==2){
            //     for(var i=0 ; i<arr.length; i++){
            //       if(arr[i].useState==101){
            //         this.list = this.list.concat(arr[i])
            //       }
            //     }
            //   }
            //   // 已退款 
            //   if(this.title==3){
            //     for(var i=0 ; i<arr.length; i++){
            //       if(arr[i].useState==201){
            //         this.list = this.list.concat(arr[i])
            //       }
            //     }
            //   }
            }
            this.$nextTick(() => {
              this.$toast('刷新成功')
              this.isLoading = false
              this.page = 1 
              this.finished = false
              resolve(true)
           })
        })
      })
    },
    change(){
      // this.list = [];
      // this.page = 0
      // this.loading = false;
      // this.finished = false;
      // this.onLoad()
      // if(this.title === 1){
      // }else if(this.title === 2){

      // }else if(this.title === 3){
      //   let orderList = JSON.parse(localStorage.getItem('orderList'))
      //   if(orderList){
      //     this.list.forEach((item, index)=>{
      //       orderList.forEach((items, i)=>{
      //         if(item.orderId=== items.orderId){

      //         }else{

      //         }
      //       })
      //     })
      //   }
      // }
    }
  },
  mounted(){
    this.$refs.listHeight.$el.style.minHeight = document.documentElement.clientHeight -  11.2 * 10 + 'px'
    
  }
}
</script>
<style lang="scss" >
  .detailList{
    // min-height: 100%;
    // margin-top: 4.2rem;
    ul{
      overflow: hidden;
    }
    ul>li{
      width:34.3rem;
      // height:21.4rem;
      background-color: #fff;
      border-radius:0.8rem;
      margin: 0 auto;
      margin-top: 1.65rem;
      overflow: hidden;
      .number{
        width: 100%;
        height: 5rem;
        background-image: url("../../assets/merchant/dingdan.png");
        background-size: 100% 100%;
        background-color: #eee;
        box-sizing: border-box;
        padding-top: 1.15rem;
        p{
          font-size: 1.3rem;
          color: #333;
          // margin-top: 1.1rem;
          padding-left: 1.7rem;
          box-sizing: border-box;
          span{
            margin-left: 8rem;
            color: #F64542;
          }
        }
      }
      .detailList_userInfo{
        // height: 16.4rem;
        width: 100%;
        border: 0;
        .name{
          height: 2rem;
          width: 100%;
          font-size: 1.1rem;
          line-height: 2rem;
          img{
            display: inline-block;
            width: 2rem;
            height: 2rem;
            vertical-align: middle;
            margin-left: 1.7rem;
            margin-right: 0.5rem;
            border-radius: 50%;
          }
        }
        .specific{
          margin-top: 1.2rem;
          padding-left: 2.25rem;
          box-sizing: border-box;
          p{
            font-size: 1.4rem;
            margin-bottom: 1.15rem;
            color: #666;
          }
        }
        .but{
          height: 2.7rem;
          width: 100%;
          // margin-top: 2.3rem;
          position: relative;
          margin-bottom: 1rem;
          
          >button{
            float: right;
            width: 8.3rem;
            height: 2.7rem;
            margin-right: 1rem;
            border-radius: 1.35rem;
            background: #fff;
            color: #333;
            font-size: 1.2rem;
          }
          :active {
            background-color: #eee;
          }
          .takeshop{
            position: absolute;
            margin-left: 18.75rem;
            top: 0
          }
        }
      }
    }
  }
</style>



