<template>
  <div class="index_refund">
    <div class="header">
      <span class="back" @click="goBack"><img src="../../assets/my/back.png" alt=""  ></span>
      <span>退款列表</span>
    </div>
    <div class="index_refund_main">
      <van-tabs v-model="active" :sticky='true' swipeable animated color="#fff" :offset-top="48.5">
        <van-tab title="全部">
          <indexChild :show="0" @refuse="refuse"></indexChild>
        </van-tab>
        <van-tab title="待审核">
          <indexChild :show="1" @refuse="refuse" ref="mychild"></indexChild>
        </van-tab>
        <van-tab title="已拒绝">
          <indexChild :show="2"></indexChild>
        </van-tab>
        <van-tab title="已退款">
          <indexChild :show="3"></indexChild>
        </van-tab>
      </van-tabs>
    </div>
    <div class="refuse" v-if="showRefuse">
      <div class="wrapper">
        <p>拒绝理由</p>
        <div class="text">
          <textarea v-model="refuseRemark" placeholder="请填写拒绝理由（必填）"></textarea>
        </div>
        <mt-button @click="showRefuse = false">取消</mt-button><mt-button @click="refuse">确定</mt-button>
      </div>
    </div>
  </div>
</template>

<script>
import { MessageBox } from 'mint-ui'
import indexChild from './indexChild'
  export default {
    data(){
      return{
        active:0,
        id: '',
        index: '',
        refuseRemark: '',
        showRefuse: false,
      }
    },
    methods:{
      goBack(){
        this.$router.goback()
      },
      refuse(e,id,index){
         if(!this.isAuth('/moneyApproval/dealOrderRefund')) return this.$toast('权限不足，请联系管理员修改')
        if(e == false){
          this.id = id
          this.index = index
          return this.showRefuse = true
        }else if(this.refuseRemark == ''){
          return this.$toast({
            message: '拒绝理由不能为空',
            position: 'bottom',
            className: 'toast'
          })
        }
        const url = this.apiList.refund.apply
        this.upload()
        this.$http({
          url: this.$http.adornUrl(url),
          method: 'post',
          data: this.$http.adornData({approvalId: this.id, agree: false, refuseRemark: this.refuseRemark,  }, url, true)
        }).then(({data})=>{
          this.closeUpload()
            if(data.result){
              this.showRefuse = false;
              this.$toast({
                message: '已拒绝',
                iconClass: 'fa fa-check fa-5x'
              })
              this.$refs.mychild.dataList.splice(this.index, 1)
            }
        })
      },
    },
    components:{
      indexChild
    }
  }
</script>
<style lang="scss">
  .index_refund{
    min-height: 100%;
    background: #f5f5f5;
    .van-tabs__wrap{
      margin-top: -0.8rem;
    }
    // .index_refund_main{
    //   .van-tabs{
    //     height: 100%;
    //     .van-tabs__content{
    //       height: 100%;
    //     }
    //   }
    // }
    .refuse{
      position: fixed;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background: rgba(0,0,0,0.4);
      z-index: 99998;
      .wrapper{
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 10;
        transform: translate(-50%, -50%);
        background: #fff;
        width: 25rem;
        height: 20rem;
        p{
          text-align: center;
          padding: 1.2rem 0;
        }
        .text{
          width: 80%;
          height: 11rem;
          border-radius: 1.1rem;
          border: 0.1rem solid #ededed;
          margin: auto;
          outline: none;
        }
        textarea{
          width: 94%;
          height: 90%;
          margin: 0.5rem 0.8rem 0.5rem;
          color: #333;
          font-size: 1.3rem;
          border: none;
          resize: none;
        }
        button{
          float: right;
          margin: 1rem 0;
          min-width: 5.3rem;
          height: 2.7rem;
          outline: none;
          background-color: none;
          border-radius: 1.35rem;
          border: 0 solid #666;
          color: #333;
          background-color: #fff;
          margin-right: .9rem;
          font-size: 1rem;
        }
      }
    }
  }
</style>


