<template>
  <div class="activityNumList">
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh"  ref="listHeight">
      <van-list v-model="loading" :finished="finished" finished-text="——没有更多了——" @load="onLoad" ref="li" >
          <div class="myActivityList" v-for="item in list" :key="item.id" @click="goDetail(item.activityId)">
              <div class="left">
                  <img
                      class="show"
                      :src="item.imgUrlSquare"
                      alt=""
                  >
                  <img class="tag" v-if="shows===0" src="../../assets/merchant/sell.png" alt="">
                  <img class="tag" v-if="shows===1" src="../../assets/merchant/group.png" alt="">
                  <img class="tag" v-if="shows===2" src="../../assets/merchant/enter.png" alt="">
                  <img class="tag" v-if="shows===3" src="../../assets/merchant/play.png" alt="">
                  <img class="tag" v-if="shows===4" src="../../assets/merchant/shop.png" alt="">
                  <!-- <div class="sort">教育</div> -->
                  <div class="time"><count :endTime='item.endTime'></count></div>
              </div>
              <div class="myActivityList-content">
                  <h5>{{item.name}}</h5>
                  <span>{{shopName}}</span>
                  <div class="plan">
                      <div class="main-progress">
                          <div class="main-progress-number">已抢{{item.alreadyNum}}件</div>
                          <div class="main-progress-gress" :style="barStyle(parseFloat(item.alreadyNum / item.totalNum * 100).toFixed(1))"></div>
                          <div class="main-progress-percent">
                              <p>{{  item.totalNum ? parseFloat(item.alreadyNum / item.totalNum * 100).toFixed(1) : 0.0 }}%</p>
                          </div>
                      </div>
                      <span>共{{item.totalNum}}件</span>
                  </div>
                  <div class="price">
                      <b>￥</b>
                      <span>{{item.price/100}}</span>
                      <del>￥{{item.showPrice/100}}</del>
                      <mt-button  @click.stop="goComment(item.activityId, item.type)" v-if="isAuth('/zComment/findComment')">查看评论</mt-button>
                  </div>
                  <div class="location">
                      <!-- <img src="../../assets/merchant/location.png" alt=""> -->
                      <!-- <span>距离我1.5km</span> -->
                  </div>
              </div>
          </div>
      </van-list>
    </van-pull-refresh>
  </div>
</template>
<script>
import count from "./count"
export default {
  data() {
    return {
      isLoading: false,
      loading: false,
      finished: false,
      page: 0,
      list: [],
      timeDif: "",
      shopName:''
    }
  },
  props: ["shows"],
  created() {
    this.shopName = localStorage.getItem('shopName')
  },
  methods: {
    onLoad() {
      return new Promise((resolve, reject) => {
        this.page++
        // 促销
        let url
        if( this.shows == 0){
           url = this.apiList.pmSale.list
        }
        // 拼团
        if(this.shows == 1){
          this.finished = true
          this.loading = false
          return
        }
        // 报名
        if(this.shows == 2){
          url = this.apiList.enterName.list
        }
        // 活动
        if(this.shows == 3){
          url = this.apiList.addActivity.list
        }
        // 商品
        if(this.shows == 4){
          url = this.apiList.commodity.list
        }
        this.$http({
          url: this.$http.adornUrl(url),
          method: "post",
          data: this.$http.adornData({ page: this.page, size: 10 }, url, true)
        }).then(({ data: res }) => {
            if (!res.result) {
              return console.log("请求接收失败")
            }
            if (res.data.records.length >= 0) {
              this.list = this.list.concat(res.data.records);
            }
            if (res.data.pages <= this.page) {
              this.finished = true
            }
            this.$nextTick(() => {
              resolve(true)
              this.loading = false
            })
          })
      })
    },
    goComment(id, type){
      this.$router.push({name:'comment_index', query: {id: id, type: type}})
    },
    barStyle(percentage) {
      const style = {}
      style.width = percentage + "%"
      return style
    },
    goDetail(id){
      let url
      if(this.shows==0){
        url = this.apiList.pmSale.orderList
        if(!this.isAuth(url)){
          return this.$toast("权限不足，请联系管理员修改")
        }
      }
      // 拼团
      if(this.shows==1){
        url = ''
      }
      if(this.shows==2){
        url = this.apiList.enterName.orderList
        if(!this.isAuth(url)){
          return this.$toast("权限不足，请联系管理员修改")
        }
      }
      if(this.shows==3){
        url = this.apiList.addActivity.orderList
        if(!this.isAuth(url)){
          return this.$toast("权限不足，请联系管理员修改")
        }
      }
      if(this.shows==4){
        url = this.apiList.commodity.orderList
        if(!this.isAuth(url)){
          return this.$toast("权限不足，请联系管理员修改")
        }
      }
      this.$router.push({name:'detail',params:{'activityId':id,url,'shows':this.shows}})
      // this.$router.push({path:`/detail${id}`})
    },
    onRefresh(){
      return new Promise((resolve, reject) => {
        // 促销
        let url
        if( this.shows == 0){
           url = this.apiList.pmSale.list
        }
        // 拼团
        if(this.shows == 1){
          this.finished = true
          this.loading = false
          this.isLoading = false;
          return
        }
        // 报名
        if(this.shows == 2){
          url = this.apiList.enterName.list
        }
        // 活动
        if(this.shows == 3){
          url = this.apiList.addActivity.list
        }
        // 活动
        if(this.shows == 4){
          url = this.apiList.commodity.list
        }
        this.$http({
          url: this.$http.adornUrl(url),
          method: "post",
          data: this.$http.adornData({ page: 1, size: 10 }, url, true)
        }).then(({ data: res }) => {
            console.log(res)
            if (!res.result) {
              return console.log("请求接收失败")
            }
            if (res.data.records.length >= 0) {
              this.list = res.data.records
            }
            this.$nextTick(() => {
              this.$toast('刷新成功')
              this.isLoading = false
              this.page = 1 
              this.finished = false
              resolve(true)
            })
          })
      })
    },
    
  },
  components:{
    count
  },
  mounted(){
    console.log(this.$refs)
    this.$refs.li.$el.style.minHeight = document.documentElement.clientHeight -  12.2 * 10 + 'px'
    // this.$refs.li.$el.style.background = 'green'
  },
  filters: {
    time: value => {
      console.log(value)
    }
  }
};
</script>
<style lang="scss">
.activityNumList {
  // min-height: 60rem;
  min-height: 100% ;
  // position: relative;
  .myActivityList {
    width: 34.3rem;
    height: 12.7rem;
    border-radius: 0.8rem;
    background-color: #fff;
    margin: 0 auto;
    margin-top: 1.6rem;
    display: flex;
    > .left {
      width: 12.2rem;
      height: 12.7rem;
      position: relative;
      > .show {
        width: 12.2rem;
        height: 12.7rem;
        background: rgba(237, 205, 205, 1);
        border-radius: 0.8rem 0 0 0.8rem;
      }
      > .tag {
        position: absolute;
        width: 3.2rem;
        height: 3.2rem;
        top: 0;
      }
      > .sort {
        position: absolute;
        width: 3.7rem;
        height: 1.8rem;
        background: linear-gradient(
          -39deg,
          rgba(129, 212, 250, 1),
          rgba(66, 165, 245, 1)
        );
        opacity: 0.9;
        border-radius: 0.9rem;
        right: 0.5rem;
        top: 0.5rem;
        color: #fff;
        font-size: 1.1rem;
        text-align: center;
        line-height: 1.8rem;
        font-weight: 400;
      }
      .time {
        position: absolute;
        width: 12.2rem;
        bottom: 0;
        height: 1.5rem;
        background: rgba(0, 0, 0, 1);
        opacity: 0.6;
        border-radius: 0px 0px 0px 0.8rem;
        font-size: 1rem;
        color: #fff;
        text-align: center;
        line-height: 1.5rem;
      }
    }
    .myActivityList-content {
      flex: 1;
      padding-left: 1.2rem;
      padding-top: 1.2rem;
      h5 {
        width: 19.35rem;
        font-size: 1.4rem;
        font-weight: 400;
        color: rgba(51, 51, 51, 1);
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        line-height: 1.8rem;
        margin-bottom: 0.6rem;
      }
      span {
        font-size: 1.1rem;
        color: rgba(153, 153, 153, 1);
      }
      .plan {
        margin-top: 0.9rem;
        display: flex;
        .main-progress {
          width: 12rem;
          height: 1.3rem;
          //line-height: 1.3rem;
          background: #d7d7d7;
          font-size: 0.9rem;
          border-radius: 0.7rem;
          position: relative;
          overflow: hidden;
          // margin-top: 2rem;
          .main-progress-number {
            color: #fff;
            position: absolute;
            left: 0.6rem;
            // top: .1rem; //手机显示正常  电脑会下沉一些
            // top: 50%;
            // transform: translateY(-50%);
            line-height: 1.45rem;
            height: 1.3rem;
            z-index: 4;
            font-size: 1rem;
          }
          .main-progress-percent {
            color: #666666;
            position: absolute;
            right: 0.5rem;
            top: 3px;
            z-index: 4;
            p {
              font-size: 1rem;
            }
          }
          .main-progress-gress {
            background: #fc993c;
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            border-radius: 0.7rem;
            z-index: 3;
          }
        }
        span {
          margin-left: 0.5rem;
        }
      }
      .price {
        position: relative;
        margin-top: 2.1rem;
        font-size: 1.2rem;
        color: #999999;
        b {
          font-weight: normal;
          color: #fa2d4f;
        }
        span {
          font-size: 1.8rem;
          color: #fa2d4f;
          font-weight: bold;
        }
        button{
          position: absolute;
          right: 0;
          top: -0.2rem;
          min-width: 6.3rem;
          height: 2.7rem;
          outline: none;
          background-color: none;
          border-radius: 1.35rem;
          color: #333;
          background-color: #fff;
          margin-right: .9rem;
          font-size: 1.2rem;
        }
      }
      .location {
        display: flex;
        justify-content: flex-end;
        img {
          width: 0.8rem;
          height: 1rem;
        }
        span {
          color: #666666;
          font-size: 1rem;
          margin-right: 0.85rem;
          margin-left: 0.5rem;
        }
      }
    }
  }
}
</style>




